import R14 from "../core";
import { PAM_CREATION, GET_BLOCK_UNBLOCK_USERS } from "../constant/app.constant";
import { MSG_TYPE } from "../constant/app.data.constant";

export default class CommonQueryDomain extends R14.Domain {
  async getBiller(opt, fields = "uid billerId billerName isBlocked payerId billerStatus") {
    let options = { ...opt };
    try {
      let res = await this.api.qry(
        `
        query Biller($filter: BillerFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
          billers(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
            nodes {
              ${fields}
            }
          }
        }`,
        options
      );
      return res.data.billers.nodes;
    } catch (error) {
      return error;
    }
  }

  async addBiller(input, fields = "uid billerId isBlocked billerStatus payerId") {
    try {
      let res = await this.api.mutate(
        `
      mutation CreateBiller($input: CreateBillerInput) {
        createBiller(input: $input){
          biller {
            ${fields}
          }
          success
        }
      }`,
        { input }
      );
      return res.data.createBiller.biller;
    } catch (error) {
      return error;
    }
  }

  async updateBiller(input, fields = "uid billerId isBlocked billerStatus") {
    try {
      let res = await this.api.mutate(
        `
      mutation UpdateBiller($input: UpdateBillerInput) {
        updateBiller(input: $input){
          biller {
            ${fields}
          }
          success
        }
      }`,
        { input }
      );
      return res.data.updateBiller.biller;
    } catch (error) {
      return error;
    }
  }

  async getBlockedUsers() {
    try {
      return await this.dm.rest.get(`${GET_BLOCK_UNBLOCK_USERS}`);
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }

  getBillerDirectoryPayload(data) {
    return {
      billerId: data.billerId,
      billerName: data.billerName,
      billerStatus: data.status,
      isBlocked: data.isBlocked,
      payerId: data.payerId,
    };
  }

  async moreInfoMessages(pamId) {
    try {
      return await this.dm.rest.get(`${PAM_CREATION}/${pamId}`, { pamId });
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }

  async addUpdateBiller(data) {
    let hasBlocked = !!data.blockedBiller && !!data.blockedBiller.length ? data.blockedBiller.find((el) => el.pid === data.billerId) : false; //has biller blocked;
    let payload = this.getBillerDirectoryPayload({ ...data, isBlocked: !!hasBlocked ? true : false });
    payload.billerStatus = data.status;
    if (data.moreInfos) {
      if (data.status === "requested" && data.moreInfos.length === 0) {
        payload.billerStatus = "requested";
      } else if (data.status === "requested" && data.moreInfos.length > 0) {
        payload.billerStatus = "inprogress";
      } else if (data.status === "accepted") {
        payload.billerStatus = "accepted";
      } else if (data.status === "denied") {
        payload.billerStatus = "denied";
      } else {
        payload.billerStatus = "new";
      }
    }

    if (!!data.biller && !!data.biller.uid) {
      return await this.updateBiller({ ...payload, uid: data.biller.uid });
    } else {
      return await this.addBiller(payload);
    }
  }
}
