import  { AsyncStorage } from "../core";

export default class AsyncStorageUtils {

    static SetDataAsync = async (key, data) => {
        let response = false;
        try {
            await AsyncStorage.setItem(
                key,
                data
            );
            response = true;
        } catch (error) {
            console.log("Error in saving AsyncStorage", error);
            response = false
        }
        return response;
    };

    static GetDataAsync = async (key) => {
        let response = null;
        try {
            const value = await AsyncStorage.getItem(key);
            if (value !== null) {
                response = JSON.parse(value);
            }
            else
                response = null;
        } catch (error) {
            console.log("Error in getting AsyncStorage", error);
            response = null
        }
        return response;
    };
    
    static GetProfileDataAsync = async () => {
        try {
            return await this.GetDataAsync("profiles");
        } catch (error) {
            console.log("Error in getting profiles", error);
            return null;
        }        
    };

}
