import React from "react";
import R14, { StyleSheet, ScreenLayout, Text, View, Touchable, Image, AsyncStorage, ScrollView } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { NOTIFICATION_OPT } from "../constant/app.data.constant";
import DateUtils from "../utils/DateUtils";
import NoRecordsFound from "../components/NoRecordsFound";
import LeftMenuComponent from "../components/LeftMenuComponent";

export default R14.connect(
  class NotificationScreens extends React.Component {
    constructor(props) {
      super(props);
      this.state = {};
    }

    onNavigate = async (type, items) => {
      let values = items.map((data, i) => {
        return {
          uid: data.uid,
          payerId: data.payerId,
        };
      });
      this.props.app.dm.notificationDomain.updateNotification(values);
      if (items.length > 1)
        this.props.app.dm.notificationDomain.setState({
          notificationThreadIds: items.map((data, i) => {
            return data.threadId;
          }),
        });

      if (type === "simpleMessage") {
        //make API call
        await this.props.app.dm.simpleMessage.displaySimpleMessage();
        if (items.length > 1) {
          //listing
          this.props.app.nav.to("simpleMessageRoute");
        } else {
          //details
          let newThreadId = items[0].threadId.replaceAll("#", ">>");
          this.props.app.nav.to("simpleMessageDetailRoute", { threadId: newThreadId });
        }
      } else if (type === "PAM") {
        //make API call
        await this.props.app.dm.billerDirectoryDomain.displayBillerDirectoryData();
        if (items.length > 1) {
          //listing
          this.props.app.nav.to("billersDirectoryRoute");
        } else {
          //details
          let newThreadId = items[0].threadId.replaceAll("#", ">>");
          this.props.app.nav.to("pamMessageRoute", { pamId: newThreadId });
        }
      } else {
        //make API call
        await this.props.app.dm.dashboard.setCompiledMsgList();
        if (items.length > 1) {
          //listing
          this.props.app.nav.to("makePaymentRoutes");
        } else {
          //details
          let newThreadId = items[0].threadId.replaceAll("#", ">>");
          const { compiledMsgList } = this.props.app.dm.dashboard.state;
          if (!!compiledMsgList.length) {
            let item = compiledMsgList.filter((message) => message.threadId === items[0].threadId);
            if (!!item.length) {
              AsyncStorage.setItem("billDetails", JSON.stringify(item[0]));
              this.props.app.nav.to("billDetailsRoute", { threadId: newThreadId });
            }
          }
        }
      }
    };

    render() {
      const { notifications } = this.props.app.dm.notificationDomain.state;
      const notificationGrp = [...new Set(notifications.map(({ subMessageType }) => subMessageType))];
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      return (
        <ScreenLayout scrollEnabled style={styles.bankAccountScreen}>
          <ScrollView style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu]}>
            <View style={[commonStyles.marginTopBottom15, commonStyles.paddingLeftRight16]}>
              <Text style={[commonStyles.heading5, commonFontStyles.workSans, commonStyles.fontWeight500]}>{this.props.app.nav.route.label} </Text>
            </View>
            {notifications.length === 0 ? (
              <View style={styles.padd10}>
                <NoRecordsFound message="No Notifications to Display" />
              </View>
            ) : (
              notificationGrp.map((ng, i) => {
                let noti = NOTIFICATION_OPT.filter((x) => x.value === ng);
                if (noti.length > 0) {
                  let gl = noti[0];
                  let messages = notifications.filter((x) => x.subMessageType === gl.value);
                  let count = messages.filter((x) => x.isRead === false).length;

                  return (
                    <View key={i} style={[commonStyles.dFlex, styles.notificationList, commonStyles.marginTop10]}>
                      {messages.length > 1 ? (
                        <>
                          <View style={[commonStyles.marginRight15, commonStyles.notificationHolder]}>
                            {/* <Image source={require("../assets/images/" + gl.img)} style={[commonStyles.icon35]} /> */}
                            {count === 0 ? null : (
                              <View style={[commonStyles.notificationCountPerMsg]}>
                                <Text style={[commonStyles.chatTiming, commonStyles.whiteColor, commonFontStyles.workSans]}>{count}</Text>
                              </View>
                            )}
                          </View>
                          <Touchable onPress={() => this.onNavigate(gl.value, messages)}>
                            <View>
                              <View>
                                <Text style={[commonStyles.heading6, commonFontStyles.workSans, commonStyles.fontWeight500]}>{gl.label}</Text>
                              </View>
                              <View>
                                <Text style={[commonStyles.smallHeading, commonFontStyles.workSans, commonStyles.fontWeight500]}>
                                  {gl.comment.replace("$", messages.length)}{" "}
                                </Text>
                              </View>

                              <View>
                                <Text
                                  style={[
                                    commonStyles.exSmallHeading,
                                    commonStyles.lightGray,
                                    commonFontStyles.workSans,
                                    commonStyles.fontWeight500,
                                  ]}>
                                  {DateUtils.dateTodayYesterday(
                                    messages.sort((a, b) => new Date(b.messageDateTime) - new Date(a.messageDateTime))[0].messageDateTime
                                  )}
                                </Text>
                              </View>
                            </View>
                          </Touchable>
                        </>
                      ) : (
                        <>
                          <View style={[commonStyles.marginRight15, commonStyles.notificationHolder]}>
                            {/* <Image source={require("../assets/images/" + gl.img)} style={[commonStyles.icon35]} /> */}
                            {count === 0 ? null : (
                              <View style={[commonStyles.notificationCountPerMsg]}>
                                <Text style={[commonStyles.chatTiming, commonStyles.whiteColor, commonFontStyles.workSans]}>{count}</Text>
                              </View>
                            )}
                          </View>
                          <Touchable onPress={() => this.onNavigate(gl.value, messages)}>
                            <View>
                              <View>
                                <Text style={[commonStyles.heading6, commonFontStyles.workSans, commonStyles.fontWeight500]}>{gl.label}</Text>
                              </View>
                              <View>
                                <Text style={[commonStyles.smallHeading, commonFontStyles.workSans, commonStyles.fontWeight500]}>
                                  {messages[0].note}
                                </Text>
                              </View>
                              <View>
                                <Text
                                  style={[
                                    commonStyles.exSmallHeading,
                                    commonStyles.lightGray,
                                    commonFontStyles.workSans,
                                    commonStyles.fontWeight500,
                                  ]}>
                                  {DateUtils.dateTodayYesterday(messages[0].messageDateTime)}
                                </Text>
                              </View>
                            </View>
                          </Touchable>
                        </>
                      )}
                    </View>
                  );
                }
              })
            )}
          </ScrollView>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  container: {
    background: "#fff",
    display: "flex",
    flexGrow: 1,
    flexDirection: "column",
  },
  notificationList: {
    padding: "10px 16px",
    borderBottom: "1px solid #DADADA",
  },
});
