import React, { PureComponent } from "react";
import { StyleSheet, View, Text, Touchable, Image } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { STATUS_CLR } from "../constant/app.data.constant";
let imgpath = require("../assets/images/bookmared_active.png");
export default class StatusComponent extends PureComponent {
  render() {
    const {
      label,
      toggleStatus,
      isSelected,
      hasClose,
      onCrossClick,
      marginRight,
      marginBottom,
      paymentStatusLabel,
      circleNotAllowed,
      isBackground,
      fontWeight,
    } = this.props;
    let imgpath = require("../assets/images/bookmared_active.png");
    let statusLabel = !!paymentStatusLabel ? paymentStatusLabel : label;
    //label === "Paid" ? ( !paymentStatusLabel ? label: paymentStatusLabel ) : label
    return (
      <Touchable
        style={[
          styles.margingBtm,
          commonStyles.marginRight10,
          commonStyles.filterChips,
          commonStyles.dInlineFlex,
          commonStyles.alignItemsCenter,
          isSelected ? styles[STATUS_CLR[statusLabel]] : null,
          marginRight,
          marginBottom,
          !!circleNotAllowed ? styles.paddwithoutcircle : null,
          isBackground,
        ]}
        onPress={toggleStatus}>
        {!!circleNotAllowed ? null : label === "Priority" ? (
          <Image source={imgpath} resizeMode="contain" style={[styles.bookedMark]} />
        ) : //(
        //   <Text
        //     style={[commonStyles.statusColor, styles[STATUS_CLR[statusLabel]], isSelected ? styles.bgCls : null, commonStyles.marginRight5]}></Text>
        // )
        null}
        <Text
          style={[
            commonStyles.smallHeading,
            commonStyles.fontWeight500,
            commonFontStyles.workSans,
            commonStyles.transformUpperCase,
            isSelected ? styles.activeCls : null,
            fontWeight,
          ]}>
          {statusLabel}
        </Text>
        {hasClose ? (
          <Touchable onPress={onCrossClick} style={styles.closeIcon}>
            <Text style={[commonStyles.heading6, styles.activeCls]}>&#10006;</Text>
          </Touchable>
        ) : null}
      </Touchable>
    );
  }
}

const styles = StyleSheet.create({
  NW: {
    background: "#7accf6",
  },
  PD: {
    background: "#008000",
  },
  DL: {
    background: "#DC143C",
  },
  OD: {
    background: "#EE82EE",
  },
  OS: {
    background: "#FFA500",
  },
  PB: {
    background: "#3f00ff",
  },
  IP: {
    background: "#5286ff",
  },
  FD: {
    background: "#b40220",
  },
  AD: {
    background: "#218666",
  },
  RD: {
    background: "#914518",
  },
  RQD: {
    background: "#b43fee",
  },
  activeCls: {
    color: "#FFFFFF",
  },
  bgCls: {
    background: "#FFFFFF",
  },
  margingBtm: {
    ...StyleSheet.margin(0, 0, 10, 0),
  },
  closeIcon: {
    margin: "0px -3px 0 10px",
  },
  bookedMark: {
    height: 12,
    width: 15,
  },
  paddwithoutcircle: {
    padding: "2px 10px",
  },
  bookedMark: {
    height: 12,
    width: 15,
    backgroundSize: "contain",
    display: "block",
  },
});
