import React from "react";
import R14, {
  Redirect,
  TextInputField,
  SubmitButton,
  StyleSheet,
  Button,
  Colors,
  ScreenLayout,
  Form,
  Image,
  Checkbox,
  Text,
  View,
  Touchable,
  Surface,
} from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import CommonUtility from "../utils/CommonUtility";
const xbpLogo = require("../assets/images/XBPlogo.svg");
const back_icon = require("../assets/images/back_icon.png");
const mainImage = require("../assets/images/mainImage.png");

export default R14.connect(
  class ForgotPasswordScreen extends React.Component {
    constructor(props) {
      super(props);
    }
    handleSubmit = async (loginForm) => {
      let password = CommonUtility.randomPassword();
      let arrUserEmailExists = await this.props.app.dm.userRegistration.bUserExistsByEmail(loginForm.values.email.toLowerCase().trim());
      if (arrUserEmailExists.length > 0) {
        await this.props.app.dm.userLoginDomain.forgetPasswordUpdate(arrUserEmailExists[0].uid, password);

        let userInputBody = {
          payerId: arrUserEmailExists[0].payerId,
          password: password + "///ForgotPassword",
          email: loginForm.values.email,
        };
        let MailRes = await this.props.app.dm.userRegistration.sendCredentialsByMail(userInputBody);
        if (!!MailRes) {
          this.props.app.dm.commonDomain.showToastr("success", "Login credential has been sent to your email");
          this.props.app.ui.progressIndicator.hide({ timeout: 750 });
          this.props.app.nav.to("pchLogin");
          return false;
        }
      } else {
        this.props.app.dm.commonDomain.showToastr("error", "Email does not exists.");
        this.props.app.ui.progressIndicator.hide({ timeout: 750 });
        return false;
      }
    };
    navPchLogin = () => {
      // let returnUrl = await AsyncStorage.getItem("returnUrl")
      let returnUrl = this.props.app.route._location.search;
      if (!!returnUrl && returnUrl.length > 0) document.location = `${window.location.origin}/pchlite/login${returnUrl}`;
      else this.props.app.nav.to("pchLogin");
    };

    render() {
      return (
        <ScreenLayout scrollEnabled>
          <View style={styles.mainLogin}>
            <View style={[styles.bgContainer, styles.leftSection]}>
              <Image source={mainImage} style={[styles.backgroundImage]} />
            </View>

            <Surface style={styles.rightSection}>
              <View style={[styles.loginHeader]}>
                <Touchable onPress={this.navPchLogin}>
                  <Image resizeMode="contain" style={[commonStyles.icon24]} source={back_icon} />
                </Touchable>
                <View style={[styles.logoImageContainer]}>
                  <Image resizeMode="contain" style={[styles.logoImage]} source={xbpLogo} />
                </View>
              </View>
              <View style={[styles.loginFormContainer]}>
                <Form
                  name="loginForm"
                  onSubmit={this.handleSubmit}
                  validateBeforeSubmit
                  controlsBottomRight={[
                    <View style={styles.loginControls} key="viewOne">
                      <SubmitButton
                        title="Submit"
                        key="submit"
                        titleColor="#fff"
                        style={[styles.loginButton]}
                        titleTextStyle={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.transformNone, styles.loginButtonTextStyle]}
                      />
                    </View>,
                  ]}>
                  <TextInputField
                    name="email"
                    label="Email ID"
                    // icon="account"
                    required="Enter Email"
                    validator="email"
                    style={[styles.emailField, commonFontStyles.workSans]}
                    labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading5]}
                    labelStyle={[commonStyles.formLabelStyle]}
                    // autoFocus
                    helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                    autoComplete={false}
                  />
                </Form>
              </View>

              <View style={[commonStyles.dFlex, styles.loginLink]}>
                <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.lightGrayColor]}>Already have an account ?</Text>
                <Touchable key="login" onPress={this.navPchLogin}>
                  <Text style={[commonFontStyles.workSans, commonStyles.heading6, styles.createAccount]}>Login</Text>
                </Touchable>
              </View>
              <View style={[styles.mrgTopAuto, commonStyles.marginBottom10, styles.copyRight]}>
                <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.lightGrayColor]}>
                  © {new Date().getFullYear()} Exela Technologies, All rights reserved.
                </Text>
              </View>
            </Surface>
          </View>
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  logoImage: {
    height: 55,
    width: 150,
    marginBottom: 50,
    marginTop: 50,
    margin: "auto",
    display: "block",
    backgroundPosition: "center",
    screen: ({ width }) => {
      if (width <= 640)
        return {
          //	height: 48,
          //	width: 144,
        };
    },
  },
  mainLogin: {
    display: "flex",
    background: "#fff",
  },
  leftSection: {
    flex: 2,
    display: "flex",
  },
  rightSection: {
    flex: 1,
    width: 550,
    maxWidth: 550,
    justifyContent: "center",
    position: "relative",
    paddingTop: 16,
    screen: ({ height, width }) => {
      if (width >= 320 && width <= 360) {
        return {
          width: "100%",
        };
      }
      if (height <= 1000) {
        return {
          // paddingTop: 45,
        };
      } else if (width <= 768) {
        return {
          width: "100%",
          maxWidth: "100%",
        };
      }
    },
  },
  backgroundImage: {
    width: "100%",
    height: "100%",
    display: "block",
    backgroundSize: "cover",
    backgroundPosition: "center left",
    screen: ({ width, height }) => {
      if (width === 1024 && height === 768)
        return {
          backgroundPosition: "-60px",
        };
    },
  },
  bgContainer: {
    width: "100%",
    display: "block",
    height: "100vh",
    screen: ({ width }) => {
      if (width <= 768)
        return {
          display: "none",
          width: 0,
        };
    },
  },
  loginButton: {
    width: "100%",
    background: "#37c2ef",
    border: 0,
    borderRadius: 10,
  },
  emailField: {
    width: "100%",
    display: "block",
    // boxShadow: "0px 0px 0px 2px #e1f3e6",
    borderRadius: 5,
  },
  passwordField: {
    width: "100%",
    display: "block",
  },
  loginLogo: {
    width: "100%",
    height: 90,
    marginBottom: 90,
  },
  rememberMeText: {
    position: "relative",
    top: -28,
    left: 30,
  },
  forgotPassword: {
    display: "flex",
    flexDirection: "row",
    width: "100%",
    marginTop: 30,
    marginBottom: 30,
    color: "#37c2ef",
    fontSize: "14",
  },
  forgotLeftSection: {
    textAlign: "right",
    color: "#38c2ef",
    width: "100%",
  },
  loginControls: {
    width: "100%",
  },
  createAccount: {
    color: "#38c2ef",
    fontSize: 14,
    textTransform: "none",
    marginLeft: 5,
  },
  twoCol: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  loginFormContainer: {
    width: "100%",
    padding: 0,
    maxWidth: 343,
    margin: "30px auto 0",
  },
  logoImageContainer: {
    display: "block",
    width: "100%",
    textAlign: "center",
    ...StyleSheet.margin(8, 0, 3, 0),
  },
  mrgTopAuto: {
    marginTop: "auto",
  },
  loginLink: {
    padding: "0 24px",
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          margin: "0 auto",
          maxWidth: 365,
          minWidth: 354,
        };
      }
    },
  },
  copyRight: {
    textAlign: "center",
  },
  loginHeader: {
    display: "flex",
    ...StyleSheet.padding(0, 16, 0, 16),
    textAlign: "center",
    alignItems: "center",
    screen: ({ width, height }) => {
      if (width <= 991) {
        return {
          ...StyleSheet.margin(-15, -15, 0, -15),
          boxShadow: "rgb(0 0 0 / 20%) 0px 0px 6px",
          ...StyleSheet.padding(16, 16, 16, 16),
        };
      }
    },
  },
  customHelperTextStyle: {
    position: "static",
  },
});
