import React from "react";
import R14, { StyleSheet, ScreenLayout, Form, TextInputField, SubmitButton, Text, View, Touchable, Image } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";

const caterpillarCorp = require("../assets/images/caterpillarCorp.png");

export default R14.connect(
  class BillRequestScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        toggleClass: false,
      };
    }
    toogleStatus = () => {
      this.setState({ toggleClass: !this.state.toggleClass });
    };
    render() {
      return (
        <ScreenLayout scrollEnabled style={styles.billRequestScreen}>
          <View style={[commonStyles.marginTop10, commonStyles.marginLeft16]}>
            <Text style={[commonFontStyles.workSans, commonStyles.heading4, commonStyles.fontWeight600]}>Bill Request</Text>
          </View>
          <Form name="billrequestForm" style={[commonStyles.dFlex, commonStyles.flexDirectionColumn]}>
            <View style={[commonStyles.dFlex, commonStyles.flexWrap]}>
              <View style={[commonStyles.flexBasis48, commonStyles.marginRight8, styles.inputFields]}>
                <TextInputField
                  name="billerID"
                  label="Biller ID"
                  required="Please enter Biller ID"
                  // validator="fName"
                  style={[styles.fNameField, styles.inputField]}
                  labelTextStyle={(commonStyles.labelText, commonFontStyles.workSans)}
                  labelStyle={[commonStyles.formLabelStyle]}
                />
              </View>
              <View style={[commonStyles.flexBasis50, commonStyles.marginLeft8, styles.inputFields]}>
                <TextInputField
                  name="billerName"
                  label="Biller Name"
                  required="Please enter First Name"
                  style={[styles.fNameField, styles.inputField]}
                  labelTextStyle={(commonStyles.labelText, commonFontStyles.workSans)}
                  labelStyle={[commonStyles.formLabelStyle]}
                />
              </View>
            </View>
            <View style={[styles.searchResult]}>
              <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.lightGray]}>Search Result</Text>
              <Touchable onPress={this.toogleStatus} style={[commonStyles.dFlex, commonStyles.alignItemsCenter, styles.resultRow]}>
                <View>
                  <Image source={caterpillarCorp} style={commonStyles.icon40} />
                </View>
                <View style={commonStyles.marginLeft16}>
                  <Text style={[commonFontStyles.workSans, commonStyles.heading5, commonStyles.darkGrayColor, commonStyles.fontWeight600]}>
                    Caterpillar Corp
                  </Text>
                </View>
              </Touchable>
            </View>
            <View style={[commonStyles.marTAuto]}>
              <SubmitButton
                title="Search Biller"
                key="submit"
                titleColor="#000"
                style={[commonStyles.blueButton, commonStyles.fullWidth]}
                titleTextStyle={[commonStyles.heading6, commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.darkGrayColor]}
              />
            </View>
          </Form>
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  billRequestScreen: {
    background: "#fff",
  },
  inputField: {
    flexGrow: 0,
  },
  resultRow: {
    background: "#FAFAFA",
    minHeight: 72,
    padding: 16,
    marginTop: 8,
  },
  inputFields: {
    screen: ({ width }) => {
      if (width >= 991 && width <= 1083) {
        return {
          flexBasis: "48%",
        };
      }
      if (width <= 991) {
        return {
          flexBasis: "100%",
          margin: 0,
        };
      }
    },
  },
});
