import React from "react";
import R14, { ScreenLayout, StyleSheet, ScrollView, View, Text, Touchable, Image, FlatList, Button } from "../../core";

import LeftMenuComponent from "../../components/LeftMenuComponent";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import PCHSignupComponent from "../../components/PCH/PCHSignupComponent";
import NoRecordsFound from "../../components/NoRecordsFound";
import CopyRight from "../../components/CopyRight";
import ModalPopUp from "../../components/ModalPopUp";
import ToggleRadioBtn from "../../components/ToggleRadioBtn";

const editIcon = require("../../assets/images/edit.png");
const plusSolidIcon = require("../../assets/images/plus_solid.png");

export default R14.connect(
  class AddUsers extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        actionMode: "Listing",
        isConfirmationVisible: false,
        rowUid: "",
        activeDeactieFlag: "",
        userItems: this.props.userItems,
      };
    }

    addUser = async () => {
      this.setState({ actionMode: "Add" });
    };

    editUser = (item) => {
      this.props.app.nav.to("editUsersRoute", { uid: item.uid });
    };

    handleCancel = async () => {
      this.setState({ actionMode: "Listing" });
    };

    openPopScreen = (uid, flag) => {
      this.setState({ rowUid: uid, activeDeactieFlag: flag });
      this.setState({ isConfirmationVisible: true });
    };

    closePopScreen = () => {
      this.setState({ isConfirmationVisible: false });
    };

    refressList = async () => {
      let allUsers = [];
      allUsers = await this.props.app.dm.user.getUserDetailsList({
        parentUid: { eq: this.props.parentUID },
      });
      this.setState({ userItems: allUsers, actionMode: "Listing" });
    };

    activeDeactive = async () => {
      // let InsuranceDetail = await this.props.app.dm.addInsuranceDomain.checkExistinUploadBill({
      //   doctorUid: { eq: this.state.doctorUid },
      // });

      // if (InsuranceDetail.length > 0) {
      //   this.props.app.dm.commonDomain.showToastr("error", "Delete not allowed. Bills uploaded with this provider.");
      // } else {
      let apiBody = {
        isActive: this.state.activeDeactieFlag,
        uid: this.state.rowUid,
        updatedDate: new Date(),
        updatedBy: this.props.parentUID,
      };

      let res = await this.props.app.dm.userRegistration.updateUserDetails(apiBody, false);

      if (!!res.uid) {
        let { userItems } = this.state;
        let filteredData = userItems.map((el, index) => {
          if (el.uid === this.state.rowUid) {
            userItems[index].isActive = this.state.activeDeactieFlag;
          }
        });

        this.setState({ userItems: userItems });
        this.props.app.dm.commonDomain.showToastr(
          "success",
          "User " + (this.state.activeDeactieFlag === "false" ? "Deactivated " : "Activated ") + "successfully!"
        );
      } else {
        this.props.app.dm.commonDomain.showToastr("error", "Failed to update!");
      }

      this.setState({ isConfirmationVisible: false, rowUid: "", activeDeactieFlag: "" });
    };

    renderUserListing() {
      const { actionMode, addedHeight } = this.state;
      const { userItems } = this.state;
      // let dashboarScroll = addedHeight > Dimensions.get("window").height - 267;
      return (
        <View style={actionMode === "Listing" ? null : commonStyles.displayNone}>
          {userItems.length > 0 ? (
            <View style={styles.table}>
              <View style={[styles.thead]}>
                {/* !!dashboarScroll ? styles.customPaddingRight16 : null */}
                <View style={styles.th}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Name</Text>
                </View>
                <View style={styles.th}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Email</Text>
                </View>
                <View style={styles.th}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Mobile No.</Text>
                </View>
                <View style={[styles.th]}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Status</Text>
                </View>
                <View
                  style={[
                    styles.th,
                    styles.action,
                    // !!dashboarScroll ? styles.customMarginRight16 : null
                  ]}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Action</Text>
                </View>
              </View>
            </View>
          ) : null}
          {/* {this.state.doctorDetails.map((el, ind) => ( */}
          <FlatList
            style={[userItems.length > 0 ? styles.insuranceListComponent : styles.noItemsComponent]}
            data={userItems}
            // onLayout={(event) => this.getHeightOfElement(event)}
            ListEmptyComponent={
              <View style={styles.padd10}>
                <NoRecordsFound noRecords={[commonStyles.marginTop15]} message={"No Users."} />
              </View>
            }
            renderItem={({ item, index }) => (
              <View style={styles.body} key={"userRow_" + index}>
                <View style={styles.tr}>
                  <View style={styles.td}>
                    <View style={[commonStyles.mView, commonStyles.marginBottom5, styles.mobileLabels]}>
                      <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Name : </Text>
                    </View>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>
                      {item.firstName + (!!item.lastName ? " " + item.lastName : "")}
                    </Text>
                  </View>
                  <View style={styles.td}>
                    <View style={[commonStyles.mView, commonStyles.marginBottom5, styles.mobileLabels]}>
                      <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Email : </Text>
                    </View>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>{item.email}</Text>
                  </View>
                  <View style={styles.td}>
                    <View style={[commonStyles.mView, commonStyles.marginBottom5, styles.mobileLabels]}>
                      <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Mobile No : </Text>
                    </View>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>{item.mobileNumber}</Text>
                  </View>
                  <View style={[styles.td, styles.memberName]}>
                    <View style={[commonStyles.mView, commonStyles.marginBottom5, styles.mobileLabels]}>
                      <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Status : </Text>
                    </View>
                    <View style={[commonFontStyles.workSans, commonStyles.heading6]}>
                      {/* {(item.isActive === "true" || !!!item.isActive) ?
                        (
                        <Touchable onPress={() => this.openPopScreen(item.uid, "false")} style={commonStyles.marginLeft15}>
                          <Text style={[commonFontStyles.workSans, styles.statusLink, commonStyles.heading6]}>Active </Text>
                        </Touchable>)
                        : (<Touchable onPress={() => this.openPopScreen(item.uid, "true")} style={commonStyles.marginLeft15}>
                          <Text style={[commonFontStyles.workSans, styles.statusLink, commonStyles.heading6]}>Deactive</Text>
                        </Touchable>)
                      } */}
                      <ToggleRadioBtn
                        toggleSwitch={() => this.openPopScreen(item.uid, item.isActive === "true" || !!!item.isActive ? "false" : "true")}
                        on={item.isActive === "true" ? true : false}
                        toggleRadioBtnSize={styles.toggleRadioBtnSize}
                      />
                    </View>
                  </View>

                  {/* <View style={styles.td}>
                    <View style={[commonStyles.mView, commonStyles.marginBottom5, styles.mobileLabels]}>
                      <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Status </Text>
                    </View>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>
                     test
                    </Text>
                  </View> */}

                  <View style={[styles.td, styles.action]}>
                    <View
                      style={[
                        commonStyles.dFlex,
                        commonStyles.alignItemsCenter,
                        // this.state.displayType == "Edit" ? commonStyles.dFlex : styles.none,
                      ]}>
                      <Touchable onPress={() => this.editUser(item)}>
                        <Image source={editIcon} style={commonStyles.icon20} />
                      </Touchable>
                      {/* {(item.isActive === "true" || !!!item.isActive) ?
                        (<Touchable onPress={() => this.openPopScreen(item.uid, "false")} style={commonStyles.marginLeft15}>
                          <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>  Active </Text>
                        </Touchable>)
                        : (<Touchable onPress={() => this.openPopScreen(item.uid, "true")} style={commonStyles.marginLeft15}>
                          <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>  Deactive </Text>
                        </Touchable>)
                      } */}
                    </View>
                  </View>
                </View>
              </View>
            )}
          />
        </View>
      );
    }

    render() {
      // const { defaultMode } = this.props;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;

      //     <ScreenLayout scrollEnabled style={[styles.uploadBillsScreen]}>
      //     <ScrollView style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu]}>
      //       <AddInsurance profileActionType={"Edit"} defaultMode={defaultMode} insuranceItems={insuranceItems} />
      //     </ScrollView>
      //     <LeftMenuComponent />
      //   </ScreenLayout>

      const { actionMode } = this.state;
      return (
        <ScreenLayout scrollEnabled style={[styles.uploadBillsScreen]}>
          <ScrollView style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu]}>
            <View
              style={[
                styles.container,
                styles.pageTitleHolder,
                commonStyles.dFlex,
                commonStyles.alignItemsCenter,
                commonStyles.justifyContentSpaceBetween,
              ]}>
              <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500]}>
                {actionMode === "Add" ? "Add User" : this.props.app.nav.route.label}
              </Text>
              {actionMode === "Listing" ? (
                <Touchable style={[commonStyles.dFlex, commonStyles.alignItemsCenter]} onPress={() => this.addUser()}>
                  <View style={styles.plusIcon}>
                    <Image source={plusSolidIcon} style={[commonStyles.icon24]} />
                  </View>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight400, commonStyles.heading6, styles.custompaddingLeftRight]}>
                    Add User
                  </Text>
                </Touchable>
              ) : null}
            </View>
            {actionMode === "Add" ? (
              <PCHSignupComponent
                formFlexDirection={styles.formFlexDirection}
                addUsersFormAlignment={styles.addUsersFormAlignment}
                leftInput={styles.leftInput}
                rightInput={styles.rightInput}
                addUserBtnHolder={styles.addUserBtnHolder}
                adduserBtns={styles.adduserBtns}
                addBtn={styles.addBtn}
                registrationType={"new"}
                screen={"addUser"}
                parentUID={this.props.parentUID}
                refressList={this.refressList}
              />
            ) : null}
            {actionMode === "Listing" ? this.renderUserListing() : null}
            <CopyRight />
          </ScrollView>
          <LeftMenuComponent />
          <ModalPopUp
            visibility={this.state.isConfirmationVisible}
            isFooter={false}
            bodyWrapper={styles.bodyWrapper}
            footerClass={commonStyles.padding0}
            modalOuterWrapper={styles.modalOuterWrapper}
            closeButton={commonStyles.displayNone}>
            <View style={commonStyles.textAlignCenter}>
              <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>Are you sure you want</Text>
              <Text style={[commonStyles.marginBottom15, commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>
                to {this.state.activeDeactieFlag === "false" ? "Deactivate" : "Activate"} ?
              </Text>
            </View>
            <View style={[styles.btnWrapper]}>
              <Button
                title={"Yes"}
                style={[commonStyles.blueBorderButton, commonStyles.marginRight15, styles.buttons, commonStyles.flexBasis48]}
                onPress={this.activeDeactive}
                titleTextStyle={[commonStyles.blueBorderButtonColor, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
              />
              <Button
                title={"No"}
                style={[commonStyles.blueButton, styles.noButton, styles.buttons, commonStyles.flexBasis48]}
                onPress={this.closePopScreen}
                titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
              />
            </View>
          </ModalPopUp>
        </ScreenLayout>
      );
    }
  }
);
const styles = StyleSheet.create({
  uploadBillsScreen: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          background: "#fff",
        };
    },
  },
  btnWrapper: {
    display: "flex",
    justifyContent: "center",
    margin: "15px 0",
  },
  modalBackdrop: {
    background: "#00000052",
    overflowX: "auto",
  },
  bodyWrapper: {
    width: 311,
    padding: 15,
    screen: ({ width }) => {
      if (width <= 359)
        return {
          width: 300,
        };
    },
  },
  addAttachmentBodyWrapper: {
    marginTop: "auto",
    borderRadius: "30px 30px 0 0",
    width: "100vw",
    padding: 15,
  },
  commentsField: {
    minHeight: 128,
    resize: "vertical",
  },
  closePopup: {
    width: "20%",
    height: 8,
    borderRadius: 25,
    background: "#e1e1e1",
    marginBottom: 25,
  },
  attachOpt: {
    textAlign: "center",
    border: "2px dotted #dadada",
    padding: 10,
  },
  attachments: {
    paddingLeft: 50,
  },
  ucountIconDiv: {
    width: 40,
    minWidth: 40,
    height: 40,
    borderRadius: "100%",
    background: "#cff2ff",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    position: "absolute",
    margin: "5px 0px 0px 5px",
  },

  filePassowrdModalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },

  modalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },

  filePassowrdModalBodyWrapper: {
    background: "#fff",
    border: "1px solid #a7a7a7",
    padding: "10px",
    width: 350,
    display: "flex",
    flexDirection: "column",
    borderRadius: 4,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          width: "100vw",
          borderRadius: "15px 15px 0 0",
          marginTop: "auto",
        };
      }
    },
  },
  inputDisable: {
    cursor: "not-allowed",
    pointerEvents: "none",
    opacity: 0.5,
  },

  table: {
    margin: "0 16px",
    background: "#fff",
    padding: "16px 0 0",
  },

  thead: {
    padding: "10px 16px 10px",
    margin: "0px 16px",
    display: "flex",
    justifyContent: "space-between",
    position: "relative",
    zIndex: 999,
    background: "#dadada",
    borderRadius: "4px 4px 0px 0px",
    margin: "0px 16px",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "none",
        };
    },
  },
  tr: {
    display: "flex",
    justifyContent: "space-between",
    border: "1px solid #dadada",
    borderTop: 0,
    padding: "10px 16px 10px",
    position: "relative",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          padding: "10px 16px 10px",
          margin: 0,
          borderLeft: 0,
          borderRight: 0,
          flexDirection: "column",
        };
    },
  },
  th: {
    flex: 1,
    flexBasis: "22.5%",
    maxWidth: "22.5%",
  },
  td: {
    flex: 1,
    flexBasis: "22.5%",
    maxWidth: "22.5%",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginBottom: 5,
          flexBasis: "75%",
          maxWidth: "75%",
          flexDirection: "row",
        };
    },
  },
  action: {
    maxWidth: "10% ",
    flexBasis: "10%",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          position: "absolute",
          top: 10,
          right: 16,
          zIndex: 99999999,
          maxWidth: "21%",
          flexBasis: "20%",
        };
    },
  },
  plusIcon: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  noItemsComponent: {
    ...StyleSheet.margin(55, 16, 50, 16),
    background: "#fff",
    padding: 0,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          ...StyleSheet.margin(55, 0, 10, 0),
        };
    },
  },
  insuranceListComponent: {
    ...StyleSheet.margin(108, 17, 50, 16),
    background: "#fff",
    padding: "0px 15px 0px 15px",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginLeft: "auto",
          ...StyleSheet.margin(50, 0, 0, 0),
          marginBottom: 10,
          padding: 0,
        };
    },
  },
  loaderimg: {
    width: 300,
    height: 190,
    display: "block",
    backgroundSize: "contain",
  },
  profileEditForm: {
    background: "#fff",
    margin: "0 16px",
    paddingTop: 0,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          margin: "0px",
        };
    },
  },
  pageTitleHolder: {
    padding: "16px 16px 10px",
  },
  customMargin: {
    ...StyleSheet.margin(15, 0, 0, 0),
    screen: ({ width }) => {
      if (width <= 991)
        return {
          ...StyleSheet.margin(15, 16, 0, 16),
        };
    },
  },
  formFlexDirection: {
    background: "#fff",
    flexDirection: "row",
    flexWrap: "wrap",
    padding: 16,
    margin: 16,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          ...StyleSheet.padding(16, 0, 0, 0),
          flexDirection: "column",
          marginBottom: 15,
        };
    },
  },
  addUsersFormAlignment: {
    flexBasis: "48%",
    flex: 1,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          flexBasis: null,
        };
    },
  },
  rightInput: {
    marginLeft: 8,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginLeft: 0,
        };
    },
  },
  leftInput: {
    marginRight: 8,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginRight: 0,
        };
    },
  },
  addUserBtnHolder: {
    flex: 1,
    justifyContent: "flex-end",
    flexDirection: "row",
  },
  adduserBtns: {
    flexBasis: "15%",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          flexBasis: "48%",
        };
    },
  },
  addBtn: {
    minWidth: 108,
  },
  mobileLabels: {
    marginRight: 10,
    minWidth: 85,
  },
  statusLink: {
    color: "#38c2ef",
  },
  custompaddingLeftRight: {
    ...StyleSheet.padding(0, 10, 0, 5),
  },
  toggleRadioBtnSize: {
    width: 70,
  },
  buttons: {
    height: 40,
  },
});
