import React from "react";
import R14, { Theme } from "../core";
import HeaderControlsLeft from "./HeaderControlsLeft";

export default R14.connect(
  class DrawerContentMain extends React.Component {
    render() {
      let logoColorScheme = Theme.componentOptions("drawerLogo").colorScheme || null;
      let sidebarOnBackground = Theme.componentColors("sidebar").onBackground || null;
      return (
        <HeaderControlsLeft
          menuIconColor={sidebarOnBackground}
          isSideBarOpen={this.props.app.ui.navigator("homeNavigator").isVisible}
          logoColorScheme={logoColorScheme}
        />
      );
    }
  }
);
