import R14, { AsyncStorage, AsyncSessionStorage } from "../core";
import RSAencryption from "../utils/RSAencryption";
import CommonUtility from "../utils/CommonUtility";
import { LOGIN_TYPE, USER_TYPE } from "../constant/app.data.constant";
import AsyncStorageUtils from "../utils/AsyncStorageUtils";
export default class UserSessionDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {
      userDetail: {},
      loggedIn: false,
      token: null,
      refreshToken: null,
      leftMenuToggle: true,
      facebookMediaLoginPID: false,
      googleMediaLoginPID: false,
      isApplePayModalVisible: false,
      pchRegistrationTypeProvider: false,
      pchRegistrationTypePatient: false,
      leftMenuItems: [],
      isAddProviderVisible: false,
    };
  }

  async loginAuth(
    loginInput,
    fields = "uid pid payerId loginAttempt isLockout updatedAt lockoutTime sessionString password changePasswordNow googleProfileID facebookProfileID amazonProfileID appleProfileID tokenKey userType firstName lastName email googleEmail facebookEmail amazonEmail appleEmail mobileNumber addressLine1 addressLine2 town state country postCode EIN parentUid parentUserDetail{ uid email firstName lastName} profileImage"
  ) {
    // Encrypt Username and Password
    let encryptUsername = RSAencryption.EnctyptString(loginInput.username);
    let encryptPassword = !!loginInput.password
      ? loginInput.bEncryptRequired === undefined || loginInput.bEncryptRequired === true
        ? (!!await CommonUtility.getWithExpiry("username") ? loginInput.password : RSAencryption.EnctyptString(loginInput.password))
        : loginInput.password
      : "";
    let data = {
      username: encryptUsername,
      password: encryptPassword,
      socialProfileId: loginInput.socialProfileId,
      loginType: !!loginInput.loginType ? loginInput.loginType : LOGIN_TYPE.NORMAL,
    };

    let res = await this.api.mutate(
      `
    mutation Authenticate($input: EncryptedAuthUserInput!) {
      authenticate(input: $input){
          access_token expires_in refresh_token scope token_type error success
          message userLoginDetail {
             ${fields}
            }
        }
    }`,
      {
        input: data,
      }
    );

    // if (res.data === null)// throw new Error("Auth failed due to invalid credentials/absolute PID")
    if (res.data.authenticate.error || res.data.authenticate.success === false) throw res.data.authenticate;
    return this._processAuthResult(res.data.authenticate);
  }

  async _processAuthResult(res) {
    if (!res.access_token || !res.refresh_token) throw new Error("Unknown Auth Error: No Access.");
    const { access_token, refresh_token, userLoginDetail } = res;
    await AsyncSessionStorage.setItem("access_token", access_token);
    await AsyncSessionStorage.setItem("refresh_token", refresh_token);
    this.setState({ access_token, refresh_token });
    //Enforcing to generate new tokens.
    await this.issueNewToken();
    this.setLoggedIn(this.getAccessToken());
    await AsyncStorage.setItem("profiles", JSON.stringify(userLoginDetail));
    let userType = !!userLoginDetail ? userLoginDetail.userType : null;
    if (userType === USER_TYPE.PCH_PATIENT || userType === USER_TYPE.PCH_PROVIDER) return this.isLoggedIn;
    else {
      let billerList = await this.dm.queryDomain.getBiller({ filter: { payerId: { like: userLoginDetail.pid } } });
      AsyncStorage.setItem("billerList", JSON.stringify(billerList));
      await this.dm.user.getBillerListFromPam();
      this.dm.user.setState({ billers: billerList });
      return this.isLoggedIn;
    }
  }

  async logout() {
    await this.removeToken();
    this.setLoggedIn(false);
  }

  setLoggedIn(loggedIn) {
    this.setState({
      loggedIn: loggedIn,
    });
  }

  get isLoggedIn() {
    return this.getAccessToken();
  }

  getAccessToken() {
    return this.state.access_token;
  }

  async removeToken() {
    await AsyncSessionStorage.removeItem("access_token");
    await AsyncSessionStorage.removeItem("refresh_token");
  }

  async createSessionString(loginUserdetail) {
    let reqBody = {
      uid: loginUserdetail.uid,
      payerId: loginUserdetail.payerId,
      sessionString: CommonUtility.generate20Character(),
      loginAttempt: 0,
      isLockout: false,
    };
    try {
      await this.dm.userRegistration.updateUserDetails(reqBody, true);
      return true;
    } catch (error) {
      return error;
    }
  }
 
  async isSessionStringValid() {
    try {
      const { payerId, sessionString } = !!await AsyncStorage.getItem("profiles") ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
      let loginUserdetail = await this.dm.userLoginDomain.checkUserloginDetail(payerId, "sessionString");
      if (!!loginUserdetail && loginUserdetail.length > 0) {
        if (loginUserdetail[0].sessionString === sessionString) {
          return true;
        } else {
          return false;
        }
      }
    } catch (error) {
      return error;
    }
  }

  async issueNewToken() {
    let encryptRefreshToken = RSAencryption.EnctyptString(await AsyncSessionStorage.getItem("refresh_token"));
    let data = {
      refreshToken: encryptRefreshToken,
    };

    let res = await this.api.mutate(
      `
     mutation EncryptedAuth($input: EncryptedAuthUserInput!) {
       encryptedAuth(input: $input){
           access_token,refresh_token,error
         }
     }`,
      {
        input: data,
      }
    );

    if (res.data.encryptedAuth.error) {
      throw new Error(res.data.encryptedAuth.error);
    } else {
      const { access_token, refresh_token } = res.data.encryptedAuth;
      await AsyncSessionStorage.setItem("access_token", access_token);
      await AsyncSessionStorage.setItem("refresh_token", refresh_token);
      this.setState({
        access_token: access_token,
        refresh_token: refresh_token,
      });
      return true;
    }
  }
}
