import React, { Component } from "react";
import R14, { StyleSheet, SubmitButton, View, Text, Form, TextInputField, ScreenLayout, Modal, Button, Redirect, ScrollView } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { DECLINERADIO_OPT, MSG_TYPE, BILL_STATUS } from "../constant/app.data.constant";
import RadioButtons from "../components/RadioButtons";
import BillerDetails from "./BillerDetails";
import BottomPaySection from "./BottomPaySection";
import CommonValidators from "../utils/CommonValidators";
import MessageUpload from "./MessageUpload";
import CopyRight from "./CopyRight";
import LeftMenuComponent from "./LeftMenuComponent";

export default R14.connect(
  class DeclineComponent extends Component {
    constructor(props) {
      super(props);

      this.form = null;
      this.state = {
        selectedOpt: null,
        isLoading: false,
        isVisible: false,
        isDeclineModalVisible: true,
      };
      this.fileAttach = null;
    }

    componentDidMount() {
      this.form = this.props.app.ui.form("declineBill");
    }

    onValueChange = (val) => {
      this.setState({
        selectedOpt: val,
      });
      // this.form.elmts.comments.setValue(val);
    };

    onAttachment = async (val) => {
      this.fileAttach = val;
    };

    onSubmit = async (form) => {
      this.setState({ isVisible: true });
      return false;
    };

    onCancel = () => {
      // this.setState({ isVisible: false, selectedOpt: null });
      // this.form.elmts.comments.setValue("");
      const { billData } = this.props;
      this.props.app.nav.to("billDetailsRoute", { threadId: billData.threadId });
    };

    onConfirmationCancel = () => {
      this.setState({
        isVisible: false,
      });
    };
    onOkPress = async () => {
      const { selectedOpt } = this.state;
      const { billData } = this.props;
      const { comments } = this.form.values;
      let newThreadId = billData.threadId.replaceAll("#", ">>");
      let leaf = {
        ...billData,
        additionalInformation: !!comments ? comments : selectedOpt,
        messageType: MSG_TYPE.DECLINE,
        attachments: this.fileAttach ? [this.fileAttach] : null,
      };
      let payload = this.props.app.dm.billDetails.getDeclinedPayload(leaf);
      try {
        let res = await this.props.app.dm.billDetails.postMessageForPayment(payload);
        let noteMessage = await this.props.app.dm.billDetails.declinedNotePayload({
          ...leaf,
          note: `Declined: ${!!comments ? comments : selectedOpt}`,
          messageType: MSG_TYPE.NOTE_TO_BILLER,
        });
        if (!!res && res.threadId) {
          this.props.app.dm.commonDomain.showToastr("error", "You have declined the bill");
          let decPayload = {
            uid: billData.uid,
            billStatus: "Declined",
            bookmark: false,
            fullPaidAndDeclined: new Date().toJSON(),
            isFailedPayment: false,
            failDate: null,
          };
          let decBill = await this.props.app.dm.dashboard.updateBill(decPayload);
          if (decBill) {
            const { compiledMsgList, filterBills } = this.props.app.dm.dashboard.state;
            if (compiledMsgList) {
              const ind = compiledMsgList.findIndex((el) => el.uid === billData.uid);
              if (compiledMsgList[ind]) {
                compiledMsgList[ind].billStatus = decPayload.billStatus;
                compiledMsgList[ind].bookmark = decPayload.bookmark;
                compiledMsgList[ind].fullPaidAndDeclined = decPayload.fullPaidAndDeclined;
              }
              const flIndx = filterBills.findIndex((el) => el.uid === billData.uid);
              if (filterBills[ind]) {
                filterBills[flIndx].billStatus = decPayload.billStatus;
                filterBills[flIndx].bookmark = decPayload.bookmark;
                filterBills[flIndx].fullPaidAndDeclined = decPayload.fullPaidAndDeclined;
              }

              await this.props.app.dm.dashboard.updateBillCount(compiledMsgList, filterBills);
            }
            res = await this.props.app.dm.billDetails.postMessageForPayment(noteMessage);
            if (!!res && res.threadId) {
              setTimeout(() => {
                this.props.app.nav.to("billReceiptRoute", { threadId: newThreadId });
              }, 100);
            }
          }
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Decline Unsucessfull.");
        }
      } catch (error) {
        console.log("Declined Error: ", error);
      } finally {
        this.setState({ isVisible: false });
      }
    };

    readText = (val) => {
      if ((!val && !!this.form) || val.length <= 140) {
        this.form.elmts.comments.setError(null);
      } else if (val.length > 140) {
        this.form.elmts.comments.setError("Character Limit Exceeds 140 Characters");
      }
    };

    render() {
      const { selectedOpt, isLoading, isVisible, isDeclineModalVisible } = this.state;
      const { billData, payType } = this.props;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      return (
        <ScreenLayout scrollEnabled style={styles.declinedContainer}>
          <ScrollView
            style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu, styles.declineScreen]}>
            {/* <View style={[commonStyles.marginBottom15]}>
            <Text style={[commonStyles.heading5, commonFontStyles.workSans, commonStyles.fontWeight500]}>
              {this.props.app.nav.route.label}{" "}
            </Text>
          </View> */}
            <View style={styles.declineScreenContainer}>
              <BillerDetails customStyles={commonStyles.padding0} billData={billData} payType={payType} />
              <View style={[commonStyles.webView]}>
                <BottomPaySection
                  threadId={billData.threadId}
                  activePage={payType}
                  isDisabled={BILL_STATUS[billData.status]}
                  extraOpt={billData.rtpMessageType}
                />
              </View>
              <View>
                <View style={[styles.commentsAttachment]}>
                  <View style={[commonStyles.dFlex, commonStyles.flexDirectionColumn]}></View>
                </View>
              </View>
              <View style={[styles.BottomPaySection, commonStyles.mView]}>
                <BottomPaySection
                  threadId={billData.threadId}
                  activePage={payType}
                  isDisabled={BILL_STATUS[billData.status]}
                  extraOpt={billData.rtpMessageType}
                />
              </View>

              <Modal visible={isDeclineModalVisible} animationType="fade" style={[styles.declineModalWrapper, styles.modalWrapper]}>
                <View style={[styles.declineModalBodyWrapper]}>
                  <Form
                    name="declineBill"
                    style={[commonStyles.padding0]}
                    onSubmit={this.onSubmit}
                    validateBeforeSubmit={true}
                    validators={CommonValidators.getFormValidator()}>
                    <View>
                      <View style={[commonStyles.textAlignCenter, commonStyles.marginBottom15]}>
                        <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading5]}>Decline Bill</Text>
                      </View>
                      <View style={[commonStyles.marginBottom5, styles.selectReasonTitle]}>
                        <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Select Reason</Text>
                      </View>
                      <View>
                        {DECLINERADIO_OPT.map((el, key) => (
                          <RadioButtons
                            radioButtonsClass={styles.radioButtonsOuter}
                            key={key}
                            options={el}
                            onValueChange={this.onValueChange}
                            isSelected={selectedOpt}
                          />
                        ))}
                      </View>
                    </View>
                    {selectedOpt === "Other" ? (
                      <View style={[commonStyles.marginTop10, commonStyles.paddingLeftRight10]}>
                        <MessageUpload
                          onValueChange={this.onAttachment}
                          onChangeText={this.readText}
                          commentsProps={{
                            required: selectedOpt === "Other" ? "Enter the reason" : "Enter the comments",
                            label: selectedOpt === "Other" ? "Other Reason*" : "Enter Message*",
                          }}
                        />
                      </View>
                    ) : null}
                    <View style={[styles.btnHolder, commonStyles.dFlex, commonStyles.justifyContentCenter]}>
                      <Button
                        title={"Cancel"}
                        style={[commonStyles.blueBorderButton, commonStyles.marginRight8, commonStyles.flex1, styles.cancelBtn]}
                        onPress={this.onCancel}
                        titleTextStyle={[
                          commonFontStyles.workSans,
                          commonStyles.fontWeight600,
                          commonStyles.heading6,
                          commonStyles.transperentBtnColor,
                        ]}
                      />
                      <SubmitButton
                        title="Decline"
                        key="decline"
                        style={[commonStyles.blueButton, styles.declinedBtn, commonStyles.marginLeft8, commonStyles.flex1]}
                        titleTextStyle={[commonStyles.fontWeight600, commonStyles.black, commonStyles.blueButtonColor, commonFontStyles.workSans]}
                        disabled={!selectedOpt && !isLoading}
                      />
                    </View>
                  </Form>
                </View>
              </Modal>
              <Modal visible={isVisible} animationType="fade" style={styles.modalWrapper}>
                <View style={[styles.modalBodyWrapper]}>
                  <View style={[commonStyles.marginTopBottom20, commonStyles.textAlignCenter]}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading5]}>Do you want to decline Bill?</Text>
                  </View>
                  <View style={[styles.modalFooter, commonStyles.marB20]}>
                    <Button
                      title={"Cancel"}
                      style={[commonStyles.blueBorderButton, commonStyles.marginRight8]}
                      onPress={this.onConfirmationCancel}
                      titleTextStyle={[
                        commonFontStyles.workSans,
                        commonStyles.fontWeight600,
                        commonStyles.heading6,
                        commonStyles.transperentBtnColor,
                      ]}
                    />
                    <Button
                      title={"Confirm"}
                      style={[commonStyles.blueButton, commonStyles.marginLeft8]}
                      onPress={this.onOkPress}
                      titleTextStyle={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6, commonStyles.blueButtonColor]}
                    />
                  </View>
                </View>
              </Modal>
            </View>
            {isMobileDevice ? null : (
              <View style={[commonStyles.copyRightSection, commonStyles.marginBottom16]}>
                <CopyRight />
              </View>
            )}
          </ScrollView>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  declinedContainer: {
    backgroundColor: "#FFFFFF",
    position: "relative",
  },
  BottomPaySection: {
    // position: "fixed",
    bottom: 0,
    width: "100%",
    marginTop: "auto",
    background: "#fff",
    padding: 0,
  },
  commentsAttachment: {},
  commentsIcon: {
    height: 44,
    width: 44,
    display: "block",
    backgroundSize: "contain",
    borderRadius: "100%",
  },
  radioButtonsOuter: {
    marginBottom: 9,
  },
  modalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },
  modalBodyWrapper: {
    background: "#fff",
    border: "1px solid #a7a7a7",
    padding: "10px",
    width: 300,
    display: "flex",
    flexDirection: "column",
    borderRadius: 4,
  },
  declineModalBodyWrapper: {
    background: "#fff",
    border: "1px solid #a7a7a7",
    padding: "10px",
    width: 350,
    display: "flex",
    flexDirection: "column",
    borderRadius: 4,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          width: "100vw",
          borderRadius: "15px 15px 0 0",
          marginTop: "auto",
        };
      }
    },
  },
  modalFooter: {
    alignItems: "center",
    justifyContent: "center",
    display: "flex",
    paddingTop: 10,
  },
  declinedBtn: {
    padding: "0 16px",
    marginLeft: 15,
  },
  declineModalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },

  selectReasonTitle: {
    padding: "0 8px",
  },
  btnHolder: {
    margin: "20px 10px",
  },
  cancelBtn: {
    borderWidth: 2,
  },
  declineScreen: {
    padding: "0 16px",
  },
  declineScreenContainer: {
    background: "#fff",
    marginTop: 16,
    padding: "16px",
  },
});
