import R14, { AsyncStorage } from "../core";
import CommonUtility from "../utils/CommonUtility";
import RSAencryption from "../utils/RSAencryption";

export default class UserLoginDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {
      socialLoginProfile: {},
      osPlatform: {},
      userDetailUID: "",
      payerId: "",
      socialLoginType: "",
    };
  }

  // added by Rampukar 
  async checkUserloginDetail(payerId, fields="uid payerId loginAttempt isLockout updatedAt lockoutTime sessionString password changePasswordNow tokenKey userType") {
    try {
      let options = {
        filter: {
          payerId: { eq: payerId },
        },
      };
      let res = await this.api.qry(
        `query FindUserLoginDetail($filter: UserLoginDetailFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
          userLoginDetails(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
            nodes {
              ${fields}
            }
          }
        }`,
        options
      );
      return res.data.userLoginDetails.nodes ? res.data.userLoginDetails.nodes : null;
    } catch (error) {
      return error;
    }
  } 
 
  // validate Captcha
  async validateCaptcha(token) {
    if (token === "Hide") {
      return {
        success: true,
      };
    } else {
      var inputToken = {
        token: token,
      };
      try {
        let res = await this.api.query(
          `
          query ValidRecaptcha($input: ValidRecaptchaInput!) {
            validRecaptcha(input: $input){       
                  success challenge_ts error              
              }            
          }`,
          {
            input: inputToken,
          }
        );
        return res.data.validRecaptcha ? res.data.validRecaptcha : null;
      } catch (error) {
        return error;
      }
    }
  }

  // Save user password.
  async updateUserPassword(loginUserData, changePasswordNow = null) {
    let loginUserDetail = {
      uid: loginUserData.uid,
      password: RSAencryption.EnctyptString(loginUserData.password),
      changePasswordNow: changePasswordNow,
      tokenKey: loginUserData.tokenKey ? RSAencryption.EnctyptString(loginUserData.tokenKey) : "",
    };
    try {
      let res = await this.api.mutate(
        `
        mutation UpdateUserLoginDetail($input: UpdateUserLoginDetailInput!) {
            updateUserLoginDetail(input: $input){
            userLoginDetail {
              password changePasswordNow
            }
          }
        }`,
        {
          input: loginUserDetail,
        }
      );
      return true;
    } catch (error) {
      return error;
    }
  }

  // Check Token(Repository Token) Issue Password is valid.
  async checkUserPassword(username, password, bEncryptRequired = true) {
    // let encryptUsername = RSAencryption.EnctyptString(username);
    // let encryptPassword = RSAencryption.EnctyptString(password);
    // let data = {
    //   username: encryptUsername,
    //   password: encryptPassword,
    // };
    // Encrypt Username and Password
    let encryptUsername = RSAencryption.EnctyptString(username);
    let encryptPassword = bEncryptRequired === undefined || bEncryptRequired === true ? RSAencryption.EnctyptString(password) : password;
    let data = {
      username: encryptUsername,
      password: encryptPassword,
    };

    try {
      let res = await this.api.mutate(
        `mutation EncryptedAuth($input: EncryptedAuthUserInput!) {
        encryptedAuth(input: $input){
            access_token,expires_in,refresh_token,scope,token_type,error
          }
      }`,
        {
          input: data,
        }
      );
      return res.data.encryptedAuth;
    } catch (error) {
      return error;
    }
  }

  // NOTE: Check Payer Application password is valid.
  async isValidPassword(req) {
    try {
      let data = {
        payerId: RSAencryption.EnctyptString(req.username),
        password: RSAencryption.EnctyptString(req.password),
      };
      // console.log(data.password);
      let res = await this.api.qry(
        `query IsValidPassword($input: UserLoginDetailInput!){
          isValidPassword(input: $input){
            success msg            
          }
        }`,
        {
          input: data,
        }
      );
      return res.data.isValidPassword;
    } catch (error) {
      return error;
    }
  }

  // Get user password history
  async threePasswordCheck(payerId, password) {
    try {
      let data = {
        payerId: payerId,
        password: password,
      };
      let res = await this.api.qry(
        `query ThreePasswordCheck($input: UserLoginDetailInput!){
          threePasswordCheck(input: $input){
             userLoginDetail{uid payerId lastThreePasswords} success msg            
          }
        }`,
        {
          input: data,
        }
      );
      return res.data.threePasswordCheck;
    } catch (error) {
      return error;
    }
  }
 

  // Forget Password update
  // Save user password.
  async forgetPasswordUpdate(uid, newPassword) {
    if (!!uid) 
      {
        let loginUserDetail = {
          uid: uid,
          password: RSAencryption.EnctyptString(newPassword),
          changePasswordNow: true,
        };
        try {
          let res = await this.api.mutate(
            `
            mutation UpdateUserLoginDetail($input: UpdateUserLoginDetailInput!) {
                updateUserLoginDetail(input: $input){
                userLoginDetail {
                  password changePasswordNow
                }
              }
            }`,
            {
              input: loginUserDetail,
            }
          );
          return true;
        } catch (error) {
          return error;
        }
      }

  }
 
  async getChildUsers() {
    if (await AsyncStorage.getItem("profiles")) {
      const { userType, uid } = JSON.parse(await AsyncStorage.getItem("profiles"));
   
        let childs = await this.api.qry(
          `query GetChildUsersByParent($uid: String!){
            getChildUsersByParent(uid: $uid) {
              nodes {
                uid, parentUid, email, mobileNumber, userType
              }
            }
          }`,
          {
            uid: uid
          }
        );
        childs = !!childs.data.getChildUsersByParent && !!childs.data.getChildUsersByParent.nodes ? childs.data.getChildUsersByParent.nodes : [];
        AsyncStorage.setItem("childs", JSON.stringify(childs));
        return childs;
    }
  }

  // Added by Azad
  async checkEIN(EIN, uid = "") {
    try {

      let childs = await this.api.qry(
        `query ValidateEin($EIN: String, $uid: String ){
        validateEin( EIN: $EIN , uid: $uid)
          {
            isDuplicate
            msg
          }
      }`,
        {
          uid: uid,
          EIN: EIN,
        }
      );
      return !!childs.data ? childs.data.validateEin : null;

    } catch (error) {
      return error;
    }
  }


  // Added by azad to get all fields of UserDetailTable
  async getLoginDetail(filterObj, fields="uid payerId loginAttempt isLockout lockoutTime sessionString password changePasswordNow googleProfileID facebookProfileID amazonProfileID appleProfileID tokenKey userType firstName lastName email googleEmail facebookEmail amazonEmail appleEmail mobileNumber addressLine1 addressLine2 town state country postCode EIN pid parentUid") {
    try {
      let options = {
        filter: {
          ...filterObj
        },
      };
      let res = await this.api.qry(
        `query FindUserLoginDetail($filter: UserLoginDetailFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
          userLoginDetails(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
            nodes {
              ${fields}
            }
          }
        }`,
        options
      );
      return res.data.userLoginDetails.nodes ? res.data.userLoginDetails.nodes : null;
    } catch (error) {
      return error;
    }
  }


}
