import React from "react";
import R14, { StyleSheet, Image, Text, View, Link, Touchable, ScrollView, TextInputField, Dimensions, AsyncStorage } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { USER_TYPE } from "../constant/app.data.constant";

const icDashboardB = require("../assets/images/icDashboardB.png");

export default R14.connect(
  class LeftMenuComponent extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        windowWidth: Dimensions.get("window").width,
        bottomVisibility: false,
        initialized: false,
      };
      this.userLoginDetail = null;
      this.initialize();
    }

    componentDidMount() {
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      if (!!isMobileDevice) {
        this.props.app.dm.userSession.setState({
          leftMenuToggle: !isMobileDevice,
        });
      }
    }

    closeMenu = () => {
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      if (isMobileDevice) {
        this.props.app.dm.userSession.setState({
          leftMenuToggle: !this.props.app.dm.userSession.state.leftMenuToggle,
        });
      }
    };

    async initialize() {
      this.userLoginDetail = JSON.parse(await AsyncStorage.getItem("profiles")) || null;
      this.setState({ initialized: true });
    }

    render() {
      if (!this.state.initialized) return null;
      let userType = USER_TYPE.XBP;
      let userLoginDetail = {};
      const { bottomVisibility } = this.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;

      if (this.userLoginDetail) {
        userLoginDetail = this.userLoginDetail;
        userType = !!this.userLoginDetail && !!this.userLoginDetail.userType ? this.userLoginDetail.userType : USER_TYPE.XBP;
      }

      const routesProp = this.props.r14.navigation._navigators.privateNavigator.routes.homeNavigator.routes;
      const asArray = Object.entries(routesProp);
      let filterdArray = asArray.filter(([key, value]) => {
        // return !!value.userType ? value.userType.includes(userType) : false
        return !!value.userType ? (!!value.userType.includes(userType) ? true : false) : true;
      });
      let currentRoute = this.props.app.nav.route;
      let currentRouteIcon = "../assets/images/icDashboardB.png";
      return (
        // <View
        //   style={
        //     !!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP
        //       ? [leftMenuToggle ? styles.pchLeftMenuWrapper : null]
        //       : [leftMenuToggle ? styles.leftMenuWrapper : null]
        //   }>
        <View
          style={
            !!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP
              ? [styles.pchLeftMenu, leftMenuToggle ? styles.pchOpenMenu : styles.pchCloseMenu]
              : [styles.leftMenu, leftMenuToggle ? styles.openMenu : styles.closeMenu]
          }>
          {filterdArray.map((ele, key) =>
            !!ele[1].label &&
            !(!!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP && ele[1].label === "Logout") &&
            !(!!userLoginDetail.parentUid && ele[1].label === "Manage User") ? (
              <View
                key={key}
                style={[
                  !!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP ? styles.leftMenuItemsHolder : null,
                  (!!ele[1].routes && Object.keys(ele[1].routes).includes(currentRoute.name)) || ele[0] === currentRoute.name
                    ? !!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP
                      ? styles.activeMenuHolder
                      : null
                    : null,
                ]}>
                <Link
                  to={!!ele[1].routes ? Object.keys(ele[1].routes)[0] : ele[0]}
                  onPress={this.closeMenu}
                  style={[
                    styles.leftMenuItems,
                    (!!ele[1].routes && Object.keys(ele[1].routes).includes(currentRoute.name)) || ele[0] === currentRoute.name
                      ? !!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP
                        ? leftMenuToggle
                          ? styles.pchActive
                          : styles.pchToggleActive
                        : leftMenuToggle
                        ? styles.active
                        : styles.toggleActive
                      : null,
                  ]}>
                  {/* {leftMenuToggle ? (
                      !!ele[1].iconPath ? (
                        <Image source={icDashboardB} style={[commonStyles.icon20, styles.menuIcon]} />
                      ) : (
                        <View style={styles.emptyIcon}></View>
                      )
                    ) : null} */}
                  {!!ele[1].whiteIconPath ? (
                    !!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP ? (
                      (!!ele[1].routes && Object.keys(ele[1].routes).includes(currentRoute.name)) || ele[0] === currentRoute.name ? (
                        <Image source={ele[1].blueIconPath} style={[commonStyles.icon20, styles.menuIcon]} />
                      ) : (
                        <Image source={ele[1].iconPath} style={[commonStyles.icon20, styles.menuIcon]} />
                      )
                    ) : leftMenuToggle ? (
                      <Image source={ele[1].iconPath} style={[commonStyles.icon20, styles.menuIcon]} />
                    ) : (
                      <Image source={ele[1].whiteIconPath} style={[commonStyles.icon20, styles.menuIcon]} />
                    )
                  ) : (
                    <View style={styles.emptyIcon}></View>
                  )}
                  {leftMenuToggle ? (
                    !!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP ? null : (
                      <Text style={[styles.menuText, commonFontStyles.workSans, commonStyles.heading6]}>{ele[1].label}</Text>
                    )
                  ) : null}
                </Link>
              </View>
            ) : null
          )}
          {!!isMobileDevice && userType === USER_TYPE.XBP ? (
            <View style={styles.copyRight}>
              <Text style={[commonFontStyles.workSans, commonStyles.smallHeading]}>
                © {new Date().getFullYear()} Exela Technologies, All rights reserved.
              </Text>
            </View>
          ) : null}
        </View>
        //{/* {!!bottomVisibility && !!isMobileDevice && userType !== USER_TYPE.XBP ? null : (
        //<View style={leftMenuToggle ? styles.leftMenuOverlayOpen : styles.leftMenuOverlayClose}></View>
        //)} */}
        // </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  leftMenuWrapper: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          position: "relative",
        };
    },
  },
  leftMenu: {
    display: "flex",
    flexDirection: "column",
    height: "calc(100vh - 64px)",
    background: "#fff",
    boxShadow: "3px 0px 2px #0000001A",
    transition: "all 0.3s ease",
    zIndex: 999999,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          height: "calc(100vh - 56px)",
        };
    },
  },
  openMenu: {
    maxWidth: 256,
    minWidth: 256,
    transition: "all 0.3s ease",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          transition: "all 0.3s ease",
          maxWidth: 256,
          position: "absolute",
          width: 256,
          marginLeft: 0,
        };
    },
  },
  closeMenu: {
    background: "#015372",
    maxWidth: 64,
    minWidth: 64,
    transition: "all 0.3s ease",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          transition: "all 0.3s ease",
          maxWidth: 0,
          marginLeft: -256,
        };
    },
  },
  leftMenuItemsHolder: {
    flex: 1,
    alignItems: "center",
    borderTop: "1px solid #38c2ef",
  },
  activeMenuHolder: {
    borderTop: "1px solid #016788",
  },
  leftMenuItems: {
    height: 56,
    display: "flex",
    alignItems: "center",
  },
  menuIcon: {
    margin: "0 22px",
  },
  whiteColorIcon: {
    ColorFill: "#fff",
  },
  menuText: {
    color: "#182832",
    // fontFamily: "WorkSans-Regular",
  },
  leftMenuOverlayOpen: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          position: "absolute",
          height: "calc(100vh - 56px)",
          width: "100vw",
          transition: "all 0.3s ease",
          background: "#00000052",
          zIndex: 998,
        };
    },
  },
  leftMenuOverlayClose: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          // position: "absolute",
          // height: "calc(100vh - 56px)",
          // width: "0vw",
          // transition: "all 0.3s ease",
          // background: "#00000052",
          display: "none",
        };
    },
  },
  active: {
    background: "#58c8f2",
    borderLeft: "2px solid #4DC5F2",
  },
  toggleActive: {
    background: "#397ea024",
    borderLeft: "2px solid #4DC5F2",
    transition: "all 0.3s ease",
  },
  emptyIcon: {
    minWidth: 64,
  },
  pchLeftMenuWrapper: {
    width: "100%",
    height: "auto",
    position: "unset",
  },
  pchLeftMenu: {
    maxWidth: "100%",
    width: "100%",
    display: "flex",
    flexDirection: "row",
    bottom: 0,
    position: "absolute",
    height: 56,
    background: "#fff",
  },
  pchOpen: {},
  pchCloseMenu: {},
  pchActive: {
    color: "red",
  },
  pchToggleActive: {
    color: "green",
  },
  copyRight: {
    ...StyleSheet.margin("auto", 16, 16, 16),
  },
});
