import React, { Component } from "react";
import R14, {
  StyleSheet,
  View,
  Text,
  Touchable,
  Image,
  ScrollView,
  FlatList,
  Modal,
  Colors,
  Form,
  Dimensions,
  TextInputField,
  DatePickerField,
  Button,
  SubmitButton,
} from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import CommonUtility from "../../utils/CommonUtility";
import CommonValidators from "../../utils/CommonValidators";
import StatusComponent from "../StatusComponent";
import NoRecordsFound from "../NoRecordsFound";
import DateUtils from "../../utils/DateUtils";
import { BILL_STATUS, USER_TYPE } from "../../constant/app.data.constant";
import PDFViewerComponent from "./PDFViewerComponent";
import ModalPopUp from "../ModalPopUp";
import CopyRight from "../CopyRight";

const newBillsIcon = require("../../assets/images/New-bills.png");
const outstandingIcon = require("../../assets/images/Outstanding.png");
const overdueIcon = require("../../assets/images/Overdue.png");
const practitionerIcon = require("../../assets/images/practitioner.png");
const attachedFileIcon = require("../../assets/images/attachedFileIcon.png");
const viewBlueIcon = require("../../assets/images/viewBlue.png");

export default R14.connect(
  class BillItemsComponent extends Component {
    constructor(props) {
      super(props);
      this.state = {
        addedHeight: 0,
        isVisible: false,
        password: "",
        uploadBill: {},
        docAction: "",
        isDocVisible: false,
        fileContent: "",
        selectedBlock: [this.props.defaultBlock],
        isProfilePic: false,
      };
      this.ssid = "";
    }

    componentDidMount() {}

    onSubmitBill = (item) => {
      this.props.app.dm.uploadBillDomain.setState({
        isSubmitted: true,
      });

      this.props.app.nav.to("uploadedBillsRoute", { uid: item.uid });
    };

    onNavigate = (item) => {
      this.props.app.dm.uploadBillDomain.setState({
        InSideNavigation: true,
      });
      this.props.app.nav.to("uploadedBillsRoute", { uid: item.uid });
    };

    onBlockNavigatation = (type) => {
      this.props.app.dm.dashboard.setState({
        selectedBlock: [type],
      });
    };

    navigateTo = async (path) => {
      this.props.app.nav.to(path);
    };

    renderSelectedFilterOpt() {
      let currentRoute = this.props.app.nav.route;
      const { statusOtp, handleCross } = this.props;
      return !!statusOtp && !!statusOtp.length ? (
        <View style={[commonStyles.dFlex, styles.filterBySection, currentRoute.name === "dashboard" ? commonStyles.displayNone : null]}>
          <View style={[styles.filtereHading, commonStyles.padding16]}>
            <Text style={[styles.marB10, commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight600]}>Filter by: </Text>
          </View>
          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.flexWrap]}>
            {statusOtp.map((el, ind) => (
              <StatusComponent
                key={ind}
                label={el}
                isSelected={true}
                circleNotAllowed={true}
                hasClose
                onCrossClick={() => {
                  handleCross(el);
                }}
              />
            ))}
          </View>
        </View>
      ) : null;
    }

    getHeightOfElement = (event) => {
      const { height } = event.nativeEvent.layout;
      this.setState({
        addedHeight: height,
      });
      // console.log(this.state.addedHeight);
    };

    onPasswordDateBlur = () => {
      const { dOBPassword } = this.props.app.ui.form("billPassword").values;
      if (!!dOBPassword) {
        let dat = this.validateDate(dOBPassword);
        if (!!dat) {
          this.props.app.ui.form("billPassword").elmts.dOBPassword.setValue(dOBPassword);
          this.props.app.ui.form("billPassword").elmts.dOBPassword.setError(null);
          this.onPassDateValueChange(dOBPassword, false);
        } else {
          // this.setState({
          //   password: "",
          // });
          this.ssid = "";
          this.props.app.ui.form("billPassword").elmts.dOBPassword.setValue("");
          this.props.app.ui.form("billPassword").elmts.dOBPassword.setError("Date format MM/DD/YYYY");
        }
      } else {
        // this.setState({
        //   password: "",
        // });
        this.ssid = "";
        this.props.app.ui.form("billPassword").elmts.dOBPassword.setError(null);
      }
    };

    validateDate = (testdate) => {
      let date_regex = /^\d{2}\/\d{2}\/\d{4}$/;
      return date_regex.test(testdate);
    };

    onPassDateValueChange = (val, autoformat = true) => {
      if (!!autoformat) this.props.app.ui.form("billPassword").elmts.dOBPassword.setValue(DateUtils.formatDateInMMDDYYYY(val));

      this.setState({
        password: val,
      });
    };

    onOk = async (form) => {
      const { dOBPassword } = form.values;
      const filePassword = this.ssid; //this.state.password;
      const { uploadBill } = this.state;

      // if (!!uploadBill.ssId ? !!!filePassword : !!!dOBPassword) {
      //   this.props.app.dm.commonDomain.showToastr("error", !!uploadBill.ssId ? "Enter SSID" : "Enter DOB");
      //   return false;
      // }
      if (!!uploadBill.ssId ? filePassword === uploadBill.ssId : new Date(dOBPassword).toString() === new Date(uploadBill.dOB).toString()) {
        await this.downloadOrViewBillDocuments();
      } else {
        this.props.app.dm.commonDomain.showToastr("error", !!uploadBill.ssId ? "Incorrect SSID" : "Incorrect DOB");
        if (form.elmts.filePassword) {
          form.elmts.filePassword.setValue(null);
          this.ssid = "";
          // this.setState(
          //   {
          //     password: "",
          //   },
          //   () => {
          //     form.elmts.filePassword.setValue(null);
          //     this.ssid = "";
          //   }
          // );
        }
      }
    };

    downloadOrViewBillDocuments = async () => {
      const { uploadBill } = this.state;
      let billDocuments = await this.props.app.dm.uploadBillDomain.fetchUploadedBillDocuments(uploadBill.uid);
      if (!!billDocuments && !!billDocuments.uploadedBillDocuments && !!billDocuments.uploadedBillDocuments.length > 0) {
        if (this.state.docAction === "View") {
          this.setState({ fileContent: billDocuments.uploadedBillDocuments[0].content, isDocVisible: true });
          this.onCancel();
        } else {
          let url = `data:application/octet-stream;base64,${billDocuments.uploadedBillDocuments[0].content}`;
          const downloadLink = document.createElement("a");
          downloadLink.href = url;
          downloadLink.download = billDocuments.uploadedBillDocuments[0].filename;
          downloadLink.click();
          this.onCancel();
        }
      }
    };
    onChangeSSIDText = (val) => {
      let text = "";
      if (val.length < this.ssid.length && this.ssid != "") {
        this.ssid = this.ssid.substr(0, this.ssid.length - 1);
      } else {
        this.ssid = val.length === 1 ? val : this.ssid + val.substr(val.length - 1);
      }
      // this.setState({
      //   password: this.ssid,
      // });

      for (let i = 0; i < val.length; i++) {
        text = text + "*";
      }
      this.props.app.ui.form("billPassword").elmts.filePassword.setValue(text);
    };

    onDownloadFile = async (item, action) => {
      this.ssid = "";
      this.setState(
        {
          isVisible: !!item.isPatientRequest ? false : true,
          uploadBill: item,
          docAction: action,
          // password: "",
        },
        async () => {
          if (!!item.isPatientRequest) await this.downloadOrViewBillDocuments();
        }
      );
      // if (!!item.isPatientRequest) await this.downloadOrViewBillDocuments();
    };

    onCancel = () => {
      this.setState({
        isVisible: false,
      });
    };

    renderModelPopup() {
      const { isVisible, password, uploadBill } = this.state;
      return (
        <Modal visible={isVisible} animationType="fade" style={[styles.filePassowrdModalWrapper, styles.modalWrapper]}>
          <View style={[styles.filePassowrdModalBodyWrapper]}>
            <Form
              name="billPassword"
              style={[commonStyles.padding0]}
              onSubmit={this.onOk}
              validateBeforeSubmit={true}
              validators={CommonValidators.getFormValidator()}>
              <View>
                <View style={[commonStyles.textAlignCenter, commonStyles.marginBottom15]}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading5]}>
                    {!!uploadBill.ssId ? "Enter SSID" : "Enter DOB"}
                  </Text>
                </View>
                {!!uploadBill.ssId ? (
                  <View style={[commonStyles.marginTop10, commonStyles.paddingLeftRight10]}>
                    <TextInputField
                      name="filePassword"
                      label="Enter SSID*"
                      required="Enter SSID"
                      secure={false}
                      autoComplete={false}
                      onChangeText={this.onChangeSSIDText}
                      style={[commonFontStyles.workSans, commonStyles.heading6]}
                      labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                      labelStyle={[commonStyles.formLabelStyle]}
                    />
                  </View>
                ) : (
                  <View style={[commonStyles.marginTop10, commonStyles.paddingLeftRight10]}>
                    <DatePickerField
                      style={[commonFontStyles.workSans, commonStyles.marginBottom10, commonStyles.heading5]}
                      name="dOBPassword"
                      label="Enter DOB*"
                      required="Enter DOB"
                      mode="dropdown"
                      textInputEditable={true}
                      value={this.state.dOBPassword}
                      // secure={true}
                      labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans]}
                      labelStyle={[commonStyles.formLabelStyle]}
                      helperTextStyle={[styles.dobLabelStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                      onValueChange={this.onPassDateValueChange}
                      onBlur={this.onPasswordDateBlur}
                      onFocus={this.onPasswordDateBlur}
                    />
                  </View>
                )}
              </View>
              <View style={[styles.btnHolder, commonStyles.dFlex, commonStyles.justifyContentCenter]}>
                <Button
                  title={"Cancel"}
                  style={[
                    commonStyles.blueBorderButton,
                    commonStyles.marginRight8,
                    commonStyles.flex1,
                    styles.cancelBtn,
                    commonStyles.flexBasis48,
                    styles.buttons,
                  ]}
                  onPress={this.onCancel}
                  key="billCancel"
                  titleTextStyle={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6, commonStyles.transperentBtnColor]}
                />
                <SubmitButton
                  title="Ok"
                  key="billOk"
                  style={[
                    commonStyles.blueButton,
                    styles.declinedBtn,
                    commonStyles.marginLeft8,
                    commonStyles.flex1,
                    commonStyles.flexBasis48,
                    styles.buttons,
                  ]}
                  titleTextStyle={[commonStyles.fontWeight600, commonStyles.black, commonStyles.blueButtonColor, commonFontStyles.workSans]}
                  // disabled={!this.ssid}
                />
              </View>
            </Form>
          </View>
        </Modal>
      );
    }

    onPDFClose = async () => {
      this.setState({ isDocVisible: false });
    };

    renderPDFViewer() {
      const { isDocVisible, fileContent } = this.state;
      return (
        <ModalPopUp
          visibility={isDocVisible}
          isFooter={false}
          bodyWrapper={styles.PDFViewBodyWrapper}
          modalContent={styles.PDFModalContent}
          heading={" "}
          handleCancel={this.onPDFClose}
          closeButton={styles.closeBtn}
          modalHeading={[]}
          animationType="fade"
          footerClass={commonStyles.padding0}
          style={[styles.filePassowrdModalWrapper, styles.modalWrapper]}>
          <View>
            <PDFViewerComponent dataSource={fileContent} dataSourceType="pdf" disableFileName={true} />
          </View>
        </ModalPopUp>
      );
    }

    renderItems = async () => {
      const { filterBills } = this.props;
      const { selectedBlock } = this.props.app.dm.dashboard.state;
      const { isSearchOpen } = this.props.app.dm.userSearchDomain.state;
      const { selectedChk, sortOrder } = this.props.app.dm.filterDomain.state;
      let blockItems = selectedBlock.includes("All") ? filterBills : CommonUtility.filterStatus(filterBills, selectedBlock, "PCH");
      this.setState({
        getData: CommonUtility.displayBillsFilter(blockItems, isSearchOpen ? true : false, selectedChk, sortOrder, "PCH"),
      });
    };

    render() {
      const { isHeader, statusOtp, filterBills } = this.props;
      const { selectedBlock } = this.props.app.dm.dashboard.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      // const { addedHeight, getData } = this.state;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      const { selectedChk, sortOrder } = this.props.app.dm.filterDomain.state;
      let isDashboard = isHeader;
      // let dashboarScroll = addedHeight > Dimensions.get("window").height - 267;
      // console.log(dashboarScroll);
      // console.log("Full height of window ", Dimensions.get("window").height);
      // console.log("Full height of window with minus 217  ", Dimensions.get("window").height - 267);
      let blockItems = selectedBlock.includes("All") ? filterBills : CommonUtility.filterStatus(filterBills, selectedBlock, "PCH");
      let getData = CommonUtility.displayBillsFilter(blockItems, false, selectedChk, sortOrder, "PCH");
      let userType = this.props.userType;
      let IsAllSubmmitedBill = !!getData && getData.filter((x) => x.billStatus === "New").length > 0 ? false : true;
      let { isProfilePic } = this.state;
      return (
        <ScrollView
          style={[
            commonStyles.dashboardScrollView,
            isDashboard ? styles.top0 : styles.top50,
            leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu,
          ]}>
          <View style={styles.pageTitle}>
            <Text style={[styles.text, commonStyles.heading3, commonStyles.fontWeight500, commonFontStyles.workSans]}>
              {this.props.app.nav.route.label}{" "}
            </Text>
          </View>
          <View style={[styles.table]}>
            <View style={[commonStyles.paddingLeftRight16]}>{this.renderSelectedFilterOpt()}</View>
            {this.renderModelPopup()}
            {this.renderPDFViewer()}
            {isDashboard ? (
              <View style={[styles.dashboardHeader]}>
                <View style={[commonStyles.dFlex, styles.countBoxHolder]}>
                  <Touchable
                    style={[selectedBlock.includes("All") ? styles.newCounter : styles.disabledCounter, styles.allBlock, styles.pchCountBox]}
                    onPress={() => this.onBlockNavigatation("All")}>
                    <View style={[commonStyles.dFlex]}>
                      {/* <View style={[commonStyles.paddingTop5, commonStyles.webView]}>
                      <Text style={[styles.billCountText, commonStyles.fontWeight500, commonFontStyles.workSans]}>
                        {newBillCount + submittedBillCount}
                      </Text>
                    </View> */}
                      <View style={[styles.countBoxTitle]}>
                        <Text
                          style={[
                            styles.countBoxTitleText,
                            commonFontStyles.workSans,
                            selectedBlock.includes("All") ? styles.selectedBlocktext : styles.unSelectedBlocktext,
                          ]}>
                          All
                        </Text>
                      </View>
                    </View>
                    {/* <View style={[styles.countBoxImage, commonStyles.justifyContentSpaceBetween]}>
                    <View style={[commonStyles.paddingTop5, commonStyles.mView]}>
                      <Text style={[styles.billCountText, commonStyles.fontWeight500, commonFontStyles.workSans]}>
                        {newBillCount + submittedBillCount}
                      </Text>
                    </View>
                    <View>
                      <Image source={newBillsIcon} style={styles.dollarSign} />
                    </View>
                  </View> */}
                  </Touchable>
                  <Touchable
                    style={[selectedBlock.includes("New") ? styles.newCounter : styles.disabledCounter, styles.newBlock, styles.pchCountBox]}
                    onPress={() => this.onBlockNavigatation("New")}>
                    <View style={[commonStyles.dFlex]}>
                      {/* <View style={[commonStyles.paddingTop5, commonStyles.webView]}>
                      <Text style={[styles.billCountText, commonStyles.fontWeight500, commonFontStyles.workSans]}>{newBillCount}</Text>
                    </View> */}
                      <View style={[styles.countBoxTitle]}>
                        <Text
                          style={[
                            styles.countBoxTitleText,
                            commonFontStyles.workSans,
                            selectedBlock.includes("New") ? styles.selectedBlocktext : styles.unSelectedBlocktext,
                          ]}>
                          New
                        </Text>
                      </View>
                    </View>
                    {/* <View style={[styles.countBoxImage, commonStyles.justifyContentSpaceBetween]}>
                    <View style={[commonStyles.paddingTop5, commonStyles.mView]}>
                      <Text style={[styles.billCountText, commonStyles.fontWeight500, commonFontStyles.workSans]}>{newBillCount}</Text>
                    </View>
                    <View>
                      <Image source={outstandingIcon} style={styles.dollarSign} />
                    </View>
                  </View> */}
                  </Touchable>
                  <Touchable
                    style={[
                      selectedBlock.includes("Submitted") ? styles.newCounter : styles.disabledCounter,
                      styles.submitedBlock,
                      styles.pchCountBox,
                    ]}
                    onPress={() => this.onBlockNavigatation("Submitted")}>
                    <View style={[commonStyles.dFlex]}>
                      {/* <View style={[commonStyles.paddingTop5, commonStyles.webView]}>
                      <Text style={[styles.billCountText, commonStyles.fontWeight500, commonFontStyles.workSans]}>{submittedBillCount}</Text>
                    </View> */}
                      <View style={[styles.countBoxTitle]}>
                        <Text
                          style={[
                            styles.countBoxTitleText,
                            commonFontStyles.workSans,
                            selectedBlock.includes("Submitted") ? styles.selectedBlocktext : styles.unSelectedBlocktext,
                          ]}>
                          Submitted
                        </Text>
                      </View>
                    </View>
                    {/* <View style={[styles.countBoxImage, commonStyles.justifyContentSpaceBetween]}>
                    <View style={[commonStyles.paddingTop5, commonStyles.mView]}>
                      <Text style={[styles.billCountText, commonStyles.fontWeight500, commonFontStyles.workSans]}>{submittedBillCount}</Text>
                    </View>
                    <View>
                      <Image source={overdueIcon} style={styles.dollarSign} />
                    </View>
                  </View> */}
                  </Touchable>
                </View>
              </View>
            ) : null}
            {!!getData.length > 0 ? (
              <View style={[styles.thead]}>
                {/* !!dashboarScroll ? styles.customPaddingRight16 : null */}
                <View style={[commonStyles.dFlex, commonStyles.flexGrow1, styles.tdLeftSection, styles.thLeftSection]}>
                  <View
                    style={[
                      styles.billerName,
                      commonStyles.dFlex,
                      styles.leftMenuToggleMakePaymentBillname,
                      styles.makePaymentBillName,
                      userType === USER_TYPE.PCH_PATIENT ? null : styles.patientBillerName,
                    ]}>
                    <View style={[styles.th]}>
                      <Text style={[commonStyles.heading6, commonStyles.fontWeight600, commonFontStyles.workSans]}>
                        {userType == USER_TYPE.PCH_PATIENT ? "Practitioner Name" : "Patient Mobile No"}
                      </Text>
                    </View>
                  </View>
                  <View style={[styles.billerDetails, commonStyles.dFlex, commonStyles.flexGrow1]}>
                    <View style={[styles.th, styles.billerSubj, commonStyles.flexGrow1]}>
                      <Text style={[commonStyles.heading6, commonStyles.fontWeight600, commonFontStyles.workSans]}>Provider Name</Text>
                    </View>
                    <View style={[styles.th, styles.th, styles.dateAndTime, commonStyles.flexGrow1]}>
                      <Text style={[commonStyles.heading6, commonStyles.fontWeight600, commonFontStyles.workSans]}>Uploaded On</Text>
                    </View>
                  </View>
                </View>
                <View
                  style={[
                    styles.billerStatus,
                    commonStyles.dFlex,
                    styles.makePaymentTheadRightSection,
                    styles.makePaymenttdRightSection,
                    styles.tdRightSection,
                    userType === USER_TYPE.PCH_PATIENT ? null : styles.providerRightTHSection,
                    IsAllSubmmitedBill ? styles.tdRightSectionWithoutSubmitBtn : null,
                  ]}>
                  <View style={[styles.th, styles.billStatus, styles.thBillStatus, styles.dashboardThBillStatus, styles.makePaymentThBillStatus]}>
                    <Text style={[commonStyles.heading6, commonStyles.fontWeight600, commonFontStyles.workSans]}>Status</Text>
                  </View>
                  {/* {userType == USER_TYPE.PCH_PROVIDER ? null : ( */}
                  <View
                    style={[
                      styles.th,
                      styles.thBillAction,
                      styles.billAction,
                      styles.thAction,
                      styles.makePaymentAction,
                      userType === USER_TYPE.PCH_PATIENT ? styles.patientBillAction : styles.providerBillActionTh,
                      IsAllSubmmitedBill ? styles.actionWithoutSubmitBtn : null,
                    ]}>
                    <Text style={[commonStyles.heading6, commonStyles.fontWeight600, commonFontStyles.workSans]}>Action</Text>
                  </View>
                  {/* )} */}
                </View>
              </View>
            ) : null}
            <View style={[styles.tboday]}>
              <FlatList
                // onLayout={(event) => this.getHeightOfElement(event)}
                style={[
                  styles.messageComponent,
                  isDashboard ? styles.dashboardMessageComponent : null,
                  !!isDashboard && !!getData.length == 0 ? styles.dashboardNoListView : !!getData.length == 0 ? styles.simpleMessageNoListView : null,
                  !!statusOtp && !!statusOtp.length ? styles.positionStatic : null,
                ]}
                contentContainerStyle={styles.contentContainerStyle}
                ListEmptyComponent={
                  <View style={styles.padd10}>
                    <NoRecordsFound noRecords={[styles.noRecords, isDashboard ? null : commonStyles.marginTop15]} message={"No bills found."} />
                  </View>
                }
                data={getData}
                keyExtractor={(el, i) => i}
                renderItem={({ item, index }) => (
                  <View style={[styles.tr, getData.length - 1 === index ? styles.borderBottom1 : null]}>
                    {userType == USER_TYPE.PCH_PATIENT ? (
                      <View style={styles.parctionerIcon}>
                        <Image source={practitionerIcon} style={commonStyles.icon30} />
                      </View>
                    ) : // <View style={styles.userIconDiv}>
                    //   <Text
                    //     style={[
                    //       commonStyles.heading5,
                    //       commonStyles.fontWeight600,
                    //       commonFontStyles.workSans,
                    //       commonStyles.userInitial,
                    //       styles.userInitial,
                    //     ]}>
                    //     {!!item.userProfile && !!item.userProfile.firstName
                    //       ? CommonUtility.shortName(item.userProfile.firstName)
                    //       : CommonUtility.shortName("NA")}
                    //   </Text>
                    // </View>
                    null}
                    <Touchable
                      style={[
                        commonStyles.dFlex,
                        commonStyles.flexGrow1,
                        styles.tdLeftSection,
                        userType === USER_TYPE.PCH_PROVIDER ? commonStyles.pointerEventsNone : null,
                      ]}
                      onPress={() => this.onNavigate(item)}>
                      <View
                        style={[
                          styles.billerName,
                          commonStyles.dFlex,
                          commonStyles.margin0,
                          styles.dashboardBillerName,
                          //userType == USER_TYPE.PCH_PROVIDER ? styles.providerMobNum:
                          styles.makePaymentBillerName,
                        ]}>
                        <View style={[styles.td, commonStyles.dFlex, commonStyles.alignItemCenter]}>
                          <View style={[commonStyles.dFlex, commonStyles.flexDirectionRow, styles.billName, commonStyles.alignItemsCenter]}>
                            <Text
                              style={[styles.cardHeadingText, commonFontStyles.workSans, commonStyles.wordBreakWord, styles.textOverflowEllipsis]}
                              numberOfLines={1}>
                              {userType == USER_TYPE.PCH_PATIENT
                                ? !!item.userProfile && !!item.userProfile.firstName
                                  ? item.userProfile.firstName
                                  : !!isMobileDevice
                                  ? ""
                                  : "Refer Doc"
                                : CommonUtility.mobileNumberFormatting(item.mobileNumber)}
                            </Text>
                          </View>
                        </View>
                      </View>
                      <View
                        style={[
                          styles.billerDetails,
                          commonStyles.dFlex,
                          commonStyles.flexGrow1,
                          styles.dashboardAlignItemsStart,
                          //  userType == USER_TYPE.PCH_PROVIDER ? styles.providerRightSection :styles.patientRightSection
                          styles.patientRightSection,
                        ]}>
                        {!!isMobileDevice ? (
                          !!item.pchDoctorDetail && item.pchDoctorDetail.DoctorName ? (
                            <View
                              style={[
                                styles.td,
                                // commonStyles.flexGrow1,
                                styles.tdType,
                                styles.billerSubj,
                                // userType == USER_TYPE.PCH_PROVIDER ? styles.providerTdBillerSubj: styles.tdBillerSubj,
                                styles.tdBillerSubj,
                              ]}>
                              <View>
                                <Touchable onPress={() => (userType === USER_TYPE.PCH_PATIENT ? this.onNavigate(item) : null)}>
                                  <Text style={[styles.cardSubHeadingText, commonFontStyles.workSans, commonStyles.wordBreakWord]} numberOfLines={1}>
                                    {!!item.pchDoctorDetail && item.pchDoctorDetail.DoctorName ? item.pchDoctorDetail.DoctorName : ""}
                                  </Text>
                                </Touchable>
                              </View>
                            </View>
                          ) : null
                        ) : (
                          <View
                            style={[
                              styles.td,
                              commonStyles.flexGrow1,
                              styles.tdType,
                              styles.billerSubj,
                              // userType == USER_TYPE.PCH_PROVIDER ? styles.providerTdBillerSubj: styles.tdBillerSubj,
                              styles.tdBillerSubj,
                            ]}>
                            <View>
                              <Text style={[styles.cardSubHeadingText, commonFontStyles.workSans, commonStyles.wordBreakWord]} numberOfLines={1}>
                                {!!item.pchDoctorDetail && item.pchDoctorDetail.DoctorName ? item.pchDoctorDetail.DoctorName : ""}
                              </Text>
                            </View>
                          </View>
                        )}
                        <View style={[styles.td, commonStyles.flexGrow1, styles.tdDueIn, styles.dueOn]}>
                          <View>
                            <Text style={[commonStyles.darkGrayColor, commonFontStyles.workSans, styles.labelValue, styles.cardSubHeadingText]}>
                              {DateUtils.dateAndTimeWithAmPm(item.createdAt, "PCH")}
                            </Text>
                          </View>
                        </View>
                        {!!isMobileDevice && !BILL_STATUS[item.billStatus] ? (
                          <View style={[commonStyles.marginTopBottom5, userType === USER_TYPE.PCH_PATIENT ? styles.submitClaimButton : null]}>
                            {userType == USER_TYPE.PCH_PROVIDER ? null : (
                              <Touchable
                                style={[
                                  styles.paynowBtn,
                                  BILL_STATUS[item.billStatus] || userType === USER_TYPE.PCH_PROVIDER ? commonStyles.disabledBtn : null,
                                ]}
                                onPress={() =>
                                  BILL_STATUS[item.billStatus] || userType === USER_TYPE.PCH_PROVIDER ? null : this.onSubmitBill(item)
                                }>
                                <Text
                                  style={[
                                    commonStyles.heading6,
                                    commonStyles.fontWeight500,
                                    commonFontStyles.workSans,
                                    commonStyles.whiteColor,
                                    commonStyles.transformUpperCase,
                                  ]}>
                                  Submit Claim
                                </Text>
                              </Touchable>
                            )}
                          </View>
                        ) : null}
                      </View>
                    </Touchable>
                    <View
                      style={[
                        styles.billerStatus,
                        commonStyles.dFlex,
                        commonStyles.flexGrowUnset,
                        styles.dashboardAlignItemsStart,
                        // userType == USER_TYPE.PCH_PROVIDER ? styles.providerRightSection :styles.rightSectionTd,
                        styles.rightSectionTd,
                        userType == USER_TYPE.PCH_PROVIDER ? styles.providerRightSectionTD : null,
                        IsAllSubmmitedBill ? styles.rightSectionTdWithoutSubmitBtn : null,
                      ]}>
                      <View style={[styles.billStatus, styles.makePaymentBillStatus]}>
                        {/* <StatusComponent marginRight={[commonStyles.marRT0, commonStyles.marB0]} label={item.billStatus} isSelected={false} paymentStatusLabel={item.paymentStatus} circleNotAllowed={true}/> */}
                        <Text
                          style={[
                            commonStyles.fontWeight500,
                            commonFontStyles.workSans,
                            styles.billStatusChip,
                            isMobileDevice ? commonStyles.fontWeight600 : null,
                          ]}>
                          {item.billStatus}
                        </Text>
                      </View>
                      {/* {userType == USER_TYPE.PCH_PROVIDER ? null : ( */}
                      <View
                        style={[
                          styles.td,
                          styles.billAction,
                          styles.makePaymentAction,
                          styles.makePaymentActionColumn,
                          styles.dashboardActionColumn,
                          userType == USER_TYPE.PCH_PATIENT ? styles.patientBillAction : styles.providerBillAction,
                        ]}>
                        {!isMobileDevice ? (
                          userType === USER_TYPE.PCH_PROVIDER || BILL_STATUS[item.billStatus] ? null : (
                            <Touchable
                              style={[
                                styles.paynowBtn,
                                BILL_STATUS[item.billStatus] || userType === USER_TYPE.PCH_PROVIDER ? commonStyles.disabledBtn : null,
                              ]}
                              onPress={() => (BILL_STATUS[item.billStatus] || userType === USER_TYPE.PCH_PROVIDER ? null : this.onSubmitBill(item))}>
                              <Text
                                style={[
                                  commonStyles.heading6,
                                  commonStyles.fontWeight500,
                                  commonFontStyles.workSans,
                                  commonStyles.whiteColor,
                                  commonStyles.transformUpperCase,
                                ]}>
                                Submit Claim
                              </Text>
                            </Touchable>
                          )
                        ) : null}
                        {1 === 2 ? (
                          <Touchable
                            style={
                              userType == USER_TYPE.PCH_PROVIDER ? (!!isMobileDevice ? null : commonStyles.marginRight25) : styles.marginLeftRight
                            }
                            onPress={() => this.onDownloadFile(item, "Download")}>
                            <Image source={attachedFileIcon} style={commonStyles.icon20} />
                          </Touchable>
                        ) : null}
                        <Touchable
                          style={[userType === USER_TYPE.PCH_PROVIDER ? null : styles.viewPdfIcon]}
                          onPress={() => this.onDownloadFile(item, "View")}>
                          <Image source={viewBlueIcon} style={styles.viewPdfIconSize} />
                        </Touchable>
                      </View>
                      {/* )} */}
                    </View>
                  </View>
                )}
              />
            </View>
          </View>
          <CopyRight />
        </ScrollView>
      );
    }
  }
);

