import React, { Component } from "react";
import R14, { StyleSheet, View, Button, Image, Text } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";

const closeCircleIcon = require("../assets/images/close-circle-icon.png");

export default R14.connect(
  class DeclineMessageComponent extends Component {
    constructor(props) {
      super(props);
    }

    render() {
      return (
        <View style={styles.declineMessageComponent}>
          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter, styles.crossIcon]}>
            <Image source={closeCircleIcon} style={commonStyles.icon36} />
          </View>
          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
            <Text style={[commonFontStyles.workSans, commonStyles.heading4, commonStyles.fontWeight500, commonStyles.redColor]}>
              You have declined the bill.
            </Text>
          </View>
          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter, commonStyles.marTop30]}>
            <Button
              key="gotohome"
              title={"Go to Dashboard"}
              style={[commonStyles.blueBorderButton]}
              titleTextStyle={[
                commonStyles.blueBorderButtonFontStyle,
                commonStyles.heading6,
                commonStyles.blueBorderButtonColor,
                commonFontStyles.workSans,
              ]}
              to="makePaymentRoutes"
            />
          </View>
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  declineMessageComponent: {
    display: "flex",
    flexDirection: "column",
    flexGrow: 1,
    height: "calc(100vh - 410px)",
    screen: ({ width }) => {
      if (width <= 360)
        return {
          height: "calc(100vh - 415px)",
        };
    },
  },
  crossIcon: {
    margin: "45px 0 15px",
    screen: ({ width }) => {
      if (width <= 360)
        return {
          margin: "15px 0",
        };
    },
  },
});
