import React, { PureComponent } from "react";
import { StyleSheet, Button, View, Text, Form } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import ModalPopUp from "./ModalPopUp";
import RadioButtons from "../components/RadioButtons";
import StatusComponent from "./StatusComponent";
import ToggleSwitch from "./ToggleSwitch";

export default class FilterComponent extends PureComponent {
  onDateChange = (selectedChk) => {
    this.props.onCheckboxChange(selectedChk);
  };

  onSwitch = (sortOrder) => {
    this.props.onSwitchChange(sortOrder);
  };

  onStatus = (ind) => {
    this.props.onStatusChange(ind);
  };

  onFilterCall = () => {
    this.props.onFilter();
  };

  render() {
    const { isVisible, onCancelFilter, defaultFilter } = this.props;
    const { selectedChk, sortOrder, statusType, selectedStatus, radioOpt } = defaultFilter;
    const statusGrp = [...new Set(statusType.map(({ groupLabel }) => groupLabel))];
    return (
      <ModalPopUp
        visibility={isVisible}
        modalPopupCancelBtn={commonStyles.displayNone}
        modalPopupOkBtn={commonStyles.displayNone}
        closeButton={commonStyles.displayNone}
        bodyWrapper={[styles.isSortModal]}
        isFooter={false}
        modalContent={commonStyles.marginTop15}
        modalOuterWrapper={styles.modalBackdrop}>
        <View style={[styles.sortByHeading]}>
          <Text style={[commonStyles.heading5, commonStyles.fontWeight600, commonFontStyles.workSans]}>Sort by</Text>
        </View>
        <View>
          <Form
            title="SortFilter"
            name="sortFilter"
            style={styles.formPadding}
            controlsBottomRight={[
              <Button
                key="cancel"
                title="Cancel"
                style={[commonStyles.blueBorderButton, commonStyles.flexBasis50, commonStyles.marginRight15]}
                titleTextStyle={[commonStyles.fontWeight600, commonStyles.blueBorderButtonColor, commonFontStyles.workSans]}
                onPress={onCancelFilter}
              />,
              <Button
                title="Apply"
                key="apply"
                style={[commonStyles.flexBasis50, commonStyles.blueButton]}
                titleTextStyle={[commonStyles.fontWeight600, commonStyles.black, commonStyles.blueButtonColor, commonFontStyles.workSans]}
                onPress={this.onFilterCall}
                disabled={(sortOrder === "NA" || !selectedChk) && !selectedStatus.length}
              />,
            ]}>
            <View>
              {radioOpt.map((el, key) => (
                <RadioButtons key={key} options={el} onValueChange={this.onDateChange} isSelected={selectedChk} />
              ))}
            </View>
            <View style={[commonStyles.marRT0, styles.toggleSwitch]}>
              <ToggleSwitch onSwiwtch={this.onSwitch} seletedOpt={sortOrder} />
            </View>
            <View style={commonStyles.marginBottom5}>
              <Text style={[commonStyles.heading5, commonStyles.fontWeight600]}>Filter by</Text>
            </View>
            {statusGrp.map((gl, i) => {
              return (
                <View key={i} style={[commonStyles.marginRight15, commonStyles.marginBottom5]}>
                  <View style={commonStyles.marginBottom10}>
                    <Text style={[commonStyles.heading6, commonStyles.fontWeight600]}>{gl}</Text>
                  </View>
                  <View style={[commonStyles.dFlex, commonStyles.flexWrap, commonStyles.marginBottom10]}>
                    {statusType
                      .filter((x) => x.groupLabel === gl)
                      .map((el, ind) => (
                        <StatusComponent
                          key={el.id}
                          label={el.label}
                          isSelected={el.isSelected}
                          toggleStatus={() => this.onStatus(el.id)}
                          paymentStatusLabel={el.label}
                        />
                      ))}
                  </View>
                </View>
              );
            })}
          </Form>
        </View>
      </ModalPopUp>
    );
  }
}

const styles = StyleSheet.create({
  isSortModal: {
    width: "100vw",
    borderRadius: "30px 30px 0 0",
    padding: 16,
    marginTop: "auto",
    screen: ({ width }) => {
      if (width >= 992) {
        return {
          width: 500,
          borderRadius: 5,
          marginTop: "initial",
        };
      }
    },
  },
  sortByHeading: {
    // marginLeft: 10,
  },
  modalBackdrop: {
    background: "#00000052",
    overflowX: "auto",
  },
  toggleSwitch: {
    margin: "18px 39px 25px",
  },
  formPadding: {
    padding: "8px 0 8px",
  },
});
