import React, { PureComponent } from "react";
import { StyleSheet, View, Text, Image, Button } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { CURRENCY } from "../constant/app.data.constant";

const verifiedDoneIcon = require("../assets/images/verified_done.png");
const googlepayIcon = require("../assets/images/googlepay.png");

export default class PaidSuccessfullyComponent extends PureComponent {
  render() {
    const { message, paymentMode } = this.props;
    return (
      <View style={[styles.paidSuccessfullyComponent, styles.dFlex, commonStyles.alignItemsCenter]}>
        <View>
          <Image source={verifiedDoneIcon} style={styles.verified_done} />
        </View>
        <View style={[styles.successTextHolder, commonStyles.textAlignCenter]}>
          <Text style={[commonFontStyles.workSans, commonStyles.heading4, commonStyles.greenColor, styles.successText]}>
            {CURRENCY.USD}
            {message}
          </Text>
        </View>

        {!!paymentMode ? (
          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.flexDirectionColumn, commonStyles.justifyContentCenter]}>
            <View>
              {/* <Image source={require(`../assets/images/${paymentMode.replace(/ /g, "").toLowerCase()}.png`)} style={[styles.payMathodIcon]} /> */}
              {/* <Image source={googlepayIcon} style={[styles.payMathodIcon, commonStyles.marginBottom16]} />   */}
            </View>
            <View style={[commonStyles.marginBottom15]}>
              <Text style={[commonFontStyles.workSans, commonStyles.heading6, styles.successText]}>Payment paid with {paymentMode}</Text>
            </View>
          </View>
        ) : null}

        <View style={styles.buttonHolder}>
          <Button
            key="gotohome"
            title="Go to Dashboard"
            style={[commonStyles.blueBorderButton, commonStyles.flexBasis50, commonStyles.marginLeftRight15, styles.gtohButton]}
            titleTextStyle={[commonStyles.fontWeight600, commonStyles.blueBorderButtonColor]}
            to="makePaymentRoutes"
          />
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  paidSuccessfullyComponent: {
    position: "relative",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: "10px 15px",
  },
  verified_done: {
    height: 36,
    width: 36,
    display: "block",
    backgroundSize: "contain",
  },
  successImage: {
    margin: "20px 0 10px",
  },
  successText: {
    lineHeight: 29,
  },
  successTextHolder: {
    margin: "0px 0 16px",
  },
  buttonHolder: {
    margin: "00px 0 25px",
    width: "100%",
  },
  gtohButton: {
    width: "initial%",
    margin: "0px auto",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          width: "100%",
        };
    },
  },
  payMathodIcon: {
    height: 24,
    width: 54,
    display: "block",
    backgroundSize: "contain",
  },
});
