import React from "react";
import R14, {
  Redirect,
  TextInputField,
  SubmitButton,
  StyleSheet,
  ScreenLayout,
  Form,
  Image,
  Checkbox,
  Text,
  View,
  Touchable,
  Surface,
  Button,
  SafeAreaView,
  AsyncStorage,
  AsyncSessionStorage,
} from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import ModalPopUp from "../components/ModalPopUp";
import { SITE_KEY } from "../constant/app.constant";
// import ReCAPTCHA from "react-google-recaptcha";
import ReCAPTCHA from "../components/ReCAPTCHA";
import CommonValidators from "../utils/CommonValidators";
// import IdleTimer from "react-idle-timer";
import GoogleLogin from "../components/SocialLogin/GoogleLogin";
import AppleLogin from "../components/SocialLogin/AppleLogin";
import AmazonLogin from "../components/SocialLogin/AmazonLogin";
//import { LOGIN_TYPE } from "../constant/app.data.constant";
import CommonUtility from "../utils/CommonUtility";
import { LOGIN_TYPE, USER_TYPE } from "../constant/app.data.constant";

const mainImg = require("../assets/images/mainImg.png");
const XBPSymbol = require("../assets/images/XBPSymbol.svg");
const XBPlogo = require("../assets/images/XBPlogo.svg");
const hideEyecon = require("../assets/images/hideEyecon.png");

// prac_12346@gmail.com / Welcome@123
export default R14.connect(
  class LoginScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isLoding: false,
        rememberMe: false,
        showPassword: true,
        isVisible: false,
        isFacebookModalVisible: false,
        isGoogleModalVisible: false,
        alertMessage: "Your account is locked, kindly try after 5 hours",
        captchaValue: "Hide",
        bEnforcePasswordChange: this.props.bEnforcePasswordChange,
        disableSubmitButton: true,
        isModalTimeoutVisible: false,
        isInactive: true,
        IsInValidReturnUrl: false,
      };
      // Idle Timer
      this.idleTimer = null;
      this.handleOnAction = this.handleOnAction.bind(this);
      this.handleOnActive = this.handleOnActive.bind(this);
      this.handleOnIdle = this.handleOnIdle.bind(this);
      this.ttl = 60000 * 5; //  1 day = 60000 * 60  * 24

      this.initialData = {
        // password: "Welcome@123",
        // username: "prac_12346@gmail.com",
        // username: this.props.username,
        payerDetails: this.props.payerDetails,
      };
      console.log("CHECK INITIAL DATA", this.initialData);
    }

    async componentDidMount() {
      // this.props.app.nav.to("pchLogin"); commented: 30/Jul/2021 On Request Hazel
      // return false;
      await this.props.app.dm.dashboard.resetDashboardDomain();
      await this.props.app.dm.billerDirectoryDomain.setState({ pamList: [] });
      await this.props.app.dm.addInsuranceDomain.setState({ insuranceItems: [] });
      await this.props.app.dm.userSession.setState({
        pchRegistrationTypeProvider: false,
        pchRegistrationTypePatient: false,
      });
      await this.props.app.dm.miscellaneousDomain.setState({
        countries: [],
      });
      let XBPUsername = null;
      let username = null;
      if (!!(await CommonUtility.getWithExpiry("XBPUsername"))) {
        XBPUsername = JSON.parse(await AsyncStorage.getItem("XBPUsername"));
      }
      if (!!(await CommonUtility.getWithExpiry("username"))) {
        username = JSON.parse(await AsyncStorage.getItem("username"));
      }

      await AsyncStorage.clear();
      await AsyncSessionStorage.clear();
      if (!!XBPUsername) await AsyncStorage.setItem("XBPUsername", JSON.stringify(XBPUsername));
      if (!!username) await AsyncStorage.setItem("username", JSON.stringify(username));

      await this.setRememberMe(false);
      if (this.captcha) {
        // this.recaptcha.reset();
      }
    }

    async setRememberMe(loginMode, userName) {
      if (!!loginMode) {
        this.state.rememberMe
          ? await CommonUtility.setWithExpiry("XBPUsername", userName, this.ttl)
          : await CommonUtility.setWithExpiry("XBPUsername", "", 0);
      } else {
        if (!!(await CommonUtility.getWithExpiry("XBPUsername"))) {
          let form = this.props.app.ui.form("loginForm");
          form.elmts.username.setValue(await CommonUtility.getWithExpiry("XBPUsername").value);
          this.setState({ rememberMe: true });
        }
      }
    }

    async setUsername() {
      let username = "";
      if (await CommonUtility.getWithExpiry("XBPUsername")) {
        username = await CommonUtility.getWithExpiry("XBPUsername");
      }
      return username;
    }

    toggleRememberMe() {
      this.setState({ rememberMe: !this.state.rememberMe });
    }

    // NOTE: Handling Submit on Login press. New
    handleSubmit = async (form) => {
      this.setState({ isLoding: true });
      const recaptchaValue = this.state.captchaValue;
      if (recaptchaValue === "") {
        this.props.app.ui.form("loginForm").addError("Please select recaptcha.");
        this.setState({ isLoding: false });
        return false;
      } else {
        let resultData = await this.props.app.dm.userLoginDomain.validateCaptcha(recaptchaValue);
        if (!!resultData && !resultData.success) {
          this.props.app.ui.form("loginForm").addError("Please re-select recaptcha.");
          this.setState({ isLoding: false });
          //this.recaptcha.reset();
          return false;
        } else if (!!resultData && resultData.success) {
          let reqBody = {
            password: form.values.password,
            username: form.values.username.toLowerCase().trim(),
            loginType: LOGIN_TYPE.NORMAL,
          };
          try {
            let bIsLoggedIn = await this.props.app.dm.userSession.loginAuth(reqBody);
            if (!!bIsLoggedIn) {
              this.setRememberMe(true, form.values.username);
              await this.redirectToReturnUrl();
              return false;
            } else {
              this.props.app.ui.form("loginForm").addError("Invalid User ID/Password");
              this.setState({ isLoding: false });
            }
          } catch (err) {
            if (err.error === "custom")
              this.setState({
                isVisible: true,
                alertMessage: err.message,
              });
            else this.props.app.ui.form("loginForm").addError(err.message);

            this.setState({ isLoding: false });
          }
        }
      }
    };

    redirectToReturnUrl = async () => {
      let returnUrl = this.props.app.route._location.search;
      if (returnUrl.indexOf("?ReturnUrl=") > -1) {
        if (returnUrl.indexOf("bill-details") > -1) {
          let threadId = returnUrl.split("/");
          threadId = threadId[threadId.length - 1];
          await this.props.app.dm.dashboard.setCompiledMsgList();
          const { compiledMsgList } = this.props.app.dm.dashboard.state;
          if (!!compiledMsgList.length) {
            let item = compiledMsgList.filter((message) => message.threadId === threadId);
            if (!!item.length) {
              threadId = threadId.replaceAll("#", ">>");
              AsyncStorage.setItem("billDetails", JSON.stringify(item[0]));
              AsyncStorage.setItem("homeScreen", "dashboard");
              this.props.app.nav.to("billDetailsRoute", { threadId: threadId });
              return false;
            } else
              this.setState({
                isVisible: true,
                IsInValidReturnUrl: true,
                alertMessage: "Invalid Bill Reference",
              });
          } else
            this.setState({
              isVisible: true,
              IsInValidReturnUrl: true,
              alertMessage: "Invalid Bill Reference",
            });
        } else if (returnUrl.indexOf("medical-bills") > -1) {
          let uid = returnUrl.split("/");
          uid = uid[uid.length - 1];
          this.props.app.nav.to("uploadedBillsRoute", { uid: uid });
          return false;
        } else
          this.setState({
            isVisible: true,
            IsInValidReturnUrl: true,
            alertMessage: "Invalid Bill Reference",
          });
      } else {
        // if password change true
        const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
        if (!!userLoginDetail && userLoginDetail.changePasswordNow === true) {
          // NOTE: Enforce the user to change the password.
          this.props.app.nav.to("changePassword");
          return false;
        } else {
          this.props.app.nav.to("dashboard");
        }
      }
    };

    toggleShowPassword() {
      this.setState({ showPassword: !this.state.showPassword });
    }
    onClose = async () => {
      const { IsInValidReturnUrl } = this.state;
      if (!!IsInValidReturnUrl) {
        this.setState({
          isVisible: false,
          IsInValidReturnUrl: false,
          alertMessage: "Your account is locked, kindly try after 5 hours",
        });
        // return <Redirect to="dashboard" />;
        this.props.app.nav.to("dashboard");
      } else this.setState({ isVisible: false });
    };
    facebookModalClose = async () => {
      this.setState({ isFacebookModalVisible: false });
    };
    googleModalClose = async () => {
      this.setState({ isGoogleModalVisible: false });
    };

    goToRegistrationPageWithFB = () => {
      this.props.app.nav.to("registration");
      this.props.app.dm.userSession.setState({
        facebookMediaLoginPID: !this.props.app.dm.userSession.state.facebookMediaLoginPID,
      });
    };
    goToRegistrationPageWithGoogle = () => {
      this.props.app.nav.to("registration");
      this.props.app.dm.userSession.setState({
        googleMediaLoginPID: !this.props.app.dm.userSession.state.googleMediaLoginPID,
      });
    };

    loginWithFacebook = () => {
      this.setState({ isFacebookModalVisible: true });
    };
    loginWithGoogle = () => {
      this.setState({ isGoogleModalVisible: true });
    };
    onChangeRecaptcha = async (value) => {
      if (value) {
        this.setState({ captchaValue: value });
      } else {
        this.recaptcha.reset();
      }
    };

    // SECTION: Change Password.
    cancelChangePassword = () => {
      this.props.app.nav.to("login");
    };

    // NOTE: On Change Password Button
    onChangePassword = async (form) => {
      this.props.app.ui.progressIndicator.show();
      // NOTE: Clear all validation messages
      form.elmts.currentPassword.setError("");
      form.elmts.newPassword.setError("");
      form.elmts.confirmPassword.setError("");

      let bIsCurrentPasswordValid = await this.validateCurrentPassword();
      let bNewPasswordCriteriaMatched = this.bIsNewPasswordValid();
      let bNewPasswordsMatched = this.bNewConfirmPasswordsMatch();
      let bOldNewPasswordDifferent = this.bCurrentNewPasswordDifferent();

      if (!bIsCurrentPasswordValid) {
        form.elmts.currentPassword.setError("Current Password is Incorrect.");
        this.props.app.ui.progressIndicator.hide({ timeout: 750 });
        return false;
      } else {
        form.elmts.currentPassword.setError("");
      }

      if (!bNewPasswordCriteriaMatched) {
        form.elmts.newPassword.setError("Enter Min 8 - Max 15 characters with 1 uppercase, 1 lowercase, 1 number with special characters @$!%*?");
        this.props.app.ui.progressIndicator.hide({ timeout: 750 });
        return false;
      } else {
        form.elmts.newPassword.setError("");
      }

      if (!bNewPasswordsMatched) {
        form.elmts.confirmPassword.setError("Entered Password not Matched.");
        this.props.app.ui.progressIndicator.hide({ timeout: 750 });
        return false;
      } else {
        form.elmts.confirmPassword.setError("");
      }

      if (!bOldNewPasswordDifferent) {
        form.elmts.newPassword.setError("New Password Should not be same as your current Password, Please try different password.");
        this.props.app.ui.progressIndicator.hide({ timeout: 750 });
        return false;
      } else {
        form.elmts.newPassword.setError("");
      }

      let isValid = bIsCurrentPasswordValid && bNewPasswordCriteriaMatched && bNewPasswordsMatched && bOldNewPasswordDifferent;
      if (isValid) {
        var data = {
          pid: this.initialData.payerDetails.payerId,
          password: form.elmts.confirmPassword.value,
          "x-requestedby": this.initialData.payerDetails.payerId,
          "x-reset2fa": false,
        };
        // Update User Password -- API.
        let res = await this.props.app.dm.user.updateUserDetails(data);
        if (res !== undefined) {
          if (res.status !== undefined && res.status !== 200) {
            this.props.app.dm.commonDomain.showToastr("error", res.data.error_description);
          } else {
            // Update User Password -- Payer App Database Update.
            let reqBody = {
              uid: this.initialData.payerDetails.uid,
              payerId: this.initialData.payerDetails.payerId,
              password: form.elmts.confirmPassword.value,
              tokenKey: form.elmts.confirmPassword.value,
            };
            res = await this.props.app.dm.userLoginDomain.updateUserPassword(reqBody, false);
            if (res.response !== undefined && res.response.status !== 200) {
              this.props.app.dm.commonDomain.showToastr("error", "Password change failed!");
              this.props.app.ui.progressIndicator.hide({ timeout: 750 });
              return false;
            } else {
              this.props.app.dm.commonDomain.showToastr("success", "You have successfully Changed Password");
              this.props.app.ui.progressIndicator.hide({ timeout: 750 });
              this.props.app.nav.to("login");
              return false;
            }
          }
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Failed to update user details!");
        }
      }
      this.props.app.ui.progressIndicator.hide({ timeout: 750 });
    };

    bCheckMandatoryFields = () => {
      let cpForm = this.props.app.ui.form("changePasswordForm");
      if (
        cpForm.elmts.currentPassword.value !== null &&
        cpForm.elmts.currentPassword.value !== "" &&
        cpForm.elmts.newPassword.value !== null &&
        cpForm.elmts.newPassword.value !== "" &&
        cpForm.elmts.confirmPassword.value !== null &&
        cpForm.elmts.confirmPassword.value !== ""
      ) {
        this.setState({
          disableSubmitButton: false,
        });
      } else {
        this.setState({
          disableSubmitButton: true,
        });
      }
    };

    // NOTE: Validate the current password.
    validateCurrentPassword = async () => {
      let cpForm = this.props.app.ui.form("changePasswordForm");
      let res = await this.props.app.dm.userLoginDomain.checkUserPassword(
        this.initialData.payerDetails.payerId,
        cpForm.elmts.currentPassword.value,
        true
      );
      if (res.error === null) {
        return true;
      } else {
        return false;
      }
    };

    // NOTE: Validate the new password criteria.
    bIsNewPasswordValid = () => {
      let form = this.props.app.ui.form("changePasswordForm");
      let passwordField = form.elmts.newPassword.value;
      let isUpper = false;
      let isNumeric = false;
      let isSpecial = false;
      let minMaxLength = false;
      let bContainsSpace = false;
      let bContainsLowerCase = false;

      // Upper Case check
      if (passwordField.replace(/[^A-Z]/g, "").length > 0) {
        isUpper = true;
      }
      // Numeric Character check
      if (passwordField.replace(/[^0-9]/g, "").length > 0) {
        isNumeric = true;
      }
      // Special Character check
      if (passwordField.replace(/[^@$!%*?]/g, "").length > 0) {
        isSpecial = true;
      }
      if (passwordField.replace(/[^a-z]/g, "").length > 0) {
        bContainsLowerCase = true;
      }
      // Check if any whitespace.
      if (/\s/.test(passwordField)) {
        bContainsSpace = true;
      }
      // Length Check
      if (passwordField.length >= 8 && passwordField.length <= 15) {
        minMaxLength = true;
      }
      if (bContainsLowerCase && isUpper && isNumeric && isSpecial && minMaxLength && !bContainsSpace) {
        return true;
      } else {
        return false;
      }
    };

    // NOTE: To check whether new and confirm password match.
    bNewConfirmPasswordsMatch = () => {
      let cpForm = this.props.app.ui.form("changePasswordForm");
      if (!(cpForm.elmts.newPassword.value === cpForm.elmts.confirmPassword.value)) {
        return false;
      } else {
        return true;
      }
    };

    // NOTE: To check whether current and new password different.
    bCurrentNewPasswordDifferent = () => {
      let cpForm = this.props.app.ui.form("changePasswordForm");
      if (cpForm.elmts.currentPassword.value === cpForm.elmts.newPassword.value) {
        return false;
      } else {
        return true;
      }
    };
    // SECTION: Route Registration.
    routeToRegistration = (val) => {
      this.props.app.nav.to("registration", { type: val.toLowerCase() });
    };

    // NOTE: Redirect to Registration using Social Media Integration
    goToRegistrationPageWithFB = (val) => {
      this.props.app.nav.to("registration", { type: val.toLowerCase() });
      this.props.app.dm.userSession.setState({
        facebookMediaLoginPID: !this.props.app.dm.userSession.state.facebookMediaLoginPID,
      });
    };

    goToRegistrationPageWithGoogle = (val) => {
      this.props.app.nav.to("registration", { type: val.toLowerCase() });
      this.props.app.dm.userSession.setState({
        googleMediaLoginPID: !this.props.app.dm.userSession.state.googleMediaLoginPID,
      });
    };
    // SECTION: Idle Time out when mandatory password change is enforced.
    handleOnAction(event) {}

    handleOnActive(event) {
      if (this.state.isInactive === false) {
        this.idleTimer.reset();
      }
    }
    // warning modal for session timeout will pop up.
    handleOnIdle = () => {
      if (!!this.state.bEnforcePasswordChange) {
        this.setState({ bEnforcePasswordChange: false });
        this.setState({ isModalTimeoutVisible: true });
        this.setState({ isInactive: true });
        setTimeout(
          function () {
            if (this.state.isInactive && !!this.idleTimer) {
              if (this.idleTimer) {
                this.idleTimer.pause();
              }
              this.props.app.nav.to("login");
            }
          }.bind(this),
          1000 * 3 * 1
        );
      }
    };

    renderIdleTimerPopUpMenu() {
      return (
        <SafeAreaView>
          <View>
            {/* <IdleTimer
              ref={(ref) => {
                this.idleTimer = ref;
              }}
              timeout={1000 * 60 * 5}
              onActive={this.handleOnActive}
              onIdle={this.handleOnIdle}
              onAction={this.handleOnAction}
              debounce={250}
              backdropStyle={styles.backdrop}
            /> */}
            <View>
              <ModalPopUp
                visibility={this.state.isModalTimeoutVisible}
                isFooter={false}
                bodyWrapper={[styles.sessionExpirPopupBodyWrapper]}
                modalOuterWrapper={styles.modalOuterWrapper}
                heading={"Change Password Expiration!"}
                handleCancel={this.handleLogoutPress}
                closeButton={styles.closeBtn}
                modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
                modalContent={commonStyles.marginTop15}>
                <View style={commonStyles.textAlignCenter}>
                  <View style={commonStyles.marginBottom20}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>
                      You will be redirected to the login screen.
                    </Text>
                  </View>
                  <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                    <Button
                      title={"OK"}
                      style={[commonStyles.blueButton, styles.sessionModelOkBtn]}
                      onPress={this.handleLogoutPress}
                      titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight500, commonFontStyles.workSans, commonStyles.heading5]}
                    />
                  </View>
                </View>
              </ModalPopUp>
            </View>
          </View>
        </SafeAreaView>
      );
    }

    // NOTE: Enforce Password Change Modal.
    renderEnforcePasswordUpdate() {
      return (
        <SafeAreaView>
          <View>
            <View>
              <ModalPopUp
                visibility={true}
                isFooter={false}
                bodyWrapper={styles.changePasswordModalBodyWrapper}
                modalOuterWrapper={styles.modalOuterWrapper}
                heading={"Change Password"}
                modalContent={commonStyles.marT0}
                handleCancel={this.cancelChangePassword}
                closeButton={styles.changePasswordModalCloseBtn}
                modalHeading={[commonStyles.textAlignCenter, commonStyles.marginBottom5, commonStyles.marginTop10]}>
                <View style={[styles.payNowWrapper]}>
                  <Form
                    name="changePasswordForm"
                    style={[commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.flexGrow1]}
                    onSubmit={this.onChangePassword}
                    validateBeforeSubmit={true}
                    validators={CommonValidators.getFormValidator()}
                    autoComplete={false}>
                    <View style={styles.passwordFieldContainer}>
                      <TextInputField
                        name="currentPassword"
                        label="Current Password*"
                        required="Enter the current password"
                        style={[commonFontStyles.workSans, commonStyles.heading5]}
                        labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                        labelStyle={[commonStyles.formLabelStyle]}
                        // autoComplete={false}
                        secure={true}
                        onChangeText={this.bCheckMandatoryFields}
                        helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                      />
                      <TextInputField
                        name="newPassword"
                        label="New Password*"
                        required="Enter the new password"
                        style={[commonFontStyles.workSans, commonStyles.heading5]}
                        labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading5]}
                        labelStyle={[commonStyles.formLabelStyle]}
                        // autoComplete={false}
                        secure={true}
                        onChangeText={this.bCheckMandatoryFields}
                        helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                      />
                      <TextInputField
                        name="confirmPassword"
                        label="Confirm Password*"
                        required="Enter the confirm password"
                        style={[commonFontStyles.workSans, commonStyles.heading5]}
                        labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading5]}
                        labelStyle={[commonStyles.formLabelStyle]}
                        // autoComplete={false}
                        secure={true}
                        onChangeText={this.bCheckMandatoryFields}
                        helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                      />
                    </View>
                    <View style={[commonStyles.marTAuto, commonStyles.dFlex]}>
                      {/* <View style={[commonStyles.flexBasis50, commonStyles.marginRight10]}>
                            <Button
                              key="cancel"
                              title="Cancel"
                              onPress={this.cancelChangePassword}
                              style={[commonStyles.blueBorderButton, commonStyles.fullWidth]}
                              titleTextStyle={[
                                commonStyles.blueBorderButtonColor,
                                commonStyles.fontWeight600,
                                commonFontStyles.workSans,
                                commonStyles.heading6,
                              ]}
                            />
                          </View> */}
                      <View style={[commonStyles.flexBasis50, commonStyles.marLAuto]}>
                        <SubmitButton
                          key="update"
                          title="Submit"
                          disabled={this.state.disableSubmitButton}
                          style={[commonStyles.blueButton, commonStyles.fullWidth]}
                          titleTextStyle={[
                            commonStyles.blueButtonColor,
                            commonStyles.fontWeight600,
                            commonFontStyles.workSans,
                            commonStyles.heading6,
                          ]}
                        />
                      </View>
                    </View>
                  </Form>
                </View>
              </ModalPopUp>
            </View>
          </View>
        </SafeAreaView>
      );
    }

    render() {
      return (
        <ScreenLayout scrollEnabled>
          <View style={styles.mainLogin}>
            <View style={[styles.bgContainer, styles.leftSection]}>
              <Image source={mainImg} style={[styles.backgroundImage]} />
            </View>

            <Surface style={styles.rightSection}>
              <View style={[styles.logoImageContainer]}>
                <Image resizeMode="contain" style={[styles.logoImage]} source={XBPlogo} />
              </View>
              <View style={[styles.loginFormContainer]}>
                <Form
                  name="loginForm"
                  onSubmit={this.handleSubmit}
                  initialValues={this.initialData}
                  autoComplete={false}
                  validateBeforeSubmit
                  style={styles.paddingTop0}
                  controlsBottomRight={[
                    <View style={styles.loginControls} key="viewOne">
                      <SubmitButton
                        title="Login"
                        key="submit"
                        titleColor="#000"
                        style={[styles.loginButton]}
                        titleTextStyle={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight600]}
                        disabled={this.state.isLoding}
                      />
                    </View>,
                  ]}>
                  <TextInputField
                    name="username"
                    label="User ID*"
                    autoComplete={false}
                    required="Enter the User ID"
                    style={[styles.emailField, commonFontStyles.workSans, commonStyles.heading5]}
                    // value={this.setUsername()}
                    labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading5]}
                    labelStyle={[commonStyles.formLabelStyle]}
                    autoFocus
                  />
                  <View style={styles.passwordFieldContainer}>
                    <TextInputField
                      name="password"
                      label="Password*"
                      autoComplete={false}
                      secure={this.state.showPassword}
                      iconRight="eye"
                      onIconRightPress={() => this.toggleShowPassword()}
                      required="Enter the Password"
                      style={[styles.passwordField, commonFontStyles.workSans, commonStyles.heading5]}
                      labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading5]}
                      labelStyle={[commonStyles.formLabelStyle]}
                    />
                    {!this.state.showPassword ? (
                      <Touchable onPress={() => this.toggleShowPassword()}>
                        <View style={styles.hideEyeIconContainer}>
                          <Image source={hideEyecon} style={[styles.backgroundImageHideIcon]} />
                        </View>
                      </Touchable>
                    ) : (
                      false
                    )}
                  </View>
                  <Checkbox name="rememberMe" label="Remember Me" value={this.state.rememberMe} onPress={() => this.toggleRememberMe()} />
                  <Text style={[styles.rememberMeText, commonFontStyles.workSans, commonStyles.heading6]}>Remember Me</Text>
                  {this.state.captchaValue === "Hide" ? null : (
                    <View style={[commonStyles.marginBottom30, styles.captchaHolder]}>
                      <ReCAPTCHA
                        sitekey={SITE_KEY}
                        onChange={this.onChangeRecaptcha}
                        ref={(el) => {
                          this.recaptcha = el;
                        }}
                        // size="100%"
                      />
                    </View>
                  )}
                </Form>
              </View>
              <View style={[styles.mViewa, styles.loginWithSocialSection]}>
                {/*  <View style={[styles.forgotPasswordSection]}>
                  <Touchable to="forgotPassword" key="forgotPassword">
                    <Text style={[styles.blueLink, commonFontStyles.workSans, commonStyles.heading6]}>Forgot Password?</Text>
                  </Touchable> 
                </View> */}
                <View style={[styles.loginWithSocialWrap]}>
                  <View style={[styles.orSection]}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, styles.or]}>Login/Signup with Social Media</Text>
                  </View>
                  <View style={[styles.loginWithSocial]}>
                    <View style={commonStyles.marginRight16}>
                      <GoogleLogin userType={USER_TYPE.XBP} buttonType={"Login"} redirectToReturnUrl={this.redirectToReturnUrl}></GoogleLogin>
                    </View>
                    <View style={commonStyles.marginRight16}>
                      <AppleLogin userType={USER_TYPE.XBP} buttonType={"Login"} redirectToReturnUrl={this.redirectToReturnUrl}></AppleLogin>
                    </View>
                    <View style={commonStyles.marginRight16}>
                      <AmazonLogin  buttonType={"Login"} userType = {USER_TYPE.XBP} redirectToReturnUrl={this.redirectToReturnUrl}></AmazonLogin>
                    </View>
                  </View>
                </View>
                <View style={[styles.createNewAcc]}>
                  <View>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>New User ? </Text>
                  </View>
                  <View>
                    {/* to="registration" key="registration" */}
                    <Touchable onPress={() => this.routeToRegistration("New")}>
                      <Text style={[styles.createAccount, commonFontStyles.workSans, commonStyles.heading6]}>Create an Account</Text>
                    </Touchable>
                  </View>
                </View>
              </View>

              <View style={[styles.mrgTopAuto, commonStyles.marginBottom10, styles.copyRight]}>
                <Text style={[commonFontStyles.workSans, commonStyles.smallHeading]}>
                  © {new Date().getFullYear()} Exela Technologies, All rights reserved.
                </Text>
              </View>
            </Surface>
            {this.state.bEnforcePasswordChange ? this.renderEnforcePasswordUpdate() : null}
            {this.renderIdleTimerPopUpMenu()}
          </View>
          {/* SECTION: Modal Popup for Account Lockout */}
          <ModalPopUp
            visibility={this.state.isVisible}
            isFooter={false}
            bodyWrapper={[styles.invalidReferenceModalBodyWrapper]}
            modalOuterWrapper={styles.modalOuterWrapper}
            heading={this.state.IsInValidReturnUrl ? "Invalid Reference !" : "Account Locked !"}
            handleCancel={this.onClose}
            closeButton={commonStyles.displayNone} //{styles.invalidReferenceModalCloseBtn}
            modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
            modalContent={commonStyles.marginTop15}>
            <View style={commonStyles.textAlignCenter}>
              <View style={commonStyles.marginBottom20}>
                <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>{this.state.alertMessage}</Text>
              </View>
              <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                <Button
                  title={"OK"}
                  style={[commonStyles.blueButton, styles.sessionModelOkBtn]}
                  onPress={this.onClose}
                  titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight500, commonFontStyles.workSans, commonStyles.heading5]}
                />
              </View>
            </View>
          </ModalPopUp>
        </ScreenLayout>
      );
    }
  }
);

