import React from "react";
import R14, { ScreenLayout, FlatList, View, Text, StyleSheet, Touchable, ScrollView } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import NoRecordsFound from "../components/NoRecordsFound";
import CommonUtility from "../utils/CommonUtility";
import DateUtils from "../utils/DateUtils";
import ResponsiveTableComponent from "../components/ResponsiveTableComponent";
import LeftMenuComponent from "../components/LeftMenuComponent";

export default R14.connect(
  class SimpleMessageScreen extends React.Component {
    constructor(props) {
      super(props);
      this.timerInstance = null;
    }

    componentDidMount() {
      this.timerInstance = setInterval(async () => {
        await this.props.app.dm.simpleMessage.displaySimpleMessage();
      }, 30000);
    }

    componentWillUnmount() {
      if (this.timerInstance) {
        clearInterval(this.timerInstance);
      }
    }

    onNavigate = (threadId) => {
      let newThreadId = threadId.replaceAll("#", ">>");
      this.props.app.nav.to("simpleMessageDetailRoute", { threadId: newThreadId });
    };

    render() {
      const { simpleMessage } = this.props.app.dm.simpleMessage.state;
      const { notificationThreadIds } = this.props.app.dm.notificationDomain.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;

      let messages = simpleMessage.map((data, i) => {
        return data;
      });

      if (notificationThreadIds.length > 0) {
        let d = notificationThreadIds.map((data, i) => {
          return data;
        });
        messages = messages.filter((message) => d.includes(message.threadId));
      }

      return (
        <ScreenLayout scrollEnabled>
          <ScrollView style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu]}>
            {/* <View style={[commonStyles.marginTopBottom15, commonStyles.paddingLeftRight16, commonStyles.pageTitle]}>
            <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500]}>{this.props.app.nav.route.label} </Text>
          </View> */}
            <ResponsiveTableComponent dashboardScrollView={styles.dashboardScrollView} simpleMessage={messages} screenName="SampleMessageScreen" />
            <LeftMenuComponent />
            {/* <FlatList
            style={[styles.messageComponent]}
            ListEmptyComponent={
              <View style={styles.padd10}>
                <NoRecordsFound message={"No Messages to display."} />
              </View>
            }
            data={simpleMessage}
            keyExtractor={(el, i) => i}
            renderItem={({ item, index }) => (
              <Touchable onPress={() => this.onNavigate(item.threadId)}>
                <View style={[commonStyles.dFlex, styles.borderBottom, styles.directoryList, commonStyles.paddingTop20]}>
                  <View style={[styles.profileIconHolder]}>
                    <View style={[styles.profileIcon]}>
                      <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading5, commonStyles.userInitial]}>
                        {CommonUtility.shortName(item.billerName)}
                      </Text>
                    </View>
                    {item.currentMsgCount - item.readMsgCount > 0 ? (
                      <View style={[styles.messageNotification]}>
                        <Text style={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.exSmallHeading, commonStyles.whiteColor]}>
                          {item.currentMsgCount - item.readMsgCount}
                        </Text>
                      </View>
                    ) : null}
                  </View>
                  <View>
                    <View>
                      <Text
                        style={[
                          commonFontStyles.workSans,
                          commonStyles.fontWeight500,
                          commonStyles.heading5,
                          commonStyles.transformCapitalize,
                          commonStyles.wordBreakWord,
                          commonStyles.transperentBtnColor,
                        ]}>
                        {item.billerName}
                      </Text>
                    </View>
                    <View>
                      <Text
                        style={[
                          commonFontStyles.workSans,
                          commonStyles.fontWeight500,
                          commonStyles.heading6,
                          commonStyles.dBlock,
                          commonStyles.wordBreakWord,
                        ]}>
                        {item.subject}
                      </Text>
                      <Text
                        style={[
                          commonFontStyles.workSans,
                          commonStyles.fontWeight500,
                          commonStyles.heading6,
                          commonStyles.dBlock,
                          commonStyles.lightGray,
                        ]}>
                        {item.lastMessage}
                      </Text>
                      <Text
                        style={[
                          commonFontStyles.workSans,
                          commonStyles.fontWeight500,
                          commonStyles.smallHeading,
                          commonStyles.dBlock,
                          commonStyles.lightGray,
                        ]}>
                        {DateUtils.dateAndTimeWithAmPm(item.lastMessageDate)}
                      </Text>
                    </View>
                  </View>
                </View>
              </Touchable>
            )}
          /> */}
          </ScrollView>
        </ScreenLayout>
      );
    }
  }
);
const styles = StyleSheet.create({
  directoryList: {
    padding: "0px 15px 20px",
  },
  profileIconHolder: {
    position: "relative",
    marginRight: 16,
  },
  profileIcon: {
    width: 40,
    height: 40,
    background: "#e0e0e0",
    borderRadius: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  messageNotification: {
    width: 18,
    height: 18,
    background: "#C63F9B",
    borderRadius: "100%",
    position: "absolute",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    right: -6,
    top: -6,
  },
  borderBottom: {
    borderBottom: "1px solid #ccc",
  },
  padd10: {
    padding: "0px 10px",
  },
  dashboardScrollView: {
    inset: "47px 0px 0px",
  },
});
