import React, { Fragment } from "react";
import R14, {
  View,
  StyleSheet,
  Colors,
  Touchable,
  Image,
  IconButton,
  Text,
  Form,
  TextInputField,
  SelectMenuField,
  Modal,
  Button,
  SafeAreaView,
  PopUpMenu,
  PopUpMenuItem,
  AsyncStorage,
} from "../core";
import ModalPopUp from "./ModalPopUp";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import IdleTimer from "react-idle-timer";
import CommonUtility from "../utils/CommonUtility";
import InAppScanner from "./InAppScanner";
import { USER_TYPE } from "../constant/app.data.constant";

const notificationIcon = require("../assets/images/notification.png");
const qrCodeScan = require("../assets/images/qrCodeScan.png");
const icLogoutB = require("../assets/images/icLogoutB.png");
const back_icon = require("../assets/images/back_icon.png");
const closeIcon = require("../assets/images/closeIcon.png");
const searchIcon = require("../assets/images/search.png");
const filterIcon = require("../assets/images/filterIcon.png");
const crateMessageIcon = require("../assets/images/crate-message.png");
const historyIcon = require("../assets/images/historyIcon.png");
const profilePic = require("../assets/images/profilePic.png");

export default R14.connect(
  class HeaderControlsRight extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isSortModalActive: false,
        isFilterOpen: false,
        //  isSearchOpen: false,
        isSearchHistoryOpen: false,
        selectedHistoryString: "",
        isSearchHistoryOpen: false,
        isModalTimeoutVisible: false,
        isInactive: true,
        bShowConcurrentLoginPopUp: false,
        lastReadDateTime: new Date(),
      };
      this.currentScreen = "";
      this.oldScreen = "";
      this.searchHistory = [];
      //For Idle Timer.
      this.idleTimer = null;
      this.handleOnAction = this.handleOnAction.bind(this);
      this.handleOnActive = this.handleOnActive.bind(this);
      this.handleOnIdle = this.handleOnIdle.bind(this);
      this.handleLogoutPress = this.handleLogoutPress.bind(this);
      // this.handleOnBeforeUnload = this.handleOnBeforeUnload.bind(this);
      // For concurrent login.
      // this.handleConcurrentUserLoginEvent = this.handleConcurrentUserLoginEvent.bind(this);
      this.generateNewSessionString = this.generateNewSessionString.bind(this);
      // this.handleOnload = this.handleOnload.bind(this);
      this.userLoginDetail = {};
      this.userDetails = {};
      this.billerList = [];
      this.billDetails = {};
    }

    async initialize() {
      this.userLoginDetail = JSON.parse((await AsyncStorage.getItem("profiles")) || "null") || {};
      this.userDetails = JSON.parse((await AsyncStorage.getItem("userDetails")) || "null") || {};
      this.billDetails = JSON.parse((await AsyncStorage.getItem("billDetails")) || "null") || {};
      this.billerList = JSON.parse((await AsyncStorage.getItem("billerList")) || "null") || [];

      this.setState({ initialized: true });
    }

    componentDidMount() {
      // this.handleConcurrentUserLoginEvent();
      this.initialize();
      setInterval(async () => {
        await this.props.app.dm.notificationDomain.setState({
          isMobileDevice: CommonUtility.isMobileDevice(),
        });
      }, 1000);

      let userType = this.userLoginDetail.userType || null;
      if (userType === USER_TYPE.XBP || !userType || userType == null)
        setInterval(async () => {
          await this.props.app.dm.notificationDomain.notificationDetail();
        }, 10000);
    }

    handleConcurrentUserLoginEvent = () => {
      let isLoggedIn = this.props.app.dm.userSession.isLoggedIn;
      if (isLoggedIn) {
        // DOM Load Event
        window.addEventListener("load", (ev) => {
          if (!this.state.bShowConcurrentLoginPopUp) {
            this.sessionChecker();
          }
        });
        // Mouse Click Event
        document.addEventListener("mouseup", (ev) => {
          if (!this.state.bShowConcurrentLoginPopUp) {
            this.sessionChecker();
          }
        });
      }
    };

    sessionChecker = () => {
      let isLoggedIn = this.props.app.dm.userSession.isLoggedIn;
      if (isLoggedIn) {
        let lastCallTime = new Date(this.state.lastReadDateTime);
        lastCallTime = new Date(lastCallTime.setMinutes(lastCallTime.getMinutes() + 5));
        if (lastCallTime <= new Date()) {
          this.props.app.dm.userSession.isSessionStringValid().then((result) => {
            if (result === false) {
              this.setState({
                bShowConcurrentLoginPopUp: true,
              });
            }
            this.setState({
              lastReadDateTime: new Date(),
            });
          });
        }
      }
      // Time out once pop-up displayed. Route to logout.
      setTimeout(
        function () {
          if (this.state.bShowConcurrentLoginPopUp) {
            this.handleLogoutPress();
          }
        }.bind(this),
        1000 * 60 * 5
      );
    };

    generateNewSessionString = async () => {
      let userDetail = await AsyncStorage.getItem("profiles");
      if (userDetail !== null && userDetail !== undefined) {
        userDetail = JSON.parse(userDetail);
        let res = await this.props.app.dm.userSession.createSessionString(userDetail);
        if (res) {
          this.props.app.dm.commonDomain.showToastr("success", "Session updated!");
          this.setState({ bShowConcurrentLoginPopUp: false });
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "An error occurred!");
        }
      }
    };

    // Get all History Data
    async searchHistoryData() {
      let userDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
      if (userDetail !== null && userDetail !== undefined) {
        this.searchHistory = await this.props.app.dm.userSearchDomain.getLastSearchHistory(
          userDetail.pid,
          3,
          this.currentScreen.name,
          "",
          "DESC",
          false
        );
      }
    }

    toggalSortModal = () => {
      let currentState = this.state.isSortModalActive;
      this.setState({
        isSortModalActive: !currentState,
      });
    };

    searchToggal = () => {
      this.searchHistoryData();
      this.setState({
        isSearchHistoryOpen: false,
      });
      this.props.app.dm.userSearchDomain.setState({
        isSearchOpen: !this.props.app.dm.userSearchDomain.state.isSearchOpen,
        textInSearchBox: "",
        currentOpenScreen: this.currentScreen.name,
      });
      this.props.app.dm.userSearchDomain.keyTypeSearch("", this.currentScreen.name);
    };

    searchTextClear = () => {
      let form = this.props.app.ui.form("searchBill");
      form.elmts.searchText.setValue("");
      this.props.app.dm.userSearchDomain.setState({
        //isSearchOpen: false,
        textInSearchBox: "",
        currentOpenScreen: this.currentScreen.name,
      });
      this.props.app.dm.userSearchDomain.keyTypeSearch("", this.currentScreen.name);
    };

    searchCloseToggal = () => {
      this.props.app.dm.userSearchDomain.setState({
        isSearchOpen: false,
        textInSearchBox: "",
        currentOpenScreen: this.currentScreen.name,
      });

      this.props.app.dm.userSearchDomain.keyTypeSearch("", this.currentScreen.name);
    };

    openSearchHistory = () => {
      let currentState = this.state.isSearchHistoryOpen;
      this.setState({
        isSearchHistoryOpen: !currentState,
      });
    };

    closeSearchHistory = () => {
      this.setState({
        isSearchHistoryOpen: false,
      });
    };

    saveSearchString = (val) => {
      const { searchHasData } = this.props.app.dm.userSearchDomain.state;
      let saveVal = "";
      if (!!val) saveVal = val.trim();
      if (!!saveVal && searchHasData) {
        let userDetail = this.userDetails;
        this.props.app.dm.userSearchDomain.CreateUserSearchHistory(userDetail.pid, this.currentScreen.name, saveVal);
        // this.closeSearchHistory();
      }
      this.searchHistoryData();
    };

    onSearch = (val) => {
      this.setState({ isSearchHistoryOpen: false });
      this.props.app.dm.userSearchDomain.setState({
        isSearchOpen: true,
        textInSearchBox: val,
        currentOpenScreen: this.currentScreen.name,
      });
      this.props.app.dm.userSearchDomain.keyTypeSearch(val, this.currentScreen.name);
    };

    onHistoryString = (historyKeyword) => {
      let form = this.props.app.ui.form("searchBill");
      form.elmts.searchText.setValue(historyKeyword);
      this.props.app.dm.userSearchDomain.setState({
        textInSearchBox: form.elmts.searchText.value,
        currentOpenScreen: this.currentScreen.name,
      });
      this.props.app.dm.userSearchDomain.keyTypeSearch(form.elmts.searchText.value, this.currentScreen.name);

      if (!!form) {
        this.saveSearchString(form.elmts.searchText.value);
      }
      this.closeSearchHistory();
    };
    goToNotification = () => {
      this.props.app.nav.to("notificationScreenRoute");
    };
    renderNotification = () => {
      const { notifications } = this.props.app.dm.notificationDomain.state;
      let d = notifications.map((data, i) => {
        return {
          uid: data.uid,
          isRead: data.isRead,
        };
      });
      let count = d.filter((x) => x.isRead === false).length;
      return (
        <Touchable style={[commonStyles.marginLeft15]} onPress={this.goToNotification}>
          <View style={[commonStyles.notificationHolder]}>
            <View>
              <Image source={notificationIcon} style={styles.filterIcon} />
            </View>
            {count === 0 ? null : (
              <View style={commonStyles.notificationSection}>
                <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.whiteColor]}>{count}</Text>
              </View>
            )}
          </View>
        </Touchable>
      );
    };

    renderQRCodeSection = () => {
      return (
        <Touchable style={[commonStyles.marginLeft15]} onPress={this.scanningQRCode}>
          <Image source={qrCodeScan} style={styles.filterIcon} />
        </Touchable>
      );
    };

    renderPCHLogoutSection = () => {
      return (
        <Touchable style={[commonStyles.marginLeft15]} onPress={() => this.props.app.nav.to("loginOut")}>
          <Image source={icLogoutB} style={styles.filterIcon} />
        </Touchable>
      );
    };
    // renderProfileOpt = () => {
    //   const userLoginDetail = !!await AsyncStorage.getItem("profiles") ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    //   let userType = !!userLoginDetail ? userLoginDetail.userType : null;
    //   let items = [];
    //   items.push(
    //     <PopUpMenuItem
    //       onPress={() => this.props.app.nav.to("pchProfileViewRoute")}
    //       label={"Profile"}
    //       key={"Profile"}
    //       value={"Profile"}
    //       icon="accountCircle"
    //     />
    //   );
    //   items.push(
    //     <PopUpMenuItem onPress={() => this.props.app.nav.to("loginOut")} label={"Logout"} key={"Logout"} value={"Logout"} icon="arrowBack" />
    //   );
    //   return (
    //     <PopUpMenu
    //       controlIcon="dotsVertical"
    //       controlIconSize="small"
    //       key="trippleDot"
    //       direction="downLeft"
    //       style={[commonFontStyles.workSans, commonStyles.heading6]}>
    //       {items}
    //     </PopUpMenu>
    //   );
    // };
    renderSearchSection = () => {
      const historyArray = this.searchHistory;
      return (
        <View style={[commonStyles.marginLeftRight5]}>
          <Form name="searchBill" style={[commonStyles.padding0, commonStyles.flexGrow1]}>
            <View style={[styles.billSearchField]}>
              <View style={[commonStyles.whiteBackgroundColor, commonStyles.dFlex, commonStyles.alignItemsCenter]}>
                <Touchable style={[commonStyles.marginLeftRight20]} onPress={this.searchCloseToggal}>
                  <Image source={back_icon} style={commonStyles.icon24} />
                </Touchable>
                <View style={[styles.textFieldOuter, commonStyles.flexGrow1]}>
                  <TextInputField
                    name="searchText"
                    value={""}
                    style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.margin0]}
                    autoFocus
                    onFocus={this.openSearchHistory}
                    autoComplete={false}
                    onChangeText={(val) => {
                      this.onSearch(val);
                    }}
                    onBlur={() => {
                      let form = this.props.app.ui.form("searchBill");
                      if (!!form) {
                        this.saveSearchString(form.elmts.searchText.value);
                      }
                    }}
                  />
                </View>
                <Touchable style={[commonStyles.marginLeftRight20]} onPress={this.searchTextClear}>
                  <Image source={closeIcon} style={commonStyles.icon14} />
                </Touchable>
              </View>
            </View>
            <View style={[commonStyles.whiteBackgroundColor, styles.autoCompleteBox, this.state.isSearchHistoryOpen ? styles.block : styles.none]}>
              <Touchable
                onPress={() => {
                  this.onHistoryString(
                    historyArray.length > 0
                      ? historyArray[0].searchedString
                      : historyArray.length > 1
                      ? historyArray[1].searchedString
                      : historyArray.length > 2
                      ? historyArray[2].searchedString
                      : ""
                  );
                }}
                style={[
                  commonStyles.dFlex,
                  commonStyles.paddingTopBottom15,
                  commonStyles.alignItemsCenter,
                  historyArray.length > 0 ? styles.noStyle : styles.none,
                ]}>
                <Image
                  source={require("../assets/images/grayHistoryIcon.png")}
                  style={[commonStyles.icon16, commonStyles.marginRight25, commonStyles.marginLeft15]}
                />{" "}
                <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>
                  {historyArray.length > 0
                    ? historyArray[0].searchedString
                    : historyArray.length > 1
                    ? historyArray[1].searchedString
                    : historyArray.length > 2
                    ? historyArray[2].searchedString
                    : ""}
                </Text>
              </Touchable>
            </View>
          </Form>
        </View>
      );
    };

    onFilterOpen = () => {
      this.props.app.dm.filterDomain.setState({ isFilterOpen: true });
    };
    onRestoreFilter = () => {
      const { compiledMsgList } = this.props.app.dm.dashboard.state;
      this.props.app.dm.dashboard.setState({ filterBills: compiledMsgList });
      this.props.app.dm.filterDomain.resetFilter();
    };

    gotDevices = (mediaDevices) =>
      new Promise((resolve, reject) => {
        const availableVideoInputs = [];
        mediaDevices.forEach((mediaDevice) => {
          if (mediaDevice.kind === "videoinput") {
            // audioinput //videoinput
            availableVideoInputs.push({
              deviceId: mediaDevice.deviceId,
              label: mediaDevice.label,
            });
          }
        });

        if (availableVideoInputs.length > 0) {
          resolve(availableVideoInputs);
        } else {
          reject(new Error("ERR::NO_MEDIA_TO_STREAM"));
        }
      });

    getMedia = async (constraints) => {
      let stream = null;
      try {
        stream = await navigator.mediaDevices.getUserMedia(constraints);
        /* use the stream */
      } catch (err) {
        console.log(err);
      }
    };

    resolvePermission = (permission) =>
      new Promise((resolve, reject) => {
        permission.onchange = function () {
          console.log("Permission changed to " + permission.state);
        };

        const permissions = [permission.state];
        if (permissions.length > 0) {
          resolve(permissions);
        } else {
          reject(new Error("ERR::NO_MEDIA_TO_STREAM_PERMISSION"));
        }
      });

    getPermission = async (PermissionDescriptor) => {
      try {
        navigator.permissions
          .query(PermissionDescriptor)
          .then(this.resolvePermission)
          .then((permission) => {
            if (permission.includes("granted")) {
              this.props.app.dm.dashboard.setState({
                IsScannerVisible: true,
                isAlertBox: false,
                alertMessage: "",
                alertMessageHeading: "Scanning",
                retryCount: 0,
              });
            } else if (permission.includes("prompt") || permission.includes("denied")) {
              this.props.app.dm.dashboard.setState({
                IsScannerVisible: true,
                isAlertBox: false,
                alertMessage: "",
                alertMessageHeading: "Scanning",
                retryCount: 0,
              });
              this.props.app.dm.dashboard.setState({
                IsScannerVisible: true,
                isAlertBox: true, //true
                alertMessage: "Allow XBP to take pictures and record video",
                alertMessageHeading: "Allow XBP to use camera",
                retryCount: 0,
              });
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } catch (err) {}
    };

    scanningQRCode = () => {
      navigator.mediaDevices
        .enumerateDevices()
        .then(this.gotDevices)
        .then((availableVideoInputs) => {
          if (availableVideoInputs.length > 0) {
            //  this.getPermission({name:"microphone"})
            this.getPermission({ name: "camera" });
          }
        })
        .catch((err) => {
          this.props.app.dm.dashboard.setState({
            IsScannerVisible: true,
            isAlertBox: true, //true
            alertMessage:
              "Check your system settings to make sure that a camera is available. If not, plug one in. You might then need to restart your browser.",
            alertMessageHeading: "Can not find the camera",
            retryCount: 0,
          });
        });
    };

    renderIcon = () => {
      const { showResetFilterBtn } = this.props.app.dm.filterDomain.state;
      const { name } = this.props.app.nav.route;
      switch (name) {
        case "makePaymentRoutes":
          return (
            <View style={[styles.makePayment]}>
              {/* <Touchable style={[commonStyles.marginRight15]} onPress={() => this.props.app.nav.to("createMessageRoute")}>
                <Image source={crateMessageIcon} style={styles.filterIcon} />
              </Touchable> */}
              <Touchable onPress={this.searchToggal} style={[commonStyles.marginRight15]}>
                <Image source={searchIcon} style={styles.filterIcon} />
              </Touchable>
              <Touchable onPress={this.onFilterOpen}>
                <Image source={filterIcon} style={styles.filterIcon} />
              </Touchable>
              {this.renderQRCodeSection()}
              {this.renderNotification()}
              {!!showResetFilterBtn ? <IconButton icon="restore" onPress={this.onRestoreFilter} /> : null}
            </View>
          );
        case "billDetailsRoute":
          const { billerId } = this.billDetails;
          let sendPid = billerId.replace("#", ">>");
          const billerList = this.billerList;
          const biller = billerList.find((el) => el.billerId.toLowerCase() === billerId.toLowerCase());
          return (
            // <View style={styles.mView}>
            <View style={[commonStyles.dFlex]}>
              {!biller.isBlocked
                ? null
                : //  <Touchable style={[commonStyles.marginRight15]} onPress={() => this.props.app.nav.to("editMessageRoute", { senderPid: sendPid })}>
                  //       <Image source={crateMessageIcon} style={styles.filterIcon} />
                  //   </Touchable>
                  null}
              <Touchable
                onPress={() =>
                  this.props.app.nav.to("billHistoryScreenRoute", {
                    billerPid: billerId,
                  })
                }>
                <Image source={historyIcon} style={styles.filterIcon} />
              </Touchable>
              {this.renderNotification()}
            </View>
            // </View>
          );
        case "billersDirectoryRoute":
        case "simpleMessageRoute":
          return (
            <View style={[commonStyles.dFlex]}>
              {/* <Touchable onPress={() => this.props.app.nav.to("createMessageRoute")}>
                <Image source={crateMessageIcon} style={styles.filterIcon} />
              </Touchable> */}
              <Touchable onPress={this.searchToggal} style={[commonStyles.marginLeft15]}>
                <Image source={searchIcon} style={styles.filterIcon} />
              </Touchable>
              {this.renderQRCodeSection()}
              {this.renderNotification()}
            </View>
          );
        case "dashboard":
          return (
            <View style={[commonStyles.dFlex]}>
              {/* <Touchable onPress={() => this.props.app.nav.to("createMessageRoute")}>
                <Image source={crateMessageIcon} style={styles.filterIcon} />
              </Touchable> */}
              <Touchable onPress={this.searchToggal} style={[commonStyles.marginLeft15]}>
                <Image source={searchIcon} style={styles.filterIcon} />
              </Touchable>
              {/* <Touchable style={[commonStyles.marginLeft15]} onPress={() => this.props.app.dm.filterDomain.setState({ isFilterOpen: true })}>
                <Image source={filterIcon} style={styles.filterIcon} />
              </Touchable> */}
              {this.renderQRCodeSection()}
              {this.renderNotification()}
            </View>
          );
        case "profileViewRoute":
          return (
            <>
              {this.renderQRCodeSection()}
              {this.renderNotification()}
            </>
          );
        case "createMessageRoute":
        case "profileEditRoute":
        case "simpleMessageDetailRoute":
        case "pamMessageRoute":
          return <>{this.renderNotification()}</>;

        default:
          return null;
      }
    };

    handleOnAction(event) {}

    handleOnActive(event) {
      if (this.state.isInactive === false) {
        this.idleTimer.reset();
      }
    }
    // warning modal for session timeout will pop up.
    handleOnIdle = async () => {
      this.setState({
        isModalTimeoutVisible: true,
        bShowConcurrentLoginPopUp: false,
      });
      this.setState({ isInactive: true });
      setTimeout(async () => {
        if (this.state.isInactive && !!this.idleTimer) {
          if (this.idleTimer) {
            this.idleTimer.pause();
          }
          await this.handleLogoutPress();
        }
      }, 1000 * 60 * 15);
    };

    async handleLogoutPress() {
      this.props.app.dm.userSession.logout();
      if (await AsyncStorage.getItem("profiles")) {
        const userLoginDetail = JSON.parse(await AsyncStorage.getItem("profiles"));
        let userType = !!userLoginDetail ? userLoginDetail.userType : null;
        if (userType === USER_TYPE.PCH_PROVIDER || userType === USER_TYPE.PCH_PATIENT) this.props.app.nav.to("pchLogin");
        else this.props.app.nav.to("login");
      } else this.props.app.nav.to("login");
    }

    renderPopUpMenu() {
      return (
        <SafeAreaView>
          <View>
            <IdleTimer
              ref={(ref) => {
                this.idleTimer = ref;
              }}
              timeout={1000 * 60 * 15}
              onActive={this.handleOnActive}
              onIdle={this.handleOnIdle}
              onAction={this.handleOnAction}
              debounce={250}
              backdropStyle={styles.backdrop}
            />
            <View>
              <ModalPopUp
                visibility={this.state.isModalTimeoutVisible}
                isFooter={false}
                bodyWrapper={[styles.sessionExpirPopupBodyWrapper]}
                modalOuterWrapper={styles.modalOuterWrapper}
                heading={"Session Expired!"}
                handleCancel={this.handleLogoutPress}
                closeButton={styles.closeBtn}
                modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
                modalContent={commonStyles.marginTop15}>
                <View style={commonStyles.textAlignCenter}>
                  <View style={commonStyles.marginBottom20}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>Your session has been expired.</Text>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>
                      You will be redirected to the login screen.
                    </Text>
                  </View>
                  <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                    <Button
                      title={"OK"}
                      style={[commonStyles.blueButton, styles.sessionModelOkBtn]}
                      onPress={this.handleLogoutPress}
                      titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight500, commonFontStyles.workSans, commonStyles.heading5]}
                    />
                  </View>
                </View>
              </ModalPopUp>
            </View>
          </View>
        </SafeAreaView>
      );
    }

    renderConcurrentLoginPopUpMenu() {
      return (
        <SafeAreaView>
          <View>
            <View>
              <ModalPopUp
                visibility={this.state.bShowConcurrentLoginPopUp}
                isFooter={false}
                bodyWrapper={[styles.sessionExpirPopupBodyWrapper]}
                modalOuterWrapper={styles.modalOuterWrapper}
                heading={"Concurent Login Detected!"}
                handleCancel={this.handleLogoutPress}
                closeButton={styles.closeBtn}
                modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
                modalContent={commonStyles.marginTop15}>
                <View style={commonStyles.textAlignCenter}>
                  <View style={commonStyles.marginBottom20}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>Do you want to continue the session?</Text>
                  </View>
                  <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                    <Button
                      title={"Yes"}
                      style={[commonStyles.blueBorderButton, commonStyles.marginRight15, styles.buttons]}
                      onPress={this.generateNewSessionString}
                      titleTextStyle={[
                        commonStyles.blueBorderButtonColor,
                        commonStyles.fontWeight600,
                        commonFontStyles.workSans,
                        commonStyles.heading6,
                      ]}
                    />
                    <Button
                      title={"No"}
                      style={[commonStyles.blueButton, styles.buttons]}
                      onPress={this.handleLogoutPress}
                      titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
                    />
                  </View>
                </View>
              </ModalPopUp>
            </View>
          </View>
        </SafeAreaView>
      );
    }

    render() {
      this.currentScreen = this.props.app.nav.route;
      const userLoginDetail = this.props.userLoginDetail;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      let userType = !!userLoginDetail ? userLoginDetail.userType : null;
      if (userType == USER_TYPE.PCH_PATIENT || userType == USER_TYPE.PCH_PROVIDER) {
      } else {
        this.searchHistoryData();
      }

      if (!this.props.app.dm.userSearchDomain.state.isSearchOpen) {
        let form = this.props.app.ui.form("searchBill");
        if (!!form) form.elmts.searchText.setValue("");
      }
      let isLoggedIn = this.props.app.dm.userSession.isLoggedIn;
      if (isLoggedIn) this.sessionChecker();

      return (
        <View style={[styles.headerControlsRight, commonStyles.dFlex, commonStyles.alignItemsCenter]}>
          {isLoggedIn ? this.renderPopUpMenu() : null}
          {this.renderConcurrentLoginPopUpMenu()}
          {userType === USER_TYPE.PCH_PATIENT || userType === USER_TYPE.PCH_PROVIDER ? (
            !!isMobileDevice ? (
              this.renderPCHLogoutSection()
            ) : null
          ) : userType === !!USER_TYPE.XBP ? (
            <>
              {this.renderIcon()}
              <View style={[styles.searchBill, this.props.app.dm.userSearchDomain.state.isSearchOpen ? styles.block : styles.none]}>
                {this.renderSearchSection()}
              </View>
              <InAppScanner />
            </>
          ) : null}
          {!!isMobileDevice ? (
            <View style={[styles.profileRightSection, commonStyles.dFlex]}>
              {/*<View style={styles.profileSection}>
                <View style={[styles.profileIconWrapper, commonStyles.marginRight10]}>
                  <Text>{CommonUtility.shortName(!!userLoginDetail ? userLoginDetail.firstName + " " + userLoginDetail.lastName : "NA")}</Text>
                  {/* <Image source={profilePic} style={[styles.profileIcon]} /> 
                </View>
                <Text style={[commonStyles.heading6, commonFontStyles.workSans]}>
                  {!!userLoginDetail ? userLoginDetail.firstName + " " + userLoginDetail.lastName : "N/A"}
                </Text>
              </View> */}
              {/* <View style={styles.addProfilePopupMenu}>
                <Touchable onPress={() => this.props.app.nav.to("loginOut")}>
                  <Image source={icLogoutB} style={commonStyles.icon24} />
                </Touchable>
              </View> */}
            </View>
          ) : null}
        </View>
      );
    }
  }
);

