import React, { Component } from "react";
import R14, { View, TextInputField, StyleSheet, FileInputField, SubmitButton } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import DeclineComponent from "./DeclineComponent";

export default R14.connect(
  class MessageUpload extends Component {
    getExtension(fname) {
      switch (fname.toUpperCase()) {
        case "CSV":
        case "DIF":
        case "ODS":
        case "OTS":
        case "TSV":
        case "XLM":
        case "XLS":
        case "XLSB":
        case "XLSM":
        case "XLSX":
        case "XLT":
        case "XLTM":
        case "XLTX":
          return "SDSH";
        case "XML":
          return "DXML";
        case "PDF":
          return "DPDF";
        case "DOC":
        case "DOCX":
        case "DOCM":
        case "DOTX":
        case "DOTM":
        case "DOCB":
          return "WORD";
        case "XSLT":
          return "XSLT";
        // case "PNG":
        //   return "PNG";
        // case "JPEG":
        //   return "JPEG";
        default:
          this.props.app.dm.commonDomain.showToastr(
            "error",
            " Invalid Format, Please attach the file in any one of the following format DPDF, DXML, SDSH, WORD, XSLT" //, PNG, JPEG"
          );
          return;
      }
    }

    getBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onloadend = () => {
          let data = reader.result.split(",");
          let fileNameXt = file.name.split(".");
          resolve({
            filename: file.name,
            contentType: this.getExtension(fileNameXt[1]),
            content: data[1],
            size: `${file.size} ${["Bytes", "Kb", "Mb", "Gb", "Tb"][Math.floor(Math.log2(file.size) / 10)]}`,
          });
        };
        reader.onerror = (err) => {
          reject(err);
        };
        reader.readAsDataURL(file);
      });
    }

    fileInputChange = async (values) => {
      let fizeSize = values.size / 1024 / 1024;
      if (fizeSize > 10) {
        this.props.app.dm.commonDomain.showToastr("error", "File size is greater than 10Mb.");
        return false;
      } else if (values.name.length > 35) {
        this.props.app.dm.commonDomain.showToastr("error", "Maximum allowed filename is 35 characters");
        return false;
      }
      const { onValueChange } = this.props;
      let fileBase64Data = await this.getBase64(values);
      onValueChange(fileBase64Data);
    };

    render() {
      const { commentsProps, onSend, isDisabled, onChangeText, padding, customHeight } = this.props;
      return (
        <View style={[commonStyles.enterMessage, commonStyles.marTAuto]}>
          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter]}>
            <View style={[styles.fieldsHolder]}>
              <View style={[styles.messageTextInput, commonStyles.flexGrow1]}>
                <TextInputField
                  labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans]}
                  labelStyle={[commonStyles.formLabelStyle]}
                  name="comments"
                  label="Enter Message*"
                  validator="cmtsVali"
                  onChangeText={onChangeText}
                  multiline={true}
                  {...commentsProps}
                  style={[commonStyles.margin0, styles.messageTextField, commonStyles.resizeVertical, padding, customHeight]}
                  helperTextStyle={[commonStyles.nowrap, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                />
              </View>
              <View style={[styles.attachmentField]}>
                <FileInputField
                  style={[commonStyles.margin0, styles.attachmentCls, customHeight]}
                  name="attachment"
                  label=""
                  iconRight=""
                  onIconRightPress={() => console.log("noop")}
                  icon="documents"
                  multiline={true}
                  onValueChange={(val) => this.fileInputChange(val)}
                  iconRightStyle={styles.fileIcon}
                  helperTextStyle={[styles.labelStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                />
              </View>
            </View>
            {onSend ? (
              <SubmitButton
                type="button"
                title="Send"
                name="sendBtn"
                disabled={isDisabled}
                style={[styles.blueButton]}
                titleTextStyle={[commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6, commonStyles.darkGrayColor]}
              />
            ) : null}
          </View>
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  fieldsHolder: {
    position: "relative",
    flexGrow: 1,
    display: "flex",
  },
  messageTextField: {
    position: "relative",
    zIndex: 1,
    resize: "vertical",
  },
  attachmentCls: {
    height: 53,
    width: 56,
  },
  attachmentCover: {
    background: "#fff",
  },
  messageTextInput: {
    color: "red",
  },
  attachmentField: {
    position: "relative",
    right: 0,
    bottom: 0,
    top: 0,
    zIndex: 99,
    background: "#fff",
  },
  blueButton: {
    background: "#37c2ef",
    color: "#333",
    height: 53,
    width: 75,
    marginLeft: 10,
    border: 0,
  },
  attachmentCls: {
    width: 52,
    padding: 0,
  },
  labelStyle: {
    whiteSpace: "nowrap",
    color: "green",
  },
});
