import R14, { AsyncStorage } from "../core";
import { PAM_CREATION, GET_BLOCK_UNBLOCK_USERS } from "../constant/app.constant";

export default class BillerDirectoryDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {
      pamList: [],
      searchPamList: [],
    };
  }

  allPamFields = `uid pamId billerId payerId totalMessages readMessages biller {uid billerId payerId billerStatus billerName isBlocked}`;

  async getPamMessagesList() {
    try {
      return await this.dm.rest.get(`${PAM_CREATION}/?status=requested`);
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }

  async blockUser(pid, block) {
    try {
      return await this.dm.rest.put(`${GET_BLOCK_UNBLOCK_USERS}`, {}, { pid, block });
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }
  async getBlockedUsers() {
    try {
      return await this.dm.rest.get(`${GET_BLOCK_UNBLOCK_USERS}`);
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }

  async moreInfoMessages(pamId) {
    try {
      return await this.dm.rest.get(`${PAM_CREATION}/${pamId}`, { pamId });
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }
  async updatePamStatus(status, pamId) {
    try {
      return await this.dm.rest.patch(`${PAM_CREATION}/${pamId}`, status, { pamId });
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }
  async createMoreInfoMessages(pamId, mimId, moreInfoMessage) {
    // let moreInfos = { moreInfoMessage: payload.moreInfoMessage };
    try {
      return await this.dm.rest.post(`${PAM_CREATION}/${pamId}/more-information/${mimId}`, moreInfoMessage, {
        pamId,
        mimId,
      });
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }
  async createMoreInfoMessagesResponse(pamId, mimId, moreInfoReply) {
    try {
      return await this.dm.rest.patch(`${PAM_CREATION}/${pamId}/more-information/${mimId}`, moreInfoReply, { pamId, mimId });
    } catch (error) {
      console.log("Thread List ::: ", error);
    }
  }

  async createPamMessages(payload, fields = this.allPamFields) {
    try {
      let res = await this.api.mutate(
        `mutation CreatePamMessage($payload: CreatePamMessageInput){
          createPamMessage(input: $payload){
            pamMessage{
              ${fields}
            }
          }
        }`,
        {
          payload,
        }
      );
      return res.data.createPamMessage ? res.data.createPamMessage.pamMessage : null;
    } catch (err) {
      return new Error(err);
    }
  }

  async createPamMesagePayload(message, pamId, payerId) {
    const { pid } = !!await AsyncStorage.getItem("profiles") ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let payload;
    let mimId = this.createMimIDFromDate(pamId);
    let res;
    if (pid === payerId) {
      payload = {
        moreInfoMessage: message,
      };

      res = await this.createMoreInfoMessages(pamId, mimId, payload);
    } else {
      payload = {
        moreInfoReply: message,
      };
      res = await this.createMoreInfoMessagesResponse(pamId, mimId, payload);
    }
    return res;
  }

  async updatePamMessageStatus(status, pamId, pam) {
    let payload = { status };
    try {
      let res = await this.updatePamStatus(payload, pamId);
      if (res.pamId) {
        let billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
        let billerRes = billerList.find((el) => el.billerId === pam.billerId && el.payerId === pam.payerId);
        if (!!billerRes && !!billerRes.length) {
          await this.dm.queryDomain.updateBiller({ billerStatus: status, uid: billerRes.uid });
        }
      }
    } catch (err) {
      return new Error(err);
    }
    return true;
  }

  async updatePamMessages(payload, fields = this.allPamFields) {
    try {
      let res = await this.api.mutate(
        `mutation UpdatePamMessage($payload: UpdatePamMessageInput){
          updatePamMessage(input: $payload){
            pamMessage{
              ${fields}
            }
          }
        }`,
        {
          payload,
        }
      );
      return res.data.updatePamMessage ? res.data.updatePamMessage.pamMessage : null;
    } catch (err) {
      return new Error(err);
    }
  }

  async createUpdatePamMessage(el) {
    let hasPamDetail = await this.getPamMessagesByPamId(el.pamId);
    let payload = {
      totalMessages: el.totalMessages,
      billerUid: el.billerUid,
      readMessages: el.readMessages,
    };
    return !!hasPamDetail && !!hasPamDetail.length
      ? await this.updatePamMessages({ ...payload, uid: hasPamDetail[0].uid })
      : await this.createPamMessages({
          ...payload,
          pamId: el.pamId,
          billerId: el.billerId,
          payerId: el.payerId,
          billerUid: el.billerUid,
          readMessages: 0,
        });
  }

  async addReadMessages(el) {
    await this.updatePamMessages({
      ...el,
      readMessages: el.totalMessages,
    });
  }
  async updateDbStatus(pam) {
    await this.updatePamMessages({ ...pam });
  }

  async getPamMessagesByPamId(pamId) {
    let options = {
      filter: {
        pamId: { eq: pamId },
      },
    };
    let res = await this.api.qry(
      `query PamMessages($filter: PamMessageFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
        pamMessages(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          nodes {
            ${this.allPamFields}
          }
        }
      }`,
      options
    );
    return res.data.pamMessages.nodes;
  }

  async getPamMessages(
    options = {},
    fieldsStr = "uid pamId billerId payerId totalMessages readMessages biller {uid billerName billerId billerStatus isBlocked}"
  ) {
    if (!fieldsStr) throw new Error("Pam Messages Domain Find Error: No fields found");
    const { pid } = !!await AsyncStorage.getItem("profiles") ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    options = {
      filter: {
        payerId: { eq: pid },
      },
    };
    if (!options.filter) options.filter = {};
    let result = await this.api.qry(
      `query PamMessages($filter: PamMessageFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
        pamMessages(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          totalCount @include(if: true)
          nodes {
            ${fieldsStr}
          }
        }
      }`,
      options
    );

    return result.data.pamMessages.nodes;
  }

  async displayBillerDirectoryData() {
    const { pid } = !!await AsyncStorage.getItem("profiles") ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let blockedBiller = await this.getBlockedUsers();
    let resData = await this.getPamMessagesList();
    if (!!resData) {
      // let pamRes = [];
      let res = JSON.parse(JSON.stringify(resData));
      if (res) {
        for (let ele of res) {
        //  res.map( async (ele, i) => {
          if (!!ele) {
            let pamDetails = await this.moreInfoMessages(ele.pamId);
            const billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
            let billerDetail = billerList.find((itm) => itm.billerId === ele.billerId && itm.payerId === ele.payerId);
            /**
             * new biller comes and requested for pam
             * and you are logged in and that time your biller is empty or it will have previous biller
             ***/
            if (!billerDetail) {
              // adding new biller if biller is not present in DB
              let newBiller = await this.dm.queryDomain.addUpdateBiller({ ...ele, blockedBiller });
              ele.billerUid = newBiller.uid;
              // billerDetail = newBiller;
            } else {
              ele.billerUid = billerDetail.uid;
            }
            /**
             * then the above block of code is required, bcoz user not need to logout and re login to get the biller details.
             * */
            ele.totalMessages = 1;
            if (!!pamDetails && pamDetails.pamId && pamDetails.moreInfos && !!pamDetails.moreInfos.length) {
              await this.dm.queryDomain.addUpdateBiller({ ...pamDetails, blockedBiller, biller: billerDetail });
              ele.totalMessages = !!pamDetails.moreInfos ? pamDetails.moreInfos.filter((msg) => !!msg.moreInfoReply).length + 1 : 1;
            }
            // pamRes.push(
              await this.createUpdatePamMessage(ele);
              //);
          }
       // })
        }
        let billerLists = await this.dm.queryDomain.getBiller({ filter: { payerId: { like: pid } } });
        await AsyncStorage.setItem("billerList", JSON.stringify(billerLists));
      }
    } else {
      if (!!blockedBiller && !!blockedBiller.length) {
        for (const ele of blockedBiller) {
          let billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
          let billerDetail = billerList.find((itm) => itm.billerId === ele.pid && itm.payerId === pid);
          if (!!billerDetail) {
            await this.dm.queryDomain.updateBiller({ isBlocked: true, uid: billerDetail.uid });
          }
        }
        let billerLists = await this.dm.queryDomain.getBiller({ filter: { payerId: { like: pid } } });
        await AsyncStorage.setItem("billerList", JSON.stringify(billerLists));
      }
    }
    let pamList = await this.getPamMessages({ filter: { payerId: { like: pid }}});
    this.dm.billerDirectoryDomain.setState({ pamList: pamList });
    return pamList;
  }

  getRandomString(length) {
    var randomChars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var result = "";
    for (var i = 0; i < length; i++) {
      result += randomChars.charAt(Math.floor(Math.random() * randomChars.length));
    }
    return result;
  }

  createMimIDFromDate(pamId) {
    let sufix = this.getRandomString(7);
    let pam = pamId.split("-");
    const mimId = "MSG-" + pam[1] + "-" + sufix;
    return mimId;
  }
}
