import R14, { AsyncStorage } from "../../core";
import { USER_TYPE } from "../../constant/app.data.constant";

export default class UploadBillsDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {
      IsSubmitted: false,
      InSideNavigation: true,
      practitioners :[],
      providers :[],
      searchPatients :[]
    };
  }

  async getBills(filter, fields)
  {
      // let opt = { ...filter, ...options };
      let result = await this.api.qry(
        `query UploadBills($filter: UploadBillFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
          uploadBills(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
            totalCount @include(if: true)
            nodes {
              ${fields}
            }
          }
        }`,
        filter
      );
      let res = !!result.data.uploadBills && !!result.data.uploadBills.nodes ? result.data.uploadBills.nodes : [];
      return res;
  }

  async getUploadedBills(fieldsStr = "uid email mobileNumber billStatus ssId dOB createdAt doctorUid isPatientRequest submittedDate pchDoctorDetail { uid DoctorName NPI } pchInsuranceDetail {uid name groupNumber} userProfileUid userProfile {uid firstName lastName EIN profileImage} poBoxNo")
  {
    if (await AsyncStorage.getItem("profiles")) {
      const { userType, uid } = JSON.parse(await AsyncStorage.getItem("profiles"));
      let childUsers = await AsyncStorage.getItem("childs");
      if (!childUsers) {
       
       let childs = await this.dm.userLoginDomain.getChildUsers();

        let filter = {
          filter: {
            mobileNumber: { in: childs.map((user) => { return user.mobileNumber }) },
            email: { in: childs.map((user) => { return user.email }) },
          },
          sort: { field: "createdAt", order: "DESC" },
        };

        if (!!userType && userType == USER_TYPE.PCH_PROVIDER) {
          filter = {
            filter: {
              userProfileUid: { in: childs.map((user) => { return user.uid }) },
            },
            sort: { field: "createdAt", order: "DESC" },
          };
        }
        return await this.getBills(filter, fieldsStr);
      }
      else {
        let childs = JSON.parse(childUsers);
        let filter = {
          filter: {
            mobileNumber: { in: childs.map((user) => { return user.mobileNumber }) },
            email: { in: childs.map((user) => { return user.email }) },
          },
          sort: { field: "createdAt", order: "DESC" },
        };

        if (!!userType && userType == USER_TYPE.PCH_PROVIDER) {
          filter = {
            filter: {
              userProfileUid: { in: childs.map((user) => { return user.uid }) },
            },
            sort: { field: "createdAt", order: "DESC" },
          };
        }
        return await this.getBills(filter, fieldsStr);
      }
    }
  }

  async uploadBill(input, fields = "uid name email mobileNumber dOB ssId doctorUid billStatus createdAt isPatientRequest submittedDate pchDoctorDetail { DoctorName NPI uid } pchInsuranceDetail {uid name groupNumber poBoxNo planName groupName insuranceCompanyID address1 address2 city state country zipCode memberID memberFirstName memberLastName email phoneNumber memberMiddleInitial } userProfile {uid firstName lastName EIN } uploadedBillDocuments { filename } poBoxNo") { 
      try {
        let res = await this.api.mutate(
          `
          mutation CreateUploadBill($input: CreateUploadBillInput){
            createUploadBill(input: $input){              
              success 
              uploadBill {
                ${fields}
              }
            }
          }`,
          { input }
        );
        return res.data.createUploadBill.uploadBill;
      } catch (error) {
        return error;
      }
  }
  
  async fetchUploadedBill(
    uid,
    fields = "uid name email mobileNumber dOB ssId doctorUid billStatus createdAt isPatientRequest submittedDate pchDoctorDetail { DoctorName NPI uid } pchInsuranceDetail {uid name groupNumber poBoxNo planName groupName insuranceCompanyID address1 address2 city state country zipCode memberID memberFirstName memberLastName email phoneNumber memberMiddleInitial } userProfileUid userProfile {uid firstName lastName EIN parentUid} uploadedBillDocuments { filename } poBoxNo",
  ) {
    let options = {
      uid: uid
    };
    try {
      let res = await this.api.qry(
        `query UploadedBill($uid: ID!){
            uploadBill(uid: $uid){
                ${fields}
            }
        }`,
        options
      );
      return res.data.uploadBill;
    } catch (error) {
      return error;
    }
  }

  async fetchUploadedBillDocuments(uid,fields = "uid uploadedBillDocuments { actualsize contentType filename size content }" )
   {
    try {
    return this.fetchUploadedBill(uid, fields)
    } catch (error) {
      return error;
    }
  }

  async submitUploadedBill(
    input,
    fields = "uid name email mobileNumber dOB ssId doctorUid billStatus createdAt isPatientRequest submittedDate pchDoctorDetail { DoctorName NPI uid } pchInsuranceDetail {uid name groupNumber poBoxNo planName groupName insuranceCompanyID address1 address2 city state country zipCode memberID memberFirstName memberLastName email phoneNumber memberMiddleInitial } userProfile {uid firstName lastName EIN } uploadedBillDocuments { filename } poBoxNo insuranceDocuments { filename }"
  ) {
    try {
      let res = await this.api.mutate(
        `
      mutation UpdateUploadedBillDetail($input: UpdateUploadBillInput) {
        updateUploadBill(input: $input){
          uploadBill {
            ${fields}
          }
          success
        }
      }`,
        { input }
      );
      return res.data.updateUploadBill.uploadBill;
    } catch (error) {
      return error;
    }
  }
  
  async sendFileToSftpServer(data) {
    try {
      let res = await this.api.mutate(
        `mutation SaveXmlDocument($input: XmlDocumentInput!){
          saveXmlDocument(input: $input){
            success msg            
          }
        }`,
        {
          input: data,
        }
      );
      return res.data.saveXmlDocument;
    } catch (error) {
      return error;
    }
  }
    
  setStatePractitioner = (input) => {
    // 1. copy existing state & update it
    let filterItems = !!input ? input : [];
    const practitioners = [...new Set([...this.state.practitioners, ...filterItems])];
    // 2. set state
    this.setState({ practitioners });
    return practitioners;
  };
    
  setStateProvider = (input) => {
    // 1. copy existing state & update it
    let filterItems = !!input ? input : [];
    const providers = [...new Set([...this.state.providers, ...filterItems])];
    // 2. set state
    this.setState({ providers });
    return providers;
  };
  
   setStatePatients = (input) => {
    // 1. copy existing state & update it
    let filterItems = !!input ? input : [];
    const searchPatients = [...new Set([...this.state.searchPatients, ...filterItems])];
    // 2. set state
    this.setState({ searchPatients });
    return searchPatients;
  };
    

async getUsersByMobileNumber(search, type, limit = 10, fields = "uid email mobileNumber firstName lastName")
{
    let filter = this.state.searchPatients.filter((c) => c.mobileNumber.startsWith(search));
      if (!filter || filter.length === 0) {
        const options = {
          searchFilter: search,
          propFilter: type,
          limit: limit,
        };

        let result = await this.api.qry(
          `query SearchUsersByParam($searchFilter: String!, $propFilter:String!, $limit: Int){
                  searchUsersByParam(searchFilter: $searchFilter, propFilter: $propFilter, limit: $limit) {
                    nodes {
                      ${fields}
                    }
                  }
                }`,
          options
        );
        filter = this.setStatePatients(result.data.searchUsersByParam.nodes);
        filter = this.state.searchPatients.filter((c) => c.mobileNumber.startsWith(search));
      }      
      return filter;
  }

async getUsersByEmail(search, type, limit = 10, fields = "uid email mobileNumber firstName lastName")
{
    let filter = this.state.searchPatients.filter((c) => c.email.toLowerCase().startsWith(search.toLowerCase()));
      if(!filter || filter.length === 0)
      {
        const options = {
          searchFilter: search,
          propFilter: type,
          limit: limit,
        };

        let result = await this.api.qry(
          `query SearchUsersByParam($searchFilter: String!, $propFilter:String!, $limit: Int){
              searchUsersByParam(searchFilter: $searchFilter, propFilter: $propFilter, limit: $limit) {
                nodes {
                  ${fields}
                }
              }
            }`,
          options
        );
        filter = this.setStatePatients(result.data.searchUsersByParam.nodes);
        filter = this.state.searchPatients.filter((c) => c.email.toLowerCase().startsWith(search.toLowerCase()));
      }
      
      return filter;
  }

  async getPractitioner(search = "", limit = 10, fields = "uid firstName EIN")
  {
    let filter = this.state.practitioners.filter((c) =>
                c.firstName.toLowerCase().startsWith(!!search ? search.toLowerCase() : c.firstName.toLowerCase()));
      if(!filter || filter.length === 0 || filter.length < limit)
      {
        const options = {
          searchFilter: search,
          limit: limit,
        };

        let result = await this.api.qry(
          `query SearchPractitioner($searchFilter: String, $limit: Int){
              searchPractitioner(searchFilter: $searchFilter, limit: $limit) {
                nodes {
                  ${fields}
                }
              }
            }`,
          options
        );
        filter = this.setStatePractitioner(result.data.searchPractitioner.nodes);
        return filter.filter((c) => c.firstName.toLowerCase().startsWith(!!search ? search.toLowerCase() : c.firstName.toLowerCase()));
      }
      else
      return filter;
  }

   async getProviders(practitionerUid, search = "", limit = 10, fields = "uid userLoginDetailUid NPI DoctorName")
   {
     let filter = this.state.providers.filter((c) =>
       c.userLoginDetailUid === practitionerUid &&
        c.DoctorName.toLowerCase().startsWith(!!search ? search.toLowerCase() : c.DoctorName.toLowerCase()));
      if(!filter || filter.length === 0 || filter.length < limit)
      {
        const options = {
          uidFilter: practitionerUid,
          searchFilter: search,
          limit: limit,
        };

        let result = await this.api.qry(
            `query SearchProvider($uidFilter: String!, $searchFilter: String, $limit: Int){
              searchProvider(uidFilter: $uidFilter, searchFilter: $searchFilter, limit: $limit) {
                nodes {
                  ${fields}
                }
              }
            }`,
            options
          ); 
        filter = this.setStateProvider(result.data.searchProvider.nodes);
        return filter.filter((c) =>
          c.userLoginDetailUid === practitionerUid &&
          c.DoctorName.toLowerCase().startsWith(!!search ? search.toLowerCase() : c.DoctorName.toLowerCase()));
      }
      else
       return filter; 
  }
}
