import React from "react";
import R14, { StyleSheet, View, ScrollView, SafeAreaView } from "../core";
import MessageComponent from "../components/MessageComponent";
import ChatInputField from "../components/ChatInputField";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { BILL_STATUS } from "../constant/app.data.constant";
import CopyRight from "../components/CopyRight";
import LeftMenuComponent from "../components/LeftMenuComponent";

export default R14.connect(
  class MessageScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        messages: props.displayMessages,
        msgCount: 0,
        messageContentHeight: 0,
        scrollCount: 0,
        isChatAllow: false,
      };
      this.scrollRef = React.createRef();
    }

    layoutChange = (event) => {
      let evtobj = event.nativeEvent.layout;
      this.setState({
        messageContentHeight: evtobj.height,
      });
      this.scrollToEnd({ y: evtobj.height - 250 });
    };

    scrollToEnd(options = { animated: true }) {
      if (this.scrollRef.current) this.scrollRef.current.scrollToEnd(options);
    }

    onSent = (msgData) => {
      this.setState({ messages: this.state.messages.concat(msgData), msgCount: ++this.state.msgCount });
    };

    render() {
      const pid = this.props.userDetails.pid;
      const { billDetails } = this.props;
      const { messages, isChatAllow } = this.state;
      let lastLeaf = messages[messages.length - 1];
      const messageType = lastLeaf.envelope.messageHeader.senderPid === pid ? "NotetoBiller" : "NotetoPayer";
      const { messageHeader } = lastLeaf.envelope;
      let newBillDetails = {
        ...billDetails,
        recipientPid: pid,
        senderPid: messageHeader.recipientPid === pid ? messageHeader.senderPid : messageHeader.recipientPid,
      };
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      return (
        <SafeAreaView style={[styles.messageScreen]}>
          <ScrollView
            style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu, styles.billDetailsScreen]}>
            <View style={[commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.flexGrow1]}>
              <ScrollView key={this.state.scrollCount} ref={this.scrollRef} onLayout={this.layoutChange}>
                {/* ABOVE ScrollView property height={this.state.messageContentHeight} */}
                <MessageComponent displayMessages={messages} key={this.state.msgCount} customClass={commonStyles.padding16} />
              </ScrollView>
              {/* {!billDetails.isExtentionRequested && !BILL_STATUS[billDetails.billStatus] && !billDetails.biller.isBlocked ? (
              <View style={[styles.chatInputFieldSection]}>
                <ChatInputField messageType={messageType} billData={newBillDetails} msgList={messages} notAllowedChat={!isChatAllow} onAfterSent={this.onSent} />
              </View>
            ) : null} */}
              {(BILL_STATUS[billDetails.billStatus] || BILL_STATUS[billDetails.billStatus] === undefined) && !billDetails.biller.isBlocked ? (
                <View style={[styles.chatInputFieldSection]}>
                  <ChatInputField
                    messageType={messageType}
                    billData={newBillDetails}
                    msgList={messages}
                    onAfterSent={this.onSent}
                    notAllowedChat={!isChatAllow}
                  />
                </View>
              ) : null}
            </View>

            {isMobileDevice ? null : (
              <View style={commonStyles.copyRightSection}>
                <CopyRight />
              </View>
            )}
          </ScrollView>
          <LeftMenuComponent />
        </SafeAreaView>
      );
    }
  }
);

const styles = StyleSheet.create({
  messageScreen: {
    flexGrow: 1,
    alignItems: "flex-start",
    background: "#fff",
    padding: "5px 15px 0px 15px",
    position: "relative",
    height: "calc(100vh - 150px)",
    display: "flex",
    flexDirection: "column",
  },
  messageComponentOuter: {
    // maxHeight: 725,
    // overflowY: "auto",
    // screen: ({ width }) => {
    //   if (width <= 640)
    //     return {
    //       maxHeight: 500,
    //     };
    // },
  },
  messageComponent: {
    background: "#fff",
    padding: 16,
  },
  chatInputFieldSection: {
    background: "#fff",
    position: "absolute",
    bottom: 25,
    width: "100%",
    left: 0,
    padding: "0 15px",
  },
});
