import React from "react";
import R14, { Redirect, AsyncStorage, View, Text } from "./core";
import LoginScreen from "./screens/LoginScreen";
import LogoutScreen from "./screens/LogoutScreen";
import DashboardScreen from "./screens/DashboardScreen";
import ForgotPasswordScreen from "./screens/ForgotPasswordScreen";
import RegistrationScreen from "./screens/RegistrationScreen";
import BillDetailsScreen from "./screens/BillDetailsScreen";
import ProfileViewScreen from "./screens/ProfileViewScreen";
import MessageScreen from "./screens/MessageScreen";
import MakePaymentScreen from "./screens/MakePaymentScreen";
import BillHistoryScreen from "./screens/BillHistoryScreen";
import PaymentScreen from "./screens/PaymentScreen";
import DeclineComponent from "./components/DeclineComponent";
import ExtensionScreen from "./screens/ExtensionScreen";
import BillersDirectoryScreen from "./screens/BillersDirectoryScreen";
import PamMessageScreen from "./screens/PamMessageScreen";
import SimpleMessageScreen from "./screens/SimpleMessageScreen";
import CreatingMessageScreen from "./screens/CreatingMessageScreen";
import SimpleMessageDetailScreen from "./screens/SimpleMessageDetailScreen";
import BillReceiptScreen from "./screens/BillReceiptScreen";
import AddNewCardScreen from "./screens/AddNewCardScreen";
import PaymentOptionsScreen from "./screens/PaymentOptionsScreen";
import ResponsiveTableScreen from "./screens/ResponsiveTableScreen";
import CommonUtility from "./utils/CommonUtility";
import AddNewAccountScreen from "./screens/AddNewAccountScreen";
import MyPreferencesScreen from "./screens/MyPreferencesScreen";
import NotificationScreen from "./screens/NotificationScreen";
import NewSimpleMessageScreen from "./screens/NewSimpleMessageScreen";
import BillRequestScreen from "./screens/BillRequestScreen";
import ExternalScannerScreen from "./screens/ExternalScannerScreen";

import { BILL_STATUS, STATUS, USER_TYPE } from "./constant/app.data.constant";
// PCH Screens
import PCHSignupScreen from "./screens/PCH/PCHSignupScreen";
import PCHProfileViewScreen from "./screens/PCH/PCHProfileViewScreen";
import UploadBillsScreen from "./screens/PCH/UploadBillsScreen";
import PCHLoginScreen from "./screens/PCH/PCHLoginScreen";
import ProviderListScreen from "./screens/PCH/ProviderListScreen";
import PatientInsuranceScreen from "./screens/PCH/PatientInsuranceScreen";
import PCHDashboardScreen from "./screens/PCH/PCHDashboardScreen";
import SubmittedReceiptScreen from "./screens/PCH/SubmittedReceiptScreen";
import AddUsers from "./screens/PCH/AddUsers";
import AsyncStorageUtils from "./utils/AsyncStorageUtils";

export default class Actions extends R14.Actions {
  async shouldActionLoad({ to, from, app }) {
    let ret = true;
    // await app.dm.userSession.init();
    if (!to.metadata.public) {
      // Forward to login   ....Azad...
      ret = <Redirect to="login" />;
      if (app.dm.userSession.isLoggedIn) {
        // if(!!from && ["pchLogin", "login", "pchSignup"].includes(from._name) && ["dashboard", "uploadedBillsRoute"].includes(to._name))
        //   {
        //     // await AsyncStorage.removeItem("returnUrl")
        //     document.location.reload();
        //   }
        // filter screen reset
        const { compiledMsgList } = app.dm.dashboard.state;
        app.dm.dashboard.setState({ filterBills: compiledMsgList });

        app.dm.filterDomain.resetFilter();
        // search screen reset
        app.dm.userSearchDomain.setState({
          isSearchOpen: false,
          textInSearchBox: "",
          currentOpenScreen: from,
        });
        ret = true;
      }
    }
    if (!!from && !!["makePaymentRoutes", "simpleMessageRoute", "billersDirectoryRoute"].includes(from._name))
      app.dm.notificationDomain.setState({
        notificationThreadIds: [],
      });

    app.dm.dashboard.setState({
      oldRoute: from ? from._name : "dashboard",
    });
    app.dm.notificationDomain.setState({
      isMobileDevice: CommonUtility.isMobileDevice(),
    });
    return ret;
  }

  /** User login actions */
  async login() {
    let username = (await AsyncStorage.getItem("username")) || "";
    return <LoginScreen bEnforcePasswordChange={false} username={username} />;
  }

  async changePassword() {
    if (!(await AsyncStorage.getItem("profiles"))) {
      return <Redirect to="login" />;
    }
    const userLoginDetail = (await AsyncStorage.getItem("profiles")) || "";
    let username = (await AsyncStorage.getItem("username")) || "";
    return <LoginScreen username={username} bEnforcePasswordChange={true} payerDetails={userLoginDetail} />;
  }

  async pchChangePassword() {
    if (!(await AsyncStorage.getItem("profiles"))) {
      return <Redirect to="pchLogin" />;
    }
    
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};    
    return <PCHLoginScreen bEnforcePasswordChange={true} payerDetails={userLoginDetail}/>;
  }

  async logout() {
    return <LogoutScreen />;
  }

  async landing() {
    if (await AsyncStorage.getItem("profiles")) {
      const userLoginDetail = JSON.parse(await AsyncStorage.getItem("profiles"));
      let userType = !!userLoginDetail && !!userLoginDetail.userType ? userLoginDetail.userType : null;
      //alert(userType);
      if (userType === USER_TYPE.PCH_PROVIDER || userType === USER_TYPE.PCH_PATIENT)
        return <Redirect to={this.dm.userSession.isLoggedIn ? "dashboard" : "pchLogin"} />;
      else return <Redirect to={this.dm.userSession.isLoggedIn ? "dashboard" : "login"} />;
    } else return <Redirect to={this.dm.userSession.isLoggedIn ? "dashboard" : "login"} />;
  }

  /** Account Actions */
  async userProfileRoute() {
    // fetch user detail
    let userProfile = await AsyncStorage.getItem("profiles");
    return <ProfileViewScreen profileData={userProfile} />;
  }

  async forgotPassword() {
    return <ForgotPasswordScreen />;
  }
  async registration(type) {
    return <RegistrationScreen registrationType={type} />;
  }
  async pchSignup(type) {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    return <PCHSignupScreen registrationType={type} userLoginDetail={userLoginDetail} />;
  }
  async dashboard() {
    const userLoginDetail = await AsyncStorageUtils.GetProfileDataAsync();    
    let userType = !!userLoginDetail && userLoginDetail.userType ? userLoginDetail.userType : USER_TYPE.XBP;
    const { compiledMsgList } = this.dm.dashboard.state;
    if (userType == USER_TYPE.PCH_PROVIDER || userType == USER_TYPE.PCH_PATIENT) {
      if (!compiledMsgList.length) {
        await this.dm.dashboard.setCompiledBillList();
      }
      return <PCHDashboardScreen userType={userType} />;
    } else {
      if (!compiledMsgList.length) {
        await this.dm.dashboard.setCompiledMsgList();
      }
      return <DashboardScreen />;
    }
  }

  async makePaymentAction() {
    const { compiledMsgList } = this.dm.dashboard.state;
    if (!compiledMsgList.length) {
      await this.dm.dashboard.setCompiledMsgList();
    }
    return <MakePaymentScreen />;
  }

  async pamMessageAction({ pamId }) {
    const userDetails = JSON.parse(await AsyncStorage.getItem("profiles"));
    let pamM = await this.dm.billerDirectoryDomain.getPamMessagesByPamId(pamId);
    let pamA = await this.dm.billerDirectoryDomain.moreInfoMessages(pamId);
    return <PamMessageScreen pam={pamM[0]} moreInfo={pamA} userDetails={userDetails} />;
  }
  async billDetailsAction({ threadId }) {
    const userDetails = JSON.parse(await AsyncStorage.getItem("profiles"));
    let newThreadId = threadId.replaceAll(">>", "#");
    const messages = await this.dm.dashboard.getBillerFromThread(newThreadId);
    const res = await this.dm.dashboard.getBills({ filter: { threadId: { eq: newThreadId } } });
    if (!messages) {
      // return <Redirect to="login" />;
      this.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
      return false;
    }
    let bd = this.dm.dashboard.manageBillDetails(messages.messages);
    let complileBill = { ...bd, ...res[0] };
    return <BillDetailsScreen billDetails={complileBill} messages={messages} />;
  }
  async profileViewAction() {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    return <ProfileViewScreen profile={userLoginDetail} profileActionType={"view"} />;
  }
  async profileEditAction() {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    const countryList = await this.dm.miscellaneousDomain.getCountries();
    return <ProfileViewScreen profile={userLoginDetail} profileActionType={"edit"} countryList={countryList} />;
  }

  //PCH Profile view/edit
  async pchProfileViewAction() {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    return <PCHProfileViewScreen profile={userLoginDetail} profileActionType={"view"} />;
  }
  async pchProfileEditAction() {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    const countryList = await this.dm.miscellaneousDomain.getCountries();
    return <PCHProfileViewScreen profile={userLoginDetail} profileActionType={"edit"} countryList={countryList} />;
  }

  async messageScreenAction({ threadId }) {
    let newThreadId = threadId.replaceAll(">>", "#");
    const res = await this.dm.dashboard.getBillerFromThread(newThreadId);
    if (!res) {
      // return <Redirect to="login" />;
      this.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
      return false;
    }
    let bd = this.dm.dashboard.onlyMessageBody(res.messages);
    const bills = await this.dm.dashboard.getBills({ filter: { threadId: { eq: newThreadId } } });
    let complileBill = bills[0];
    return <MessageScreen displayMessages={bd} billDetails={complileBill} />;
  }
  async billHistoryScreenAction() {
    const { pid } = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let billDetails = JSON.parse(await AsyncStorage.getItem("billDetails"));
    const messagesList =
      (await this.dm.dashboard.getBills({
        filter: { billerId: { like: billDetails.billerId }, payerId: { like: pid } },
      })) || [];
    const messages = CommonUtility.displayHistoryBills(messagesList, billDetails);
    return <BillHistoryScreen billDetails={billDetails} messages={messages} />;
  }

  async payTypeAction({ paytype }) {
    let billDetails = JSON.parse(await AsyncStorage.getItem("billDetails"));
    const res = await this.dm.dashboard.getBillerFromThread(billDetails.threadId);
    const billDt = await this.dm.dashboard.getBills({ filter: { threadId: { eq: billDetails.threadId } } });
    if (!res) {
      // return <Redirect to="login" />;
      this.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
      return false;
    }
    let filterMsgs = [];
    if (res && res.messages) {
      filterMsgs = res.messages.filter((el) => el.envelope.messageBody);
    }
    let bd = this.dm.dashboard.manageBillDetails(res.messages);
    let cardDisplayList = await this.dm.billDetails.getPayerCardDetailsList();
    cardDisplayList.sort((a, b) => (a.isSetDefault === b.isSetDefault ? 0 : a ? -1 : 1));
    let filterCardList = [];
    filterCardList = cardDisplayList.filter((item) => item.isSetDefault === true);
    if (!!cardDisplayList.length > 0 && !!filterCardList.length > 0) {
      if (cardDisplayList[0].isSetDefault === false) {
        let old_index = 0;
        cardDisplayList.map((el, index) => {
          if (el.uid === filterCardList[0].uid) old_index = index;
        });
        cardDisplayList.splice(0, 0, cardDisplayList.splice(old_index, 1)[0]);
      }
    }
    return <PaymentScreen payType={paytype} billData={{ ...bd, ...billDt[0] }} msgList={filterMsgs} cardDisplayList={cardDisplayList} />;
  }

  async declinedAction({ paytype }) {
    let billDetails = JSON.parse(await AsyncStorage.getItem("billDetails"));
    const res = await this.dm.dashboard.getBillerFromThread(billDetails.threadId);
    const billDt = await this.dm.dashboard.getBills({ filter: { threadId: { eq: billDetails.threadId } } });
    if (!res) {
      // return <Redirect to="login" />;
      this.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
      return false;
    }
    let bd = this.dm.dashboard.manageBillDetails(res.messages);
    return <DeclineComponent payType={paytype} billData={{ ...bd, ...billDt[0] }} />;
  }
  async extensionAction({ paytype }) {
    let billDetails = JSON.parse(await AsyncStorage.getItem("billDetails"));
    const res = await this.dm.dashboard.getBillerFromThread(billDetails.threadId);
    const billDt = await this.dm.dashboard.getBills({ filter: { threadId: { eq: billDetails.threadId } } });
    if (!res) {
      // return <Redirect to="login" />;
      this.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
      return false;
    }
    let bd = this.dm.dashboard.manageBillDetails(res.messages);
    return <ExtensionScreen payType={paytype} billData={{ ...bd, ...billDt[0] }} msgList={null} />;
  }

  async billersDirectoryScreenAction() {
    const pam = await this.dm.billerDirectoryDomain.displayBillerDirectoryData(); // api call....
    // this.dm.billerDirectoryDomain.setState({ pamList: pam });
    return <BillersDirectoryScreen pam={pam} />;
  }
  async simpleMessageScreenAction() {
    await this.dm.simpleMessage.displaySimpleMessage();
    return <SimpleMessageScreen />;
  }

  async simpleMessagesDetailsAction({ threadId }) {
    let newThreadId = threadId.replaceAll(">>", "#");
    let data = await this.dm.simpleMessage.getBillerFromThread(newThreadId);
    let dbData = await this.dm.simpleMessage.getMessageById(newThreadId);
    if (!data) {
      // return <Redirect to="login" />;
      this.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
      return false;
    } else {
      let msg = this.dm.simpleMessage.hasOnlyMessageBody(data.messages);
      let userDetails = JSON.parse((await AsyncStorage.getItem("profiles")) || "null") || {};
      await this.dm.simpleMessage.setState({ messageList: msg });
      let savedBillerList = JSON.parse((await AsyncStorage.getItem("billerList")) || "null") || [];

      const pid = userDetails.pid;
      const { threadHeader } = data.messages[0].envelope;
      const billerId = threadHeader.originatorPid === pid ? threadHeader.respondentPid : threadHeader.originatorPid;
      let billerList = await this.dm.simpleMessage.distinctBillerById(billerId);
      let billerDetail = billerList.filter((el) => el.payerId === pid);
      return <SimpleMessageDetailScreen messageDetails={msg} dbData={dbData[0]} billerDetails={billerDetail[0]} />;
    }
  }

  async createMessageAction() {
    let data = await this.dm.simpleMessage.getBillerList();
    let { list, billNameList } = data;
    list = list.sort((a, b) => a.label.localeCompare(b.label));
    return <CreatingMessageScreen billerList={list} billerNameList={billNameList} />;
  }
  async editMessageAction({ senderPid }) {
    let newPid = senderPid ? senderPid.replaceAll(">>", "#") : null;
    let data = await this.dm.simpleMessage.getBillerList();
    let { list, billNameList } = data;
    return <CreatingMessageScreen billerList={list} billerNameList={billNameList} senderPid={newPid} />;
  }

  async billReceiptAction({ threadId }) {
    let newThreadId = threadId.replaceAll(">>", "#");
    const thrdBill = await this.dm.dashboard.getBillerFromThread(newThreadId);
    const res = await this.dm.dashboard.getBills({ filter: { threadId: { eq: newThreadId } } });
    let bd = this.dm.dashboard.manageBillDetails(thrdBill.messages);
    let complileBill = { ...bd, ...res[0] };
    return <BillReceiptScreen billDetails={complileBill} />;
  }
  async addNewCardAction() {
    const userDetails = JSON.parse(await AsyncStorage.getItem("profiles"));
    return <AddNewCardScreen userDetails={userDetails} />;
  }
  async responsiveTableAction() {
    return <ResponsiveTableScreen />;
  }
  async addNewAccountAction({ uid = null }) {
    const userDetails = JSON.parse(await AsyncStorage.getItem("profiles"));
    let formData = await this.dm.billDetails.fetchPayerAccountDetails(uid);
    return <AddNewAccountScreen formData={formData} userDetails={userDetails} />;
  }
  async editAccountAction({ uid = null }) {
    let formData = await this.dm.billDetails.fetchPayerAccountDetails(uid);
    return <AddNewAccountScreen formData={formData} />;
  }
  async myPreferencesAction() {
    let myPreferenceList = await this.dm.myPreferenceDomain.getMyPreference();
    return <MyPreferencesScreen myPreferenceList={myPreferenceList} />;
  }
  async paymentOptionsAction() {
    let cardDisplayList = await this.dm.billDetails.getPayerCardDetailsList();
    let bankAccountList = await this.dm.billDetails.getPayerAccountDetailsList();
    let prevRoute = this.nav._r14.navigation._prevRoute != null ? this.nav._r14.navigation._prevRoute.name : null;
    if (!!prevRoute && prevRoute != "addNewAccountRoute" && prevRoute != "editAccountRoute") {
      this.dm.billDetails.setState({ bankTabActive: "Card" });
    }
    return <PaymentOptionsScreen cardDisplayList={cardDisplayList} bankAccountList={bankAccountList} />;
  }
  async notificationScreenAction() {
    await this.dm.notificationDomain.notificationDetail();
    return <NotificationScreen />;
  }
  async newSimpleMessageAction() {
    return <NewSimpleMessageScreen />;
  }
  async billRequestScreenAction() {
    return <BillRequestScreen />;
  }

  async externalScannerAction({ threadId }) {
    return <ExternalScannerScreen threadId={threadId} />;
  }

  /* PCH Actions Start */

  async pchLogin() {
    let username = (await AsyncStorage.getItem("username")) || "";
    let  remembermeUsername = await CommonUtility.getWithExpiry("username") ? await CommonUtility.getWithExpiry("username") : "";
    return <PCHLoginScreen username={username} remembermeUsername= {remembermeUsername}/>;
  }

  async uploadBillScreenAction() {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let userType = !!userLoginDetail && !!userLoginDetail.userType ? userLoginDetail.userType : USER_TYPE.XBP;
    let insurances = [];
    if (userType === USER_TYPE.PCH_PATIENT) {
      insurances = await this.dm.addInsuranceDomain.getInsuranceDetailsList({
        userLoginDetailUid: { eq: userLoginDetail.uid },
        isActive: { eq: "true" },
      });
    }
    return <UploadBillsScreen insurances={insurances} userLoginDetail={userLoginDetail} />;
  }
  async uploadedBillAction({ uid }) {
    if (!!uid && uid !== "undefined")
      return <ExternalScannerScreen uid={uid} />;
    else this.nav.to("pchLogin");
  }

  async viewBillDetailsAction({ uid }) {
    const { InSideNavigation } = this.dm.uploadBillDomain.state;
    if (!!uid) {
      let bills = await this.dm.uploadBillDomain.fetchUploadedBill(uid);
      let parentName = "";
      if (!!bills) {
        let insurances = [];
        const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
        if (!BILL_STATUS[bills.billStatus]) {
          let userUID = !!userLoginDetail ? userLoginDetail.uid : null;
          insurances = await this.dm.addInsuranceDomain.getInsuranceDetailsList({
            userLoginDetailUid: { eq: userUID },
            isActive: { eq: "true" },
          });
        }

        if (!!bills.userProfile.parentUid) {
          {
            let parentUserDetails = await this.dm.userLoginDomain.getLoginDetail(
              {
                uid: { eq: bills.userProfile.parentUid }
              },
              "uid firstName"
            );

            if (parentUserDetails.length > 0) {
              parentName = parentUserDetails[0].firstName;
            }
          }
        }

        return <UploadBillsScreen uid={uid} bills={bills} insurances={insurances} userLoginDetail={userLoginDetail} parentName={parentName} />;
      } else {
        this.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
        if (!!InSideNavigation) {
          this.nav.back();
        } else return <Redirect to="login" />;
      }
    } else {
      if (!!InSideNavigation) {
        this.nav.back();
      } else return <Redirect to="login" />;
    }
  }
  async manageProviderAction({ mode }) {
   
    let providerItems = [];
    if (!!mode) {
      providerItems = [];
    }
    else {
       providerItems = await this.dm.addDoctorsDomain.getDoctorsDetailsList();
    }
    return <ProviderListScreen defaultMode={mode} providerItems={providerItems} />;
  }

  async insuranceListAction({ mode }) {
    let insuranceItems = [];
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};

    if (!!mode) {
      insuranceItems = [];
    } else {
      let userUID = !!userLoginDetail ? userLoginDetail.uid : null;
      insuranceItems = await this.dm.addInsuranceDomain.getInsuranceDetailsList({
        userLoginDetailUid: { eq: userUID },
        isActive: { eq: "true" },
      });
    }

    return <PatientInsuranceScreen userLoginDetail={userLoginDetail} defaultMode={mode} insuranceItems={insuranceItems} />;
  }

  async submittedReceipt({ uid }) {
    if (!!uid) {
      const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
      let bills = await this.dm.uploadBillDomain.fetchUploadedBill(uid);
      if (!!bills) return <SubmittedReceiptScreen uid={uid} bills={bills} userLoginDetails={userLoginDetail} />;
      else this.nav.back();
    } else this.nav.back();
  }

  async manageUsersAction() {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let userUID = !!userLoginDetail ? userLoginDetail.uid : null;
    if (userLoginDetail.userType === USER_TYPE.PCH_PROVIDER) {
      this.dm.userSession.setState({
        pchRegistrationTypeProvider: true,
        pchRegistrationTypePatient: false,
      });
    } else if (userLoginDetail.userType === USER_TYPE.PCH_PATIENT) {
      this.dm.userSession.setState({
        pchRegistrationTypeProvider: false,
        pchRegistrationTypePatient: true,
      });
    }

    let userItems = [];
    userItems = await this.dm.user.getUserDetailsList({
      parentUid: { eq: userUID },
    });

    return <AddUsers parentUID={userUID} userItems={userItems} />;
  }

  async userEditAction({ uid }) {
    const res = await this.dm.userLoginDomain.getLoginDetail({
      uid: { eq: uid },
    });
    let userLoginDetail = res.length > 0 ? res[0] : null;
    const countryList = await this.dm.miscellaneousDomain.getCountries();
    return <PCHProfileViewScreen profile={userLoginDetail} profileActionType={"edit"} countryList={countryList} screen={"addUser"} />;
  }
  /* PCH Actions End */

  /* PCH Actions End */
}
