import React, { Component } from "react";
import R14, { StyleSheet, View, Text, PopUpMenu, Button, Modal, Touchable, AsyncStorage } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { MSG_TYPE } from "../constant/app.data.constant";
import { RETREIVE_BILL } from "../constant/app.constant";

export default R14.connect(
  class BlockMenu extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isVisible: false,
        isDirectroy: false,
        menuItems: [],
      };
    }

    componentDidMount() {
      if (!this.props.directory) {
        this.setState({
          menuItems: [
            // { label: "Block Biller", value: "blockBiller" },
            // { label: "View", value: "View" },
            { label: "Download", value: "Download" },
          ],
        });
      } else {
        if (this.props.status === "blocked") {
          this.setState({ menuItems: [{ label: "Unblock Biller", value: "unblockBiller" }] });
        } else {
          this.setState({ menuItems: [{ label: "Block Biller", value: "blockBiller" }] });
        }
      }
    }

    onSelect = () => {
      this.setState({ isVisible: true });
    };

    onCancel = () => {
      this.setState({
        isVisible: false,
        isDirectroy: false,
      });
    };

    onOkPress = async () => {
      const { compiledMsgList, filterBills } = this.props.app.dm.dashboard.state;
      const { billData } = this.props;
      const { uid, billerId } = billData;
      try {
        let declinedBlockPalyload = await this.props.app.dm.billDetails.declinedNotePayload({
          ...billData,
          note: `Declined and Blocked`,
          messageType: MSG_TYPE.NOTE_TO_BILLER,
        });
        await this.props.app.dm.billDetails.postMessageForPayment(declinedBlockPalyload);
        let payload = this.props.app.dm.billDetails.getDeclinedPayload({ ...billData, messageType: MSG_TYPE.DECLINE_BLOCK, attachments: null });
        let res = await this.props.app.dm.billDetails.postMessageForPayment(payload);
        if (res && res.threadId) {
          let allBillers = JSON.parse(await AsyncStorage.getItem("billerList"));
          let ind = allBillers.findIndex((el) => el.uid === billData.biller.uid);
          allBillers[ind].isBlocked = true;
          await AsyncStorage.setItem("billerList", JSON.stringify(allBillers));
          let currentDate = new Date();
          await this.props.app.dm.queryDomain.updateBiller({ uid: billData.biller.uid, isBlocked: true, blockedDate: currentDate });

          let decPayload = {
            uid: billData.uid,
            billStatus: "Declined",
            bookmark: false,
            fullPaidAndDeclined: new Date().toJSON(),
            rtpMessageType: MSG_TYPE.DECLINE_BLOCK,
            isFailedPayment: false,
            failDate: null,
          };
          await this.props.app.dm.dashboard.updateBill(decPayload);

          if (!!compiledMsgList && !!compiledMsgList.length && !!filterBills.length) {
            let ind = compiledMsgList.findIndex((el) => el.uid === uid);
            compiledMsgList[ind].billStatus = decPayload.billStatus;
            compiledMsgList[ind].fullPaidAndDeclined = decPayload.fullPaidAndDeclined;
            compiledMsgList[ind].rtpMessageType = decPayload.rtpMessageType;

            compiledMsgList.forEach((el, ind, arr) => {
              if (el.billerId === billerId) {
                el.biller.isBlocked = true;
                el.biller.blockedDate = currentDate;
              }
            });
            let findx = filterBills.findIndex((el) => el.uid === uid);
            filterBills[findx].billStatus = decPayload.billStatus;
            filterBills[findx].fullPaidAndDeclined = decPayload.fullPaidAndDeclined;
            filterBills[findx].rtpMessageType = decPayload.rtpMessageType;

            filterBills.forEach((el, ind, arr) => {
              if (el.billerId === billerId) {
                el.biller.isBlocked = true;
                el.biller.blockedDate = currentDate;
              }
            });

            await this.props.app.dm.dashboard.updateBillCount(compiledMsgList, filterBills);
            // this.props.app.dm.dashboard.setState({
            //   compiledMsgList,
            //   filterBills,
            //   newBillCount: compiledMsgList.filter((el) => el.billStatus === "New").length,
            //   outstandingBillCount: compiledMsgList.filter((el) => el.billStatus === "Outstanding").length,
            //   overdueBillCount: compiledMsgList.filter((el) => el.billStatus === "Overdue").length,
            // });
          }
          this.props.app.nav.to("makePaymentRoutes");

          this.props.app.dm.commonDomain.showToastr("success", "You have blocked the Biller and declined the bill");
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Decline Block Unsucessfull.");
        }
      } catch (error) {
        console.log("Declined Error: ", error);
      } finally {
        this.setState({ isVisible: false });
      }
    };

    onDirectoryBlock = async () => {
      const { onSuccess } = this.props;
      const { biller } = this.props.biller;
      const { uid, billerId } = biller;
      try {
        let res = await this.props.app.dm.billerDirectoryDomain.blockUser(billerId, true);
        if (res) {
          this.props.app.dm.commonDomain.showToastr("success", "You have blocked the Biller");
          let allBillers = JSON.parse(await AsyncStorage.getItem("billerList"));
          let ind = allBillers.findIndex((el) => el.uid === uid);
          allBillers[ind].isBlocked = true;
          await AsyncStorage.setItem("billerList", JSON.stringify(allBillers));
          let currentDate = new Date();
          await this.props.app.dm.queryDomain.updateBiller({ uid: uid, isBlocked: true, blockedDate: currentDate });
          const { compiledMsgList, filterBills } = this.props.app.dm.dashboard.state;
          if (!!compiledMsgList && !!compiledMsgList.length && !!filterBills.length) {
            compiledMsgList.forEach((el, ind, arr) => {
              if (el.billerId === billerId) {
                el.biller.isBlocked = true;
                el.biller.blockedDate = currentDate;
              }
            });
            filterBills.forEach((el, ind, arr) => {
              if (el.billerId === billerId) {
                el.biller.isBlocked = true;
                el.biller.blockedDate = currentDate;
              }
            });
            await this.props.app.dm.dashboard.updateBillCount(compiledMsgList, filterBills);

            // await this.props.app.dm.dashboard.setState({
            //   compiledMsgList,
            //   filterBills,
            //   newBillCount: compiledMsgList.filter((el) => el.billStatus === "New").length,
            //   outstandingBillCount: compiledMsgList.filter((el) => el.billStatus === "Outstanding").length,
            //   overdueBillCount: compiledMsgList.filter((el) => el.billStatus === "Overdue").length,
            // });
          }
          this.setState({
            isVisible: false,
            menuItems: [{ label: "Unblock Biller", value: "unblockBiller" }],
          });
          onSuccess({ uid, isBlocked: true });
        }
      } catch (error) {
        console.log("Block Status Error: ", error);
      }
    };

    blockStatus = async () => {
      this.setState({
        isVisible: true,
        isDirectroy: true,
      });
    };

    unblockStatus = async () => {
      const { onSuccess } = this.props;
      const { biller } = this.props.biller;
      const { uid, billerId } = biller;
      try {
        let res = await this.props.app.dm.billerDirectoryDomain.blockUser(billerId, false);
        if (res) {
          this.props.app.dm.commonDomain.showToastr("success", "You have unblocked the Biller");
          await this.props.app.dm.queryDomain.updateBiller({ uid: uid, isBlocked: false });
          let allBillers = JSON.parse(await AsyncStorage.getItem("billerList"));
          let ind = allBillers.findIndex((el) => el.uid === uid);
          allBillers[ind].isBlocked = false;
          await AsyncStorage.setItem("billerList", JSON.stringify(allBillers));
          const { compiledMsgList, filterBills } = this.props.app.dm.dashboard.state;
          if (!!compiledMsgList && !!compiledMsgList.length && !!filterBills.length) {
            compiledMsgList.forEach((el, ind, arr) => {
              if (el.billerId === billerId) {
                el.biller.isBlocked = false;
              }
            });
            filterBills.forEach((el, ind, arr) => {
              if (el.billerId === billerId) {
                el.biller.isBlocked = false;
              }
            });
            await this.props.app.dm.dashboard.updateBillCount(compiledMsgList, filterBills);

            // await this.props.app.dm.dashboard.setState({
            //   compiledMsgList,
            //   filterBills,
            //   newBillCount: compiledMsgList.filter((el) => el.billStatus === "New").length,
            //   outstandingBillCount: compiledMsgList.filter((el) => el.billStatus === "Outstanding").length,
            //   overdueBillCount: compiledMsgList.filter((el) => el.billStatus === "Overdue").length,
            // });
          }
          this.setState({
            isVisible: false,
            menuItems: [{ label: "Block Biller", value: "blockBiller" }],
          });
          onSuccess({ uid, isBlocked: false });
        }
      } catch (error) {
        console.log("Block Status Error: ", error);
      }
    };

    downloadFile = async ({ fileName, threadId }) => {
      try {
        // threadId = "XBP2021000000001-XBP2021000000085-1623067771438214144-pqgWmrIuvVo="
        let base64Format = await this.props.app.dm.rest.get(`${RETREIVE_BILL}?RefID=${threadId}`);
        if (!!base64Format) {
          let url = `data:application/octet-stream;base64,${base64Format}`;
          const downloadLink = document.createElement("a");
          downloadLink.href = url;
          downloadLink.download = fileName;
          downloadLink.click();
        } else this.props.app.dm.commonDomain.showToastr("error", "No Bill docs available");
      } catch (err) {
        this.props.app.dm.commonDomain.showToastr("error", "Something went wrong! Please try again.");
      }
    };

    onMenuChange = async (val) => {
      const { directory } = this.props;
      if (directory) {
        if (val === "blockBiller") {
          this.blockStatus();
        } else {
          this.unblockStatus();
        }
      } else {
        if (val === "blockBiller") {
          this.onSelect();
        } else if (val === "Download") {
          const { billData } = this.props;
          const { threadId, biller } = billData;
          this.downloadFile({
            fileName: `${biller.billerName}_${threadId}.pdf`,
            threadId: threadId,
          });
        } else if (val === "View") {
        }
      }
    };

    renderMenuItem() {
      const { menuItems } = this.state;
      return (
        <PopUpMenu controlIcon="dotsVertical" controlIconSize="small" key="trippleDot" direction="downLeft" style={[styles.green]}>
          {menuItems.map((el, key) => (
            <Touchable key={`__${key}`} style={[styles.menuItemWrapper]} value={el.value} onPress={() => this.onMenuChange(el.value)}>
              <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>{el.label}</Text>
            </Touchable>
          ))}
        </PopUpMenu>
      );
    }

    render() {
      const { isVisible, isDirectroy, popupMenuStyle } = this.state;
      return (
        <>
          <View style={[commonStyles.marLAuto, styles.popupMenu, popupMenuStyle]}>{this.renderMenuItem()}</View>
          <Modal visible={isVisible} animationType="fade" style={styles.modalWrapper}>
            <View style={[styles.modalBodyWrapper]}>
              <View style={[commonStyles.marginTopBottom20, commonStyles.textAlignCenter]}>
                <Text style={[commonFontStyles.workSans, commonStyles.heading5]}>Do you want to block Biller?</Text>
              </View>
              <View style={[styles.modalFooter, commonStyles.marB20]}>
                <Button
                  title={"Cancel"}
                  style={[commonStyles.transperentBtn, commonStyles.marginRight8]}
                  onPress={this.onCancel}
                  titleTextStyle={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.heading6, commonStyles.transperentBtnColor]}
                />

                {isDirectroy ? (
                  <Button
                    title={"Confirm"}
                    style={[commonStyles.blueButton, commonStyles.marginLeft8]}
                    onPress={this.onDirectoryBlock}
                    titleTextStyle={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.heading6, commonStyles.blueButtonColor]}
                  />
                ) : (
                  <Button
                    title={"Confirm"}
                    style={[commonStyles.blueButton, commonStyles.marginLeft8]}
                    onPress={this.onOkPress}
                    titleTextStyle={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.heading6, commonStyles.blueButtonColor]}
                  />
                )}
              </View>
            </View>
          </Modal>
        </>
      );
    }
  }
);

const styles = StyleSheet.create({
  modalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },
  modalBodyWrapper: {
    background: "#fff",
    border: "1px solid #a7a7a7",
    padding: "10px",
    width: 300,
    display: "flex",
    flexDirection: "column",
    borderRadius: 4,
  },
  modalFooter: {
    alignItems: "center",
    justifyContent: "center",
    display: "flex",
    paddingTop: 10,
  },

  popupMenu: {
    // marginTop: -6,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginTop: -9,
          marginBottom: 5,
        };
      }
    },
  },
  menuWrap: {
    padding: 0,
  },
  menuItemWrapper: {
    // borderBottom: "1px solid #ccc",
    padding: "5px 10px",
  },
  radioButtonsClass: {
    marginBottom: 0,
    padding: "5px 10px 5px 10px",
    // borderBottom: "1px solid #ccc",
  },
  green: {
    color: "green",
  },
});
