import React from "react";
import R14, { View, IconButton, StyleSheet, Image, Text, AsyncStorage } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { USER_TYPE } from "../constant/app.data.constant";
const xbpLogo = require("../assets/images/XBPlogo.svg");
const xbpSymbolLogo = require("../assets/images/XBPSymbol.svg");
const userIconLeftPanel = require("../assets/images/userIconLeftPanel.png");

export default R14.connect(
  class HeaderControlsLeft extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        toggle: false,
        initialized: false,
      };
      this.userDetails = null;
    }
    async initialize() {
      this.userDetails = JSON.parse((await AsyncStorage.getItem("userDetails")) || "null") || {};
      this.setState({ initialized: true });
    }
    componentDidMount() {
      this.initialize();
    }
    onToggle = () => {
      // console.log(this.props.app.dm.userSession.state.leftMenuToggle);
      this.props.app.ui.navigator("homeNavigator").toggle();
      this.props.app.dm.userSession.setState({
        leftMenuToggle: !this.props.app.dm.userSession.state.leftMenuToggle,
      });
      // console.log(this.props.app.dm.userSession.state.leftMenuToggle);
    };
    onBack = async () => {
      if (this.props.app.nav.route.name === "billDetailsRoute") {
        this.props.app.nav.to(await AsyncStorage.getItem("homeScreen"));
      } else if (["makePaymentRoutes", "uploadedBillsRoute"].includes(this.props.app.nav.route.name)) {
        this.props.app.nav.to("dashboard");
      } else {
        this.props.app.nav.back();
      }
    };

    render() {
      if (!this.state.initialized) return null;
      let currentRoute = this.props.app.nav.route;
      let userDetail = this.userDetails;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      let userType = !!userDetail && !!userDetail.userType ? userDetail.userType : USER_TYPE.XBP;
      let listRoutes = [
        "dashboard",
        "profileViewRoute",
        "simpleMessageRoute",
        "notificationScreenRoute",
        "billersDirectoryRoute",
        "paymentOptionsRoute",
        "myPreferencesViewRoute",
        "notificationScreenRoute",
        "pchProfileViewRoute",
        "insuranceListRoute",
        "providerListRoute",
        "uploadBillsRoute",
        "addUsersRoute",
        "editUsersRoute",
      ];
      return (
        <View style={styles.headerControlsRight}>
          <View style={[commonStyles.dFlex, styles.flex1]}>
            {this.props.isSideBarOpen ? (
              <View style={[commonStyles.dFlex, styles.userDetail, commonStyles.alignItemsCenter]}>
                <View style={styles.userIcon}>
                  <Image source={userIconLeftPanel} style={styles.userImage} />
                </View>
                <View>
                  <Text style={[commonStyles.dBlock, commonStyles.heading5, commonStyles.fontWeight500, styles.userName]}>
                    {userDetail.firstName} {userDetail.lastName}
                  </Text>
                  {/* <Text style={[commonStyles.dBlock, commonStyles.smallHeading, styles.userId]}>{userDetail.username}</Text> */}
                </View>
              </View>
            ) : (
              <View style={styles.routeName}>
                {1 === 2 && !!isMobileDevice && userType !== USER_TYPE.XBP ? (
                  listRoutes.includes(currentRoute.name) ? null : (
                    <IconButton icon="arrowBack" style={[styles.menuIcon]} onPress={this.onBack} />
                  )
                ) : (
                  <IconButton
                    icon={listRoutes.includes(currentRoute.name) ? "menu" : "arrowBack"}
                    style={[styles.menuIcon]}
                    onPress={listRoutes.includes(currentRoute.name) ? this.onToggle : this.onBack}
                  />
                )}
                <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500, styles.headerTitle, commonStyles.dFlex]}>
                  <View style={commonStyles.webView}>
                    <Image source={xbpLogo} style={styles.logoImg} />
                  </View>
                  <View style={commonStyles.mView}>
                    <Image source={xbpSymbolLogo} style={styles.logoIconImg} />
                  </View>
                  {/* {this.props.app.nav.route.label} */}
                </Text>
              </View>
            )}
          </View>
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  headerControlsRight: {
    flex: 1,
    flexDirection: "row",
    // paddingRight: 6,
    // paddingLeft: 8,
    alignItems: "center",
  },
  menuIcon: {
    ...StyleSheet.padding(12, 12, 12, 12),
    height: 48,
    ...StyleSheet.margin(0, 8, 0, 5),
    screen: ({ width }) => {
      if (width <= 991)
        return {
          ...StyleSheet.padding(12, 8, 12, 12),
          ...StyleSheet.margin(0, 0, 0, 1),
        };
    },
  },
  flex1: {
    flex: 1,
  },
  userDetail: {
    borderBottom: "1px solid #ccc",
    flex: 1,
    flexDirection: "row",
    ...StyleSheet.padding(0, 0, 4, 5),
    boxShadow: "rgba(0, 0, 0, 0.2) 0px 2px 4px 0px",
  },
  userIcon: {
    height: 48,
    minWidth: 48,
    background: "#D1D1D1",
    borderRadius: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    marginRight: 10,
  },
  userImage: {
    height: 24,
    width: 21,
    backgroundSize: "contain",
  },
  userName: {
    color: "#182832",
  },
  userId: {
    color: "#727272",
  },
  routeName: {
    display: "flex",
    alignItems: "center",
  },
  headerTitle: {
    marginLeft: 6,
    screen: ({ width }) => {
      if (width <= 992) {
        return {
          marginLeft: 10,
        };
      }
    },
  },
  backAndHamburgIcon: {
    color: "red",
  },
  logoImg: {
    width: 96,
    height: 39,
    display: "block",
    backgroundSize: "contain",
  },
  logoIconImg: {
    width: 35,
    height: 35,
    display: "block",
    backgroundSize: "contain",
  },
});
