import React, { Component } from "react";
import R14, { AsyncStorage, StyleSheet, View, Text, Touchable, Button } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import ModalPopUp from "./ModalPopUp";
import QrReader from "react-qr-reader";

// const url = window.location.origin + "/make-payment";
// const url = window.location.origin + "/bill-details/XBP2021000000103-XBP2021000000089-1622525125276772864-aifkmykAPv4=";

export default R14.connect(
  class InAppScannerComponent extends Component {
    constructor(props) {
      super(props);
    }

    async componentDidMount() {}

    onScan = async (data) => {
      // const domailurl = window.location.origin;
      const domailurl = null;
      throw new Error("window.location.origin not allowed");
      if (data) {
        if (data.indexOf(domailurl) > -1) {
          if (data.indexOf("bill-details") > -1) {
            data = data.split("/");
            let threadId = data[data.length - 1];
            await this.props.app.dm.dashboard.setCompiledMsgList();
            const { compiledMsgList } = this.props.app.dm.dashboard.state;
            if (!!compiledMsgList.length) {
              let item = compiledMsgList.filter((message) => message.threadId === threadId);
              if (!!item.length) {
                threadId = threadId.replaceAll("#", ">>");
                AsyncStorage.setItem("billDetails", JSON.stringify(item[0]));
                this.onCancel();
                this.props.app.nav.to("billDetailsRoute", { threadId: threadId });
              } else this.onRetry();
            } else this.onRetry();
          } else this.onRetry();
        } else this.onRetry();
      } else this.onRetry();
    };

    onRetry = (message = "Invaild QR Code", isOnError = false) => {
      let retryCount = this.props.app.dm.dashboard.state.retryCount;
      this.props.app.dm.commonDomain.showToastr("error", message);
      this.props.app.dm.dashboard.setState({
        retryCount: retryCount + 1,
      });

      if (!!isOnError) {
        if (retryCount > 3)
          setTimeout(() => {
            this.onCancel();
          }, 5000);
        else
          setTimeout(() => {
            this.onRetry(message, isOnError);
          }, 5000);
      }
    };

    onError = (err) => {
      this.onRetry(err.message, true);
    };

    onCancel = () => {
      this.props.app.dm.dashboard.setState({
        IsScannerVisible: false,
        retryCount: 0,
      });
    };

    render() {
      const { IsScannerVisible, isAlertBox, alertMessage, alertMessageHeading } = this.props.app.dm.dashboard.state;
      return (
        <ModalPopUp
          visibility={IsScannerVisible}
          modalPopupCancelBtn={this.onCancel}
          modalPopupOkBtn={this.onCancel}
          closeButton={this.onCancel}
          bodyWrapper={[styles.qrCodeModal, commonStyles.marTAuto]}
          isFooter={false}
          modalOuterWrapper={styles.modalBackdrop}
          modalContent={styles.popupModalContent}>
          <View style={[styles.sortByHeading, commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.alignItemsCenter]}>
            {/* <Touchable style={styles.closePopup} onPress={this.onCancel}></Touchable> */}
            <View style={commonStyles.marginBottom10}>
              <Text style={[commonStyles.heading5, commonStyles.fontWeight600, commonFontStyles.workSans]}>{alertMessageHeading}</Text>
            </View>
          </View>
          <View>
            {!!(isAlertBox && alertMessage) ? (
              <View style={[commonStyles.textAlignCenter, commonStyles.marginBottom15]}>
                <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>{alertMessage}</Text>
              </View>
            ) : (
              <QrReader delay={5000} onError={this.onError} onScan={this.onScan} style={{ width: "100%" }} />
            )}
          </View>
          <View
            style={[
              styles.modalFooter,
              commonStyles.dFlex,
              commonStyles.justifyContentCenter,
              !!(isAlertBox && alertMessage) ? null : commonStyles.marginTop20,
            ]}>
            {!!(isAlertBox && alertMessage) ? (
              <Button
                title={"Ok"}
                style={[
                  commonStyles.blueBorderButton,
                  commonStyles.marginRight8,
                  commonStyles.blueButton,
                  commonStyles.minWidth110,
                  commonStyles.blueButtonBorder,
                ]}
                onPress={this.onCancel}
                titleTextStyle={[
                  commonFontStyles.workSans,
                  commonStyles.fontWeight500,
                  commonStyles.heading6,
                  commonStyles.transperentBtnColor,
                  commonStyles.whiteColor,
                ]}
              />
            ) : (
              <Button
                title={"Cancel"}
                style={[commonStyles.blueBorderButton, commonStyles.marginRight8, commonStyles.blueBorderButton, commonStyles.minWidth110]}
                onPress={this.onCancel}
                titleTextStyle={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.heading6, commonStyles.transperentBtnColor]}
              />
            )}
          </View>
        </ModalPopUp>
      );
    }
  }
);

const styles = StyleSheet.create({
  closePopup: {
    width: "20%",
    height: 8,
    borderRadius: 25,
    background: "#e1e1e1",
    // marginBottom: 30,
    screen: ({ width }) => {
      if (width >= 991)
        return {
          display: "none",
        };
    },
  },
  qrCodeModal: {
    borderRadius: "30px 30px 0 0",
    width: "100vw",
    marginTop: "auto",
    ...StyleSheet.padding(15, 20, 10, 20),
    screen: ({ width }) => {
      if (width >= 991)
        return {
          margin: "auto",
          borderRadius: 5,
          width: 420,
        };
    },
  },
  sortByHeading: {
    marginLeft: 10,
  },
  modalBackdrop: {
    background: "#00000052",
    overflowX: "auto",
  },
  toggleSwitch: {
    margin: "18px 30px",
  },
  popupModalContent: {
    margin: 0,
  },
  labelStyle: {
    left: 12,
    bottom: "auto",
    // lineHeight: "unset",
  },
  customHeight: {
    minHeight: 100,
  },
  modalFooter: {
    alignItems: "center",
    justifyContent: "center",
    display: "flex",
  },
});
