import React from "react";
import R14, { ScreenLayout, AsyncStorage, View, StyleSheet } from "../core";
import ListContainer from "../components/dashboard/ListContainer";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import ResponsiveTableComponent from "../components/ResponsiveTableComponent";
import InAppScanner from "../components/InAppScanner";
import CopyRight from "../components/CopyRight";
import LeftMenuComponent from "../components/LeftMenuComponent";
export default R14.connect(
  class DashboardScreen extends React.Component {
    constructor(props) {
      super(props);
      AsyncStorage.setItem("homeScreen", this.props.app.nav.route.name);
    }

    componentDidMount() {
      this.timerInstance = setInterval(async () => {
        await this.props.app.dm.dashboard.setCompiledMsgList();
      }, 30000);
    }

    componentWillUnmount() {
      if (this.timerInstance) {
        clearInterval(this.timerInstance);
      }
    }
    render() {
      // added code for responsiveness
      const { compiledMsgList, filterBills, headerSearchList } = this.props.app.dm.dashboard.state;
      const { isSearchOpen } = this.props.app.dm.userSearchDomain.state;

      let withBookmarked = [];
      let isHeader = true;
      if (isHeader) {
        withBookmarked = isSearchOpen
          ? headerSearchList
          : compiledMsgList.filter(
              (el) => el.biller.isBlocked === false && el.bookmark === true && el.billStatus !== "Paid" && el.billStatus !== "Declined"
            );
      } else if (isSearchOpen) {
        withBookmarked = headerSearchList;
      } else {
        withBookmarked = filterBills;
      }
      return (
        <ScreenLayout scrollEnabled>
          {/* <ListContainer isHeader={true} /> */}
          {/* added responsive Table component */}
          <View style={styles.dashboardHolder}>
            <ResponsiveTableComponent isHeader={true} filterBills={withBookmarked} screenName="DashboardScreen" />
          </View>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);
const styles = StyleSheet.create({
  dashboardHolder: {
    padding: "0px",
  },
});
