import { jsPDF } from "jspdf";
export default class GeneratePdf {
    static async mergeImagesinPdf(items) {
         const pdfDoc = new jsPDF("p", "pt", "a4", true);
        //  const pdfDoc = new jsPDF({compress: true});
        if (!!items && items.length > 0) {
            items.map((doc, i) => {                    
                let img = `${!!doc.contentUri ? doc.contentUri : `data:image/${doc.contentType};base64`},${doc.content}`;
                if (i > 0) pdfDoc.addPage();
                pdfDoc.addImage(img, doc.contentType, 50, 150, 485, 270, undefined, "FAST"); //choose between NONE, FAST, MEDIUM and SLOW,
          });
        }
        return pdfDoc;
    }
}
