import React, { Component } from "react";
import R14, { StyleSheet, View, Text, Touchable, Image, Button } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
export default R14.connect(
  class CopyRight extends Component {
    constructor(props) {
      super(props);
    }
    getCurrentDate = () => {
      let date = new Date();
      let year = date.getFullYear();
      return year;
    };
    render() {
      let { customClass } = this.props;
      return (
        <View style={[customClass, styles.copyRightWrapper]}>
          <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.darkGrayColor]}>
            © {new Date().getFullYear()} Exela Technologies, All rights reserved.
          </Text>
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  copyRightWrapper: {
    margin: "auto 0 15px",
    textAlign: "center",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "none",
          margin: "0",
        };
    },
  },
});
