export default class CommonValidators {
  static getFormValidator = () => {
    return {
      validateEmail: (value) => {
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        const isValid = re.test(String(value).toLowerCase());
        return isValid
          ? true
          : {
              error: "Enter valid email ID",
            };
      },
      phoneNoValidator: (value) => {
        let isValid = /^[0-9]{5,20}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 5 - Max 20 digits are allowed",
            };
      },
      max10phoneNoValidator: (value) => {
        let isValid = /^(1\s|1|)?((\(\d{3}\))|\d{3})(\-|\s)?(\d{3})(\-|\s)?(\d{4})$/.test(value);
        return isValid
          ? true
          : {
              error: "Invalid Phone number, Please enter atleast 10 digit",
            };
      },
      cardValidator: (value) => {
        let isValid = /^[0-9-]{19}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "16 Digits numeric Allowed",
            };
      },
      monthValidator: (value) => {
        let isValid = /^[1-9]$|^[1][0-2]$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Only 1 to 12 allowed",
            };
      },
      onlyNumbers: (value) => {
        let isValid = /^[0-9]*$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Only digits are allowed",
            };
      },
      checkSpclChar: async (value) => {
        let isValid = /^[\w\d\s&=_'+.,<>-]{2,60}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 - Max 60 characters with 0-9 @-_+&* are allowed",
            };
      },
      min2Max60: async (value) => {
        let isValid = /^[a-zA-Z0-9\s]{2,60}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 - Max 60 characters are allowed",
            };
      },
      min2Max26: async (value) => {
        let isValid = /^[a-zA-Z0-9\s]{2,26}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 – Max 26 characters with spaces are allowed",
            };
      },
      max10Num: async (value) => {
        let isValid = /^[1-9][0-9]{0,9}$/gi.test(value);
        return isValid ? true : { error: "Min 1 - Max 9999999999 are allowed" };
      },
      max3Num: async (value) => {
        let isValid = /^[1-9][0-9]{0,2}$/gi.test(value);
        return isValid ? true : { error: "Min 1 - Max 999 are allowed" };
      },
      only4Num: async (value) => {
        let isValid = /^[0-9]{4}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Only 4 digits are allowed",
            };
      },
      min2Max300: async (value) => {
        let isValid = /^[\w\d\s&=_'+.,<>-]{2,300}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 - Max 300 characters with &=_'-+,<>. are allowed",
            };
      },
      min2Max15: async (value) => {
        let isValid = /^([a-zA-Z0-9\s]{2,15})$/gi.test(value);
        return isValid ? true : { error: "Min 2 - Max 15 alphanumeric characters are allowed" };
      },
       min0Max15: async (value) => {
        let isValid = /^([a-zA-Z0-9\s]{0,15})$/gi.test(value);
        return isValid ? true : { error: "Max 15 alphanumeric characters are allowed" };
      },
      poBoxNoWihSemiCol: async (value) => {
        let isValid = /^([a-zA-Z0-9\s/;]{0,250})$/gi.test(value);
        return isValid ? true : { error: "Invalid PO Box No, alphanumeric characters character are allowed" };
      },
      max15: async (value) => {
        let isValid = /^[\w\d\s]{0,15}$/gi.test(value);
        return isValid ? true : { error: "Min 2 - Max 15 characters are allowed" };
      },
      min1max100: async (value) => {
        let isValid = /^[0-9]*\.?[0-9]*$/gi.test(value);
        if (isValid && (value <= 0 || value > 100)) {
          isValid = false;
        }
        return isValid ? true : { error: "Max 100 are allowed" };
      },
      max10WithDecimal: async (value) => {
        let isValid = /^\+?(\d*[1-9]\d*\.?|\d*\.\d*[0-9]\d*)$/gi.test(value);
        if (isValid && (value < 0.1 || value.length > 10)) {
          isValid = false;
        }
        return isValid ? true : { error: "Min 1 and Max 10 digits allowed" };
      },
      max140: async (value) => {
        let isValid = /^[\w\d\s&=_'+.,<>-]{0,140}$/gi.test(value);
        return isValid ? true : { error: "Character Limit Exceeds 140 Characters" };
      },
      cmtsVali: async (value) => {
        let isValid = /^.{0,140}$/gm.test(value);
        return isValid ? true : { error: "Character Limit Exceeds 140 Characters" };
      },
      min2Max30: async (value) => {
        let isValid = /^[a-zA-Z0-9\s]{2,30}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 - Max 30 characters are allowed",
            };
      },
      min2Max10: async (value) => {
        let isValid = /^([a-zA-Z0-9\s]{2,10})$/gi.test(value);
        return isValid ? true : { error: "Min 2 - Max 10 character are allowed" };
      },
      minMax10: async (value) => {
        let isValid = /^([a-zA-Z0-9\s]{10})$/gi.test(value);
        return isValid ? true : { error: "Invalid NPI. Only 10 alphanumeric characters are allowed" };
      },
      min8Max15: async (value) => {
        let isValid = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[@$!%*?]).{8,15}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Enter Min 8 - Max 15 characters with atleast 1 uppercase, 1 lowercase, 1 number and @$!%*? are allowed",
            };
      },
      bankAccountValidator: (value) => {
        let isValid = /^[0-9X]{6,17}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 6 - Max 17 digits are allowed",
            };
      },
      min10Max50: async (value) => {
        let isValid = /^[a-zA-Z0-9\s]{10,50}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 10 – Max 50 characters with spaces are allowed",
            };
      },
      min9Max9Digit: async (value) => {
        let isValid = /^[0-9]{9}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Only 9 digits are allowed",
            };
      },
      min10Max10DigitNPI: async (value) => {
        let isValid = /^[0-9]{10}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "NPI must be 10 digits",
            };
      },
      min8Max11: async (value) => {
        let isValid = /^[a-zA-Z0-9\s]{8,11}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 8 - Max 11 alphanumeric characters are allowed",
            };
      },
      min2Max50: async (value) => {
        let isValid = /^[a-zA-Z0-9\s]{2,50}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 - Max 50 alphanumeric characters are allowed only",
            };
      },
      max75: async (value) => {
        let isValid = /^[\w\d\s-!@#$%^&*()_+|~=`{}\[\]:";'<>?,.\\\/]{0,75}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Character Limit Exceeding 75 characters",
            };
      },
      min5max10: async (value) => {
        let isValid = /^[0-9/-]{5,10}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Invalid Zip Code, Enter atleast 5 digits",
            };
      },

      // EIN number validation.
      EINValidation(value) {
        let isValid = /^[a-zA-Z0-9\s]{6,15}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 6 - Max 15 alphanumeric characters are allowed",
            };
      },

      min2Max150: async (value) => {
        let isValid = /^[a-zA-Z0-9\s]{2,150}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 - Max 150 characters are allowed",
            };
      },
      min2Max6: async (value) => {
        let isValid = /^[0-9/-]{2,6}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 2 and Max 6 Digits",
            };
      },
       min5Max10: async (value) => {
        let isValid = /^[0-9/-]{5,10}$/gi.test(value);
        return isValid
          ? true
          : {
              error: "Min 5 and Max 10 Digits",
            };
      },
    };
  };
}
