import axios from "axios";
import R14 from "../core";
const BASE_URL = "https://rtp.exelatech.com";

export default class ApiDomain extends R14.Domain {
  constructor() {
    super();
    this._metadata = { instance: null };
  }

  fetchInstance() {
    if (!this._metadata.instance) {
      const instance = axios.create({
        baseURL: BASE_URL,
        timeout: 1000000,
        headers: {
          "content-type": "application/json",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Expose-Headers": "Access-Token, Uid",
          "Access-Control-Allow-Credentials": true,
          "Access-Control-Allow-Headers": true,
        },
      });

      axios.defaults.headers.common[
        "Authorization"
      ] = `Bearer ${this.dm.userSession.getAccessToken()}`;
      // axios.defaults.headers.common["grant_type"] = `refresh_token`;

      instance.interceptors.request.use(
        (req) => {
          if (axios.defaults.headers.common["Authorization"]) return req;
          throw { message: "Authorization token is not available" };
        },
        (error) => {
          return Promise.reject(error);
        }
      );
      instance.interceptors.response.use(
        (response) => response.data,
        (error) => {
          return Promise.reject(error);
        }
      );
      this._metadata.instance = instance;
    }
    return this._metadata.instance;
  }

  getAccessToken() {
    return `Bearer ${this.dm.userSession.getAccessToken()}`;
  }

  getRefreshToken() {
    return `Bearer ${this.dm.userSession.getRefreshToken()}`;
  }

  async get(url, config = null) {    
    try {
      let instance = this.fetchInstance();
      return await instance({
        method: "GET",
        url,
        headers: { Authorization: this.getAccessToken(), ...config },
      });
    } catch (error) {
      console.log("GET Error: ", error);
      if (error.response.status === 401) {
        return await this.dm.userSession.issueNewToken();
      }
    }
  }
  async post(url, data, config = null) {
    try {
      let instance = this.fetchInstance();
      return await instance({
        method: "POST",
        url,
        data,
        headers: { Authorization: this.getAccessToken(), ...config },
      });
    } catch (error) {
      console.log("GET Error: ", error);
      if (error != null) {
        if (error.response.status === 401) {
          return await this.dm.userSession.issueNewToken();
        }
      }
      return error;
    }
  }

  async put(url, data, config = null) {
    try {
      let instance = this.fetchInstance();
      return await instance({
        method: "PUT",
        url,
        data,
        headers: { Authorization: this.getAccessToken(), ...config },
      });
    } catch (error) {
      console.log("GET Error: ", error);
      if (error.response.status === 401) {
        return await this.dm.userSession.issueNewToken();
      }
    }
  }

  async patch(url, data, config = null) {    
    try {
      let instance = this.fetchInstance();
      return await instance({
        method: "PATCH",
        url,
        data,
        headers: { Authorization: this.getAccessToken(), ...config },
      });
    } catch (error) {      
      console.log("GET Error: ", error);
      if (error.response.status === 401) {
        return await this.dm.userSession.issueNewToken();
      }
    }
  }

  async delete(url, data, config = null) {    
    try {
      let instance = this.fetchInstance();
      return await instance({
        method: "DELETE",
        url,
        data,
        headers: { Authorization: this.getAccessToken(), ...config },
      });
    } catch (error) {    
      console.log("GET Error: ", error); 
      if (error.response.status === 401) {
        return await this.dm.userSession.issueNewToken();
      }
    }
  }
}
