import React from "react";
import R14, { ScreenLayout, FlatList, Form, View, Text, StyleSheet, Touchable, SwitchField } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import NoRecordsFound from "../components/NoRecordsFound";
import BlockMenu from "../components/BlockMenu";
import CommonUtility from "../utils/CommonUtility";
import { BILLER_STATUS, BLOCK_UNBLOCK } from "../constant/app.data.constant";
import ResponsiveTableComponent from "../components/ResponsiveTableComponent";
import LeftMenuComponent from "../components/LeftMenuComponent";

export default R14.connect(
  class BillersDirectoryScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        keyCnt: 0,
      };
      this.timerInstance = null;
    }
    pamMessageRoute = () => {
      this.props.app.nav.to("pamMessageRoute");
    };

    seeMessages = async () => {
      this.props.app.nav.to("pamMessageRoute");
    };
    navigateToDetails = async (item) => {
      this.props.app.nav.to("pamMessageRoute", { pamId: item.pamId });
    };

    componentDidMount() {
      this.timerInstance = setInterval(async () => {
        const pam = await this.props.app.dm.billerDirectoryDomain.displayBillerDirectoryData();
        // this.props.app.dm.billerDirectoryDomain.setState({ pamList: pam });
      }, 30000);
    }

    componentWillUnmount() {
      if (this.timerInstance) {
        clearInterval(this.timerInstance);
      }
    }

    onSuccessCall = (data, ind) => {
      const { pamList } = this.props.app.dm.billerDirectoryDomain.state;
      pamList[ind].biller.isBlocked = data.isBlocked;
      this.props.app.dm.billerDirectoryDomain.setState({ pamList });
    };

    render() {
      const { keyCnt } = this.state;
      const { pamList, searchPamList } = this.props.app.dm.billerDirectoryDomain.state;
      const { isSearchOpen } = this.props.app.dm.userSearchDomain.state;
      let displayPamList = [];
      if (isSearchOpen) displayPamList = searchPamList;
      else displayPamList = pamList ? pamList : [];

      const { notificationThreadIds } = this.props.app.dm.notificationDomain.state;
      let messages = displayPamList.map((data, i) => {
        return data;
      });
      if (notificationThreadIds.length > 0) {
        let d = notificationThreadIds.map((data, i) => {
          return data;
        });
        messages = messages.filter((message) => d.includes(message.pamId));
      }

      return (
        <ScreenLayout scrollEnabled style={[styles.billerDirectory]}>
          {/* <View style={[commonStyles.marginTopBottom15, commonStyles.paddingLeftRight16, commonStyles.pageTitle]}>
            <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500]}>
              {this.props.app.nav.route.label}{" "}
            </Text>
          </View> */}
          <ResponsiveTableComponent dashboardScrollView={styles.dashboardScrollView} displayPamList={messages} screenName="BillerDirectoryScreen" />
          <LeftMenuComponent />
          {/* <FlatList
            ListEmptyComponent={<NoRecordsFound message={"No billers found."} />}
            data={displayPamList}
            keyExtractor={(el, i) => i}
            renderItem={({ item, index }) => {
              return (
                <View
                  style={[
                    commonStyles.dFlex,
                    styles.margin0,
                    commonStyles.borderBottomPaddingBottom10,
                    styles.directoryList,
                    commonStyles.justifyContentSpaceBetween,
                  ]}>
                  <View style={[commonStyles.dFlex]}>
                    <View style={[styles.profileIconHolder]}>
                      <View style={[styles.profileIcon]}>
                        <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading5, commonStyles.userInitial]}>
                          {CommonUtility.shortName(item.biller.billerName)}
                        </Text>
                      </View>
                      <View>
                        {item.totalMessages - item.readMessages > 0 ? (
                          <View style={[styles.messageNotification]}>
                            <Text style={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.exSmallHeading, commonStyles.whiteColor]}>
                              {item.totalMessages - item.readMessages}
                            </Text>
                          </View>
                        ) : null}
                      </View>
                    </View>

                    <Touchable onPress={() => this.navigateToDetails(item)}>
                      <View>
                        <Text
                          style={[
                            commonFontStyles.workSans,
                            commonStyles.fontWeight500,
                            commonStyles.heading5,
                            commonStyles.transperentBtnColor,
                            commonStyles.wordBreakWord,
                          ]}>
                          {item.biller.billerName}
                        </Text>
                      </View>
                      <View>
                        <Text
                          style={[
                            commonFontStyles.workSans,
                            commonStyles.fontWeight500,
                            commonStyles.smallHeading,
                            styles.blocked,
                            styles[item.biller.isBlocked ? "blocked" : `${item.biller.billerStatus}`],
                          ]}>
                          {item.biller.isBlocked ? "Blocked" : BILLER_STATUS[item.biller.billerStatus]}
                        </Text>
                      </View>
                    </Touchable>
                  </View>
                  {BLOCK_UNBLOCK[item.biller.billerStatus] || item.biller.isBlocked ? (
                    <View style={[commonStyles.marLAuto, styles.toggleSwitch, commonStyles.dFlex, commonStyles.flexDirectionColumn]} key={keyCnt}>
                      <View style={styles.blockMenu}>
                        <BlockMenu
                          status={item.biller.isBlocked ? "blocked" : "unblocked"}
                          directory={true}
                          biller={item}
                          onSuccess={(val) => this.onSuccessCall(val, index)}
                        />
                      </View>
                    </View>
                  ) : null}
                </View>
              );
            }}
          /> */}
        </ScreenLayout>
      );
    }
  }
);
const styles = StyleSheet.create({
  directoryList: {
    padding: "10px 15px",
  },
  profileIconHolder: {
    position: "relative",
    marginRight: 15,
  },
  profileIcon: {
    width: 40,
    height: 40,
    background: "#e0e0e0",
    borderRadius: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  messageNotification: {
    width: 18,
    height: 18,
    background: "#C63F9B",
    borderRadius: "100%",
    position: "absolute",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    right: 0,
    top: -6,
  },
  active: {
    color: "#218666",
  },
  requested: {
    color: "#800080",
  },
  new: {
    color: "#800080",
  },
  inprogress: {
    color: "#EE781E",
  },
  accepted: {
    color: "#218666",
  },
  denied: {
    color: "#B40220",
  },
  blocked: {
    color: "#B40220",
  },
  toggleSwitch: {
    // margin: "18px 30px",
  },
  blockMenu: {
    // margin: "8px 0 -8px 0",
  },
  margin0: {
    margin: 0,
  },
  billerDirectory: {
    // background: "#fff",s
  },
  dashboardScrollView: {
    inset: "47px 0px 0px",
  },
});
