import React from "react";
import R14, { StyleSheet, Colors, ScreenLayout, Image, Text, View, Touchable, Button, ScrollView } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import ChatInputField from "../components/ChatInputField";
import MessageComponent from "../components/MessageComponent";
import BottomPaySection from "../components/BottomPaySection";
import { BILL_STATUS, RTP_STATUS } from "../constant/app.data.constant";
import BillerDetails from "../components/BillerDetails";
import DeclineMessageComponent from "../components/DeclineMessageComponent";
import ModalPopUp from "../components/ModalPopUp";
import CopyRight from "../components/CopyRight";
import LeftMenuComponent from "../components/LeftMenuComponent";

const crateMessageIcon = require("../assets/images/crate-message.png");
const historyIcon = require("../assets/images/historyIcon.png");
const midumChatBlackIcon = require("../assets/images/midumChatBlackIcon.png");

export default R14.connect(
  class BillDetailsScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        billDetails: this.props.billDetails,
        height: 0,
        messages: this.props.messages.messages.filter((element) => !element.envelope.errorBody),
        msgCount: 0,
        isChatAllow: false,
      };
      this.isBook = null;
    }
    fullMessageWindow = () => {
      let newThread = this.props.billDetails.threadId.replaceAll("#", ">>");
      this.props.app.nav.to("messageScreenRoute", { threadId: newThread });
    };

    async componentDidMount() {
      const { billDetails } = this.props;
      this.timerInstance = setInterval(async () => {
        const billRes = await this.props.app.dm.dashboard.getBillerFromThread(billDetails.threadId);
        if (!!billRes) {
          let bd = this.props.app.dm.dashboard.manageBillDetails(billRes.messages);
          if (!!bd) {
            let saveDBPayload = await this.props.app.dm.dashboard.payloadForGraphQl(bd);
            delete saveDBPayload.payedAt;
            await this.props.app.dm.dashboard.updateBill({ ...saveDBPayload, uid: billDetails.uid });
            const res = await this.props.app.dm.dashboard.onloadupdateBillDetails(
              await this.props.app.dm.dashboard.getBills({ filter: { threadId: { eq: billDetails.threadId } } })
            );
            let complileBill = { ...bd, ...res[0] };

            const { compiledMsgList, filterBills } = this.props.app.dm.dashboard.state;
            if (!!compiledMsgList.length && !!filterBills.length) {
              let ind = compiledMsgList.findIndex((el) => el.threadId === complileBill.threadId);
              compiledMsgList[ind].billStatus = complileBill.billStatus;
              compiledMsgList[ind].paymentStatus = complileBill.paymentStatus;
              compiledMsgList[ind].dueDate = complileBill.dueDate;
              compiledMsgList[ind].amountDue =
                complileBill.billStatus === "Paid"
                  ? parseFloat(complileBill.billAmount)
                  : parseFloat(complileBill.billAmount) - parseFloat(complileBill.paidAmount);
              compiledMsgList[ind].rtpMessageType = complileBill.rtpMessageType;
              compiledMsgList[ind].fullPaidAndDeclined = complileBill.fullPaidAndDeclined;

              let fndx = filterBills.findIndex((el) => el.threadId === complileBill.threadId);
              filterBills[fndx].billStatus = complileBill.billStatus;
              filterBills[fndx].paymentStatus = complileBill.paymentStatus;
              filterBills[fndx].dueDate = complileBill.dueDate;
              filterBills[fndx].amountDue =
                complileBill.billStatus === "Paid"
                  ? parseFloat(complileBill.billAmount)
                  : parseFloat(complileBill.billAmount) - parseFloat(complileBill.paidAmount);
              filterBills[fndx].rtpMessageType = complileBill.rtpMessageType;
              filterBills[fndx].fullPaidAndDeclined = complileBill.fullPaidAndDeclined;

              await this.props.app.dm.dashboard.updateBillCount(compiledMsgList, filterBills);

              // this.props.app.dm.dashboard.setState({
              //   compiledMsgList,
              //   filterBills,
              //   newBillCount: compiledMsgList.filter((el) => el.billStatus === "New").length,
              //   outstandingBillCount: compiledMsgList.filter((el) => el.billStatus === "Outstanding").length,
              //   overdueBillCount: compiledMsgList.filter((el) => el.billStatus === "Overdue").length,
              // });
            }

            this.setState({
              billDetails: complileBill,
              messages: billRes.messages.filter((element) => !element.envelope.errorBody),
              msgCount: ++this.state.msgCount,
            });
          }
        }
      }, 3000);
    }

    componentWillUnmount() {
      if (this.timerInstance) {
        clearInterval(this.timerInstance);
      }
      // this.checkDevice();
    }

    checkDevice() {
      var ua = navigator.userAgent;
      var isMobile = /Android|webOS|iPhone|iPad|iPod/i.test(ua);
      if (isMobile) {
        this.setState({
          height: 200,
        });
      } else {
        this.setState({
          height: window.innerHeight - 500,
        });
      }
    }

    toggelBookmark = async (bkmrk) => {
      const { billDetails } = this.state;
      this.setState({ billDetails: { ...billDetails, bookmark: bkmrk } });
      const { compiledMsgList, filterBills } = this.props.app.dm.dashboard.state;
      if (!!compiledMsgList.length && !!filterBills.length) {
        let ind = compiledMsgList.findIndex((el) => el.uid === billDetails.uid);
        compiledMsgList[ind].bookmark = bkmrk;
        let findx = filterBills.findIndex((el) => el.uid === billDetails.uid);
        filterBills[findx].bookmark = bkmrk;
        await this.props.app.dm.dashboard.setState({ compiledMsgList, filterBills });
      }
    };

    onSent = (msgData) => {
      this.setState({ messages: this.state.messages.concat(msgData), msgCount: ++this.state.msgCount });
    };
    openPayOpt = () => {
      this.setState({
        isPayOptVisible: !this.state.isPayOptVisible,
      });
      console.log(this.state.isPayOptVisible);
    };

    render() {
      const { pid } = this.props.userDetails;
      const { messages, isChatAllow } = this.state;
      const messageType = messages[0].envelope.messageHeader.senderPid !== pid ? "NotetoBiller" : "NotetoPayer";
      const lastTwoMessages = messages.slice(Math.max(messages.length - 2, 0));
      let newThread = this.props.billDetails.threadId.replaceAll("#", ">>");
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      return (
        <>
          <ScreenLayout scrollEnabled>
            <ScrollView
              style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu, styles.billDetailsScreen]}>
              <View style={commonStyles.marginLeftRight16}>
                <View style={[styles.titleBreadcrumbs, commonStyles.marginTopBottom15, styles.webView]}>
                  <View>
                    <View style={[styles.title]}>
                      <Text style={[commonStyles.heading3, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.darkGrayColor]}>
                        {this.props.app.nav.route.label}{" "}
                      </Text>
                    </View>
                    {/* <View style={[styles.breadcrumbs]}>
                  <Text style={[commonStyles.exSmallHeading, commonFontStyles.workSans, commonStyles.darkGrayColor]}>
                    Bills / All Bills / Bill Details
                  </Text>
                </View> */}
                  </View>
                  {/* <View style={[commonStyles.marLAuto, commonStyles.dFlex]}>
                <Touchable>
                  <Image source={crateMessageIcon} style={styles.filterIcon} />
                </Touchable>
                <Touchable>
                  <Image source={historyIcon} style={[commonStyles.icon24, commonStyles.marginLeft15]} />
                </Touchable>
              </View> */}
                </View>
                <View style={[commonStyles.dFlex, styles.billerDetailsWrap]}>
                  <View
                    style={[
                      styles.container,
                      commonStyles.flex1,
                      commonStyles.flexBasis50,
                      commonStyles.dFlex,
                      commonStyles.flexDirectionColumn,
                      commonStyles.padding0,
                    ]}>
                    <BillerDetails
                      padding1={styles.padding1}
                      padding2={styles.padding2}
                      padding3={styles.padding3}
                      userIconDiv={styles.userIconDiv}
                      blockMenu={styles.blockMenu}
                      billData={this.state.billDetails}
                      onBkmkToggle={this.toggelBookmark}
                    />
                    {isMobileDevice ? (
                      <>
                        {!this.state.billDetails.biller.isBlocked ? (
                          <View style={[styles.mView]}>
                            {!RTP_STATUS[this.state.billDetails.rtpStatus] && !BILL_STATUS[this.state.billDetails.billStatus] ? (
                              <View style={styles.bottomPaySection}>
                                <BottomPaySection threadId={newThread} extraOpt={this.state.billDetails.rtpMessageType} />
                              </View>
                            ) : null}
                          </View>
                        ) : null}
                        <View style={[styles.mView]}>
                          <View style={[styles.twoCol, styles.messageHeader]}>
                            <View>
                              <Text
                                style={[
                                  styles.cardSubHeadingText,
                                  commonStyles.heading6,
                                  commonStyles.fontWeight600,
                                  commonFontStyles.workSans,
                                  commonStyles.darkGrayColor,
                                ]}>
                                Messages
                              </Text>
                            </View>

                            <View>
                              <Touchable onPress={this.fullMessageWindow}>
                                <Image source={midumChatBlackIcon} style={styles.meadiumChatICon} />
                              </Touchable>
                            </View>
                          </View>
                          <View style={[commonStyles.paddingLeftRight16]}>
                            <MessageComponent displayMessages={lastTwoMessages} height={this.state.height - 70} key={this.state.msgCount} />
                          </View>
                          {!this.state.billDetails.biller.isBlocked ? (
                            <View style={[commonStyles.marTAuto]}>
                              <View style={[styles.chatInputField]}>
                                <ChatInputField
                                  messageType={messageType}
                                  billData={this.state.billDetails}
                                  msgList={messages}
                                  onAfterSent={this.onSent}
                                  notAllowedChat={!isChatAllow}
                                />
                              </View>
                            </View>
                          ) : null}
                        </View>
                      </>
                    ) : null}

                    {!this.state.billDetails.biller.isBlocked && !isMobileDevice ? (
                      <View style={[commonStyles.marTAuto, styles.webView, commonStyles.flexDirectionColumn]}>
                        {!RTP_STATUS[this.state.billDetails.rtpStatus] && !BILL_STATUS[this.state.billDetails.billStatus] ? (
                          <View style={[styles.bottomPaySection, commonStyles.webView]}>
                            <BottomPaySection threadId={newThread} extraOpt={this.state.billDetails.rtpMessageType} />
                          </View>
                        ) : null}
                      </View>
                    ) : null}
                  </View>
                  {!isMobileDevice ? (
                    <View
                      style={[
                        commonStyles.flex1,
                        commonStyles.flexBasis50,
                        commonStyles.dFlex,
                        commonStyles.flexDirectionColumn,
                        commonStyles.whiteBox,
                        commonStyles.padding0,
                        styles.messageSection,
                        styles.webView,
                      ]}>
                      <View style={[styles.twoCol, styles.messageHeader]}>
                        <View>
                          <Text
                            style={[
                              styles.cardSubHeadingText,
                              commonStyles.heading3,
                              commonStyles.fontWeight600,
                              commonFontStyles.workSans,
                              commonStyles.darkGrayColor,
                            ]}>
                            Messages
                          </Text>
                        </View>

                        {/* <View>
                    <Touchable onPress={this.fullMessageWindow}>
                      <Image source={midumChatBlackIcon} style={styles.meadiumChatICon} />
                    </Touchable>
                  </View> */}
                      </View>
                      <View>
                        <View style={[styles.BorderTop, styles.messageOuter]}>
                          <MessageComponent displayMessages={messages} height={this.state.height - 70} key={this.state.msgCount} />
                          {/* displayMessages={lastTwoMessages} */}
                          {/* {height={this.state.height}} */}
                        </View>
                      </View>
                      {!this.state.billDetails.biller.isBlocked ? (
                        <View style={[commonStyles.marTAuto]}>
                          <View style={[styles.chatInputField]}>
                            <ChatInputField
                              messageType={messageType}
                              billData={this.state.billDetails}
                              msgList={messages}
                              onAfterSent={this.onSent}
                              notAllowedChat={!isChatAllow}
                            />
                          </View>
                        </View>
                      ) : null}
                    </View>
                  ) : null}
                </View>
                <ModalPopUp
                  // visibility={isApplePayModalVisible}
                  isFooter={false}
                  bodyWrapper={[styles.bodyWrapper]}
                  modalOuterWrapper={styles.modalOuterWrapper}
                  heading={"Account Locked !"}
                  handleCancel={this.onClose}
                  closeButton={styles.closeBtn}
                  modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
                  modalContent={commonStyles.marginTop15}>
                  <View style={commonStyles.textAlignCenter}>
                    <View style={commonStyles.marginBottom20}>
                      <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>{this.state.alertMessage}</Text>
                    </View>
                    <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                      <Button
                        title={"OK"}
                        style={[commonStyles.blueButton, styles.sessionModelOkBtn]}
                        onPress={this.onClose}
                        titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight500, commonFontStyles.workSans, commonStyles.heading5]}
                      />
                    </View>
                  </View>
                </ModalPopUp>
                {isMobileDevice ? null : (
                  <View style={commonStyles.copyRightSection}>
                    <CopyRight />
                  </View>
                )}
              </View>
            </ScrollView>
            <LeftMenuComponent />
          </ScreenLayout>
        </>
      );
    }
  }
);

const styles = StyleSheet.create({
  twoCol: {
    display: "flex",
    justifyContent: "space-between",
  },
  billDetailsScreen: {
    flex: 1,
    height: "100%",
    alignItems: "flex-start",
    // background: "#fff",
    // padding: "0px 15px 0px 15px",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          alignItems: "center",
          margin: 0,
          padding: 0,
        };
    },
  },

  BorderTop: {
    borderTop: "1px solid #D6D6D6",
  },
  meadiumChatICon: {
    width: 30,
    height: 27,
    display: "inline-block",
    backgroundSize: "contain",
  },

  bottomSectionOuter: {
    position: "absolute",
    bottom: 0,
    width: "100%",
    background: "#fff",
    padding: "15px 15px 0",
  },
  chatInputField: {
    border: 0,
    marginBottom: 16,
    padding: "0 16px 0 16px",
    screen: ({ width }) => {
      if (width <= 375)
        return {
          marginBottom: 10,
        };
    },
  },
  bottomPaySection: {
    border: 0,
    // margin: "0 -15px",
  },
  messageOuter: {
    padding: "7px 16px 10px",
    screen: ({ width }) => {
      if (width <= 320)
        return {
          // height: 100,
          // overflow: "hidden auto",
        };
    },
  },
  titleBreadcrumbs: {
    margin: "0 0 0",
    alignItems: "center",
    display: "flex",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "none",
        };
    },
  },
  container: {
    padding: 10,
    // maxWidth: "47%",
    boxShadow: "0px 0px 6px #00000033",
    background: "#fff",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          maxWidth: "100%",
          boxShadow: "none",
        };
    },
  },
  billerDetailsWrap: {
    flexGrow: 1,
    display: "flex",
    marginBottom: 15,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          flexWrap: "wrap",
          paddingBottom: 15,
          marginBottom: 0,
          background: "#fff",
        };
    },
  },
  messageSection: {
    margin: "0 0 0 15px",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          margin: "15px 0 0 0",
        };
    },
  },
  filterIcon: {
    height: 24,
    width: 24,
    display: "block",
    backgroundSize: "contain",
  },
  padding1: {
    padding: "8px 16px 0",
  },
  padding2: {
    padding: "0 16px",
    display: "flex",
  },
  padding3: {
    padding: "10px 16px",
  },
  messageHeader: {
    margin: "0 16px 0",
    borderTop: "1px solid #DADADA",
    borderBottom: "1px solid #DADADA",
    alignItems: "center",
    screen: ({ width }) => {
      if (width >= 991)
        return {
          padding: "8px 0px",
          border: 0,
        };
    },
  },
  userIconDiv: {
    display: "none",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "flex",
        };
    },
  },
  blockMenu: {
    display: "none",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "flex",
        };
    },
  },
  webView: {
    display: "none",
    screen: ({ width }) => {
      if (width >= 991)
        return {
          display: "flex",
        };
    },
  },
  mView: {
    display: "none",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "flex",
          flexDirection: "column",
        };
    },
  },
  blueButton: {
    display: "flex",
    flexGrow: 1,
    alignItems: "center",
    justifyContent: "center",
    background: "#4dc5f2",
    minHeight: 34,
    borderRadius: 4,
    marginRight: 13,
    marginLeft: 13,
  },
  displayNoneInMView: {
    display: "none",
  },
  dBlockInMView: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "block",
        };
    },
  },
});
