import React from "react";
import R14, { View, StyleSheet, Button, Modal, IconButton, SectionHeader } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";

export default class ModalPopUp extends React.Component {
  onCrossPress = () => {
    const { handleCancel } = this.props;
    if (handleCancel && typeof handleCancel === "function") {
      handleCancel();
    }
  };

  onClose = () => {
    const { handleClose } = this.props;
    if (handleClose && typeof handleClose === "function") {
      handleClose();
    }
  };

  onOkPress = () => {
    const { handleOk } = this.props;
    if (handleOk && typeof handleOk === "function") {
      handleOk();
    }
  };

  // static getDerivedStateFromProps(nextProps, prevState) {
  //   if (prevState.visibility !== nextProps.visibility) {
  //     return { visibility: nextProps.visibility };
  //   }

  //   return null;
  // }

  render() {
    const {
      visibility,
      heading,
      headerStyle,
      bodyWrapper,
      contentInner,
      isFooter,
      okLabel,
      closeLabel,
      handleClose,
      footerClass,
      modalPopupOkBtn,
      modalPopupCancelBtn,
      closeButton,
      handleOk,
      modalOuterWrapper,
      modalContent,
      modalHeading,
      onBackdropPress,
      cancelBtnTextStyle,
      okBtnTextStyle,
    } = this.props;
    return (
      <Modal animationType="fade" transparent={false} visible={visibility} style={[styles.modalWrapper, modalOuterWrapper]}>
        <View style={[styles.modalBodyWrapper, bodyWrapper]}>
          {heading ? (
            <View style={[styles.modalHeader, commonStyles.dFlex, commonStyles.alignItemsCenter, headerStyle]}>
              <SectionHeader
                style={[styles.heading, commonFontStyles.workSans, commonStyles.heading5, commonStyles.fontWeight500, modalHeading]}
                level={2}>
                {heading}
              </SectionHeader>

              <IconButton icon="close" size="small" style={[styles.crossIcon, closeButton]} onPress={this.onCrossPress} />
            </View>
          ) : null}
          <View style={[styles.modalContent, modalContent]}>
            <View style={[styles.innerContent, contentInner]}>{this.props.children}</View>
          </View>
          {!isFooter ? (
            <View style={[styles.modalFooter, footerClass]}>
              {handleClose ? (
                <Button
                  title={closeLabel || "Close"}
                  style={[commonStyles.transperentBtn, modalPopupCancelBtn]}
                  onPress={this.onClose}
                  titleTextStyle={[
                    commonFontStyles.workSans,
                    commonStyles.fontWeight500,
                    commonStyles.heading6,
                    commonStyles.transperentBtnColor,
                    cancelBtnTextStyle,
                  ]}
                />
              ) : null}
              {handleOk ? (
                <Button
                  title={okLabel || "Ok"}
                  style={[commonStyles.blueButton, modalPopupOkBtn]}
                  onPress={this.onOkPress}
                  titleTextStyle={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.heading6, okBtnTextStyle]}
                />
              ) : null}
            </View>
          ) : null}
        </View>
      </Modal>
    );
  }
}

const styles = StyleSheet.create({
  modalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },
  modalBodyWrapper: {
    background: "#fff",
    border: "1px solid #a7a7a7",
    padding: "10px",
    width: 400,
    display: "flex",
    flexDirection: "column",
  },
  crossIcon: {
    height: 38,
  },
  modalHeader: {
    flex: 1,
    flexDirection: "row",
    alignItems: "flex-start",
  },
  heading: {
    flex: 1,
    padding: 0,
  },
  modalContent: {
    marginTop: 20,
    alignItems: "flex-start",
    flexGrow: 1,
  },
  innerContent: {
    height: "auto",
  },
  modalFooter: {
    alignItems: "flex-end",
    justifyContent: "flex-end",
    display: "flex",
    paddingTop: 10,
  },
  spaceBtw: {
    marginRight: 10,
  },
  onButton: {
    backgroundColor: "rgb(76, 197, 242)",
  },
});
