import React, { PureComponent } from "react";
import R14, { StyleSheet, View, Text, Image } from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import DateUtils from "../../utils/DateUtils";
import DownloadComponent from "./DownloadComponent";
import CommonUtility from "../../utils/CommonUtility";

export default R14.connect(
  class SenderComponent extends React.Component {
    render() {
      const { leaf } = this.props;
      //Getting the values for outgoing message details.
      const { additionalUserInfo } = this.props.userDetails;
      let userShortName = additionalUserInfo.firstName.charAt(0) + additionalUserInfo.lastName.charAt(0);
      return (
        <View style={[styles.chatBoxWidth, commonStyles.dFlex, commonStyles.flexDirectionColumn]}>
          <View style={[commonStyles.dInlineFlex, commonStyles.flexDirectionColumn, commonStyles.marLAuto]}>
            <View style={[commonStyles.minWidth150]}>
              <View style={[commonStyles.dFlex]}>
                <View style={[styles.userInitialOuter, styles.payerInital]}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fileSize, commonStyles.fontWeight500]}>
                    {CommonUtility.shortName(userShortName)}
                  </Text>
                </View>
                <View>
                  <Text style={[commonStyles.exSmallHeading, commonStyles.transperentBtnColor, commonStyles.guestName, commonFontStyles.workSans]}>
                    {additionalUserInfo.firstName + " " + additionalUserInfo.lastName}
                  </Text>
                </View>
              </View>
            </View>
            <View style={[commonStyles.guestChat, commonStyles.alignItemsEnd]}>
              <View style={[commonStyles.minWidth150, commonStyles.hostText]}>
                <View style={styles.responceMsg}>
                  <Text style={[commonStyles.smallHeading, commonFontStyles.workSans]}>{leaf.envelope.messageBody.messageBody}</Text>
                </View>
                {!!leaf.attachments && !!leaf.attachments.length ? <DownloadComponent item={leaf} /> : null}
                <Text style={[commonStyles.hostTextBorder]}></Text>
              </View>
              <View style={commonStyles.dFlex}>
                <Text
                  style={[
                    commonStyles.exSmallHeading,
                    commonStyles.chatTiming,
                    commonStyles.transperentBtnColor,
                    commonStyles.marLAuto,
                    commonFontStyles.workSans,
                  ]}>
                  {DateUtils.dateTodayYesterday(leaf.envelope.messageHeader.messageDateTime)}
                </Text>
              </View>
            </View>
          </View>
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  chatBoxWidth: {
    marginBottom: 15,
    maxWidth: "85%",
    marginLeft: "auto",
    paddingRight: 12,
    marginRight: -16,
  },
  responceMsg: {
    marginBottom: 3,
  },
  userInitialOuter: {
    height: 20,
    width: 20,
    borderRadius: 100,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  payerInital: {
    background: "#cff2fe",
  },
});
