import React from "react";
import R14, { View, Text, StyleSheet, Touchable, Image, ControlsView, AsyncStorage } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import DateUtils from "../utils/DateUtils";

const acceptIcon = require("../assets/images/accept_icon.png");
const declineIcon = require("../assets/images/decline-icon.png");

export default R14.connect(
  class PamMessasgeBottomSection extends React.Component {
    constructor(props) {
      super(props);
    }

    accept = async () => {
      const { pamId, pam } = this.props;
      let message = "accepted";
      try {
        let date = new Date();
        let newdate = DateUtils.formatDate(date); // `${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}`;
        let preMessage = `Status: Accepted on ${newdate}`;
        /**
         * Due to system does not allowed to send message before accepted the status, after status message send is not allowed in repository for biller
         */
        let sendMessage = await this.props.app.dm.billerDirectoryDomain.createPamMesagePayload(preMessage, pam.pamId, pam.payerId);
        if (sendMessage) {
          const { pamList } = this.props.app.dm.billerDirectoryDomain.state;
          await this.props.app.dm.billerDirectoryDomain.updatePamMessageStatus(message, pamId, pam);
          const billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
          let indx = billerList.findIndex((itm) => itm.billerId === pam.billerId && itm.payerId === pam.payerId);
          billerList[indx].billerStatus = message;
          await AsyncStorage.setItem("billerList", JSON.stringify(billerList));
          let billerRes = await this.props.app.dm.queryDomain.addUpdateBiller({ ...pam, status: message, biller: billerList[indx] });
          if (!!pamList && !!pamList.length) {
            let ind = pamList.findIndex((itm) => itm.uid === pam.uid);
            pamList[ind].biller.billerStatus = message;
            this.props.app.dm.billerDirectoryDomain.setState({ pamList });
          }
          this.props.app.dm.commonDomain.showToastr("success", "Request Accepted");
          // this is required due, navigtion only call for request not for all due that localstorage override the with old values.
          setTimeout(() => {
            this.props.app.nav.to("billersDirectoryRoute");
          }, 900);
        }
      } catch (err) {
        console.log("error", err);
        return new Error(err);
      }
    };

    decline = async () => {
      const { pamId, pam } = this.props;
      let message = "denied";
      try {
        let date = new Date();
        let newdate = DateUtils.formatDate(date); //`${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}`;
        let preMessage = `Status: Rejected on ${newdate}`;
        /**
         * Due to system does not allowd to send message before denied the status, after status message send is not allowed in repository for biller
         */
        let sendMsg = await this.props.app.dm.billerDirectoryDomain.createPamMesagePayload(preMessage, pam.pamId, pam.payerId);
        if (sendMsg) {
          const { pamList } = this.props.app.dm.billerDirectoryDomain.state;
          await this.props.app.dm.billerDirectoryDomain.updatePamMessageStatus(message, pamId, pam);
          const billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
          let indx = billerList.findIndex((itm) => itm.billerId === pam.billerId && itm.payerId === pam.payerId);
          billerList[indx].billerStatus = message;
          await AsyncStorage.setItem("billerList", JSON.stringify(billerList));
          await this.props.app.dm.queryDomain.addUpdateBiller({ ...pam, status: message, biller: billerList[indx] });
          if (!!pamList && !!pamList.length) {
            let ind = pamList.findIndex((itm) => itm.uid === pam.uid);
            pamList[ind].biller.billerStatus = message;
            this.props.app.dm.billerDirectoryDomain.setState({ pamList });
          }
          this.props.app.dm.commonDomain.showToastr("error", "Request Declined");
          // this is required due, navigtion only call for request not for all due that localstorage override the with old values.
          setTimeout(() => {
            this.props.app.nav.to("billersDirectoryRoute");
          }, 900);
        }
      } catch (err) {
        console.log("error", err);
        return new Error(err);
      }
    };

    render() {
      return (
        <ControlsView
          type="buttons"
          style={[styles.bottomControlsView]}
          children={[
            <View key="controlViewBottom" style={[styles.bottomPaySection, commonStyles.dFlex, commonStyles.justifyContentSpaceEvenly]}>
              <View style={commonStyles.marginRight30}>
                <Touchable style={[commonStyles.textAlignCenter]} onPress={this.accept}>
                  <Image source={acceptIcon} style={[styles.payOptImages]} />
                  <Text style={[commonFontStyles.workSans, commonStyles.smallHeading]}>Accept</Text>
                </Touchable>
              </View>
              <View style={commonStyles.marginLeft30}>
                <Touchable style={[commonStyles.textAlignCenter]} onPress={this.decline}>
                  <Image source={declineIcon} style={[styles.payOptImages]} />
                  <Text style={[commonFontStyles.workSans, commonStyles.smallHeading]}>Decline</Text>
                </Touchable>
              </View>
            </View>,
          ]}
        />
      );
    }
  }
);

const styles = StyleSheet.create({
  bottomPaySection: {
    borderTop: "1px solid #C8C8C8",
    padding: "10px 15px",
  },
  payOptImages: {
    display: "block",
    width: 24,
    height: 24,
    backgroundSize: "contain",
    margin: "0 auto",
  },
  active: {
    color: "#37c2ef",
  },
  disable: {
    opacity: 0.5,
    cursor: "auto",
  },
  bottomControlsView: {
    display: "block",
  },
});
