import React from "react";
import R14, { View, Text, StyleSheet, ScreenLayout, FlatList, AsyncStorage, Touchable, Image } from "../core";
import NoRecordsFound from "../components/NoRecordsFound";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import DateUtils from "../utils/DateUtils";
import CommonUtility from "../utils/CommonUtility";
import { CURRENCY } from "../constant/app.data.constant";
import StatusComponent from "../components/StatusComponent";

const delinedIcon = require("../assets/images/delined.png");

export default R14.connect(
  class BillHistoryScreen extends React.Component {
    constructor(props) {
      super(props);
    }

    onPress = async (item) => {
      let newThreadId = item.threadId.replaceAll("#", ">>");
      // AsyncStorage.setItem("billDetails", JSON.stringify(item));
      this.props.app.nav.to("billHistoryDetailsRoute", { threadId: newThreadId });
    };

    render() {
      const { billDetails, messages } = this.props;

      return (
        <ScreenLayout scrollEnabled style={[styles.billHistoryScreen]}>
          <View style={[commonStyles.marginBottom10]}>
            <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500]}>{this.props.app.nav.route.label} </Text>
          </View>
          <View style={[styles.billerName]}>
            <View
              style={[
                commonStyles.userIconDiv,
                commonStyles.dFlex,
                commonStyles.alignItemsCenter,
                commonStyles.justifyContentCenter,
                commonStyles.marginRight15,
                styles.billerShortName,
              ]}>
              <Text style={[commonStyles.heading6, commonStyles.fontWeight600, commonStyles.userInitial, commonFontStyles.workSans]}>
                {CommonUtility.shortName(billDetails.biller.billerName)}
              </Text>
            </View>
            <Text
              style={[
                styles.cardHeadingText,
                commonStyles.heading5,
                commonStyles.fontWeight500,
                commonFontStyles.workSans,
                commonStyles.wordBreakWord,
              ]}>
              {!!billDetails ? billDetails.biller.billerName : null}
            </Text>
            {!!billDetails.biller.isBlocked ? <Image source={delinedIcon} resizeMode="contain" style={[commonStyles.declineIcon]} /> : null}
          </View>
          <FlatList
            style={styles.billerHistoryScreen}
            ListEmptyComponent={<NoRecordsFound message={"No bills found."} />}
            data={messages}
            keyExtractor={(el) => el.threadId}
            renderItem={({ item, index }) => (
              <Touchable onPress={() => this.onPress(item)}>
                <View style={[styles.cardBox, commonStyles.dFlex]}>
                  <View style={styles.listCircle}></View>
                  <View>
                    <View>
                      <Text
                        style={[
                          styles.cardSubHeadingText,
                          styles.cardSubject,
                          commonStyles.heading6,
                          commonStyles.fontWeight500,
                          commonFontStyles.workSans,
                          commonStyles.wordBreakWord,
                          commonStyles.marginBottom5,
                        ]}>
                        {item.subject}
                      </Text>
                    </View>
                    <View>
                      <Text style={styles.cardSmlHeadingText}>
                        {/* {!!item.fullPaidAndDeclined ? DateUtils.dateAndTimeWithAmPm(item.fullPaidAndDeclined) : null} */}
                        {DateUtils.dateAndTimeWithAmPm(CommonUtility.displayBillsHisotryDate(item))}
                      </Text>
                    </View>
                    <View>
                      <Text
                        style={[
                          styles.cardSubHeadingText,
                          commonStyles.fontWeight600,
                          commonFontStyles.workSans,
                          commonStyles.heading6,
                          styles.dueIn,
                          styles.marginBottom0,
                          commonStyles.marginTop5,
                        ]}>
                        Due On
                      </Text>
                      <Text
                        style={[
                          styles.cardSubHeadingText,
                          commonStyles.marginTop5,
                          commonStyles.marginBottom5,
                          commonFontStyles.workSans,
                          commonStyles.heading6,
                        ]}>
                        {item.dueDate}
                      </Text>
                    </View>
                  </View>
                  <View style={styles.cardRightDiv}>
                    <StatusComponent
                      marginBottom={[styles.marginBottom0, commonStyles.marLAuto, commonStyles.marRT0]}
                      label={item.billStatus}
                      paymentStatusLabel={item.paymentStatus}
                    />
                    <View style={styles.twoCol}>
                      <View style={[commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.alignItemsEnd]}>
                        <Text
                          style={[
                            styles.cardSubHeadingText,
                            commonStyles.fontWeight600,
                            commonFontStyles.workSans,
                            commonStyles.heading6,
                            styles.marginBottom0,
                            styles.totalAmount,
                          ]}>
                          Total Amount
                        </Text>
                        <Text
                          style={[
                            styles.cardSubHeadingText,
                            commonStyles.marginTop5,
                            commonStyles.marginBottom5,
                            commonFontStyles.workSans,
                            commonStyles.heading6,
                          ]}>
                          {CURRENCY["USD"]} {item.billAmount}
                        </Text>
                      </View>
                    </View>
                  </View>
                </View>
              </Touchable>
            )}
          />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  billHistoryScreen: {
    flex: 0,
    alignItems: "flex-start",
    background: "#fff",
    padding: "9px 15px",
    height: "100%",
    screen: ({ width }) => {
      if (width <= 640)
        return {
          alignItems: "center",
          margin: 0,
        };
    },
  },
  cardSmlHeadingText: {
    color: "#888888",
    display: "block",
    fontSize: 12,
    margin: 0,
  },
  userIconDiv: {
    minWidth: 32,
  },
  circleText: {
    width: 40,
    height: 40,
    borderRadius: "50%",
    background: "rgba(0,173,255,.20)",
    color: "rgb(12, 125, 166)",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  twoCol: {
    display: "flex",
    justifyContent: "space-between",
  },
  cardBox: {
    padding: "8px 15px 15px",
    borderBottom: "1px solid #DADADA",
    marginBottom: 3,
  },
  cardSubHeadingText: {
    color: "#444444",
    display: "block",
    fontSize: 14,
    // margin: "5px 0",
  },
  cardRightDiv: {
    marginLeft: "auto",
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-end",
  },
  boldF: {
    fontWeight: "bold",
  },
  listCircle: {
    height: 12,
    width: 12,
    background: "rgb(77, 197, 242)",
    borderRadius: "100%",
    marginRight: 8,
    marginTop: 5,
  },
  marginBottom0: {
    marginBottom: 0,
  },
  dueIn: {
    margin: "11px 0 7px 0",
  },
  decline: {
    height: 13,
    width: 15,
    margin: "3px 5px 0px",
  },
  billerName: {
    display: "flex",
    alignItems: "center",
  },
  billerHistoryScreen: {
    marginTop: 93,
  },
  totalAmount: {
    margin: "23px 0px 0px",
  },
});
