import React from "react";
import R14, { View, Text, Image, StyleSheet, AsyncStorage } from "../../core";
import { AMAZON_AUTHKEY } from "../../constant/app.constant";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import AmazonLoginButton from "./AmazonLoginButton";
import { SOCIAL_MEDIA_ENTITY, LOGIN_TYPE, USER_TYPE } from "../../constant/app.data.constant";

const amazonShortIcon = require("../../assets/images/amazonShortIcon.png");
const amazonIcon = require("../../assets/images/amazonIcon48.png");

export default R14.connect(
  class AmazonLogin extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        buttonType: this.props.buttonType,
        buttonClickable: true,
        currentRoute: "",
        randomKey: "",
      };
    }

    componentDidMount() {
      this.setState({
        currentRoute: this.props.app.nav.route.name,
      });
    }
    // SECTION: Route Registration.
    routeToRegistration = (val) => {
      let returnUrl = this.props.app.route._location.search;
      if(this.props.userType === USER_TYPE.XBP)
      {
      	this.props.app.nav.to("registration", { type: `${val.toLowerCase()}${!!returnUrl ? returnUrl : ""}` });
      } else{
        this.props.app.nav.to("pchSignup", { type: `${val.toLowerCase()}${!!returnUrl ? returnUrl : ""}` });
      }
      
      
    };

    onSuccess = async (res) => {
      this.setState({ buttonClickable: false });

      const { _profile } = res;
      this.props.app.dm.userLoginDomain.setState({
        socialLoginProfile: _profile,
        socialLoginType: LOGIN_TYPE.AMAZON,
      });
      if(this.props.userType === USER_TYPE.XBP)
      {
        if (this.state.buttonType === "Profile") {
          const { userDetailUID, payerId } = this.props.app.dm.userLoginDomain.state;
          let reqBody = {
            uid: userDetailUID,
            amazonProfileID: _profile.id,
          };

          // Check same email in user detail 
          let emailExist = await this.props.app.dm.userLoginDomain.getLoginDetail(
            {
              email: { eq: _profile.email.toLowerCase().trim() },
              uid:{eq: userDetailUID}
            }
          );

          if (emailExist.length > 0) {
            let res = await this.props.app.dm.userRegistration.updateUserDetails(reqBody, true);
            // userLoginDomain.socialLoginUpdate(reqBody);
            if (!!res) {
              this.props.app.dm.commonDomain.showToastr("success", "Amazon Profile is synced");
              this.setState({ buttonClickable: true });
              this.setState({ buttonType: "ProfileSynced" });
              return false;
            }
          }
          else {
            this.props.app.dm.commonDomain.showToastr("error", "Email ID not matched");
            this.setState({ buttonClickable: true });
            return false;
          }

        } else {
          try {
            let response = await this.props.app.dm.userSession.loginAuth({
              socialProfileId: _profile.id,
              username: _profile.email.toLowerCase().trim(),
              loginType: LOGIN_TYPE.AMAZON,
            });

            if (!!response) {
              const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
              if (!!userLoginDetail) {
                //NOTE: check obsolute pid
                if (userLoginDetail.payerId.includes("#") || userLoginDetail.tokenKey === "" || userLoginDetail.tokenKey === undefined) {
                  this.props.app.dm.commonDomain.showToastr("error", "Auth failed due to invalid credentials");
                  this.setState({ buttonClickable: true });
                  return false;
                }
    
                if (!!!userLoginDetail.userType) {
                  this.setState({ buttonClickable: true });
                  this.routeToRegistration("existingamazon");
                  return false;
                } else {
                  this.props.app.dm.commonDomain.showToastr("success", "You are successfully logged in");
                  await this.props.redirectToReturnUrl();
                  this.setState({ buttonClickable: true });
                  return false;
                }
              }
              else
              this.routeToRegistration("SocialLogin");
            }
            else
              this.routeToRegistration("SocialLogin");
          }
          catch(err)
          {
            this.routeToRegistration("SocialLogin");
          }  
        }
      }
       
    };

    onFailure = (res) => {
      console.log("Login failed: ", res);
      this.setState({ buttonClickable: true, randomKey: Math.random() });
    };

    cssClassLogic = () => {
      return { background: "none", padding: 0, border: 0, cursor: "pointer" };
    };

    render() {
      return (
        <View key={this.state.randomKey} style={[commonStyles.dFlex, commonStyles.alignItemsCenter]}>
     {
          this.props.userType === USER_TYPE.XBP ? 
          (
            <AmazonLoginButton
            provider="amazon"
            appId={AMAZON_AUTHKEY}
            onLoginSuccess={this.onSuccess}
            onLoginFailure={this.onFailure}
            // autoLogin={false}
            style={
              (this.state.buttonType === "Login" || this.state.buttonType === "Profile") && this.state.currentRoute != "profileEditRoute"
                ? this.state.buttonClickable
                  ? { background: "none", padding: 0, border: 0, cursor: "pointer" }
                  : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }
                : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none" }
            }
            key={"amazon"}>
            {/* // prfile synced */}
            <View
              style={
                this.state.buttonType === "ProfileSynced"
                  ? [commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.grayBorderradious50, commonStyles.padding4x8]
                  : commonStyles.invisibleBlock
              }>
              <Image source={amazonShortIcon} style={commonStyles.icon24x25} />
              <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.darkGray]}>Synced</Text>
            </View>

            <View style={this.state.buttonType === "Login" ? null : commonStyles.invisibleBlock}>
              <Image source={amazonIcon} style={commonStyles.icon48} />
            </View>

            <View style={this.state.buttonType === "Profile" ? null : commonStyles.invisibleBlock}>
              <Image source={amazonShortIcon} style={commonStyles.icon24x25} />
            </View>
          </AmazonLoginButton>
          ):
          (
            null
          )
     }
         
        </View>
      );
    }
  }
);
