import R14, { AsyncStorage, AsyncSessionStorage } from "../core";

export default class NotificationDomain extends R14.Domain {
    constructor(props) {
        super(props);   
        this.state={
          notifications:[],
          notificationThreadIds:[]
        }   
    }
    async notificationDetail(){
      this.insertNotification();
      let notifications = await this.getNotificationList();
      let finalNotifications = [];
      let uniqueData = [];
      
      if(!!notifications && notifications.length > 0)
      {
       notifications.map(
        (notification) => {
          //  24 hr = 24 * 60 = 1440 min 
          if(!!notification.isRead && !!notification.readDateTime)
          {
            let delayMin = 1440; // 5; //1440;
            let readTime = new Date(notification.readDateTime);
            let readTimeAfter24Hr = new Date(readTime.setMinutes(readTime.getMinutes() + delayMin)); 
              if(readTimeAfter24Hr >= new Date())
                finalNotifications.push(notification)
          }
          else
            finalNotifications.push(notification)
        });   
      
        // remove duplicate messages..
        finalNotifications = finalNotifications.sort((a,b) => new Date(b.messageDateTime) -new Date(a.messageDateTime))
        finalNotifications.map((item, i) =>{
          if( item.subMessageType === "PAM")
          {
          if(uniqueData.filter((notification) => 
             notification.threadId === item.threadId
          ).length === 0)
            uniqueData.push(item);
        }
        else{
          if(uniqueData.filter((notification) => 
              notification.threadId === item.threadId
              && notification.subMessageType === item.subMessageType
              && notification.threadDateTime === item.threadDateTime
              && notification.messageDateTime === item.messageDateTime
          ).length === 0)
          {
            if(["Over Due Reminder", "Bill Due Date Reminder"].includes(item.subMessageType))
            {
              if(uniqueData.filter((notification) => 
                                  notification.threadId === item.threadId
                                  && notification.subMessageType === item.subMessageType).length === 0)
                {
                  uniqueData.push(item);
                }
            }
            else
            {
              uniqueData.push(item);
            }
          }
        }
        });
      }
      this.setState({
        notifications : uniqueData // finalNotifications
      })
    }
 
    
    async getNotificationList(fields = "uid payerId threadId originatorPid respondentPid messageType profile subject note subMessageType,messageDateTime isRead,readCount, readDateTime"){
        const { pid } = !!localStorage.getItem("profiles") ? JSON.parse(localStorage.getItem("profiles")) : {};
        try {
            let res = await this.api.qry(
              `
            query notifications($filter: NotificationFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
                notifications(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
                totalCount @include(if: true)
                nodes {
                  ${fields}
                }
              }
            }`,
              {
                filter: {
                  payerId: { eq: pid },                  
                },
              }
            );            
            return  res.data.notifications.nodes;
         
        } catch (err) {
            return err;
        }
    }
    async updateNotification(  input, fields = "uid payerId isRead") {
      try {
        let res = await this.api.mutate(
          `
          mutation updateNotificationData($input: [NotificationData!]){
            updateNotificationData(input: $input){              
              success
            }
          }`,
          { input }
        );
        return res.data.updateNotificationData;
      } catch (error) {
        return error;
      }
    }
    async insertNotification() {
      let { pid } = !!localStorage.getItem("profiles") ? JSON.parse(localStorage.getItem("profiles")) : {};
        let input = {
          token: sessionStorage.getItem("access_token"),
          payerId: pid
        }
        try {
          let res = await this.api.mutate(
            `
            mutation insertNotification($input: InsertNotificationInput!){
              insertNotification(input: $input){              
                success msg
              }
            }`,
            { input }
          );
          return res.data.insertNotification;
        } catch (error) {
          return error;
        }
    }
}