import React from "react";
import R14, { StyleSheet, Button, Form, SubmitButton, TextInputField, Colors, ScreenLayout, Text, View, ScrollView } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import PamMessasgeBottomSection from "../components/PamMessasgeBottomSection";
import CommonValidators from "../utils/CommonValidators";
import CommonUtility from "../utils/CommonUtility";
import { BILLER_STATUS } from "../constant/app.data.constant";
import CopyRight from "../components/CopyRight";
import LeftMenuComponent from "../components/LeftMenuComponent";

export default R14.connect(
  class PamMessageScreen extends React.Component {
    constructor(props) {
      super(props);
      this.scrollRef = React.createRef();
      const { moreInfo } = this.props;
      const chat = [];
      if (moreInfo && moreInfo.moreInfos) {
        const { moreInfos } = moreInfo;
        moreInfos.map((ele) => {
          if (ele.moreInfoMessage) chat.push({ payer: ele.moreInfoMessage });
          if (ele.moreInfoReply) chat.push({ biller: ele.moreInfoReply });
        });
      }

      this.state = {
        messageContentHeight: 0,
        scrollCount: 0,
        chatMessage: chat,
        biller: props.pam.biller,
        isChatAllow: false,
      };

      this.timerInstance = null;
    }

    async componentDidMount() {
      this.form = this.props.app.ui.form("chatBox");
      const { pam, moreInfo } = this.props;
      if (moreInfo && moreInfo.status === "requested") {
        const moreInfoReply = moreInfo && moreInfo.moreInfos ? moreInfo.moreInfos.filter((el) => !!el.moreInfoReply).length + 1 : 1;
        await this.props.app.dm.billerDirectoryDomain.updatePamMessages({ readMessages: moreInfoReply, uid: pam.uid });
      }
      this.timerInstance = setInterval(() => {
        this.updatePam();
      }, 30000);
    }

    updatePam = async () => {
      const { pam } = this.props;
      const { pamList } = this.props.app.dm.billerDirectoryDomain.state;
      const { biller } = this.state;

      const pamRes = await this.props.app.dm.billerDirectoryDomain.moreInfoMessages(pam.pamId); // api call....
      if (!!pamRes && !!pamRes.moreInfos && !!pamRes.moreInfos.length && pamRes.status === "requested") {
        const { moreInfos } = pamRes;
        const moreInfoReply = moreInfos.filter((el) => !!el.moreInfoReply);
        this.props.app.dm.billerDirectoryDomain.updatePamMessages({ readMessages: moreInfoReply.length + 1, uid: pam.uid });

        if (!!pamList.length) {
          let ind = pamList.findIndex((el) => el.uid === pam.uid);
          pamList[ind].readMessages = moreInfoReply.length;
          this.props.app.dm.billerDirectoryDomain.setState({ pamList });
        }
        if (moreInfos) {
          const chat = [];
          moreInfos.map((ele) => {
            if (ele.moreInfoMessage) chat.push({ payer: ele.moreInfoMessage });
            if (ele.moreInfoReply) chat.push({ biller: ele.moreInfoReply });
          });
          this.setState({
            chatMessage: chat,
            biller: { ...biller, billerStatus: "inprogress" },
          });
        }
      }
    };

    componentWillUnmount() {
      if (this.timerInstance) {
        clearInterval(this.timerInstance);
      }
    }

    onSubmit = async (form) => {
      const { chatMessage, messageContentHeight, biller } = this.state;
      form = this.props.app.ui.form("chatBox");
      let res = await this.props.app.dm.billerDirectoryDomain.createPamMesagePayload(
        form.values.enterMessage,
        this.props.pam.pamId,
        this.props.pam.payerId
      );
      form.reset();
      if (!!res) {
        this.setState({
          chatMessage: chatMessage.concat({ payer: res.moreInfoMessage }),
          biller: { ...biller, billerStatus: "inprogress" },
        });
        this.props.app.dm.queryDomain.updateBiller({ ...biller, billerStatus: "inprogress" });
        this.scrollToEnd({ y: messageContentHeight });
      }
      return false;
    };

    layoutChange = (event) => {
      let evtobj = event.nativeEvent.layout;
      this.setState({
        messageContentHeight: evtobj.height,
      });

      this.scrollToEnd({ y: evtobj.height });
    };

    scrollToEnd(options = { animated: true }) {
      if (this.scrollRef.current) {
        this.scrollRef.current.scrollToEnd(options);
      }
    }

    readText = (val) => {
      if ((!val && !!this.form) || val.length <= 140) {
        this.form.elmts.enterMessage.setError(null);
      } else if (val.length > 140) {
        this.form.elmts.enterMessage.setError("Character Limit Exceeds 140 Characters");
      }
    };

    render() {
      const { pam, moreInfo } = this.props;
      const { chatMessage, biller, isChatAllow } = this.state;
      const { additionalUserInfo } = this.props.userDetails;
      let userShortName = additionalUserInfo.firstName + (additionalUserInfo.lastName ? " " + additionalUserInfo.lastName : "");
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      return (
        <ScreenLayout scrollEnabled={false}>
          <ScrollView style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu]}>
            <View style={[styles.pamMessageScreen, commonStyles.dFlex, commonStyles.flexDirectionColumn]}>
              <View style={[styles.pageTitleHolder]}>
                <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500]}>{this.props.app.nav.route.label} </Text>
              </View>
              <View>
                <View style={[styles.twoCol, styles.cardBox]}>
                  <View style={[styles.userIconDiv, commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                    <Text style={[commonStyles.heading6, commonStyles.fontWeight600, commonStyles.userInitial, commonFontStyles.workSans]}>
                      {CommonUtility.shortName(biller.billerName)}
                    </Text>
                  </View>
                  <View style={styles.cardRightDiv}>
                    <View style={styles.twoCol}>
                      <View>
                        <Text
                          style={[
                            styles.cardHeadingText,
                            commonStyles.heading5,
                            commonStyles.fontWeight500,
                            commonFontStyles.workSans,
                            commonStyles.wordBreakWord,
                          ]}>
                          {biller.billerName}
                        </Text>
                      </View>
                    </View>
                    <View style={[commonStyles.dFlex]}>
                      <View>
                        <Text
                          style={[
                            styles.cardSubHeadingText,
                            commonStyles.heading6,
                            commonStyles.fontWeight600,
                            commonFontStyles.workSans,
                            commonStyles.margin0,
                          ]}>
                          Request Type
                        </Text>
                        <Text style={[styles.cardSubHeadingText, commonStyles.heading6, commonFontStyles.workSans, commonStyles.margin0]}>
                          Pre Authorization Request
                        </Text>
                      </View>
                      <View style={[commonStyles.marLAuto]}>
                        <Text
                          style={[
                            styles.cardSubHeadingText,
                            commonStyles.heading6,
                            commonStyles.fontWeight600,
                            commonFontStyles.workSans,
                            commonStyles.margin0,
                          ]}>
                          Status
                        </Text>
                        <Text
                          style={[
                            styles.cardSubHeadingText,
                            styles[`${biller.isBlocked ? "blocked" : biller.billerStatus}`],
                            commonStyles.heading6,
                            commonFontStyles.workSans,
                            commonStyles.margin0,
                            commonStyles.transformUpperCase,
                          ]}>
                          {biller.isBlocked ? "Blocked" : BILLER_STATUS[biller.billerStatus]}
                        </Text>
                      </View>
                    </View>
                  </View>
                </View>
              </View>
              <ScrollView
                key={this.state.scrollCount}
                ref={this.scrollRef}
                onLayout={this.layoutChange}
                height={this.state.messageContentHeight}
                style={[styles.centerScrollable]}>
                <View style={[commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.padding5]}>
                  <View style={[commonStyles.textAlignCenter, commonStyles.marginBottom10]}>
                    <Text
                      style={[
                        commonStyles.exSmallHeading,
                        commonStyles.chatTiming,
                        commonStyles.transperentBtnColor,
                        commonStyles.marLAuto,
                        commonFontStyles.workSans,
                      ]}></Text>
                  </View>
                  <View style={[styles.chatBoxWidth]}>
                    <View style={[commonStyles.dFlex]}>
                      <View style={[commonStyles.userInitialOuter, styles.billerInitial]}>
                        <Text style={[commonFontStyles.workSans, commonStyles.fileSize, commonStyles.fontWeight500]}>
                          {CommonUtility.shortName(biller.billerName)}
                        </Text>
                      </View>
                      <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter]}>
                        <Text style={[commonStyles.exSmallHeading, commonStyles.guestName, commonFontStyles.workSans]}>{biller.billerName}</Text>
                      </View>
                    </View>
                    <View>
                      <View style={[commonStyles.minWidth150, commonStyles.guestText, commonStyles.dInlineFlex, commonStyles.flexDirectionColumn]}>
                        <View>
                          <Text style={[commonStyles.smallHeading, commonStyles.fontWeight500, commonFontStyles.workSans]}>
                            Request For Authorisation
                          </Text>
                        </View>
                        <View>
                          {moreInfo ? <Text style={[commonStyles.smallHeading, commonFontStyles.workSans]}>{moreInfo.requestMessage}</Text> : null}
                        </View>
                        <Text style={[commonStyles.guestTextBorder]}></Text>
                      </View>
                    </View>
                    <View style={commonStyles.marLAuto}>
                      <Text
                        style={[
                          commonStyles.exSmallHeading,
                          commonStyles.chatTiming,
                          commonStyles.transperentBtnColor,
                          commonStyles.marLAuto,
                          commonFontStyles.workSans,
                        ]}></Text>
                    </View>
                  </View>
                  <View>
                    {chatMessage.map((item, indx) => (
                      <View key={indx}>
                        {item.payer ? (
                          <View style={[commonStyles.marginBottom15, commonStyles.dFlex, commonStyles.flexDirectionColumn]}>
                            <View style={[commonStyles.minWidth150, commonStyles.marLAuto]}>
                              <View style={[commonStyles.dFlex]}>
                                <View style={[styles.userInitialOuter, styles.payerInital]}>
                                  <Text
                                    style={[
                                      commonFontStyles.workSans,
                                      commonStyles.fileSize,
                                      commonStyles.transperentBtnColor,
                                      commonStyles.fontWeight500,
                                    ]}>
                                    {CommonUtility.shortName(userShortName)}
                                  </Text>
                                </View>
                                <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter]}>
                                  <Text
                                    style={[
                                      commonStyles.exSmallHeading,
                                      commonStyles.guestName,
                                      commonFontStyles.workSans,
                                      commonStyles.transperentBtnColor,
                                    ]}>
                                    {userShortName}
                                  </Text>
                                </View>
                              </View>
                              <View style={[styles.responceMsg, commonStyles.hostText]}>
                                <Text style={[commonStyles.smallHeading, commonFontStyles.workSans]}>{item.payer}</Text>
                                <Text style={[commonStyles.hostTextBorder]}></Text>
                              </View>
                            </View>
                          </View>
                        ) : (
                          <View style={[commonStyles.marginBottom15, styles.chatBoxWidth, commonStyles.dFlex, commonStyles.flexDirectionColumn]}>
                            <View style={[commonStyles.minWidth150, commonStyles.marRAuto]}>
                              <View style={[commonStyles.dFlex]}>
                                <View style={[styles.userInitialOuter, styles.billerInitial]}>
                                  <Text style={[commonFontStyles.workSans, commonStyles.fileSize, commonStyles.fontWeight500]}>
                                    {CommonUtility.shortName(pam.biller.billerName)}
                                  </Text>
                                </View>
                                <View>
                                  <Text style={[commonStyles.exSmallHeading, commonStyles.guestName, commonFontStyles.workSans]}>
                                    {pam.biller.billerName}
                                  </Text>
                                </View>
                              </View>
                              <View style={[styles.responceMsg, styles.billerText]}>
                                <Text style={[commonStyles.smallHeading, commonFontStyles.workSans]}>{item.biller}</Text>
                                <Text style={[styles.billerBorder]}></Text>
                              </View>
                            </View>
                          </View>
                        )}
                        <View style={commonStyles.marLAuto}>
                          <Text
                            style={[
                              commonStyles.exSmallHeading,
                              commonStyles.chatTiming,
                              commonStyles.transperentBtnColor,
                              commonStyles.marLAuto,
                              commonFontStyles.workSans,
                            ]}></Text>
                        </View>
                      </View>
                    ))}
                  </View>
                </View>
              </ScrollView>
              {biller.billerStatus !== "accepted" && biller.billerStatus !== "denied" && !biller.isBlocked ? (
                <View style={[styles.bottomSectionOuter]}>
                  {isChatAllow ? (
                    <View style={[styles.chatInputField, commonStyles.marginBottom20]}>
                      <View style={[commonStyles.enterMessage, commonStyles.marTAuto]}>
                        <Form
                          name="chatBox"
                          style={[commonStyles.padding0]}
                          validators={CommonValidators.getFormValidator()}
                          validateBeforeSubmit={true}
                          onSubmit={this.onSubmit}>
                          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter]}>
                            <View style={[styles.fieldsHolder, commonStyles.marginRight15]}>
                              <View style={[styles.messageTextInput, commonStyles.flexGrow1]}>
                                <TextInputField
                                  labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans]}
                                  labelStyle={[commonStyles.formLabelStyle]}
                                  name="enterMessage"
                                  label="Enter Message*"
                                  validator="cmtsVali"
                                  required="Please enter the message"
                                  multiline
                                  onChangeText={this.readText}
                                  style={[commonStyles.margin0, styles.messageTextField, commonStyles.resizeVertical]}
                                  helperTextStyle={[commonStyles.nowrap, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                                />
                              </View>
                            </View>
                            <SubmitButton
                              type="button"
                              title="Send"
                              name="sendBtn"
                              style={[styles.blueButton]}
                              titleTextStyle={[
                                commonStyles.fontWeight600,
                                commonFontStyles.workSans,
                                commonStyles.heading6,
                                commonStyles.darkGrayColor,
                              ]}
                            />
                          </View>
                        </Form>
                      </View>
                    </View>
                  ) : null}
                  <View style={styles.bottomPaySection}>
                    <PamMessasgeBottomSection pamId={this.props.pam.pamId} pam={this.props.pam} />
                  </View>
                </View>
              ) : null}
            </View>
            {isMobileDevice ? null : (
              <View style={commonStyles.copyRightSection}>
                <CopyRight />
              </View>
            )}
          </ScrollView>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  twoCol: {
    display: "flex",
    justifyContent: "space-between",
  },
  pamMessageScreen: {
    flex: 0,
    alignItems: "flex-end",
    background: "#fff",
    padding: "5px 15px 5px 15px",
    flexGrow: 1,
    margin: 0,
    // position: "relative",
    height: "calc(100vh - 150px)",
  },

  logoView: {
    ...StyleSheet.margin(32, 0, 48, 0),
  },
  textWrapperView: {
    paddingBottom: 16,
  },
  text: {
    color: Colors.onBackground,
  },
  userIconDiv: {
    width: 32,
    height: 32,
    borderRadius: "100%",
    background: "#e0e0e0",
  },
  userIcon: {
    height: 40,
    width: 40,
    marginRight: 10,
    display: "block",
  },
  cardRightDiv: {
    width: "calc(100% - 50px)",
  },
  titleDiv: {
    marginBottom: 15,
  },
  cardBox: {
    padding: "15px 15px",
    margin: "0px -15px 0",
    borderBottom: "1px solid rgb(218, 218, 218)",
  },
  cardHeadingText: {
    // color: "#0C7DA6",
    display: "block",
    fontSize: 16,
  },
  cardSubHeadingText: {
    color: "#444444",
    display: "block",
    fontSize: 14,
    margin: "5px 0",
  },
  cardSmlHeadingText: {
    color: "#888888",
    display: "block",
    fontSize: 12,
    margin: 0,
  },
  statusDiv: {
    background: "#F1F1F1",
    border: "0.5px solid #D6D6D6",
    borderRadius: 50,
    padding: 5,
    fontSize: 12,
    display: "flex",
    alignItems: "center",
  },
  statusColorDiv: {
    background: "#7accf6",
    borderRadius: "50%",
    padding: 4,
    height: 8,
    width: 8,
    margin: " 0 5px 0 2px",
  },
  statusPaid: {
    background: "#218666",
  },
  statusOutstanding: {
    background: "#FFA500",
  },
  statusOverdue: {
    background: "#EE82EE",
  },
  BorderTop: {
    borderTop: "1px solid #D6D6D6",
  },
  smallChatICon: {
    width: 15,
    height: 15,
    display: "inline-block",
    backgroundSize: "contain",
  },
  meadiumChatICon: {
    width: 30,
    height: 27,
    display: "inline-block",
    backgroundSize: "contain",
  },
  blueButton: {
    background: "#37c2ef",
    color: "#333",
    height: 52,
    width: 75,
    border: 0,
  },
  bottomSectionOuter: {
    // position: "absolute",
    margin: "auto -15px 0 -15px",
    background: "#fff",
  },
  chatInputField: {
    padding: "15px 15px 0",
    border: 0,
  },
  bottomPaySection: {
    border: 0,
  },
  decline: {
    height: 13,
    width: 15,
    margin: "0px 5px",
  },
  statusSection: {
    marginBottom: 3,
  },
  requestDate: {
    color: "red",
  },
  chatBoxWidth: {
    maxWidth: "85%",
    marginBottom: 15,
    display: "inline-flex",
    flexDirection: "column",
  },
  acceptDeclineIcon: {
    width: 16,
    height: 16,
    display: "inline-block",
    backgroundSize: "contain",
    marginRight: 5,
  },
  responceMsg: {
    marginBottom: 3,
  },
  active: {
    color: "#218666",
  },
  requested: {
    color: "#800080",
  },
  new: {
    color: "#800080",
  },
  inprogress: {
    color: "#EE781E",
  },
  accepted: {
    color: "#218666",
  },
  denied: {
    color: "#B40220",
  },
  blocked: {
    color: "#B40220",
  },
  fieldsHolder: {
    position: "relative",
    flexGrow: 1,
    display: "flex",
  },
  messageTextField: {
    position: "relative",
    zIndex: 1,
    padding: 0,
    paddingTop: 5,
    paddingLeft: 5,
    resize: "none",
  },
  attachmentCls: {
    height: 52,
    width: 56,
    paddingTop: 5,
  },
  attachmentCover: {
    background: "#fff",
  },
  messageTextInput: {
    color: "red",
  },
  attachmentField: {
    position: "absolute",
    right: 0,
    zIndex: 99,
    background: "#fff",
  },
  fileIcon: {
    color: "red",
  },
  billerBorder: {
    height: 10,
    width: 10,
    position: "absolute",
    bottom: -6,
    left: -4,
    border: "6px solid",
    borderColor: "#e8e5e5 rgba(255,255,255, 0.0) rgba(255,255,255, 0.0) rgba(255,255,255, 0.0)",
    transform: "rotate(-43deg)",
    borderRadius: 8,
  },
  // conversation: {
  //   // maxHeight: 350,
  //   overflowX: "auto",
  //   padding: "0 10px",
  //   screen: ({ width }) => {
  //     if (width <= 375)
  //       return {
  //         // maxHeight: 470,
  //       };
  //   },
  // },
  billerText: {
    display: "block",
    background: "#e8e5e5",
    padding: "5px 10px",
    position: "relative",
    borderRadius: 3,
    margin: "2px 0 0",
  },
  userInitialOuter: {
    height: 20,
    width: 20,
    borderRadius: 100,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  payerInital: {
    background: "#cff2fe",
  },
  billerInitial: {
    background: "#e8e5e5",
    borderRadius: "100%",
    height: 20,
    width: 20,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  centerScrollable: {
    top: 70,
    bottom: 70,
    // margin: "0px 0px 0px 15px",
    overflowY: "auto",
    // paddingRight: "15px",
  },
  pageTitleHolder: {
    background: "#fff",
    padding: "8px 16px 0",
  },
});
