import R14, { AsyncStorage } from "../core";
import { USER_API } from "../constant/app.constant";

export default class UserDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {
      billers: [],
    };
  }

  async updateUserDetails(data) {
    try {
      return await this.dm.rest.put(USER_API, data);
    } catch (error) {
      console.log("UpdateUserDetails error ", error);
      return error;
    }        
  }
 
  async getBillerListFromPam() {
    const { pid } = !!await AsyncStorage.getItem("profiles") ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let blockedBiller = await this.dm.billerDirectoryDomain.getBlockedUsers();
    let resData = await this.dm.billerDirectoryDomain.getPamMessagesList();
    if (!!resData) {
      let res = JSON.parse(JSON.stringify(resData));
      for (let ele of res) {
        if (!!ele) {
          let billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
          let billerDetail = billerList.find((itm) => itm.billerId === ele.billerId && itm.payerId === ele.payerId);
          await this.dm.queryDomain.addUpdateBiller({ ...ele, blockedBiller, biller: billerDetail });
        }
      }
    } else {
      if (!!blockedBiller && !!blockedBiller.length) {
        for (const ele of blockedBiller) {
          let billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
          let billerDetail = billerList.find((itm) => itm.billerId === ele.pid && itm.payerId === pid);
          if (!!billerDetail) {
            await this.dm.queryDomain.updateBiller({ isBlocked: true, uid: billerDetail.uid });
          }
        }
      }
    }
    let billerList = await this.dm.queryDomain.getBiller({ filter: { payerId: { like: pid } } });
    AsyncStorage.setItem("billerList", JSON.stringify(billerList));
    return true;
  }


  async getUserDetailsList(filter, fields = "uid pid email firstName mobileNumber parentUid isActive parentUserDetail{ uid email firstName lastName }") {
    try {
      
        let res = await this.api.qry(
          `
          query FindUserLoginDetail($filter: UserLoginDetailFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
            userLoginDetails(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
              nodes {
                  ${fields}
                }
              }
            }`,
          {
            filter: {
              ...filter,
            },
          }
        );
        return res.data.userLoginDetails.nodes ? res.data.userLoginDetails.nodes : null;
      }
     catch (err) {
      return err;
    }
  }

  
}
