import React, { Component } from "react";
import R14, { Touchable, Text, Image, StyleSheet } from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import { ATTACHMENTS } from "../../constant/app.constant";

const attachedFileIcon = require("../../assets/images/attachedFileIcon.png");

export default R14.connect(
  class DownloadComponent extends Component {
    downloadFile = async ({ item, pathid, threadId, messageId }) => {
      let base64Format = await this.props.app.dm.rest.get(`${ATTACHMENTS}/${pathid}`, {
        "thread-id": threadId,
        "message-id": messageId,
        download: true,
      });
      let url = `data:application/octet-stream;base64,${base64Format}`;
      const downloadLink = document.createElement("a");
      downloadLink.href = url;
      downloadLink.download = item.fileName;
      downloadLink.click();
    };

    render() {
      const { item } = this.props;
      return (
        <>
          {!!item.attachments && !!item.attachments.length
            ? item.attachments.map((el, i) => (
                <Touchable
                  key={`download_${i}`}
                  onPress={() => {
                    this.downloadFile({
                      item: el,
                      pathid: el.path,
                      threadId: item.envelope.threadHeader.threadId,
                      messageId: item.envelope.messageHeader.messageId,
                    });
                  }}
                  style={[commonStyles.dFlex, commonStyles.alignItemsStart, commonStyles.marginBottom5]}>
                  <Image source={attachedFileIcon} style={[commonStyles.icon14, styles.attachedFileIcon]} />
                  <Text style={[commonStyles.smallHeading, commonFontStyles.workSans, commonStyles.fileName]}> {el.fileName || el.filename}</Text>
                </Touchable>
              ))
            : null}
        </>
      );
    }
  }
);
const styles = StyleSheet.create({
  attachedFileIcon: {
    marginTop: 3,
    marginRight: 5,
  },
  fileName: {
    whiteSpace: "initial",
  },
});
