import React, { Component, PureComponent } from "react";
import R14, { View, StyleSheet, FileInputField } from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";

export default R14.connect(
  class FileUploadIcon extends Component {
    shouldComponentUpdate() {
      return false;
    }
    getExtension(fname) {
      const { isOnlyPDF } = this.props;
      if (!!isOnlyPDF) {
        switch (fname.toUpperCase()) {
          case "PDF":
            return "PDF";
          default:
            this.props.app.dm.commonDomain.showToastr("error", " Invalid Format, Please attach the file in PDF format only.");
            return false;
        }
      } else {
        switch (fname.toUpperCase()) {
          case "CSV":
          case "DIF":
          case "ODS":
          case "OTS":
          case "TSV":
          case "XLM":
          case "XLS":
          case "XLSB":
          case "XLSM":
          case "XLSX":
          case "XLT":
          case "XLTM":
          case "XLTX":
            return "SDSH";
          case "XML":
            return "DXML";
          case "PDF":
            return "DPDF";
          case "DOC":
          case "DOCX":
          case "DOCM":
          case "DOTX":
          case "DOTM":
          case "DOCB":
            return "WORD";
          case "XSLT":
            return "XSLT";
          default:
            this.props.app.dm.commonDomain.showToastr(
              "error",
              " Invalid Format, Please attach the file in any one of the following format DPDF, DXML, SDSH, WORD, XSLT"
            );
            return false;
        }
      }
    }

    convertBytes(x) {
      const units = ["bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];
      let l = 0;
      let n = parseInt(x, 10) || 0;

      while (n >= 1024 && ++l) {
        n = n / 1024;
      }
      //include a decimal point and a tenths-place digit if presenting
      //less than ten of KB or greater units
      // return n.toFixed(n < 10 && l > 0 ? 2 : 0) + " " + units[l];
      return n.toFixed(2) + " " + units[l];
    }

    getBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onloadend = () => {
          let data = reader.result.split(",");
          let fileNameXt = file.name.split(".");
          if (!!this.getExtension(fileNameXt[1])) {
            resolve({
              filename: file.name,
              contentType: this.getExtension(fileNameXt[1]),
              content: data[1],
              size: this.convertBytes(file.size),
              actualsize: file.size,
              // size: `${Math.round(file.size / 1024).toFixed(2)} ${["Bytes", "Kb", "Mb", "Gb", "Tb"][Math.floor(Math.log2(file.size) / 10)]}`,
            });
          } else {
            return false;
          }
        };
        reader.onerror = (err) => {
          reject(err);
        };
        reader.readAsDataURL(file);
      });
    }

    fileInputChange = async (values) => {
      let fizeSize = values.size / 1024 / 1024;
      if (fizeSize > 10) {
        this.props.app.dm.commonDomain.showToastr("error", "File size is greater than 10Mb.");
        return false;
      } else if (values.name.length > 35) {
        this.props.app.dm.commonDomain.showToastr("error", "Maximum allowed filename is 35 characters");
        return false;
      }
      const { onValueChange } = this.props;
      let fileBase64Data = await this.getBase64(values);
      onValueChange(fileBase64Data);
    };

    render() {
      const { fileInputStyle } = this.props;
      return (
        <View style={[styles.attachmentFieldOuter]}>
          <FileInputField
            style={[commonStyles.margin0, styles.attachmentField, fileInputStyle]}
            name="attachment"
            label=""
            iconRight=""
            onIconRightPress={() => console.log("noop")}
            icon="arrowUp"
            multiline
            onValueChange={this.fileInputChange}
            variant="outlined"
          />
        </View>
      );
    }
  }
);
const styles = StyleSheet.create({
  attachmentFieldOuter: {
    height: 52,
    width: 52,
    padding: 0,
    borderRadius: "100%",
    background: "#cff2ff",
    overFlow: "hidden",
    margin: "0 auto",
  },
  attachmentField: {
    zIndex: 99,
    height: 52,
    width: 52,
    padding: 0,
    borderRadius: "100%",
    borderColor: "rgba(255,255,255, 0.0)",
    background: "rgba(255,255,255, 0.0)",
  },
});
