import React, { PureComponent } from "react";
import R14, { StyleSheet, View, Text, Image } from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import DateUtils from "../../utils/DateUtils";
import DownloadComponent from "./DownloadComponent";
import CommonUtility from "../../utils/CommonUtility";

export default R14.connect(
  class ReceiverComponent extends React.Component {
    render() {
      const { leaf } = this.props;
      let allBillers = this.props.billerList;
      //Filter the elements to find the incoming message details.
      let billerDetail = allBillers.filter((el) => el.billerId === leaf.envelope.messageHeader.senderPid);
      return (
        <View>
          <View style={[styles.chatBoxWidth, commonStyles.dInlineFlex, commonStyles.flexDirectionColumn, commonStyles.minWidth150]}>
            <View style={[commonStyles.dFlex]}>
              <View style={[styles.userInitialOuter, styles.billerInitial]}>
                <Text style={[commonFontStyles.workSans, commonStyles.fileSize, commonStyles.fontWeight500]}>
                  {CommonUtility.shortName(billerDetail[0].billerName)}
                </Text>
              </View>
              <View>
                <Text style={[commonStyles.exSmallHeading, commonStyles.guestName, commonFontStyles.workSans]}>{billerDetail[0].billerName}</Text>
              </View>
            </View>
            <View style={[commonStyles.dFlex, commonStyles.hostChat, commonStyles.marginBottom5, commonStyles.flexDirectionColumn]}>
              <View style={[commonStyles.minWidth150, commonStyles.guestText]}>
                <View>
                  <Text style={[commonStyles.smallHeading, commonFontStyles.workSans]}>{leaf.envelope.messageBody.messageBody}</Text>
                </View>
                {!!leaf.attachments && !!leaf.attachments.length ? <DownloadComponent item={leaf} /> : null}
                <Text style={[commonStyles.guestTextBorder]}></Text>
              </View>
              <View style={commonStyles.dFlex}>
                <Text style={[commonStyles.exSmallHeading, commonStyles.chatTiming, commonStyles.marLAuto, commonFontStyles.workSans]}>
                  {DateUtils.dateTodayYesterday(leaf.envelope.messageHeader.messageDateTime)}
                </Text>
              </View>
            </View>
          </View>
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  chatBoxWidth: {
    padding: "0 0 0 5px",
    marginBottom: 15,
    maxWidth: "85%",
  },
  userInitialOuter: {
    height: 20,
    width: 20,
    borderRadius: 100,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  billerInitial: {
    background: "#e8e5e5",
  },
});
