import React, { Component } from "react";
import R14, { StyleSheet, ScreenLayout, View, Text, ScrollView } from "../core";
import { BILL_STATUS } from "../constant/app.data.constant";
import BillerDetails from "../components/BillerDetails";
import BottomPaySection from "../components/BottomPaySection";
import ExtensionRequestPopUp from "../components/ExtensionRequestPopUp";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import CopyRight from "../components/CopyRight";
import LeftMenuComponent from "../components/LeftMenuComponent";

export default R14.connect(
  class ExtensionScreen extends Component {
    constructor(props) {
      super(props);
    }

    onSuccessCallback = async (data) => {
      //navigate to billDetails page.
      this.props.app.nav.to("makePaymentRoutes");
    };

    render() {
      const { billData, payType } = this.props;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      return (
        <ScreenLayout scrollEnabled>
          <ScrollView
            style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu, styles.billDetailsScreen]}>
            <View style={styles.declinedContainer}>
              {/* <View style={[commonStyles.marginTop15]}>
            <Text style={[commonStyles.heading5, commonFontStyles.workSans, commonStyles.fontWeight500]}>
              {this.props.app.nav.route.label}{" "}
            </Text>
          </View> */}
              <BillerDetails customStyles={commonStyles.padding0} billData={billData} />
              <ExtensionRequestPopUp billData={billData} onSuccessCallback={this.onSuccessCallback} />
              <BottomPaySection
                threadId={billData.threadId}
                activePage={payType}
                isDisabled={BILL_STATUS[billData.status]}
                extraOpt={billData.rtpMessageType}
              />
            </View>
            {isMobileDevice ? null : (
              <View style={commonStyles.copyRightSection}>
                <CopyRight />
              </View>
            )}
          </ScrollView>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  declinedContainer: {
    backgroundColor: "#FFFFFF",
    padding: 16,
    position: "relative",
    margin: 16,
  },
  commentsAttachment: {},
  commentsIcon: {
    height: 44,
    width: 44,
    display: "block",
    backgroundSize: "contain",
    borderRadius: "100%",
  },
  radioButtonsOuter: {
    color: "red",
  },
});
