import React from "react";
import { StyleSheet, Touchable, Image } from "../core";

export default class Bookmarked extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { isBookmarked, onToggle } = this.props;
    let imgpath = isBookmarked ? require("../assets/images/bookmared_active.png") : require("../assets/images/bookmared_unactive.png");
    return (
      <Touchable style={styles.dFlex} onPress={onToggle}>
        <Image resizeMode="contain" style={[styles.bookedMark]} source={imgpath} />
      </Touchable>
    );
  }
}

const styles = StyleSheet.create({
  dFlex: {
    display: "flex",
  },
  bookedMark: {
    height: 22,
    width: 15,
    marginLeft: 16,
  },
});
