import React, { Component, PureComponent } from "react";
import R14, { View, StyleSheet, FileInputField, Button } from "../core";
import "react-dropzone-uploader/dist/styles.css";
import Dropzone from "react-dropzone-uploader";

export default R14.connect(
  class FileUploadComponent extends Component {
    constructor(props) {
      super(props);
      this.child = React.createRef();
    }

    shouldComponentUpdate() {
      return false;
    }
    getExtension(fname) {
      const { fileTypes } = this.props;
      if (!!fileTypes && fileTypes.includes(fname.toLowerCase())) {
        switch (fname.toUpperCase()) {
          case "PDF":
            return "PDF";
          case "JPEG":
            return "JPEG";
          case "JPG":
            return "JPG";
          case "PNG":
            return "PNG";
          default:
            this.props.app.dm.commonDomain.showToastr(
              "error",
              `Please attach bills in ${fileTypes
                .split(",")
                .map((item) => {
                  return item.split("/")[1];
                })
                .join(",")
                .toUpperCase()} format.`
            );
            return false;
        }
      } else {
        switch (fname.toUpperCase()) {
          case "CSV":
          case "DIF":
          case "ODS":
          case "OTS":
          case "TSV":
          case "XLM":
          case "XLS":
          case "XLSB":
          case "XLSM":
          case "XLSX":
          case "XLT":
          case "XLTM":
          case "XLTX":
            return "SDSH";
          case "XML":
            return "DXML";
          case "PDF":
            return "DPDF";
          case "DOC":
          case "DOCX":
          case "DOCM":
          case "DOTX":
          case "DOTM":
          case "DOCB":
            return "WORD";
          case "XSLT":
            return "XSLT";
          default:
            this.props.app.dm.commonDomain.showToastr(
              "error",
              " Invalid Format, Please attach the file in any one of the following format DPDF, DXML, SDSH, WORD, XSLT"
            );
            return false;
        }
      }
    }

    convertBytes(x) {
      const units = ["bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];
      let l = 0;
      let n = parseInt(x, 10) || 0;

      while (n >= 1024 && ++l) {
        n = n / 1024;
      }
      //include a decimal point and a tenths-place digit if presenting
      //less than ten of KB or greater units
      // return n.toFixed(n < 10 && l > 0 ? 2 : 0) + " " + units[l];
      return n.toFixed(2) + " " + units[l];
    }

    getBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onloadend = () => {
          let data = reader.result.split(",");
          let fileNameXt = this.getExtension(file.name.split(".").pop());
          if (!!fileNameXt) {
            resolve({
              filename: file.name,
              contentType: fileNameXt,
              content: data[1],
              size: this.convertBytes(file.size),
              actualsize: file.size,
              contentUri: data[0],
              // size: `${Math.round(file.size / 1024).toFixed(2)} ${["Bytes", "Kb", "Mb", "Gb", "Tb"][Math.floor(Math.log2(file.size) / 10)]}`,
            });
          } else {
            return false;
          }
        };
        reader.onerror = (err) => {
          reject(err);
        };
        reader.readAsDataURL(file);
      });
    }

    fileInputChange = async ({ meta, file }, status) => {
      const { onValueChange, fileTypes, maxFiles } = this.props;

      if (status === "done") {
        if (file.name.length > 35) {
          this.props.app.dm.commonDomain.showToastr("error", "Maximum allowed filename is 35 characters");
          return false;
        }
        let fileBase64Data = await this.getBase64(file);
        onValueChange(fileBase64Data, status);
        this.handleSubmit();
        return false;
      }
      // else if (status === "removed") {
      //   let fileBase64Data = await this.getBase64(file);
      //   onValueChange(fileBase64Data, status);
      //   return false;
      // }
      else if (status === "rejected_max_files") {
        this.props.app.dm.commonDomain.showToastr("error", `You can upload only ${maxFiles} bill(s)`);
        return false;
      } else if (status === "rejected_file_type") {
        this.props.app.dm.commonDomain.showToastr(
          "error",
          !!fileTypes
            ? `Please attach bills in ${fileTypes
                .split(",")
                .map((item) => {
                  return item.split("/")[1];
                })
                .join(",")
                .toUpperCase()} format.`
            : "Invaild Format"
        );
        return false;
      }
    };

    handleSubmit = () => {
      this.child.files.filter((f) => ["headers_received", "done"].includes(f.remove()));
    };

    render() {
      const { maxFiles, fileTypes, labelText, attachmentFieldOuterCustomHeight } = this.props;
      return (
        <View style={[styles.attachmentFieldOuter, attachmentFieldOuterCustomHeight]}>
          <Dropzone
            ref={(instance) => {
              this.child = instance;
            }}
            onChangeStatus={this.fileInputChange}
            accept={!!fileTypes ? fileTypes : "application/pdf"}
            inputContent={!!labelText ? labelText : ""}
            canCancel={false}
            maxFiles={!!maxFiles ? maxFiles : 1}
            multiple={maxFiles > 1 ? true : false}
          />
        </View>
      );
    }
  }
);
const styles = StyleSheet.create({
  attachmentFieldOuter: {
    width: "100%",
    padding: 0,
    // borderRadius: "100%",
    // background: "#cff2ff",
    overFlow: "hidden",
    margin: "0 auto",
    flex: 2,
    flexGrow: 2,
    flexBasis: "100%",
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          // width: "48%",
          // margin: "0 0px 0 16px",
        };
      }
    },
  },
  attachmentField: {
    zIndex: 99,
    height: 52,
    width: 52,
    padding: 0,
    borderRadius: "100%",
    borderColor: "rgba(255,255,255, 0.0)",
    background: "rgba(255,255,255, 0.0)",
  },
});
