import React from "react";
import R14, { View, Text, Image, StyleSheet } from "../../core";
import DocViewer, { DocViewerRenderers } from "react-doc-viewer";

export default R14.connect(
  class PDFViewerComponent extends React.Component {
    constructor(props) {
      super(props);
      this.state = {};
    }

    documentSource = (dataSourceType, dataSource) => {
      let source = "";
      if (dataSourceType === "pdf") {
        source = `data:application/pdf;base64,${dataSource}`;
      } else {
        source = dataSource;
      }
      return [{ uri: source }];
    };

    render() {
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      const { dataSourceType, dataSource, disableFileName } = this.props;
      return (
        <View>
          <DocViewer
            pluginRenderers={DocViewerRenderers}
            // className="my-doc-viewer-style"
            documents={this.documentSource(dataSourceType, dataSource)}
            style={isMobileDevice ? { width: "94vw", height: 400, margin: "0 auto" } : { width: "900px", height: 500, margin: "0 auto" }}
            config={{
              header: {
                disableHeader: disableFileName,
                disableFileName: disableFileName,
                retainURLParams: false,
              },
            }}></DocViewer>
        </View>
      );
    }
  }
);
