import React from "react";
import R14, { ScreenLayout, AsyncStorage, View, StyleSheet } from "../../core";
import BillItemsComponent from "../../components/PCH/BillItemsComponent";
import LeftMenuComponent from "../../components/LeftMenuComponent";
import { PCH_STATUS, USER_TYPE } from "../../constant/app.data.constant";

export default R14.connect(
  class PCHDashboardScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {};
      AsyncStorage.setItem("homeScreen", this.props.app.nav.route.name);
    }

    componentDidMount() {
      const { userType } = this.props;
      this.props.app.dm.dashboard.setState({
        selectedBlock: ["All"]
      });
      if (userType === USER_TYPE.PCH_PATIENT) {
        this.timerInstance = setInterval(async () => {
          await this.props.app.dm.dashboard.setCompiledBillList();
        }, 70000);
      }
    }

    componentWillUnmount() {
      if (this.timerInstance) {
        clearInterval(this.timerInstance);
      }
    }

    render() {
      // added code for responsiveness
      const { filterBills, headerSearchList } = this.props.app.dm.dashboard.state;
      const { isSearchOpen } = this.props.app.dm.userSearchDomain.state;
      let withBookmarked = [];
      if (isSearchOpen) {
        withBookmarked = headerSearchList;
      } else {
        withBookmarked = filterBills;
      }
      return (
        <ScreenLayout scrollEnabled>
          <View style={styles.dashboardHolder}>
            <BillItemsComponent
              userType={this.props.userType}
              isHeader={true}
              filterBills={withBookmarked}
              screenName="DashboardScreen"
              defaultBlock={PCH_STATUS[0].label}
            />
          </View>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);
const styles = StyleSheet.create({
  dashboardHolder: {
    padding: "0px",
  },
});
