import React, { Component } from "react";
import R14, { StyleSheet, ScreenLayout, Text, View, Button, ScrollView, AsyncStorage } from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import SubmittedComponent from "../../components/PCH/SubmittedComponent";
import DateUtils from "../../utils/DateUtils";
import CommonUtility from "../../utils/CommonUtility";
import { USER_TYPE } from "../../constant/app.data.constant";
import LeftMenuComponent from "../../components/LeftMenuComponent";
import CopyRight from "../../components/CopyRight";

export default R14.connect(
  class SubmittedReceiptScreen extends Component {
    constructor(props) {
      super(props);
    }

    render() {
      const userLoginDetail = this.props.userLoginDetails;
      let userType = !!userLoginDetail ? userLoginDetail.userType : null;
      let message = "Bill uploaded successfully.";
      if (userType == USER_TYPE.PCH_PROVIDER) message = "Bill uploaded successfully.";
      else if (userType == USER_TYPE.PCH_PATIENT) message = "Your claim submitted successfully.";
      let uploadBill = this.props.bills;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      return (
        <ScreenLayout scrollEnabled style={styles.billDetailsWrapper}>
          <ScrollView style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu]}>
            <View style={[styles.pageTitle]}>
              <Text style={[commonStyles.heading5, commonFontStyles.workSans, commonStyles.fontWeight500]}>
                {userType == USER_TYPE.PCH_PROVIDER ? "Bill Details" : this.props.app.nav.route.label}{" "}
              </Text>
            </View>
            <View
              style={[
                // commonStyles.dFlex,
                // commonStyles.flexDirectionColumn,
                // commonStyles.flexGrow1,
                commonStyles.marginBottom15,
                styles.billDetailsScreen,
              ]}>
              {!!uploadBill &&
              !!uploadBill.pchInsuranceDetail &&
              !!uploadBill.pchInsuranceDetail.name &&
              uploadBill.pchInsuranceDetail.name.length > 0 ? (
                <View style={[commonStyles.dFlex, styles.customflexDirection]}>
                  <View style={[commonStyles.flex1, styles.leftInputHolder, styles.detailRow]}>
                    <View style={[]}>
                      <Text
                        style={[
                          commonStyles.dBlock,
                          commonStyles.fontWeight600,
                          commonStyles.heading6,
                          commonFontStyles.workSans,
                          commonStyles.marginBottom5,
                        ]}>
                        Insurance
                      </Text>
                    </View>
                    <View>
                      <Text style={[commonStyles.dBlock, commonStyles.heading6, commonFontStyles.workSans, styles.userDetailsVla]}>
                        {!!uploadBill && !!uploadBill.pchInsuranceDetail ? uploadBill.pchInsuranceDetail.name : ""}
                      </Text>
                    </View>
                  </View>
                  <View style={[commonStyles.flex1, styles.rightInputHolder, styles.detailRow]}>
                    <View style={[]}>
                      <Text
                        style={[
                          commonStyles.dBlock,
                          commonStyles.fontWeight600,
                          commonStyles.heading6,
                          commonFontStyles.workSans,
                          commonStyles.marginBottom5,
                        ]}>
                        Group#
                      </Text>
                    </View>
                    <View>
                      <Text style={[commonStyles.dBlock, commonStyles.heading6, commonFontStyles.workSans, styles.userDetailsVla]}>
                        {!!uploadBill && !!uploadBill.pchInsuranceDetail ? uploadBill.pchInsuranceDetail.groupNumber : ""}
                      </Text>
                    </View>
                  </View>
                </View>
              ) : null}
              {!!uploadBill.userProfile && uploadBill.userProfile.firstName ? (
                <View style={[commonStyles.dFlex, styles.customflexDirection]}>
                  <View style={[commonStyles.flex1, styles.leftInputHolder, styles.detailRow]}>
                    <View style={[]}>
                      <Text
                        style={[
                          commonStyles.dBlock,
                          commonStyles.fontWeight600,
                          commonStyles.heading6,
                          commonFontStyles.workSans,
                          commonStyles.marginBottom5,
                        ]}>
                        Practitioner
                      </Text>
                    </View>
                    <View>
                      <Text style={[commonStyles.dBlock, commonStyles.heading6, commonFontStyles.workSans, styles.userDetailsVla]}>
                        {!!uploadBill.userProfile && uploadBill.userProfile.firstName ? uploadBill.userProfile.firstName : ""}
                      </Text>
                    </View>
                  </View>
                  <View style={[commonStyles.flex1, styles.rightInputHolder, styles.detailRow]}>
                    <View style={[]}>
                      <Text
                        style={[
                          commonStyles.dBlock,
                          commonStyles.fontWeight600,
                          commonStyles.heading6,
                          commonFontStyles.workSans,
                          commonStyles.marginBottom5,
                        ]}>
                        Provider
                      </Text>
                    </View>
                    <View>
                      <Text style={[commonStyles.dBlock, commonStyles.heading6, commonFontStyles.workSans, styles.userDetailsVla]}>
                        {!!uploadBill.pchDoctorDetail && uploadBill.pchDoctorDetail.DoctorName ? uploadBill.pchDoctorDetail.DoctorName : ""}
                      </Text>
                    </View>
                  </View>
                </View>
              ) : null}

              <View style={[commonStyles.dFlex, styles.customflexDirection]}>
                <View style={[commonStyles.flex1, styles.leftInputHolder, styles.detailRow]}>
                  <View style={[]}>
                    <Text
                      style={[
                        commonStyles.dBlock,
                        commonStyles.fontWeight600,
                        commonStyles.heading6,
                        commonFontStyles.workSans,
                        commonStyles.marginBottom5,
                      ]}>
                      Uploaded On
                    </Text>
                  </View>
                  <View>
                    <Text style={[commonStyles.dBlock, commonStyles.heading6, commonFontStyles.workSans, styles.userDetailsVla]}>
                      {DateUtils.dateAndTimeWithAmPm(uploadBill.createdAt, "PCH")}
                    </Text>
                  </View>
                </View>
                <View style={[commonStyles.flex1, styles.rightInputHolder, styles.detailRow, commonStyles.border0]}>
                  <View style={[styles.displayNone]}>
                    <Text
                      style={[
                        commonStyles.dBlock,
                        commonStyles.fontWeight600,
                        commonStyles.heading6,
                        commonFontStyles.workSans,
                        commonStyles.marginBottom5,
                      ]}>
                      Patient Phone
                    </Text>
                  </View>
                  <View style={[styles.displayNone]}>
                    <Text style={[commonStyles.dBlock, commonStyles.heading6, commonFontStyles.workSans, styles.userDetailsVla]}>
                      {CommonUtility.mobileNumberFormatting(uploadBill.mobileNumber)}
                    </Text>
                  </View>
                </View>
              </View>
              <SubmittedComponent message={message} customMargin={styles.customMargin} />
            </View>
            <CopyRight />
          </ScrollView>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  billDetailsWrapper: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          background: "#FFF",
        };
    },
  },
  pageTitle: {
    margin: 16,
  },
  billDetailsScreen: {
    // flex: 0,
    alignItems: "flex-end",
    background: "#fff",
    padding: 15,
    margin: "0 16px",
    screen: ({ width }) => {
      if (width <= 640)
        return {
          alignItems: "center",
          margin: 0,
        };
    },
  },
  redClr: {
    color: "#CC0000",
  },

  detailRow: {
    borderBottom: "1px solid rgb(218, 218, 218)",
    paddingBottom: 5,
    marginBottom: 15,
    flexBasis: "48%",
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          flexBasis: "100%",
          marginBottom: 10,
        };
      }
    },
  },
  customflexDirection: {
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          flexDirection: "column",
        };
      }
    },
  },
  mViewMarginBottom: {
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginBottom: 25,
          paddingTop: 0,
        };
      }
    },
  },
  leftInputHolder: {
    marginRight: 8,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginRight: 0,
        };
      }
    },
  },
  rightInputHolder: {
    marginLeft: 8,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginLeft: 0,
        };
      }
    },
  },
  customMargin: {
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginTop: 5,
        };
      }
    },
  },
  displayNone: {
    display: "none",
  },
});
