import React from "react";
import R14, { StyleSheet, Surface, View, ScreenLayout, Touchable, Image, Text } from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import WelcomeToPCH from "../../components/PCH/WelcomeToPCH";
import PCHSignupComponent from "../../components/PCH/PCHSignupComponent";
const xbpLogo = require("../../assets/images/XBPlogo.svg");
const back_icon = require("../../assets/images/back_icon.png");
const mainImage = require("../../assets/images/mainImage.png");

export default R14.connect(
  class PCHSignupScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        enableErrorStyle: false,
        defaultDomain: "#xbp.exela.global",
        // socialMediaRegistrationEnabled: this.props.registrationType.type !== "new" ? true : false,
        currentContextProfileId: "",
        isOpenRegistrationHelpTooltip: false,
      };
      this.form = null;
    }

    componentDidMount() {
      let unbounceEmail = this.props.app.route._location.search;
      if (!!unbounceEmail && unbounceEmail.indexOf("?UnbEmail=") > -1) {
        unbounceEmail = decodeURIComponent(unbounceEmail.split("=").pop());
        if (!!unbounceEmail && unbounceEmail.length > 0)
          this.props.app.dm.userSession.setState({
            pchRegistrationTypeProvider: true,
            pchRegistrationTypePatient: false,
            unbounceEmail: unbounceEmail,
          });
      }
    }

    navToWelcomeToPch = () => {
      let unbounceEmail = this.props.app.route._location.search;
      if (!!unbounceEmail && unbounceEmail.indexOf("?UnbEmail=") > -1) return false;
      else
        this.props.app.dm.userSession.setState({
          pchRegistrationTypeProvider: false,
          pchRegistrationTypePatient: false,
        });
    };

    navPchLogin = () => {
      // let returnUrl = await AsyncStorage.getItem("returnUrl")
      let returnUrl = this.props.app.route._location.search;
      if (!!returnUrl && returnUrl.length > 0) document.location = `${window.location.origin}/pchlite/login${returnUrl}`;
      else this.props.app.nav.to("pchLogin");
    };

    render() {
      const { pchRegistrationTypeProvider, pchRegistrationTypePatient } = this.props.app.dm.userSession.state;
      return (
        <ScreenLayout scrollEnabled>
          <View style={styles.mainLogin}>
            <View style={[styles.bgContainer, styles.leftSection]}>
              <Image source={mainImage} style={[styles.backgroundImage]} />
            </View>
            <Surface style={styles.rightSection}>
              {/* <View style={[styles.header]}>
                <PublicRoutesHeader screenName={"Registration"} />
              </View> */}
              <View style={[commonStyles.mView]}>
                <View style={[styles.logoImageContainer]}>
                  {!!pchRegistrationTypeProvider || !!pchRegistrationTypePatient ? (
                    <Touchable onPress={this.navToWelcomeToPch}>
                      <Image resizeMode="contain" style={[commonStyles.icon24]} source={back_icon} />
                    </Touchable>
                  ) : (
                    <Touchable onPress={this.navPchLogin}>
                      <Image resizeMode="contain" style={[commonStyles.icon24]} source={back_icon} />
                    </Touchable>
                  )}
                  <View style={[commonStyles.margin0Auto, commonStyles.justifyContentCenter]}>
                    <Image resizeMode="contain" style={[styles.icon55150]} source={xbpLogo} />
                  </View>
                  {/* <View>
                    <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500]}>Registration</Text>
                  </View> */}
                </View>
              </View>
              <View style={[styles.loginHeader, commonStyles.justifyContentCenter]}>
                {!!pchRegistrationTypeProvider || !!pchRegistrationTypePatient ? (
                  <Touchable onPress={this.navToWelcomeToPch}>
                    <Image resizeMode="contain" style={[commonStyles.icon24]} source={back_icon} />
                  </Touchable>
                ) : (
                  <Touchable onPress={this.navPchLogin}>
                    <Image resizeMode="contain" style={[commonStyles.icon24]} source={back_icon} />
                  </Touchable>
                )}
                <View style={commonStyles.margin0Auto}>
                  <Image resizeMode="contain" style={[styles.logoImage]} source={xbpLogo} />
                </View>
              </View>
              {pchRegistrationTypeProvider || pchRegistrationTypePatient ? (
                <PCHSignupComponent
                  navToWelcomeToPch={this.navToWelcomeToPch}
                  registrationType={this.props.registrationType.type}
                  navPchLogin={this.navPchLogin}
                  userLoginDetail={this.props.userLoginDetail}
                />
              ) : (
                <WelcomeToPCH registrationType={this.props.registrationType.type} />
              )}

              <View style={[styles.mrgTopAuto, commonStyles.marginBottom10, styles.copyRight]}>
                <Text style={[commonFontStyles.workSans, commonStyles.smallHeading]}>
                  © {new Date().getFullYear()} Exela Technologies, All rights reserved.
                </Text>
              </View>
            </Surface>
          </View>
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  loginHeader: {
    display: "flex",
    padding: "8px 0 10px 0",
    textAlign: "center",
    alignItems: "center",
    screen: ({ width, height }) => {
      if (width <= 991) {
        return {
          display: "none",
        };
      }
    },
  },
  mainLogin: {
    display: "flex",
    background: "#fff",
    flex: 1,
    flexDirection: "row",
  },
  leftSection: {
    flex: 2,
    display: "flex",
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          display: "none",
        };
      }
    },
  },
  rightSection: {
    flex: 1,
    width: 550,
    maxWidth: 550,
    // justifyContent: "center",
    position: "relative",
    paddingBottom: 0,

    screen: ({ width, height }) => {
      if (width === 540 && height === 720) {
        return {
          height: "auto",
        };
      }

      if (width <= 767) {
        return {
          width: "auto",
          maxWidth: "unset",
        };
      }
      if (width >= 320 && width <= 359) {
        return {
          width: 300,
          height: "auto",
        };
      }
      //  only for iPhone X start
      if (width <= 375 && height === 812) {
        return {
          height: "100vh",
        };
      } else if (width <= 375) {
        return {
          height: "auto",
        };
      }
      //  only for iPhone X end
      if (width <= 410) {
        return {
          height: "auto",
        };
      }
      //  only for iPhone 6/7/8 plus start
      if (width <= 414 && height === 736) {
        return {
          maxWidth: 415,
          padding: "0px 16px 16px",
          height: "100vh",
        };
      }
      //  only for iPhone 6/7/8 plus End

      if (width >= 410 && width <= 411) {
        return {
          padding: "0px 16px 16px",
          height: "100vh",
        };
      }
      if (width <= 991) {
        return {
          // paddingTop: 80,
          margin: "0 auto",
          maxWidth: "100%",
          minWidth: "100%",
          boxShadow: "none",
          height: "100vh",
        };
      }
    },
  },
  backgroundImage: {
    width: "100%",
    height: "100%",
    display: "block",
    backgroundSize: "cover",
    backgroundPosition: "center left",
    screen: ({ width, height }) => {
      if (width === 1024 && height === 768)
        return {
          backgroundPosition: "-65px",
        };
    },
  },
  bgContainer: {
    width: "100%",
    display: "block",
    height: "100vh",
    screen: ({ width }) => {
      if (width <= 768)
        return {
          display: "none",
          width: 0,
        };
    },
  },
  registerButton: {
    width: "100%",
  },
  fNameField: {
    width: "100%",
  },
  lNameField: {
    width: "100%",
  },
  uNameField: {
    width: "100%",
  },
  mNumberField: {
    width: "100%",
  },
  emailField: {
    width: "100%",
  },
  passwordField: {
    width: "100%",
  },
  cPasswordField: {
    width: "100%",
  },
  loginLogo: {
    width: "100%",
    height: 90,
    marginBottom: 90,
  },
  rememberMeText: {
    position: "relative",
    top: -28,
    left: 30,
  },
  addBank: {
    display: "flex",
    flexDirection: "row",
    width: "100%",
    marginTop: 30,
    marginBottom: 30,
    color: "#37c2ef",
    fontSize: "14",
  },
  addBankSection: {
    textAlign: "center",
    color: "#0B4A87",
    width: "100%",
  },
  loginControls: {
    width: "100%",
  },
  createAccount: {
    color: "#0B4A87",
    fontSize: "14",
    textTransform: "none",
    marginLeft: 5,
  },
  twoCol: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  hideField: {
    display: "none",
  },
  redHelperText: {
    color: "#B00020",
    position: "static",
  },
  greenHelperText: {
    color: "green",
  },
  logoImageContainer: {
    display: "flex",
    width: "100%",
    // marginBottom: 15,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "flex",
          width: "auto",
          alignItems: "center",
          margin: "-16px -15px 15px -15px",
          boxShadow: "0px 0px 6px #00000033",
          padding: 16,
        };
    },
  },
  logoImage: {
    height: 55,
    width: 150,
    marginBottom: 50,
    marginTop: 50,
    margin: "auto",
    display: "block",
    screen: ({ width }) => {
      if (width <= 640)
        return {
          //	height: 48,
          //	width: 144,
        };
    },
  },
  form: {
    zIndex: 1,
    padding: "16px",
    flexGrow: 0,
    screen: ({ width, height }) => {
      if (width <= 414 && height === 736) {
        return {
          padding: "20px 0 0",
        };
      } else if (width <= 991)
        return {
          padding: "5px 0 0",
          margin: "0 0 20px",
        };
    },
  },
  countryCode: {
    maxWidth: 70,
  },
  customHelperTextStyle: {
    position: "static",
    // marginLeft:12,
    // marginTop:0,
    // marginBottom:0,
    // marginRight:0,
    ...StyleSheet.margin(0, 0, 0, 12),
    display: "block",
  },
  customHelperTextStyleMobile: {
    position: "static",
    display: "block",
    ...StyleSheet.margin(0, 0, 0, -58),
  },
  copyRight: {
    textAlign: "center",
    marginTop: "auto",
  },
  helpIcon: {
    position: "absolute",
    right: 0,
    top: 0,
  },
  registrationHelpTooltipHolder: {
    position: "relative",
  },
  registrationHelpTooltip: {
    position: "absolute",
    background: "#333",
    color: "#fff",
    top: 45,
    zIndex: 999,
    padding: 10,
    borderRadius: 5,
  },
  registrationHelpTooltipArrow: {
    width: 10,
    height: 10,
    display: "block",
    position: "absolute",
    background: "#333",
    top: -4,
    right: 17,
    transform: "rotate(45deg);",
  },
  logoIcon: {
    width: 160,
    height: 64,
    display: "block",
    backgroundSize: "contain",
  },
  icon55150: {
    height: 55,
    width: 150,
    display: "block",
    backgroundSize: "contain",
  },
});
