import React from "react";
import { View, Text, StyleSheet, Touchable } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
export default class ToggleRadioBtn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      on: false,
    };
  }
  // toggleSwitch = () => {
  //   this.setState({
  //     on: !this.state.on,
  //   });
  // };

  render() {
    const { toggleSwitch, value, on, disable, toggleRadioBtnSize } = this.props;
    //const { on } = this.state;
    return (
      <View
        style={[
          commonStyles.dFlex,
          styles.toggleSwitchBtnouter,
          commonStyles.alignItemsCenter,
          on ? commonStyles.skyBlueBackground : commonStyles.exLightGray,
          toggleRadioBtnSize,
        ]}>
        <Touchable
          onPress={() => toggleSwitch(value)}
          style={[on ? commonStyles.marLAuto : commonStyles.marRAuto, styles.switchBtn, disable]}></Touchable>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  toggleSwitchBtnouter: {
    width: 40,
    height: 24,
    background: "#e6e6e6",
    borderRadius: 100,
    transition: "all 0.5s ease",
    padding: "0 1px",
  },
  switchBtn: {
    width: 22,
    height: 22,
    borderRadius: "100%",
    border: "1px solid #fff",
    background: "#fff",
    transition: "all 0.5s ease",
    display: "block",
  },
  deactivate: {
    display: "none",
  },
  ascendingActive: {
    marginRight: "auto",
    background: "#37c2ef",
  },
  descendingActive: {
    marginLeft: "auto",
    background: "#37c2ef",
  },
  normal: {
    minWidth: 20,
    height: 20,
    borderRadius: "100%",
    border: "1px solid #37c2ef",
    background: "#fff",
  },
  visibilityHidden: {
    opacity: 0,
  },
});
