import R14, { AsyncStorage } from "../core";
import { GET_MSG_FROM_THREAD, GET_BLOCK_UNBLOCK_USERS } from "../constant/app.constant";
import CommonUtility from "../utils/CommonUtility";
import DateUtils from "../utils/DateUtils";

export default class BillDetailsDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {
      msgPayload: {},
      bankTabActive: "Card",
    };
  }

  async getMessagesByThread(threadId) {
    try {
      return await this.dm.rest.get(`${GET_MSG_FROM_THREAD}`, {
        "thread-id": threadId,
      });
    } catch (err) {
      console.log("Get Message :::: ", err);
    }
  }

  async createCcPayload(sessionToken, bancHubPayload) {
    let user = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let payload = {
      header: {
        username: "EXELARTPPay",
        session_token: sessionToken,
      },

      carddetails: {
        card_number: bancHubPayload.cardNumber ? bancHubPayload.cardNumber : "4111 1111 1111 1111",
        card_present: 1,
        card_type: "VISA",
        cardholder_name: bancHubPayload.cardHolderName ? bancHubPayload.cardHolderName : "Payer",
        cvv2: bancHubPayload.validCVV ? parseInt(bancHubPayload.validCVV) : "345",
        expire_month: bancHubPayload.monthExpDate ? parseInt(bancHubPayload.monthExpDate) : 1,
        expire_year: bancHubPayload.yearExpDate ? parseInt(bancHubPayload.yearExpDate) : 2022,
        payment_cardtype: 0,
        payment_intent: 0,
      },
      transactiondetails: {
        total_amount: bancHubPayload.amount,
        currency: "USD",
        client_referid: user.pid,
        purchase_level: 1,
        transaction_date: new Date(),
        transaction_description: "bill payment",
      },
      billingaddress: {
        billing_username: user.pid,
        user_zip: user.postCode,
        user_city: user.town,
        user_state: user.country,
        user_address1: user.addressLine1,
        user_address2: user.addressLine2,
      },
    };
    return payload;
  }

  async getTransactionId(bancHubPayload) {
    let finalResult;

    let cardTokenPaymentPayload = await this.createCardTokenPaymentPayload(bancHubPayload);
    let cardTokenPaymentResult = await this.cardTokenPayment(cardTokenPaymentPayload);
    if (cardTokenPaymentResult.CardTokenPaymentResult.status === "SUCCESS" || cardTokenPaymentResult.CardTokenPaymentResult.status === "SUBMITTED") {
      finalResult = {
        success: true,
        transactionId: cardTokenPaymentResult.CardTokenPaymentResult.transaction_id,
      };
      return finalResult;
    } else {
      finalResult = {
        success: false,
        error: cardTokenPaymentResult.CardTokenPaymentResult.error_desc,
      };
      return finalResult;
    }
  }

  getPaymentPayload(lastLeaf = null) {
    let cDate = DateUtils.isoFormateDate();
    cDate = `${cDate.substr(0, cDate.length - 5)}Z`;

    let { billData } = lastLeaf;
    let payload = {
      envelope: {
        messageHeader: {
          ...lastLeaf.envelope.messageHeader,
          senderName: billData.recipientPid.split("#")[0],
          senderPid: billData.recipientPid,
          recipientPid: billData.senderPid,
          messageId: CommonUtility.generate20Character(),
          messageDateTime: cDate,
        },
        threadHeader: {
          ...lastLeaf.envelope.threadHeader,
        },
        messageBody: {
          endToEndIdentification: billData.endToEndIdentification,
          amount: { instructedAmount: billData.amount, currency: billData.currencyType },
          transactionId: billData.transactionId,
          messageType: lastLeaf.messageType,
          subMessageType: "NoteToBiller",
          requestedExecutionDate: cDate,
          acceptanceDateTime: cDate,
          transactionStatus: "ACSC",
          additionalInformation: billData.additionalInformation || null,
        },
      },
      messageMeta: {
        ...lastLeaf.messageMeta,
        deliveryPath: {
          ...lastLeaf.messageMeta.deliveryPath,
          timestamp: cDate,
        },
      },
      attachments: null,
    };

    return !!lastLeaf ? payload : {};
  }

  async createGetCardTokenPayload(sessionToken, bancHubPayload) {
    let payload = {
      header: {
        username: "EXELARTPPay",
        session_token: sessionToken,
      },

      carddetails: {
        card_number: bancHubPayload.cardNumber ? bancHubPayload.cardNumber : "",
        cardholder_name: bancHubPayload.cardHolderName ? bancHubPayload.cardHolderName : "",
        expire_month: bancHubPayload.monthExpDate ? parseInt(bancHubPayload.monthExpDate) : 0,
        expire_year: bancHubPayload.yearExpDate ? parseInt(bancHubPayload.yearExpDate) : 0,
        payment_cardtype: 0,
      },
    };
    return payload;
  }

  async createCardTokenPaymentPayload(bancHubPayload) {
    let user = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let payload = {
      tokendetails: {
        cardtoken: bancHubPayload.cardToken,
        card_present: 1,
        payment_intent: 0,
      },
      transactiondetails: {
        total_amount: bancHubPayload.amount,
        currency: "USD",
        client_referid: bancHubPayload.client_referid,
        purchase_level: 1,
        transaction_date: new Date(),
        transaction_description: "bill payment",
        cvv2: bancHubPayload.cvv ? bancHubPayload.cvv : "",
      },
      billingaddress: {
        billing_username: user.pid,
        user_zip: user.postCode,
        user_city: user.town,
        user_state: user.country,
        user_address1: user.addressLine1,
        user_address2: user.addressLine2,
      },
    };
    return payload;
  }
  async postMessageForPayment(payload) {
    try {
      return await this.dm.rest.post(`${GET_MSG_FROM_THREAD}`, payload);
    } catch (err) {
      console.log("Payment Message :::: ", err);
    }
  }

  async getNotePayload(lastLeaf = null) {
    let { billData, attac } = lastLeaf;
    let attachment = [];
    const additionalUserInfo = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};

    if (lastLeaf.attac) {
      attachment.push({
        filename: attac.filename,
        contentType: attac.contentType,
        content: attac.content,
      });
    }
    let payload = {
      envelope: {
        messageHeader: {
          ...lastLeaf.envelope.messageHeader,
          senderName:
            additionalUserInfo.firstName && additionalUserInfo.lastName
              ? `${additionalUserInfo.firstName} ${additionalUserInfo.lastName}`
              : `${additionalUserInfo.firstName}`,
          //billData.recipientPid.split("#")[0],
          senderPid: billData.recipientPid,
          recipientPid: billData.senderPid,
          messageId: CommonUtility.generate20Character(),
          messageDateTime: DateUtils.isoFormateDate(),
        },
        threadHeader: {
          ...lastLeaf.envelope.threadHeader,
          messageDateTime: DateUtils.isoFormateDate(),
        },
        messageBody: {
          endToEndIdentification: lastLeaf.envelope.messageBody.endToEndIdentification,
          messageType: billData.messageType,
          subMessageType: "NoteToBiller",
          note: billData.note,
        },
      },
      messageMeta: {
        ...lastLeaf.messageMeta,
        deliveryPath: {
          ...lastLeaf.messageMeta.deliveryPath,
          timestamp: DateUtils.isoFormateDate(),
        },
      },
      attachments: !!attachment.length ? attachment : null,
    };

    return !!lastLeaf ? payload : {};
  }

  getPaymentMessagePayload(data) {
    const { threadHeader, messageHeader, messageBody } = data.envelope;
    return {
      threadId: threadHeader.threadId,
      subject: threadHeader.subject,
      messageDateTime: messageHeader.messageDateTime,
      recipientPid: messageHeader.recipientPid,
      senderPid: messageHeader.senderPid,
      messageType: messageBody.messageType,
      amount: messageBody.amount.instructedAmount,
      billerName: messageBody.creditor.name,
    };
  }

  async getCardToken(payload) {
    try {
      let res = await this.api.qry(
        `
      query AddNewCard($input: addNewCardInput!) {
        addNewCard(input: $input){
          GetCardTokenResult
          {
            display_number
            error_desc
            status
            token_id
          }
        }
      }`,
        {
          input: payload,
        }
      );
      return res.data.addNewCard;
    } catch (err) {
      return err;
    }
  }

  async cardTokenPayment(payload) {
    try {
      let res = await this.api.qry(
        `
        query CardPayment($input: CardPaymentInput!) {
          cardPayment(input: $input){
            CardTokenPaymentResult
            {
              transaction_id
              error_desc
              status 
            }
        }
      }`,
        {
          input: payload,
        }
      );
      return res.data.cardPayment;
    } catch (err) {
      return err;
    }
  }

  async saveFullPayment(input, fields = "uid") {
    try {
      let res = await this.api.mutate(
        `
      mutation CreatePaymentMessage($input: CreatePaymentMessageInput) {
        createPaymentMessage(input: $input){
          paymentMessage {
            ${fields}
          }
          success
        }
      }`,
        { input }
      );
      return res.data.paymentMessage;
    } catch (error) {
      return error;
    }
  }

  async savePayerCardDetails(input, fields = "uid payerID paymentType cardType token displayNumber status cardHolderName ") {
    try {
      let res = await this.api.mutate(
        `
        mutation CreatePayerCardDetails($input: CreatePayerCardDetailsInput){
          createPayerCardDetails(input: $input){
            payerCardDetails{
              ${fields}
            }
            success
          }
        }`,
        { input }
      );
      return res.data.createPayerCardDetails.payerCardDetails;
    } catch (error) {
      return error;
    }
  }
  async updatePayerCardDetails(input, fields = "uid payerID paymentType cardType token displayNumber status isSetDefault cardHolderName") {
    try {
      let res = await this.api.mutate(
        `
        mutation UpdatePayerCardDetails($input: UpdatePayerCardDetailsInput){
          updatePayerCardDetails(input: $input){
            payerCardDetails{
              ${fields}
            }
            success
          }
        }`,
        { input }
      );
      return res.data.updatePayerCardDetails.payerCardDetails;
    } catch (error) {
      return error;
    }
  }

  async getPayerCardDetailsList(fields = "uid payerID paymentType cardType token displayNumber status isSetDefault cardHolderName") {
    let user = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    try {
      let res = await this.api.qry(
        `
      query PayerCardDetailss($filter: PayerCardDetailsFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
        payerCardDetailss(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          totalCount @include(if: true)
          nodes {
            ${fields}
          }
        }
      }`,
        {
          filter: {
            payerID: { eq: user.pid },
            status: { eq: "Enabled" },
          },
        }
      );
      return res.data.payerCardDetailss.nodes;
    } catch (err) {
      return err;
    }
  }

  async deleteBookmark(uid, fields = "uid threadId") {
    try {
      let res = await this.api.mutate(
        `
      mutation DeleteBookmark($uid: ID!) {
        deleteBookmark(uid: $uid){
          bookmark {
            ${fields}
          }
          success
        }
      }`,
        { uid }
      );
      return res.data.bookmark;
    } catch (error) {
      return error;
    }
  }

  async getBookmark(data) {
    const { threadId } = data;

    try {
      let res = await this.api.qry(
        `
      query Bookmark($filter: BookmarkFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
        bookmarks(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          totalCount @include(if: true)
          nodes {
            uid threadId
          }
        }
      }`,
        {
          filter: {
            threadId: { eq: threadId },
          },
          sort: [
            {
              field: "pid",
              order: "ASC",
            },
          ],
        }
      );
      return res.data.bookmarks.nodes;
    } catch (err) {
      return err;
    }
  }

  getDeclinedPayload(billData) {
    return {
      envelope: {
        threadHeader: billData.threadHeader,
        messageHeader: {
          senderName: billData.recipientPid.split("#")[0],
          senderPid: billData.recipientPid,
          recipientPid: billData.senderPid,
          messageId: CommonUtility.generate20Character(),
          messageDateTime: DateUtils.isoFormateDate(),
        },
        messageBody: {
          messageType: billData.messageType,
          subMessageType: "NoteToBiller",
          endToEndIdentification: billData.endToEndIdentification,
        },
      },
      messageMeta: billData.messageMeta,
      attachments: billData.attachments,
    };
  }

  getRequestExtPayload(billData) {
    // const billData = { ...JSON.parse(await AsyncStorage.getItem("billDetails")), ...data };
    return {
      envelope: {
        threadHeader: billData.threadHeader,
        messageHeader: {
          senderName: billData.recipientPid.split("#")[0],
          senderPid: billData.recipientPid,
          recipientPid: billData.senderPid,
          messageId: CommonUtility.generate20Character(),
          messageDateTime: DateUtils.isoFormateDate(),
        },
        messageBody: {
          messageType: billData.messageType,
          subMessageType: "NoteToBiller",
          endToEndIdentification: billData.endToEndIdentification,
          additionalInformation: billData.additionalInformation || null,
          requestedExtensionDate: billData.requestedExecutionDate || new Date(),
        },
      },
      messageMeta: billData.messageMeta,
      attachments: billData.attachments,
    };
  }

  async declinedNotePayload(data) {
    const billData = { ...JSON.parse(await AsyncStorage.getItem("billDetails")), ...data };
    return {
      envelope: {
        threadHeader: billData.threadHeader,
        messageHeader: {
          senderName: billData.recipientPid.split("#")[0],
          senderPid: billData.recipientPid,
          recipientPid: billData.senderPid,
          messageId: CommonUtility.generate20Character(),
          messageDateTime: DateUtils.isoFormateDate(),
        },
        messageBody: {
          messageType: billData.messageType,
          subMessageType: "NoteToBiller",
          endToEndIdentification: billData.endToEndIdentification,
          note: billData.note,
        },
      },
      messageMeta: billData.messageMeta,
      attachments: billData.attachments || null,
    };
  }

  async blockUnBlockBiller(billerpid, value) {
    try {
      return await this.dm.rest.put(`${GET_BLOCK_UNBLOCK_USERS}`, {}, { pid: billerpid, block: value });
    } catch (error) {
      console.log("Block unblock error ", error);
      return error;
    }
  }
  async getPayerAccountDetailsList(
    fields = "uid payerId accountType accountHolderName bankName accountNumber displayAccountNumber rtnNumber bicCode displayBicCode isSetDefault status"
  ) {
    let user = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    try {
      let res = await this.api.qry(
        `
      query PayerAccountDetailss($filter: PayerAccountDetailsFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
        payerAccountDetailss(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          totalCount @include(if: true)
          nodes {
            ${fields}
          }
        }
      }`,
        {
          filter: {
            payerId: { eq: user.pid },
            status: { eq: "Enabled" },
          },
        }
      );
      return res.data.payerAccountDetailss.nodes;
    } catch (err) {
      return err;
    }
  }
  async getAllPayerAccountDetailsList(
    fields = "uid payerId accountType accountHolderName bankName accountNumber displayAccountNumber rtnNumber bicCode displayBicCode isSetDefault status"
  ) {
    try {
      let res = await this.api.qry(
        `
      query PayerAccountDetailss($filter: PayerAccountDetailsFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
        payerAccountDetailss(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          totalCount @include(if: true)
          nodes {
            ${fields}
          }
        }
      }`
      );
      return res.data.payerAccountDetailss.nodes;
    } catch (err) {
      return err;
    }
  }
  async savePayerAccountDetails(
    input,
    fields = "uid payerId accountType accountHolderName bankName accountNumber displayAccountNumber rtnNumber bicCode displayBicCode isSetDefault status "
  ) {
    try {
      let res = await this.api.mutate(
        `
        mutation CreatePayerAccountDetails($input: CreatePayerAccountDetailsInput){
          createPayerAccountDetails(input: $input){
            payerAccountDetails{
              ${fields}
            }
            success
          }
        }`,
        { input }
      );
      return res.data.createPayerAccountDetails.payerAccountDetails;
    } catch (error) {
      return error;
    }
  }
  async updatePayerAccountDetails(
    input,
    fields = "uid payerId accountType accountHolderName bankName accountNumber displayAccountNumber rtnNumber bicCode displayBicCode isSetDefault status"
  ) {
    try {
      let res = await this.api.mutate(
        `
        mutation UpdatePayerAccountDetails($input: UpdatePayerAccountDetailsInput){
          updatePayerAccountDetails(input: $input){
            payerAccountDetails{
              ${fields}
            }
            success
          }
        }`,
        { input }
      );
      return res.data.updatePayerAccountDetails.payerAccountDetails;
    } catch (error) {
      return error;
    }
  }
  async fetchPayerAccountDetails(uid = null) {
    try {
      let res = uid
        ? await this.api.qry(
            `
      query FetchPayerAccountDetails($uid: ID!) {
        payerAccountDetails(uid: $uid){
        uid payerId accountType accountHolderName bankName accountNumber displayAccountNumber rtnNumber 
        bicCode displayBicCode isSetDefault status
        }
      }`,
            {
              uid: uid,
            }
          )
        : {};
      return {
        values: res.payerAccountDetails || {},
      };
    } catch (error) {
      return error;
    }
  }
  async checkBankAccountExist(accountNumber, payerId, uid) {
    let data = {
      accountNumber: accountNumber,
      payerId: payerId,
      uid: uid,
    };
    try {
      let res = await this.api.query(
        `
        query checkBankAccountExist($input: BankAccountInput!) {
          checkBankAccountExist(input: $input){       
                success msg              
            }            
        }`,
        {
          input: data,
        }
      );
      return res.data.checkBankAccountExist;
    } catch (error) {
      return error;
    }
  }
  
    async stripeCheckout(token) {
      try {
      let res = await this.api.mutate(`
              mutation StripeCheckout($input: CheckoutInput!) {
              stripeCheckout(input: $input){                        
                                  success
                                  transactionId
                                  error                                  
                              }                               
              }`,
              {
                  input: token
              }
          );
          return res.data.stripeCheckout;
      }
     catch (error) {
      return error;
    }
  }
}
