import React from "react";
import { View, Text, StyleSheet, Touchable } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
const ORD = {
  ascending: "ASC",
  normal: "NA",
  descending: "DESC",
};
export default class ToggleSwitch extends React.Component {
  constructor(props) {
    super(props);
    // this.sortType = null;
    this.state = {
      ASC: false,
      DESC: false,
      NA: false,
    };
  }

  componentDidMount() {
    const { seletedOpt } = this.props;
    this.setState({
      [seletedOpt]: true,
    });
  }

  toggleSwitch = async (st) => {
    // this.sortType = ORD[st];
    const { onSwiwtch } = this.props;
    await this.setState({
      ASC: false,
      DESC: false,
      NA: false,
    });
    this.setState({
      ...this.state,
      [st]: !this.state[st],
    });
    onSwiwtch(st);
  };

  render() {
    const { ASC, NA, DESC } = this.state;
    return (
      <View style={[styles.toggleSwitch]}>
        <View style={[commonStyles.dFlex]}>
          <View>
            <Text style={[commonStyles.heading6, commonStyles.fontWeight500]}>A to Z</Text>
          </View>
          <View style={[commonStyles.dFlex, styles.toggleSwitchBtnouter, commonStyles.justifyContentSpaceBetween]}>
            <Touchable
              onPress={() => this.toggleSwitch("ASC")}
              style={[ASC ? styles.deactivate : commonStyles.dBlock, styles.switchBtn, styles.visibilityHidden]}></Touchable>
            <Touchable
              onPress={() => this.toggleSwitch("NA")}
              style={[
                NA ? (ASC ? styles.ascendingActive : styles.descendingActive) : DESC ? styles.descendingActive : styles.ascendingActive,
                DESC || ASC ? styles.switchBtn : styles.normal,
              ]}></Touchable>
            <Touchable
              onPress={() => this.toggleSwitch("DESC")}
              style={[DESC ? styles.deactivate : commonStyles.dBlock, styles.switchBtn, styles.visibilityHidden]}></Touchable>
          </View>
          <View>
            <Text style={[commonStyles.heading6, commonStyles.fontWeight500]}>Z to A</Text>
          </View>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  toggleSwitch: {
    display: "block",
  },
  toggleSwitchBtnouter: {
    width: 50,
    height: 20,
    margin: "0 15px",
    background: "#e6e6e6",
    borderRadius: 100,
  },
  switchBtn: {
    width: 20,
    height: 20,
    borderRadius: "100%",
    border: "1px solid #37c2ef",
    background: "#37c2ef",
    transition: "all 0.5s ease",
  },
  deactivate: {
    display: "none",
  },
  ascendingActive: {
    marginRight: "auto",
    background: "#37c2ef",
  },
  descendingActive: {
    marginLeft: "auto",
    background: "#37c2ef",
  },
  normal: {
    minWidth: 20,
    height: 20,
    borderRadius: "100%",
    border: "1px solid #37c2ef",
    background: "#fff",
  },
  visibilityHidden: {
    opacity: 0,
  },
});
