import React from "react";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { StyleSheet, View, Text } from "../core";

export default class NoRecordsFound extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { message, noRecords } = this.props;
    return (
      <View style={[styles.nofoundmsg, styles.dFlex, noRecords]}>
        <Text style={[commonFontStyles.workSans]}>{message}</Text>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  dFlex: {
    display: "flex",
  },
  nofoundmsg: {
    background: "rgb(76, 197, 242)",
    padding: 10,
    borderRadius: 4,
    margin: 15,
  },
});
