import R14, { AsyncStorage } from "../core";
import AsyncStorageUtils from "../utils/AsyncStorageUtils";
import RSAencryption from "../utils/RSAencryption";

export default class UserRegistrationDomain extends R14.Domain {
   
  async updateUserDetails(
    userDetails,
    profileUpdate = true,
    fields = "uid pid payerId loginAttempt isLockout lockoutTime sessionString password changePasswordNow googleProfileID facebookProfileID amazonProfileID appleProfileID tokenKey userType firstName lastName email googleEmail facebookEmail amazonEmail appleEmail mobileNumber addressLine1 addressLine2 town state country postCode EIN profileImage"
  ) {
    try {
      let res = await this.api.mutate(
        `mutation UpdateUserDetails($input: UpdateUserLoginDetailInput){
          updateUserLoginDetail(input: $input){
            userLoginDetail {
                ${fields}
              }              
            }
          }`,
        { input: userDetails }
      );
      let details = res.data.updateUserLoginDetail.userLoginDetail
      // update user details on update into local storage
      if (profileUpdate)
      await AsyncStorage.setItem("profiles", JSON.stringify(details));
      return details;
    } catch (error) {
      return error;
    }
  }
  
  async signup(data, fields="uid payerId loginAttempt isLockout updatedAt lockoutTime sessionString password changePasswordNow googleProfileID facebookProfileID amazonProfileID appleProfileID tokenKey userType firstName lastName email googleEmail facebookEmail amazonEmail appleEmail mobileNumber addressLine1 addressLine2 town state country postCode EIN") {
    try {
        data.email = data.email.toLowerCase();
        data.password = RSAencryption.EnctyptString(data.password);
          // addressLine1: data.addressLine1 ? data.addressLine1 : "",
          // addressLine2: data.addressLine2 ? data.addressLine2 : "",
          // town: data.town ? data.town : "",
          // state: data.state ? data.state : "",
          // country: data.country ? data.country : "",
          // postCode: data.postCode ? data.postCode : "",
    let res = await this.api.mutate(
      `mutation Signup($input: SignupInput!) {
        signup(input: $input){
          access_token expires_in refresh_token scope token_type error success message 
          userLoginDetail {
             ${fields}
            }
          }
        }`,
        {
          input: data,
        });
      return res.data.signup;
    } catch (error) {
      return error;
    }
  }

  async bUserExistsByEmail(inputData, getByUserLoginDetailUid = false) {
    try {
      let options = {};
      if (getByUserLoginDetailUid) {
        options = {
          filter: {
            userLoginDetailUid: { eq: inputData },
          },
        };
      } else {
        options = {
          filter: {
            email: { eq: inputData.toLowerCase() },
          },
        };
      }

      let res = await this.api.qry(
        `query FindUserLoginDetail($filter: UserLoginDetailFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
          userLoginDetails(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
            nodes {
               payerId firstName lastName email mobileNumber addressLine1 addressLine2 town state country postCode EIN pid loginAttempt isLockout updatedAt lockoutTime sessionString password changePasswordNow tokenKey userType googleProfileID appleProfileID amazonProfileID uid
            }
          }
        }`,
        options
      );

      return res.data.userLoginDetails.nodes ? res.data.userLoginDetails.nodes : null;
    } catch (error) {
      return error;
    }
  }

  async sendCredentialsByMail(data) {
    let userInputBody = {
      payerId: data.payerId.trim(),
      password: data.password,
      email: data.email,
    };
    if (!!data.uid) userInputBody.uid = data.uid;
    if (!!data.deepLinkUrl) userInputBody.deepLinkUrl = data.deepLinkUrl;

    try {
      let res = await this.api.mutate(
        `query SendCredentialsByMail($input: SendMailInput!){
          sendCredentialsByMail(input: $input){
            success msg            
          }
        }`,
        {
          input: userInputBody,
        }
      );
      return res.data.sendCredentialsByMail;
    } catch (error) {
      return error;
    }
  }

  async sendForgotCredentialsByMail(inputs) {
    try {
      let res = await this.api.mutate(
        `query SendCredentialsByMail($input: SendMailInput!){
          sendCredentialsByMail(input: $input){
            success msg            
          }
        }`,
        {
          input: inputs,
        }
      );
      return res.data.sendCredentialsByMail;
    } catch (error) {
      return error;
    }
  }
}
