import R14, { AsyncStorage } from "../core";
import { THREAD_LIST, GET_MSG_FROM_THREAD } from "../constant/app.constant";
import DateUtils from "../utils/DateUtils";
import CommonUtility from "../utils/CommonUtility";
// import { MSG_TYPE } from "../constant/app.data.constant";

export default class SimpleMessageDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {
      simpleMessage: [],
      searchSimpleMessage: [],
      messageList: [],
    };
  }

  async getThreadList() {
    const { pid } = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let res = await this.dm.dashboard.getAllDateTimeHistory({ filter: { payerId: { eq: pid } } });
    let x = new Date(
      !!res.length && !!res[0].simpleMessageLastDateTime
        ? res[0].simpleMessageLastDateTime
        : `${new Date().getMonth() + 1}/01/${new Date().getFullYear()}`
    );
    // x.setDate(x.getDate() - 1);
    x.setHours(x.getHours() - 1);
    let fromDate = parseInt((x.valueOf() / 1000).toFixed());
    let nowDt = parseInt((new Date().valueOf() / 1000).toFixed());

    try {
      let threadsRes = await this.dm.rest.get(`${THREAD_LIST}?pagesize=20&from=${fromDate}&to=${nowDt}`);
      if (!!threadsRes && threadsRes.threads) {
        let threadMsgList = await this.simpleMsgThreadIdCollection(threadsRes);
        await this.compiledMessages(threadMsgList);
      }
    } catch (error) {
      console.log("Thread List ::: ", error);
    } finally {
      let payload = { payerId: pid };
      if (!!res.length) {
        let dt = `${new Date().getMonth() + 1}/01/${new Date().getFullYear()}`;
        payload.simpleMessageLastDateTime = !!res[0].simpleMessageLastDateTime ? new Date() : new Date(dt);
        payload.uid = res[0].uid;
        await this.dm.dashboard.updateMsgDateHistory(payload);
      }
    }
  }

  async getBillerFromThread(threadId) {
    try {
      let msgDetails = await this.dm.rest.get(`${GET_MSG_FROM_THREAD}`, { "thread-id": threadId });
      if (!!msgDetails) {
        let msg = msgDetails.messages.sort((a, b) =>         
              new Date(a.envelope.messageHeader.messageDateTime) - new Date(b.envelope.messageHeader.messageDateTime)
        )
        msgDetails.messages = msg
      }
      return msgDetails;
    } catch (error) {
      console.log("Messages  ::: ", error);
    }
  }

  async simpleMsgThreadIdCollection(res) {
    const { threads } = res;
    let allThreadApis = [];
    for (let el of threads) {
      try {
        let res = await this.getBillerFromThread(el["thread-id"]);
        if (!!res && res.messages[0].envelope.messageBody && res.messages[0].envelope.threadHeader.profile.toLowerCase() === "simplemessage") {
          allThreadApis.push(res);
        }
      } catch (error) {
        console.log("Thread message ::: ", error);
      }
    }
    return await allThreadApis;
  }

  addedNewMessage(data) {
    const { threadHeader, messageHeader, messageBody } = data.envelope;
    const { threadId, subject, originatorPid } = threadHeader;
    const { messageId, messageDateTime, recipientPid, senderPid, senderName } = messageHeader;
    let filterThreads = {
      threadHeader,
      subject,
      threadId,
      messageId,
      originatorPid,
      recipientPid,
      recipientName: recipientPid.split("#")[0],
      senderPid,
      senderName,
      messageDateTime: `${DateUtils.formatDate(messageDateTime)} ${DateUtils.formatAMPM(messageDateTime)}`,
      messageBody: messageBody.messageBody,
      shortName: CommonUtility.shortName(recipientPid.split("#")[0]),
      messageMeta: data.messageMeta,
      messageCount: 0,
    };
    return filterThreads;
  }

  async compiledMessages(arrayOfThreads) {
    const { pid } = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let compiledSM = [];
    try {
      for (const ele of arrayOfThreads) {
        const { messageBody } = ele.messages[0].envelope;
        if (!!messageBody) {
          const msgCount = ele.messages.filter((el) => !!el.envelope.messageBody && el.envelope.messageHeader.recipientPid === pid);
          compiledSM.push(await this.addUpdateDB({ ...ele.messages[ele.messages.length - 1], currentMsgCount: msgCount.length }, "listing"));
        }
      }
      return compiledSM;
    } catch (error) {
      console.error("Compiled Message::: ", error);
    }
  }

  async displaySimpleMessage() {
    const { pid } = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    await this.getThreadList();
    let res = await this.getSimpleMessages({ filter: { payerId: { like: pid } }, sort: [{ field: "lastMessageDate", order: "DESC" }] });
    this.setState({ simpleMessage: res, searchSimpleMessage: res });
    const { isSearchOpen, textInSearchBox, currentOpenScreen } = this.dm.userSearchDomain.state;
    if (isSearchOpen) this.dm.userSearchDomain.keyTypeSearch(textInSearchBox, currentOpenScreen);
  }

  async getBillerList() {
    const { pid } = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    let billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
    let filterRes = billerList
      .filter((el) => el.billerStatus === "accepted" && el.payerId.toLowerCase() === pid.toLowerCase() && !el.isBlocked)
      .sort(CommonUtility.ascDescOrder("billerName", "ASC"));
    let list = [];
    let billNameList = [];
    for (const el of filterRes) {
      list.push({
        label: el.billerId,
        value: el.billerName,
      });
      billNameList.push({
        label: el.billerName,
        value: el.billerId,
      });
    }
    return { list, billNameList };
  }

  async getMessagePayload(data) {
    const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    const pid = userLoginDetail.pid;
    const { subject, recipientPid, messageBody, attachments } = data;
    return {
      envelope: {
        threadHeader: {
          subject,
          profile: "simpleMessage",
          //threadId: `${recipientPid}-${pid}-${CommonUtility.generate20Character()}`,
          // originatorPid: recipientPid,
          // respondentPid: pid,
          threadId: `${pid}-${recipientPid}-${CommonUtility.generate20Character()}`,
          originatorPid: pid,
          respondentPid: recipientPid,
          threadDateTime: DateUtils.isoFormateDate(),
          threadPriority: "high",
        },
        messageHeader: {
          senderName:
          userLoginDetail.firstName && userLoginDetail.lastName
              ? `${userLoginDetail.firstName} ${userLoginDetail.lastName}`
              : pid.split("#")[0],
          senderPid: pid,
          recipientPid: recipientPid,
          messageId: CommonUtility.generate20Character(),
          messageDateTime: DateUtils.isoFormateDate(),
        },
        messageBody: {
          messageBody,
        },
      },
      messageMeta: null,
      attachments,
      messageMeta: {
        numMessages: 1,
        deliveryPath: {
          fromID: Date.now(),
          timestamp: DateUtils.isoFormateDate(),
          toID: Date.now(),
          signature: "",
        },
      },
    };
  }

  async postSimpleMessage(payload) {
    try {
      return await this.dm.rest.post(`${GET_MSG_FROM_THREAD}`, payload);
    } catch (err) {
      console.log("Simple Message :::: ", err);
    }
  }

  getMsgPayloadr14(data) {
    const { subject, recipientPid, recipientName, envelope, lastMessage, lastMessageDate, payerId, currentMsgCount } = data;
    return {
      subject,
      threadId: envelope.threadHeader.threadId,
      originator: envelope.threadHeader.originatorPid,
      billerId: recipientPid,
      billerName: recipientName,
      currentMsgCount: currentMsgCount,
      readMsgCount: 0,
      lastMessage,
      lastMessageDate,
      payerId,
    };
  }

  async saveMessage(
    input,
    fields = "uid threadId originator subject billerId billerName currentMsgCount readMsgCount lastMessage lastMessageDate payerId"
  ) {
    try {
      let res = await this.api.mutate(
        `
      mutation CreateSimpleMessage($input: CreateSimpleMessageInput) {
        createSimpleMessage(input: $input){
          simpleMessage {
            ${fields}
          }
          success
        }
      }`,
        { input }
      );
      return res.data.createSimpleMessage.simpleMessage;
    } catch (error) {
      return error;
    }
  }

  async updateMessage(
    input,
    fields = "uid threadId originator subject billerId billerName currentMsgCount readMsgCount lastMessage lastMessageDate payerId"
  ) {
    try {
      let res = await this.api.mutate(
        `
      mutation UpdateSimpleMessage($input: UpdateSimpleMessageInput) {
        updateSimpleMessage(input: $input){
          simpleMessage {
            ${fields}
          }
          success
        }
      }`,
        { input }
      );
      return res.data.updateSimpleMessage.simpleMessage;
    } catch (error) {
      return error;
    }
  }

  hasOnlyMessageBody(arrayOfThreads) {
    try {
      return arrayOfThreads.filter((ele) => ele.envelope.messageBody);
    } catch (error) {
      console.error("hasOnlyMessageBody Message::: ", error);
    }
  }

  async updateReadUnreadMsgCount(input) {
    try {
      let res = await this.api.mutate(
        `
      mutation UpdateMessageCount($input: SimpleMsgInput) {
        updateMessageCount(input: $input){
          success,
          threadId
        }
      }`,
        { input }
      );
      return res.data.updateMessageCount.simpleMessage;
    } catch (error) {
      return error;
    }
  }

  async getSimpleMessages(
    opt,
    fields = "uid threadId payerId originator billerId billerName subject lastMessage lastMessageDate currentMsgCount readMsgCount"
  ) {
    let dfts = { filter: {} };
    let options = { ...dfts, ...opt };
    try {
      let res = await this.api.qry(
        `query SimpleMessages($filter: SimpleMessageFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
        simpleMessages(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          totalCount @include(if: true)
          nodes {
            ${fields}
          }
        }
      }`,
        options
      );
      return res.data.simpleMessages.nodes;
    } catch (error) {
      return error;
    }
  }

  async getMessageById(
    threadId,
    fields = "uid threadId originator billerId billerName subject lastMessage lastMessageDate currentMsgCount readMsgCount"
  ) {
    let options = {
      filter: { threadId: { eq: threadId } },
    };
    try {
      let res = await this.api.qry(
        `query SimpleMessages($filter: SimpleMessageFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]){
        simpleMessages(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
          totalCount @include(if: true)
          nodes {
            ${fields}
          }
        }
      }`,
        options
      );
      return res.data.simpleMessages.nodes;
    } catch (error) {
      return error;
    }
  }

  async addUpdateDB(data, source) {
    const { pid } = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
    const billerList = JSON.parse(await AsyncStorage.getItem("billerList"));
    const { threadHeader, messageHeader, messageBody } = data.envelope;
    let hasThread = await this.getMessageById(threadHeader.threadId);

    const billerId = threadHeader.originatorPid === pid ? threadHeader.respondentPid : threadHeader.originatorPid;
    const payerId = threadHeader.originatorPid === pid ? threadHeader.originatorPid : threadHeader.respondentPid;

    // let billerDetail = await this.distinctBillerById(billerId);
    let billerDetail = billerList.filter((el) => el.billerId.toLowerCase() === billerId.toLowerCase());

    if (!billerDetail && !billerDetail.length) {
      return null;
    }

    const billerName = !!billerDetail ? billerDetail[0].billerName : billerId.split("#")[0];

    if (!!hasThread.length) {
      let updatePayload = {
        lastMessage: messageBody.messageBody,
        lastMessageDate: messageHeader.messageDateTime,
        uid: hasThread[0].uid,
        billerName: billerName,
        currentMsgCount: hasThread[0].currentMsgCount,
        readMsgCount: hasThread[0].readMsgCount,
      };
      if (source === "listing") {
        updatePayload.currentMsgCount = data.currentMsgCount;
      } else {
        updatePayload.readMsgCount = data.readMsgCount;
      }

      return await this.updateMessage({ ...updatePayload, payerId: payerId });
    } else {
      let newPayload = {
        subject: threadHeader.subject,
        recipientPid: billerId,
        recipientName: billerName,
        lastMessage: messageBody.messageBody,
        lastMessageDate: messageHeader.messageDateTime,
        payerId: payerId,
        currentMsgCount: data.currentMsgCount,
      };

      let payload = this.getMsgPayloadr14({ ...data, ...newPayload });
      return await this.saveMessage(payload);
    }
  }

  async distinctBillerById(billerPid, options = {}) {
    let defult = {
      filter: {
        billerId: { eq: billerPid },
      },
    };
    let opt = { ...defult, ...options };
    let result = this.dm.queryDomain.getBiller(opt);

    return result;
  }
}
