import React from "react";
import R14, { View, TextInputField, StyleSheet, Form, FileInputField, SubmitButton } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import CommonValidators from "../utils/CommonValidators";

export default R14.connect(
  class ChatInputField extends React.Component {
    constructor(props) {
      super(props);
      this.message = this.props.messageType;
      this.fileToSave = null;
      this.form = null;
    }

    componentDidMount() {
      this.form = this.props.app.ui.form("chatBox");
    }

    onSubmit = async (form) => {
      const { onAfterSent } = this.props;
      form = this.props.app.ui.form("chatBox");

      if (form.values.attachment) {
        let filename = form.values.attachment.name;
        let compareName = filename.substring(filename.lastIndexOf(".") + 1, filename.length) || filename;
        let contentType = this.getExtension(compareName.toUpperCase());
        if (!contentType) return;
        var attac = {
          filename: filename,
          contentType,
          content: this.fileToSave,
        };
      }
      if (form.values.attachment && !form.values.enterMessage) {
        form.elmts.enterMessage.setValue("Attachment");
      }
      if (await form.validate()) {
        const { billData, messageType } = this.props;

        let messages = this.props.msgList;
        messages = messages.filter((el) => el.envelope.messageBody);
        billData.note = form.values.enterMessage;
        billData.messageType = messageType;
        let lastLeaf;
        if (form.values.attachment) {
          lastLeaf = { ...messages[messages.length - 1], billData, attac };
        } else {
          lastLeaf = { ...messages[messages.length - 1], billData };
        }
        let payload = this.props.app.dm.billDetails.getNotePayload(lastLeaf);

        try {
          let res = await this.props.app.dm.billDetails.postMessageForPayment(payload);
          if (res.threadId) {
            form.reset();
            onAfterSent(payload);
            this.props.app.dm.commonDomain.showToastr("success", "Message has been sent");
          }
        } catch (err) {
          this.props.app.dm.commonDomain.showToastr("error", "Message failed to sent");
        }
        //   form.reset();
        //   setTimeout(() => {
        //     window.location.reload(false);
        //   }, 5000);
      }
    };

    input = async (val) => {
      let fizeSize = val.size / 1024 / 1024;
      if (fizeSize > 10) {
        this.props.app.dm.commonDomain.showToastr("error", "File size is greater than 10Mb.");
        return false;
      } else if (val.name.length > 35) {
        this.props.app.dm.commonDomain.showToastr("error", "Maximum allowed filename is 35 characters");
        return false;
      }
      this.getBase64(val, (result) => {
        this.fileToSave = result.split(",")[1];
      });
    };

    async getBase64(file, cb) {
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = () => {
        let fileNameXt = file.name.split(".");
        if (!!this.getExtension(fileNameXt[1])) {
          cb(reader.result);
        } else return false;
      };
      reader.onerror = function (error) {
        console.log("Error: ", error);
      };
    }

    getExtension = (fname) => {
      switch (fname.toUpperCase()) {
        case "CSV":
        case "DIF":
        case "ODS":
        case "OTS":
        case "TSV":
        case "XLM":
        case "XLS":
        case "XLSB":
        case "XLSM":
        case "XLSX":
        case "XLT":
        case "XLTM":
        case "XLTX":
          return "SDSH";
        case "XML":
          return "DXML";
        case "PDF":
          return "DPDF";
        case "DOC":
        case "DOCX":
        case "DOCM":
        case "DOTX":
        case "DOTM":
        case "DOCB":
          return "WORD";
        case "XSLT":
          return "XSLT";
        default:
          this.props.app.dm.commonDomain.showToastr(
            "error",
            " Invalid Format, Please attach the file in any one of the following format DPDF, DXML, SDSH, WORD, XSLT"
          );
          return false;
      }
    };

    readText = (val) => {
      if ((!val && !!this.form) || val.length <= 140) {
        this.form.elmts.enterMessage.setError(null);
      } else if (val.length > 140) {
        this.form.elmts.enterMessage.setError("Character Limit Exceeds 140 Characters");
      }
    };

    render() {
      const { notAllowedChat } = this.props;

      return !!notAllowedChat ? null : (
        <View style={[commonStyles.enterMessage, commonStyles.marTAuto, commonStyles.marginBottom10]}>
          <Form
            name="chatBox"
            style={[commonStyles.padding0]}
            validators={CommonValidators.getFormValidator()}
            validateBeforeSubmit={true}
            onSubmit={this.onSubmit}>
            <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter]}>
              <View style={[styles.fieldsHolder, commonStyles.marginRight15]}>
                <View style={[styles.messageTextInput, commonStyles.flexGrow1]}>
                  <TextInputField
                    labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans]}
                    labelStyle={[commonStyles.formLabelStyle]}
                    name="enterMessage"
                    label="Enter Message*"
                    validator="cmtsVali"
                    required="Please enter the message"
                    multiline
                    onChangeText={this.readText}
                    style={[commonStyles.margin0, styles.messageTextField, commonStyles.resizeVertical]}
                    helperTextStyle={[styles.errorTestStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                  />
                </View>
                <View style={[styles.attachmentField]}>
                  <FileInputField
                    style={[styles.attachmentCls, commonStyles.margin0]}
                    name="attachment"
                    label=""
                    icon="documents"
                    iconRightStyle={styles.fileIcon}
                    onValueChange={(val) => this.input(val)}
                  />
                  {/* <Text style={[styles.attachmentCover]}></Text> */}
                </View>
              </View>
              <SubmitButton
                type="button"
                title="Send"
                name="sendBtn"
                style={[styles.blueButton]}
                titleTextStyle={[commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6, commonStyles.darkGrayColor]}
              />
            </View>
          </Form>
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  fieldsHolder: {
    position: "relative",
    flexGrow: 1,
    display: "flex",
  },
  messageTextField: {
    position: "relative",
    zIndex: 1,
    padding: "5px 5px 5px 0", //5,
    // padding: "0px 45px 0 10px",
  },
  attachmentCls: {
    height: 52,
    width: 56,
    paddingTop: 5,
  },
  attachmentCover: {
    background: "#fff",
  },
  messageTextInput: {
    color: "red",
  },
  attachmentField: {
    position: "relative",
    right: 5,
    zIndex: 99,
    background: "#fff",
  },
  blueButton: {
    background: "#37c2ef",
    color: "#333",
    height: 52,
    width: 75,
    border: 0,
  },
  fileIcon: {
    color: "red",
  },
  errorTestStyle: {
    screen: ({ width }) => {
      if (width <= 420)
        return {
          width: "100vw",
          left: 0,
        };
    },
  },
});
