import React from "react";
import R14, { View, Text, Image, Button, StyleSheet, Touchable, AsyncStorage } from "../../core";
import { GOOGLE_AUTHKEY } from "../../constant/app.constant";
import { GoogleLogin } from "react-google-login";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import { SOCIAL_MEDIA_ENTITY, LOGIN_TYPE , USER_TYPE} from "../../constant/app.data.constant";
import ModalPopUp from "../ModalPopUp";
import PCHLoaderComponent from "../../components/PCH/PCHLoaderComponent";

//const googleIcon = require("../../assets/images/googleIcon48.png");
const googleIcon48 = require("../../assets/images/googleIcon48.png");

export default R14.connect(
  class GoogleLogin extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        buttonType: this.props.buttonType,
        buttonClickable: true,
        currentRoute: "",
        tpcookiesEnabled: true,
        cookiesPopup: false,
        isLoding:false,
      };
    }

    componentDidMount() {
      //await AsyncSessionStorage.setItem("tpcStatus", true);
      this.setState({
        currentRoute: this.props.app.nav.route.name,
      });
    }
    // SECTION: Route Registration.
    routeToRegistration = (val) => {
      let returnUrl = this.props.app.route._location.search;
      if(this.props.userType === USER_TYPE.XBP)
      	this.props.app.nav.to("registration", { type: `${val.toLowerCase()}${!!returnUrl ? returnUrl : ""}` });
      else
      this.props.app.nav.to("pchSignup", { type: `${val.toLowerCase()}${!!returnUrl ? returnUrl : ""}` });
      
    };

    onSuccess = async (res) => {
      const googleObj = res.profileObj;
      this.setState({ buttonClickable: false });

      this.props.app.dm.userLoginDomain.setState({
        socialLoginProfile: res.profileObj,
        socialLoginType: LOGIN_TYPE.GOOGLE,
      });

      if (this.props.userType === USER_TYPE.XBP)
      {
        // XBP
          if (this.state.buttonType === "Profile") {
            const { userDetailUID, payerId } = this.props.app.dm.userLoginDomain.state;
            let reqBody = {
              uid: userDetailUID,
              googleProfileID: res.profileObj.googleId,
            };
            // Check same email in user detail
            let emailExist = await this.props.app.dm.userLoginDomain.getLoginDetail({
              email: { eq: res.profileObj.email.toLowerCase().trim() },
              uid: { eq: userDetailUID },
            });
    
            if (emailExist.length > 0) {
              let res = await this.props.app.dm.userRegistration.updateUserDetails(reqBody, true);
              if (!!res) {
                this.props.app.dm.commonDomain.showToastr("success", "Google Profile is synced");
                this.setState({ buttonClickable: true });
                this.setState({ buttonType: "ProfileSynced" });
                return false;
              }
            } else {
              this.props.app.dm.commonDomain.showToastr("error", "Email ID not matched");
              this.setState({ buttonClickable: true });
              return false;
            }
            //
          } else {
            try {
              let response = await this.props.app.dm.userSession.loginAuth({
                socialProfileId: googleObj.googleId,
                username: googleObj.email.toLowerCase().trim(),
                loginType: LOGIN_TYPE.GOOGLE,
              });
    
              if (!!response) {
                const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
                if (!!userLoginDetail) {
                  //NOTE: check obsolute pid
                  if (userLoginDetail.payerId.includes("#") || userLoginDetail.tokenKey === "" || userLoginDetail.tokenKey === undefined) {
                    this.props.app.dm.commonDomain.showToastr("error", "Auth failed due to invalid credentials");
                    this.setState({ buttonClickable: true });
                    return false;
                  }
    
                  if (!!!userLoginDetail.userType) {
                    this.setState({ buttonClickable: true });
                    this.routeToRegistration("existinggoogle");
                    return false;
                  } else {
                    this.props.app.dm.commonDomain.showToastr("success", "You are successfully logged in");
                    await this.props.redirectToReturnUrl();
                    this.setState({ buttonClickable: true });
                    return false;
                  }
                }
                else
                this.routeToRegistration("SocialLogin");
              }
              else
                this.routeToRegistration("SocialLogin");
            }
            catch(err)
            {
              this.routeToRegistration("SocialLogin");
            }
          }
      }
      else
      {
        // PCH
            try {
              let response = await this.props.app.dm.userSession.loginAuth({
                socialProfileId: googleObj.googleId,
                username: googleObj.email.toLowerCase().trim(),
                loginType: LOGIN_TYPE.GOOGLE,
              });

              if (!!response) {
                const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
                if (!!userLoginDetail) {
                  //NOTE: check obsolute pid
                  if (userLoginDetail.payerId.includes("#") || userLoginDetail.tokenKey === "" || userLoginDetail.tokenKey === undefined) {
                    this.props.app.dm.commonDomain.showToastr("error", "Auth failed due to invalid credentials");
                    this.setState({ buttonClickable: true });
                    return false;
                  }

                  if (!!!userLoginDetail.userType) {
                    this.setState({ buttonClickable: true });
                    this.routeToRegistration("existinggoogle");
                    return false;
                  } else {
                    this.props.app.dm.commonDomain.showToastr("success", "You are successfully logged in");
                    await this.props.redirectToReturnUrl();
                    this.setState({ buttonClickable: true });
                    return false;
                  }
                } else this.routeToRegistration("SocialLogin");
              } else this.routeToRegistration("SocialLogin");
            } catch (err) {
              this.routeToRegistration("SocialLogin");
            }
      }

    };

    onFailure = (res) => {
      console.log("Login failed:", res);
      this.setState({ buttonClickable: true });
      if (!this.state.tpcookiesEnabled) {
        this.setState({ cookiesPopup: true });
      }
    };

    checkThridPartyCookies = (res) => {
      const status_ = res.details;
      if (status_.includes("Cookies are not enabled in current environment")) {
        //await AsyncSessionStorage.setItem("tpcStatus", false);
        this.setState({ tpcookiesEnabled: false });
      }
    };

    onClose = async () => {
      this.setState({ cookiesPopup: false });
    };

    render() {
      return (
        <View>
          {
            this.props.userType === USER_TYPE.XBP ? 
            (
              <>
                <GoogleLogin
                clientId={GOOGLE_AUTHKEY}
              buttonText=" "
              onSuccess={this.onSuccess}
              onFailure={this.onFailure}
              cookiePolicy={"single_host_origin"}
              //isSignedIn={true}
              onScriptLoadFailure={this.checkThridPartyCookies}
              render={(renderProps) =>
                this.state.buttonType === "Login" ? (
                  <button
                    style={
                      this.state.buttonClickable
                        ? { background: "none", padding: 0, border: 0, cursor: "pointer" }
                        : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }
                    }
                    onClick={renderProps.onClick}
                    disabled={renderProps.disabled}>
                    <Image source={googleIcon48} style={commonStyles.icon48} />
                  </button>
                ) : this.state.buttonType === "Profile" ? (
                  <button
                    style={
                      this.state.buttonClickable && this.state.currentRoute != "profileEditRoute"
                        ? { background: "none", padding: 0, border: 0, cursor: "pointer" }
                        : this.state.currentRoute === "profileEditRoute"
                        ? { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none" }
                        : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }
                    }
                    onClick={renderProps.onClick}
                    disabled={renderProps.disabled}>
                    <Image source={googleIcon48} style={commonStyles.icon24} />
                  </button>
                ) : (
                  <button
                    style={{
                      background: "none",
                      padding: 0,
                      border: "1px solid #dadada",
                      cursor: "pointer",
                      padding: "4px 8px",
                      borderRadius: 50,
                      display: "flex",
                      alignItems: "center",
                      pointerEvents: "none",
                    }}
                    onClick={renderProps.onClick}
                    disabled={renderProps.disabled}>
                    <Image source={googleIcon48} style={[commonStyles.icon24, commonStyles.marginRight4]} />
                    <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.darkGray]}>Synced</Text>
                  </button>
                )
                }/>
                <ModalPopUp
            visibility={this.state.cookiesPopup}
            isFooter={false}
            bodyWrapper={[styles.invalidReferenceModalBodyWrapper]}
            modalOuterWrapper={styles.modalOuterWrapper}
            heading={"Cookies are disabled"}
            handleCancel={this.onClose}
            closeButton={commonStyles.displayNone} //{styles.invalidReferenceModalCloseBtn}
            modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
            modalContent={commonStyles.marginTop15}>
            <View style={commonStyles.textAlignCenter}>
              <View style={commonStyles.marginBottom20}>
                <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>
                  Your browser has cookies disabled. Make sure that your cookies are enabled and try again.
                </Text>
              </View>
              <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                <Button
                  title={"Try Again"}
                  style={[commonStyles.blueButton, styles.sessionModelOkBtn]}
                  onPress={this.onClose}
                  titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight500, commonFontStyles.workSans, commonStyles.heading5]}
                />
              </View>
            </View>
          </ModalPopUp>
              </>
            ):
            (
              <>
                <GoogleLogin
                clientId={GOOGLE_AUTHKEY}
            buttonText=" "
            onSuccess={this.onSuccess}
            onFailure={this.onFailure}
            cookiePolicy={"single_host_origin"}
            //isSignedIn={true}
            onScriptLoadFailure={this.checkThridPartyCookies}
            render={(renderProps) =>
              this.state.buttonType === "Login" ? (
                <button
                  style={
                    this.state.buttonClickable
                      ? {
                          background: "none",
                          display: "flex",
                          width: "100%",
                          border: "1px solid #dadada",
                          minHeight: 40,
                          alignItems: "center",
                          borderRadius: 5,
                          // justifyContent: "center",
                          cursor: "pointer",
                          paddingLeft: 20,
                        }
                      : {
                          background: "none",
                          display: "flex",
                          width: "100%",
                          border: "1px solid #dadada",
                          minHeight: 40,
                          alignItems: "center",
                          borderRadius: 5,
                          // justifyContent: "center",
                          pointerEvents: "none",
                          opacity: 0.5,
                          paddingLeft: 20,
                        }
                  }
                  onClick={renderProps.onClick}
                  disabled={renderProps.disabled}>
                  <Image
                    resizeMode="contain"
                    style={[commonStyles.icon24, commonStyles.marginRight10]}
                    source={require("../../assets/images/Google__G__Logo.svg")}
                  />
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.darkGrayColor, styles.socialLoginText]}>
                    Sign-up/Sign-in with Google
                  </Text>
                </button>
              ) : // <button style={[commonStyles.dFlex, styles.pchSocialLoginBtn, commonStyles.justifyContentCenter, commonStyles.marginBottom10]}
              //   onClick={renderProps.onClick}
              //   disabled={renderProps.disabled}>
              //   <Image
              //     resizeMode="contain"
              //     style={[commonStyles.icon24, commonStyles.marginRight16]}
              //     source={require("../../assets/images/Google__G__Logo.svg")}
              //   />
              //   <Text style={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.darkGrayColor]}>Continue with Google</Text>
              // </button>

              this.state.buttonType === "Profile" ? (
                <button
                  style={
                    this.state.buttonClickable && this.state.currentRoute != "profileEditRoute"
                      ? { background: "none", padding: 0, border: 0, cursor: "pointer" }
                      : this.state.currentRoute === "profileEditRoute"
                      ? { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none" }
                      : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }
                  }
                  onClick={renderProps.onClick}
                  disabled={renderProps.disabled}>
                  <Image source={googleIcon48} style={commonStyles.icon24} />
                </button>
              ) : (
                <button
                  style={{
                    background: "none",
                    padding: 0,
                    border: "1px solid #dadada",
                    cursor: "pointer",
                    padding: "4px 8px",
                    borderRadius: 50,
                    display: "flex",
                    alignItems: "center",
                    pointerEvents: "none",
                  }}
                  onClick={renderProps.onClick}
                  disabled={renderProps.disabled}>
                  <Image source={googleIcon48} style={[commonStyles.icon24, commonStyles.marginRight4]} />
                  <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.darkGray]}>Synced</Text>
                </button>
              )
                }/>
                <ModalPopUp
            visibility={this.state.cookiesPopup}
            isFooter={false}
            bodyWrapper={[styles.invalidReferenceModalBodyWrapper]}
            modalOuterWrapper={styles.modalOuterWrapper}
            heading={"Cookies are disabled"}
            handleCancel={this.onClose}
            closeButton={commonStyles.displayNone} //{styles.invalidReferenceModalCloseBtn}
            modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
            modalContent={commonStyles.marginTop15}>
            <View style={commonStyles.textAlignCenter}>
              <View style={commonStyles.marginBottom20}>
                <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>
                  Your browser has cookies disabled. Make sure that your cookies are enabled and try again.
                </Text>
              </View>
              <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.justifyContentCenter]}>
                <Button
                  title={"Try Again"}
                  style={[commonStyles.blueButton, styles.sessionModelOkBtn]}
                  onPress={this.onClose}
                  titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight500, commonFontStyles.workSans, commonStyles.heading5]}
                />
              </View>
            </View>
          </ModalPopUp>
                <PCHLoaderComponent isVisiable={!this.state.buttonClickable} />
            </>
          )}
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  invalidReferenceModalBodyWrapper: {
    position: "relative",
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          width: "100vw",
          marginTop: "auto",
          borderRadius: "15px 15px 0 0",
        };
      }
    },
  },

  pchSocialLoginBtn: {
    border: "1px solid #DADADA",
    borderRadius: 4,
    minHeight: 40,
    alignItems: "center",
  },
  socialLoginText: {
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          fontSize: 12,
        };
      }
    },
  },
});
