import React, { Component, PureComponent } from "react";
import R14, { StyleSheet, Button, View, Text, Form, Touchable, TextInputField, DatePickerField, SubmitButton } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import ModalPopUp from "./ModalPopUp";
import CommonValidators from "../utils/CommonValidators";
import MessageUpload from "./MessageUpload";
import { MSG_TYPE } from "../constant/app.data.constant";
import DateUtils from "../utils/DateUtils";

export default R14.connect(
  class ExtensionRequestPopUp extends Component {
    constructor(props) {
      super(props);
      this.initialValues = {
        senderPid: props.billData.senderPid,
        senderName: props.billData.senderName,
      };
      this.state = {
        isVisible: true,
        isLoading: false,
      };
      this.fileData = null;
    }
    navigateToBack = async () => {
      let newThread = this.props.billData.threadId.replaceAll("#", ">>");
      this.props.app.nav.to("billDetailsRoute", { threadId: newThread });
    };

    componentDidMount() {
      this.form = this.props.app.ui.form("requestExt");
    }

    shouldComponentUpdate(nextprop, nextstate) {
      const { isLoading, isVisible } = this.state;
      if (nextstate.isVisible !== isVisible) {
        return true;
      } else if (nextstate.isLoading !== isLoading) {
        return true;
      }
    }

    onAttachment = (val) => {
      this.fileData = val;
    };

    onSubmit = async (form) => {
      const { onSuccessCallback, billData } = this.props;
      const { comments, requestedExecutionDate } = form.values;

      let mergedData = {
        ...billData,
        additionalInformation: comments,
        requestedExecutionDate: new Date(`${requestedExecutionDate} 14:00:00`),
        messageType: MSG_TYPE.REQ_PAY_EXT,
        attachments: !!this.fileData ? [this.fileData] : null,
      };

      let payload = this.props.app.dm.billDetails.getRequestExtPayload(mergedData);
      this.setState({ isLoading: true });
      try {
        let res = await this.props.app.dm.billDetails.postMessageForPayment(payload);
        if (res && res.threadId) {
          this.props.app.dm.commonDomain.showToastr("success", "Extension Request Successful"); // given message by Hazel

          let decPayload = {
            uid: mergedData.uid,
            isExtentionRequested: true,
            extentionRequestedDate: mergedData.requestedExecutionDate,
            isExtentionApproved: false,
            extentionApprovedDate: null,
            isExtentionDeclined: false,
            extentionDeclinedDate: null,
            // isFailedPayment: false,
            // failDate: null,
            rtpMessageType: MSG_TYPE.REQ_PAY_EXT,
          };
          await this.props.app.dm.dashboard.updateBill(decPayload);

          const { compiledMsgList, filterBills } = this.props.app.dm.dashboard.state;
          if (compiledMsgList) {
            const ind = compiledMsgList.findIndex((el) => el.uid === decPayload.uid);
            compiledMsgList[ind].isExtentionRequested = decPayload.isExtentionRequested;
            compiledMsgList[ind].extentionRequestedDate = decPayload.extentionRequestedDate;
            compiledMsgList[ind].rtpMessageType = decPayload.rtpMessageType;
            const flIndx = filterBills.findIndex((el) => el.uid === decPayload.uid);
            filterBills[flIndx].isExtentionRequested = decPayload.isExtentionRequested;
            filterBills[flIndx].extentionRequestedDate = decPayload.extentionRequestedDate;
            filterBills[flIndx].rtpMessageType = decPayload.rtpMessageType;

            await this.props.app.dm.dashboard.updateBillCount(compiledMsgList, filterBills);

            // await this.props.app.dm.dashboard.setState({
            //   compiledMsgList,
            //   filterBills,
            //   newBillCount: compiledMsgList.filter((el) => el.billStatus === "New").length,
            //   outstandingBillCount: compiledMsgList.filter((el) => el.billStatus === "Outstanding").length,
            //   overdueBillCount: compiledMsgList.filter((el) => el.billStatus === "Overdue").length,
            // });
          }

          setTimeout(() => {
            onSuccessCallback(payload);
          }, 100);
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Extension Request Unsuccessful"); // given message by Hazel
        }
      } catch (error) {
        console.log("Error: ", error);
      } finally {
        this.setState({ isLoading: false });
      }
      return false;
    };

    onDateValueChange = (val) => {
      this.form.elmts.requestedExecutionDate.setError(null);
      if (!val) {
        return false;
      }
      const { dueDate } = this.props.billData;
      this.form.elmts.requestedExecutionDate.setValue(DateUtils.formatDate(val));
      let duDate = new Date(dueDate);
      let reDate = new Date(val);
      if (!!duDate >= reDate) {
        this.form.elmts.requestedExecutionDate.setError("Intended Payment Date must not be lesser than or Same as Bill due date");
        return false;
      } else if (reDate <= duDate) {
        this.form.elmts.requestedExecutionDate.setError("Intended Payment Date must not be lesser than or Same as Bill due date");
        return false;
      }
      return true;
    };

    onPressDate = () => {};

    readText = (val) => {
      if ((!val && !!this.form) || val.length <= 140) {
        this.form.elmts.comments.setError(null);
      } else if (val.length > 140) {
        this.form.elmts.comments.setError("Character Limit Exceeds 140 Characters");
      }
    };

    render() {
      const { isVisible, isLoading } = this.state;
      return (
        <ModalPopUp
          visibility={isVisible}
          modalPopupCancelBtn={commonStyles.displayNone}
          modalPopupOkBtn={commonStyles.displayNone}
          closeButton={commonStyles.displayNone}
          bodyWrapper={[styles.extensionRequestModal, commonStyles.marTAuto]}
          isFooter={false}
          modalOuterWrapper={styles.modalBackdrop}
          modalContent={styles.popupModalContent}>
          <View style={[styles.sortByHeading, commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.alignItemsCenter]}>
            <View>
              <Text style={[commonStyles.heading5, commonStyles.fontWeight600, commonFontStyles.workSans]}>Request Extension</Text>
            </View>
          </View>
          <View>
            <Form
              title="SortFilter"
              name="requestExt"
              style={styles.customPadding15}
              onSubmit={this.onSubmit}
              validateBeforeSubmit={true}
              validators={CommonValidators.getFormValidator()}
              initialValues={this.initialValues}
              controlsBottomRight={[
                <Button
                  key="cancel"
                  title="Cancel"
                  style={[commonStyles.blueBorderButton, commonStyles.flexBasis50, commonStyles.marginRight15]}
                  titleTextStyle={[commonStyles.fontWeight600, commonStyles.blueBorderButtonColor, commonFontStyles.workSans]}
                  onPress={this.navigateToBack}
                  disabled={isLoading}
                />,
                <SubmitButton
                  title="Request"
                  key="request"
                  style={[commonStyles.flexBasis50, commonStyles.blueButton]}
                  titleTextStyle={[commonStyles.fontWeight600, commonStyles.black, commonStyles.blueButtonColor, commonFontStyles.workSans]}
                  disabled={isLoading}
                />,
              ]}>
              {/* <View style={[commonStyles.marginBottom45]}>
                <TextInputField
                  labelTextStyle={[commonStyles.labelText,commonFontStyles.workSans]}
                  name="senderName"
                  label="Biller Name"
                  style={[commonStyles.margin0, styles.messageTextField]}
                  disabled={true}
                />
              </View>
              <View style={[commonStyles.marginBottom45]}>
                <TextInputField
                  labelTextStyle={[commonStyles.labelText,commonFontStyles.workSans]}
                  name="senderPid"
                  label="Biller PID"
                  style={[commonStyles.margin0, styles.messageTextField]}
                  disabled={true}
                />
              </View> */}
              <View style={[commonStyles.marginBottom45]}>
                <DatePickerField
                  style={[commonFontStyles.workSans, commonStyles.margin0, commonStyles.heading5, styles.datePickerField]}
                  name="requestedExecutionDate"
                  label="Intended Payment Date*"
                  mode="modal"
                  required="Select the Intended Payment Date"
                  labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans]}
                  labelStyle={[commonStyles.formLabelStyle]}
                  helperTextStyle={[styles.labelStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                  onValueChange={this.onDateValueChange}
                  onPress={this.onPressDate}
                />
              </View>
              <View style={[commonStyles.marginBottom45]}>
                <MessageUpload
                  commentsProps={{
                    required: "Enter the Notes To Biller",
                    label: "Notes To Biller*",
                    validator: "cmtsVali",
                    onChangeText: this.readText,
                  }}
                  onValueChange={this.onAttachment}
                  isDisabled={isLoading}
                  onChangeText={this.readText}
                  customHeight={styles.customHeight}
                />
              </View>
            </Form>
          </View>
        </ModalPopUp>
      );
    }
  }
);

const styles = StyleSheet.create({
  closePopup: {
    width: "20%",
    height: 8,
    borderRadius: 25,
    background: "#e1e1e1",
    marginBottom: 15,
    screen: ({ width }) => {
      if (width >= 991)
        return {
          display: "none",
        };
    },
  },
  extensionRequestModal: {
    borderRadius: "15px 15px 0 0",
    width: "100vw",
    marginTop: "auto",
    screen: ({ width }) => {
      if (width >= 991)
        return {
          margin: "auto",
          borderRadius: 5,
          width: 420,
        };
    },
  },
  sortByHeading: {
    ...StyleSheet.margin(0, 10, 5, 10),
  },
  modalBackdrop: {
    background: "#00000052",
    overflowX: "auto",
  },
  toggleSwitch: {
    margin: "18px 30px",
  },
  popupModalContent: {
    margin: 0,
  },
  labelStyle: {
    left: 12,
    bottom: "auto",
    // lineHeight: "unset",
  },
  marginBottom20: {
    marginBottom: 20,
    screen: ({ width }) => {
      if (width >= 991)
        return {
          marginBottom: 0,
        };
    },
  },
  customHeight: {
    minHeight: 65,
  },
  customPadding15: {
    ...StyleSheet.padding(15, 15, 0, 15),
  },
});
