import React from "react";
import R14, { ScreenLayout, IconButton, Image, View, Text, Touchable, StyleSheet } from "../core";
import ListContainer from "../components/dashboard/ListContainer";
import FilterComponent from "../components/FilterComponent";
import DateUtils from "../utils/DateUtils";
import CommonUtility from "../utils/CommonUtility";
import ResponsiveTableComponent from "../components/ResponsiveTableComponent";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import LeftMenuComponent from "../components/LeftMenuComponent";

const bookmaredActiveIcon = require("../assets/images/bookmared_active.png");

export default R14.connect(
  class MakePaymentScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isOpenPriorityInfoTooltip: false,
      };
    }

    componentDidMount() {
      this.timerInstance = setInterval(async () => {
        await this.props.app.dm.dashboard.setCompiledMsgList();
      }, 30000);
    }

    navigateTo = async (path) => {
      this.props.app.nav.to(path);
    };

    componentWillUnmount() {
      if (this.timerInstance) {
        clearInterval(this.timerInstance);
      }

      const { compiledMsgList } = this.props.app.dm.dashboard.state;
      this.props.app.dm.dashboard.setState({
        filterBills: compiledMsgList,
        isBlockNavigation: false,
        selectedBlock: [],
      });
      this.props.app.dm.filterDomain.resetFilter();
    }

    onCheckboxChange = (selectedChk) => {
      this.props.app.dm.filterDomain.setState({ selectedChk });
    };

    onSwitchChange = (sortOrder) => {
      this.props.app.dm.filterDomain.setState({ sortOrder });
    };

    onStatusChange = (ind) => {
      const { statusType } = this.props.app.dm.filterDomain.state;
      statusType[ind].isSelected = !statusType[ind].isSelected;
      this.props.app.dm.filterDomain.setState({ statusType });
    };
    onBillsStatusChange = (ind) => {
      const { filterByBills } = this.props.app.dm.filterDomain.state;
      filterByBills[ind].isSelected = !filterByBills[ind].isSelected;
      this.props.app.dm.filterDomain.setState({ filterByBills });
    };
    onPaymentsStatusChange = (ind) => {
      const { filterByPayments } = this.props.app.dm.filterDomain.state;
      filterByPayments[ind].isSelected = !filterByPayments[ind].isSelected;
      this.props.app.dm.filterDomain.setState({ filterByPayments });
    };
    onExtensionsStatusChange = (ind) => {
      const { filterByExtensions } = this.props.app.dm.filterDomain.state;
      filterByExtensions[ind].isSelected = !filterByExtensions[ind].isSelected;
      this.props.app.dm.filterDomain.setState({ filterByExtensions });
    };

    applyFilter = () => {
      const { selectedChk, sortOrder, statusType, radioOpt } = this.props.app.dm.filterDomain.state;
      let data = this.props.app.dm.dashboard.state.compiledMsgList;
      let list = CommonUtility.displayBillsFilter(data, false, selectedChk, sortOrder);
      let selectedStatus = statusType.filter((el) => el.isSelected === true).map((itm) => itm.label);
      if (!!selectedStatus.length) {
        list = CommonUtility.filterStatus(list, selectedStatus);
      }
      // list.sort(DateUtils.dateAscDescSort(selectedChk, sortOrder));
      this.props.app.dm.dashboard.setState({ filterBills: list });
      this.props.app.dm.filterDomain.setState({
        selectedStatus,
        showResetFilterBtn: true,
        isFilterOpen: false,
        oldFilterState: { selectedChk, sortOrder, selectedStatus, statusType, radioOpt },
      });
    };
    openPriorityInfoTooltip = () => {
      this.setState({
        isOpenPriorityInfoTooltip: !this.state.isOpenPriorityInfoTooltip,
      });
    };

    onCloseFilter = () => {
      const {
        selectedStatus,
        selectedChk,
        sortOrder,
        statusType,
        filterByBills,
        filterByPayments,
        filterByExtensions,
        radioOpt,
      } = this.props.app.dm.filterDomain.state.oldFilterState;
      this.props.app.dm.filterDomain.setState({
        isFilterOpen: false,
        selectedChk,
        sortOrder,
        statusType,
        filterByBills,
        filterByPayments,
        filterByExtensions,
        radioOpt,
        selectedStatus,
      });
    };

    onCrossClick = async (el) => {
      const { selectedStatus, statusType, filterByBills, filterByPayments, filterByExtensions } = this.props.app.dm.filterDomain.state;
      statusType.forEach((itm) => {
        if (itm.label.toLowerCase() === el.toLowerCase()) {
          itm.isSelected = false;
        }
      });
      if (!!filterByBills)
        filterByBills.forEach((itm) => {
          if (itm.label.toLowerCase() === el.toLowerCase()) {
            itm.isSelected = false;
          }
        });
      if (!!filterByExtensions)
        filterByExtensions.forEach((itm) => {
          if (itm.label.toLowerCase() === el.toLowerCase()) {
            itm.isSelected = false;
          }
        });
      if (!!filterByPayments)
        filterByPayments.forEach((itm) => {
          if (itm.label.toLowerCase() === el.toLowerCase()) {
            itm.isSelected = false;
          }
        });
      let idx = selectedStatus.indexOf(el);
      if (idx > -1) {
        selectedStatus.splice(idx, 1);
        await this.props.app.dm.filterDomain.setState({ selectedStatus: selectedStatus, statusType: statusType });
        this.applyFilter();
      }
    };

    render() {
      const { isFilterOpen, selectedChk, sortOrder, statusType, selectedStatus, radioOpt } = this.props.app.dm.filterDomain.state;
      const { compiledMsgList, filterBills, headerSearchList, isBlockNavigation, selectedBlock } = this.props.app.dm.dashboard.state;
      let option = { selectedChk, sortOrder, statusType, selectedStatus, radioOpt };
      const { isSearchOpen, textInSearchBox, currentOpenScreen } = this.props.app.dm.userSearchDomain.state;
      let withBookmarked = [];

      if (!!isBlockNavigation) {
        withBookmarked = CommonUtility.filterStatus(compiledMsgList, selectedBlock);
      } else if (!!isSearchOpen) {
        withBookmarked = headerSearchList;
      } else {
        withBookmarked = filterBills;
      }
      const { notificationThreadIds } = this.props.app.dm.notificationDomain.state;
      withBookmarked = withBookmarked.map((data, i) => {
        return data;
      });
      if (notificationThreadIds.length > 0) {
        let d = notificationThreadIds.map((data, i) => {
          return data;
        });
        withBookmarked = withBookmarked.filter((message) => d.includes(message.threadId));
      }

      return (
        <ScreenLayout scrollEnabled>
          {/* <View style={[commonStyles.marginTop15, commonStyles.paddingLeftRight16, commonStyles.pageTitle]}>
            <Text style={[commonStyles.heading3, commonFontStyles.workSans, commonStyles.fontWeight500]}>
              {!!(isBlockNavigation && selectedBlock)
                ? `${selectedBlock[0]} Bills`
                : this.props.app.nav.route.label}{" "}
            </Text>
          </View> */}
          {/* <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, styles.priorityInfoTooltipHolder]}>
            <Touchable onPress={() => this.navigateTo("dashboard")} style={styles.priorityListTxt}>
              <Text style={[commonStyles.heading6, commonStyles.fontWeight500, commonFontStyles.workSans]}>Priority List</Text>
            </Touchable>
            <IconButton icon="help" size="small" key="help" onPress={this.openPriorityInfoTooltip} />
            <View style={[this.state.isOpenPriorityInfoTooltip ? commonStyles.dFlex : commonStyles.displayNone, styles.priorityInfoTooltip]}>
              <Text style={[commonStyles.heading6, commonFontStyles.workSans, commonStyles.whiteColor]}>List of "Prioritized"</Text>
              <Image source={bookmaredActiveIcon} style={styles.bookedMark} />{" "}
              <Text style={[commonStyles.heading6, commonFontStyles.workSans, commonStyles.whiteColor]}>bills</Text>
              <Text style={styles.priorityInfoTooltipArrow}></Text>
            </View>
          </View> */}
          <ResponsiveTableComponent
            style={styles.dashboardScrollView}
            statusOtp={selectedStatus}
            handleCross={this.onCrossClick}
            filterBills={withBookmarked}
            screenName="MakePaymentScreen"
          />
          {/* <ListContainer isHeader={false} statusOtp={selectedStatus} handleCross={this.onCrossClick} /> */}
          <FilterComponent
            isVisible={isFilterOpen}
            defaultFilter={option}
            onCancelFilter={this.onCloseFilter}
            onFilter={this.applyFilter}
            onCheckboxChange={this.onCheckboxChange}
            onSwitchChange={this.onSwitchChange}
            onStatusChange={this.onStatusChange}
            onBillsStatusChange={this.onBillsStatusChange}
            onPaymentsStatusChange={this.onPaymentsStatusChange}
            onExtensionsStatusChange={this.onExtensionsStatusChange}
          />
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  priorityInfoTooltipHolder: {
    position: "relative",
    background: "#fff",
    padding: "7px 16px 0",
  },
  priorityInfoTooltip: {
    position: "absolute",
    background: "#333",
    color: "#fff",
    top: 41,
    zIndex: 999999,
    padding: 10,
    borderRadius: 5,
    minWidth: 220,
    textAlign: "center",
  },
  priorityInfoTooltipArrow: {
    width: 10,
    height: 10,
    display: "block",
    position: "absolute",
    background: "#333",
    top: -4,
    left: 87,
    transform: "rotate(45deg);",
  },
  bookedMark: {
    height: 22,
    width: 15,
    margin: "0 5px",
    display: "inline-block",
    backgroundSize: "contain",
  },
  dashboardScrollView: {
    inset: "75px 0px 0px",
  },
  priorityListTxt: {
    marginRight: -10,
  },
});
