import R14 from "../core";

export default class MiscellaneousDomain extends R14.Domain {
 constructor(props){
   super(props);
   this.state ={
    countries: []
   }
 }

 //Unused...
 async qryCountry(options){
  return new Promise((resolve, reject) => {
    return resolve(this.api.qry(
        `
        query Countrys($filter: CountryFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
            countrys(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
                totalCount @include(if: true)
                nodes {  
                    uid city city_ascii country cityid admin_name
                }
            }
        }`,
        options
        )
   )})
 }
 //Unused...
 async getPromiseCountry(search, type) {
  let totalCount = 1; //16000/20;
  let res = await Promise.all(Array.from({ length: totalCount }, async(_, idx) => await this.qryCountry({
    filter:{
      country : {like: search}
    },
    sort:
      {
        field: "country",
        order: "ASC",
      },
  })));
  console.log(res)
 }
 
// Not IN Use
async getAllCountries(countryInput) {
  let options = {
    filter:{
        country : {eq: countryInput}
      },
      sort:
        {
          field: "country",
          order: "ASC",
        },
        page: 1,
        resultsPerPage: 20
      };

  try {
    let res = await this.api.qry(
      `
      query Countrys($filter: CountryFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
          countrys(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
              totalCount @include(if: true)
              nodes {  
                  uid city city_ascii country cityid admin_name
              }
          }
      }`,
      options
    );
    
    let countries = res.data.countrys.nodes;
    this.setState({
      countries: countries
    });
    return countries;
  } catch (err) {
    return err;
  }
}

 async qryCountryStateCity(limit = 10, countryFilter="", stateFilter="", cityFilter="", fields = "uid city city_ascii country cityid admin_name"){
    const options = {
      countryFilter,
      stateFilter,
      cityFilter,
      limit: limit,
    };
    try{
    let result = await this.api.qry(
        `
        query GetCountryStateCity($countryFilter: String, $stateFilter: String,  $cityFilter: String!, $limit: Int){
          getCountryStateCity( countryFilter: $countryFilter, stateFilter: $stateFilter, cityFilter: $cityFilter, limit: $limit) {
            nodes {
              ${fields}
            }
          }
        }`,
        options
      );
      return this.setStateCompany(!!result && result.data && result.data.getCountryStateCity ? result.data.getCountryStateCity.nodes:[]);
    } catch (err) {
      return err;
    }
 }

 setStateCompany = (inputCompany) => {
  // 1. copy existing state & update it
 let filterItems = !!inputCompany ? inputCompany.map((el, i) => {
      return {
        uid: el.uid,
        cityid: el.cityid,
        city: el.city.normalize("NFD").replace(/[\u0300-\u036f]/g, ""),
        city_ascii: el.city_ascii.normalize("NFD").replace(/[\u0300-\u036f]/g, ""),
        country: el.country.normalize("NFD").replace(/[\u0300-\u036f]/g, ""),
        admin_name: el.admin_name.normalize("NFD").replace(/[\u0300-\u036f]/g, ""),
      }
    }) : [];

  const countries = [...new Set([...this.state.countries, ...filterItems])];
  // 2. set state
  this.setState({ countries });
  return countries;
};


  async getCountries(limit = 10, countryInput = "", stateInput = "", cityInput = "") {
    countryInput = !!countryInput ? countryInput : "";
    stateInput = !!stateInput ? stateInput : "";
    cityInput = !!cityInput ? cityInput : "";
  let filter = this.state.countries.filter((c) => c.country.toLowerCase().startsWith(!!countryInput ? countryInput.toLowerCase() : c.country.toLowerCase()) 
                                && c.admin_name.toLowerCase().startsWith(!!stateInput ? stateInput.toLowerCase() : c.admin_name.toLowerCase())
                                && c.city.toLowerCase().startsWith(!!cityInput ? cityInput.toLowerCase() : c.city.toLowerCase()));
  if(!filter || filter.length === 0 || filter.length < limit)
  {
    filter = await this.qryCountryStateCity(limit, countryInput, stateInput, cityInput);
    return filter.filter((c) => c.country.toLowerCase().startsWith(!!countryInput ? countryInput.toLowerCase() : c.country.toLowerCase()) 
                                && c.admin_name.toLowerCase().startsWith(!!stateInput ? stateInput.toLowerCase() : c.admin_name.toLowerCase())
                                && c.city.toLowerCase().startsWith(!!cityInput ? cityInput.toLowerCase() : c.city.toLowerCase()));
  }
  else
    return filter; 
}

  async getCountries_Old(countryInput) {
    let options = {};
    if (!!countryInput)
    {
       options = {
        filter: {
          country: { eq:countryInput }
        },
        sort:
          {
            field: "country",
            order: "ASC",
          },
      };
    }
    else
    {
      if (!!countryInput)
      {
         options = {
          filter: {
            country: { eq: countryInput  }
          },
          sort:
            {
              field: "country",
              order: "ASC",
            },
        };
      }
    }
 

    try {
      let res = await this.api.qry(
        `
        query Countrys($filter: CountryFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
            countrys(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
                totalCount @include(if: true)
                nodes {  
                    uid city city_ascii country cityid admin_name
                }
            }
        }`,
        options
      );
      return res.data.countrys.nodes;
    } catch (err) {
      return err;
    }
  }
}
