import R14, { AsyncStorage } from "../../core";

export default class AddDoctorsDomain extends R14.Domain {
  constructor(props) {
    super(props);
    this.state = {};
  }

  // NOTE: insert Doctors Detail.
  async insertDoctorsDetail(input) {
    try {
      let res = await this.api.mutate(
        `
          mutation CreatePchDoctorDetail($input: CreatePchDoctorDetailInput){
            createPchDoctorDetail(input: $input){              
              success pchDoctorDetail {
                uid
                DoctorName
              }
            }
          }`,
        { input }
      );
      return res.data.createPchDoctorDetail.pchDoctorDetail;
    } catch (error) {
      return error;
    }
  }

  async getDoctorsDetailsList(fields = "uid DoctorName NPI userLoginDetailUid") {
    try {
      let userDetails = await AsyncStorage.getItem("childs");
      if (userDetails !== null) {
        let childUsers = JSON.parse(userDetails);
        let res = await this.api.qry(
          `
          query PchDoctorDetails($filter: PchDoctorDetailFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
            pchDoctorDetails(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
              totalCount @include(if: true)
              nodes {
                ${fields}
              }
            }
          }`,
          {
            filter: {
              userLoginDetailUid: { in: childUsers.map((user) => { return user.uid }) },
              isActive: { eq: "true" },
            },
          }
        );
        return res.data.pchDoctorDetails.nodes;
      }
      return [];
    } catch (err) {
      return err;
    }
  }
  
  async isValidateNPI(npi, fields = "uid DoctorName NPI userLoginDetailUid")
  {
    try {
       let res = await this.api.qry(
          `
          query PchDoctorDetails($filter: PchDoctorDetailFilter, $page: Int, $resultsPerPage: Int, $sort: [SortOption!]) {
            pchDoctorDetails(filter: $filter, page: $page, resultsPerPage: $resultsPerPage, sort: $sort){
              totalCount @include(if: true)
              nodes {
                ${fields}
              }
            }
          }`,
          {
            filter: {
              NPI: { eq: npi },
              isActive: { eq: "true" },
            },
          }
        );
      let result = !!res.data.pchDoctorDetails && !!res.data.pchDoctorDetails.nodes ? res.data.pchDoctorDetails.nodes : [];
      return !(result.length > 0);
    } catch (error) {
      return false;
    }
  }
  // NOTE: Delete Doctor.
  async deleteDoctor(uid, fields = "uid DoctorName") {
    try {
      let res = await this.api.mutate(
        `
      mutation DeleteDoctor($uid: ID!) {
        deletePchDoctorDetail(uid: $uid){
          pchDoctorDetail {
            ${fields}
          }
          success
        }
      }`,
        { uid }
      );
      return res.data;
    } catch (error) {
      return error;
    }
  }

  // NOTE: Update Doctor.
  async updateDoctorsDetail(input) {
    try {
      let res = await this.api.mutate(
        `
          mutation UpdatePchDoctorDetail($input: UpdatePchDoctorDetailInput){
            updatePchDoctorDetail(input: $input){              
              success pchDoctorDetail {
                uid
                DoctorName
              }
            }
          }`,
        { input }
      );
      return res.data.updatePchDoctorDetail.pchDoctorDetail;
    } catch (error) {
      return error;
    }
  }
}
