import React from "react";
import R14, {
  StyleSheet,
  Button,
  ScreenLayout,
  Image,
  Text,
  View,
  Touchable,
  ScrollView,
  TextInputField,
  Form,
  SubmitButton,
  FlatList,
  Dimensions,
  AsyncStorage,
} from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import NoRecordsFound from "../NoRecordsFound";
import ModalPopUp from "../../components/ModalPopUp";
import CommonValidators from "../../utils/CommonValidators";
import CopyRight from "../CopyRight";
const editImage = require("../../assets/images/edit.png");
const deleteImage = require("../../assets/images/delete.png");

export default R14.connect(
  class AddDoctors extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        buttonType: "",
        doctorDetails: [],
        displayType: this.props.DisplayType,
        actionMode: "Add",
        doctorUid: "",
        isConfirmationVisible: false,
        addedHeight: 0,
        provider: null,
      };
    }

    componentDidMount() {
      const { providerItems } = this.props;
      if (!!providerItems) this.setState({ doctorDetails: providerItems });
      else this.getDoctorDetails();
    }

    componentWillUnmount() {
      this.props.app.dm.userSession.setState({
        isAddProviderVisible: false,
      });
    }

    onCancel = () => {
      this.clearAllValidationMessages();
      this.setState({
        actionMode: "Add",
      });
      this.props.app.dm.userSession.setState({
        isAddProviderVisible: false,
      });
      this.onGoToBack();
    };

    onGoToBack = () => {
      const { defaultMode } = this.props;
      if (defaultMode === "add") {
        this.props.app.nav.back();
      }
    };

    getDoctorDetails = async (uid) => {
      let doctorDetails = await this.props.app.dm.addDoctorsDomain.getDoctorsDetailsList();
      this.setState({ doctorDetails: doctorDetails });
    };

    // Clear Validation Message
    clearAllValidationMessages = () => {
      let docForm = this.props.app.ui.form("addDoctor");
      docForm.elmts.doctorName.setValue("");
      docForm.elmts.NPI.setValue("");
      docForm.elmts.doctorName.setError("");
      docForm.elmts.NPI.setError("");
    };

    handleSubmit = async (form) => {
      const { provider, actionMode } = this.state;
      if ((!!provider && provider.NPI !== form.elmts.NPI.value) || !!!provider) {
        let isValid = await this.props.app.dm.addDoctorsDomain.isValidateNPI(form.elmts.NPI.value);
        if (!!!isValid) {
          form.elmts.NPI.setError(`NPI [${form.elmts.NPI.value}] already exists`);
          return false;
        } else form.elmts.NPI.setError(null);
      }

      if (actionMode == "Add") {
        const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
        let userUid = !!userLoginDetail ? userLoginDetail.uid : null;

        let apiBody = {
          DoctorName: form.elmts.doctorName.value,
          NPI: form.elmts.NPI.value,
          userLoginDetailUid: userUid,
        };

        // NOTE: Doctor Insert.
        let doctorAPIRes = await this.props.app.dm.addDoctorsDomain.insertDoctorsDetail(apiBody);
        if (!!doctorAPIRes.uid) {
          this.props.app.dm.commonDomain.showToastr("success", "Provider added successfully!");
          if (this.props.defaultMode === "add") {
            this.props.app.nav.back();
          } else {
            this.getDoctorDetails();
            this.clearAllValidationMessages();
          }
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Failed to add provider details!");
        }
      } else {
        let apiBody = {
          DoctorName: form.elmts.doctorName.value,
          NPI: form.elmts.NPI.value,
          uid: provider.uid,
        };
        let doctorAPIRes = await this.props.app.dm.addDoctorsDomain.updateDoctorsDetail(apiBody);
        if (!!doctorAPIRes.uid) {
          this.getDoctorDetails();
          this.clearAllValidationMessages();
          this.setState({ actionMode: "Add", provider: null });
          this.props.app.dm.commonDomain.showToastr("success", "Provider updated successfully!");
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Failed to update provider details!");
        }
      }
      this.props.app.dm.userSession.setState({
        isAddProviderVisible: !this.props.app.dm.userSession.state.isAddProviderVisible,
      });
    };

    // NOTE: Doctor Delete.
    deleteDoctor = async (uid) => {
      // let doctorAPIRes = await this.props.app.dm.addDoctorsDomain.deleteDoctor(uid);
      let InsuranceDetail = await this.props.app.dm.addInsuranceDomain.checkExistinUploadBill({
        doctorUid: { eq: this.state.doctorUid },
      });

      if (InsuranceDetail.length > 0) {
        this.props.app.dm.commonDomain.showToastr("error", "Delete not allowed. Bills uploaded with this provider.");
      } else {
        let apiBody = {
          isActive: "false",
          uid: this.state.doctorUid,
        };
        let doctorAPIRes = await this.props.app.dm.addDoctorsDomain.updateDoctorsDetail(apiBody);
        if (!!doctorAPIRes.uid) {
          this.getDoctorDetails();
          this.props.app.dm.commonDomain.showToastr("error", "Provider deleted successfully!");
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Failed to delete provider details!");
        }
      }
      this.setState({ isConfirmationVisible: false, doctorUid: "" });
    };

    openPopScreen = (uid) => {
      this.setState({ doctorUid: uid });
      this.setState({ isConfirmationVisible: true });
    };

    closePopScreen = () => {
      this.setState({ isConfirmationVisible: false });
    };
    // NOTE: Doctor Update.
    updateDoctor = async (item) => {
      this.props.app.dm.userSession.setState({
        isAddProviderVisible: true,
      });
      let docForm = this.props.app.ui.form("addDoctor");
      docForm.elmts.doctorName.setValue(item.DoctorName);
      docForm.elmts.NPI.setValue(item.NPI);
      this.setState({ actionMode: "Update", provider: item });
    };
    getHeightOfElement = (event) => {
      const { height } = event.nativeEvent.layout;
      this.setState({
        addedHeight: height,
      });
      // console.log(this.state.addedHeight);
    };

    renderAddProviderForm() {
      const { isAddProviderVisible } = this.props.app.dm.userSession.state;
      return (
        <View style={[styles.addDoctorsSection, !!isAddProviderVisible ? styles.block : styles.none]}>
          <Form
            name="addDoctor"
            style={[commonStyles.dFlex, commonStyles.flexDirectionColumn]}
            onSubmit={this.handleSubmit}
            validators={CommonValidators.getFormValidator()}
            validateBeforeSubmit={true}>
            <View style={[commonStyles.dFlex, styles.textFieldsHolder]}>
              <View style={[commonStyles.flex1, commonStyles.flexBasis48, styles.provideName]}>
                <TextInputField
                  name="doctorName"
                  label="Provider Name*"
                  required="Enter Provider Name"
                  tooltip="Min 2 - Max 50 alphanumeric characters are allowed only"
                  validator="min2Max50"
                  autoComplete={false}
                  labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                  labelStyle={[commonStyles.formLabelStyle]}
                  helperTextStyle={[commonStyles.left0, styles.customHelperTextCss, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                />
              </View>
              <View style={[commonStyles.flex1, commonStyles.flexBasis48, styles.enterNPI]}>
                <TextInputField
                  name="NPI"
                  label="NPI#*"
                  required="Enter NPI"
                  tooltip="Please enter 10 character only"
                  autoComplete={false}
                  validator="min10Max10DigitNPI"
                  labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                  labelStyle={[commonStyles.formLabelStyle]}
                  helperTextStyle={[commonStyles.left0, styles.customHelperTextCss, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                />
              </View>
            </View>

            <View style={[commonStyles.marTAuto, commonStyles.dFlex, styles.buttonWrapper]}>
              <View style={[styles.rightInputHolder, styles.buttonHolderLeft]}>
                <Button
                  key="cancel"
                  title="Cancel"
                  onPress={this.onCancel}
                  style={[commonStyles.blueBorderButton, commonStyles.fullWidth]}
                  titleTextStyle={[commonStyles.blueBorderButtonColor, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
                />
              </View>
              <View style={[styles.leftInputHolder, styles.buttonHolderRight, styles.updatebtnHolder]}>
                <SubmitButton
                  key="Add"
                  title={this.state.actionMode}
                  style={[commonStyles.blueButton, styles.addBtn]}
                  titleTextStyle={[commonStyles.blueButtonColor, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
                />
              </View>
            </View>
          </Form>
        </View>
      );
    }
    renderEditandDeleteSection(item) {
      return (
        <View style={[styles.td, styles.action]}>
          <View style={[commonStyles.dFlex, commonStyles.alignItemsCenter, this.state.displayType == "Edit" ? commonStyles.dFlex : styles.none]}>
            <Touchable onPress={() => this.updateDoctor(item)}>
              <Image source={editImage} style={commonStyles.icon20} />
            </Touchable>
            <Touchable onPress={() => this.openPopScreen(item.uid)} style={commonStyles.marginLeft15}>
              <Image source={deleteImage} style={commonStyles.icon20} />
            </Touchable>
          </View>
        </View>
      );
    }
    renderProviderList() {
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      return (
        <View>
          {this.state.doctorDetails.length > 0 ? (
            <View style={styles.table}>
              <View
                style={[
                  styles.thead,
                  // !!dashboarScroll ? styles.customPaddingRight16 : null
                ]}>
                <View style={styles.th}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Provider Name</Text>
                </View>
                <View style={styles.th}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>NPI</Text>
                </View>
                <View
                  style={[
                    styles.th,
                    styles.action,
                    // !!dashboarScroll ? styles.customMarginRight16 : null
                  ]}>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight600, commonStyles.heading6]}>Action</Text>
                </View>
              </View>
            </View>
          ) : null}
          {/* this.state.doctorDetails.map((el, ind) => (  */}
          <View>
            <FlatList
              style={[styles.providerListComponent, this.state.doctorDetails.length > 0 ? null : styles.noRecordsStyle]}
              data={this.state.doctorDetails}
              // onLayout={(event) => this.getHeightOfElement(event)}
              ListEmptyComponent={
                <View style={styles.padd10}>
                  <NoRecordsFound noRecords={[commonStyles.marginTop15]} message={"No Provider to display."} />
                </View>
              }
              renderItem={({ item, index }) => (
                <View style={styles.body}>
                  <View style={[styles.tr, isMobileDevice ? [commonStyles.dFlex, commonStyles.flexDirectionRow] : null]}>
                    <View
                      style={
                        isMobileDevice ? [commonStyles.dFlex, commonStyles.flex1] : [commonStyles.dFlex, commonStyles.justifyContentSpaceBetween]
                      }>
                      <View style={isMobileDevice ? styles.mViewTd : styles.td}>
                        <Text style={[commonFontStyles.workSans, commonStyles.heading6, styles.customFontWeight600, styles.breakWord]}>
                          {item.DoctorName}
                        </Text>
                      </View>
                      <View style={isMobileDevice ? styles.mViewTd : styles.td}>
                        <Text style={[commonFontStyles.workSans, commonStyles.heading6, styles.breakWord]}>{item.NPI}</Text>
                      </View>
                      {isMobileDevice ? null : this.renderEditandDeleteSection(item)}
                    </View>
                    {isMobileDevice ? this.renderEditandDeleteSection(item) : null}
                  </View>
                </View>
              )}
            />
          </View>
        </View>
      );
    }
    render() {
      // const { addedHeight } = this.state;
      const { isAddProviderVisible } = this.props.app.dm.userSession.state;
      // let dashboarScroll = addedHeight > Dimensions.get("window").height - 267;
      return (
        <ScrollView style={[styles.customTopMargin]}>
          <View style={styles.sectionHolder}>
            {this.renderAddProviderForm()}
            {!!isAddProviderVisible ? null : this.renderProviderList()}
          </View>
          <ModalPopUp
            visibility={this.state.isConfirmationVisible}
            isFooter={false}
            bodyWrapper={styles.bodyWrapper}
            modalOuterWrapper={styles.modalOuterWrapper}
            closeButton={commonStyles.displayNone}>
            <View style={commonStyles.textAlignCenter}>
              <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>Are you sure you want</Text>
              <Text style={[commonStyles.marginBottom15, commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>to delete ?</Text>
            </View>
            <View style={[styles.btnWrapper]}>
              <Button
                title={"Yes"}
                style={[commonStyles.blueBorderButton, commonStyles.marginRight15, styles.buttons]}
                onPress={this.deleteDoctor}
                titleTextStyle={[commonStyles.blueBorderButtonColor, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
              />
              <Button
                title={"No"}
                style={[commonStyles.blueButton, styles.noButton, styles.buttons]}
                onPress={this.closePopScreen}
                titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
              />
            </View>
          </ModalPopUp>
          <CopyRight />
        </ScrollView>
      );
    }
  }
);

const styles = StyleSheet.create({
  table: {
    margin: "0 16px",
    background: "#fff",
    padding: 16,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          padding: 0,
          margin: "2px 0 0",
        };
    },
  },
  block: { display: "block" },
  none: { display: "none" },
  noStyle: {},
  textFieldsHolder: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          flexDirection: "column",
        };
    },
  },
  aadBtn: { minWidth: 100 },
  thead: {
    padding: "10px 16px 10px",
    display: "flex",
    justifyContent: "space-between",
    position: "relative",
    zIndex: 999,
    background: "#dadada",
    borderRadius: "4px 4px 0px 0px",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          display: "none",
        };
    },
  },
  tr: {
    justifyContent: "space-between",
    margin: "0 16px",
    border: "1px solid #dadada",
    borderTop: 0,
    padding: "10px 16px 10px",
    screen: ({ width }) => {
      if (width <= 991)
        return {
          margin: 0,
          borderLeft: 0,
          borderRight: 0,
          flexDirection: "column",
        };
    },
  },
  th: {
    flex: 1,
    flexBasis: "33%",
    maxWidth: "33%",
  },
  td: {
    flex: 1,
    maxWidth: "33%",
    flexBasis: "33%",
  },
  mViewTd: {
    flex: 1,
    maxWidth: "100%",
    flexBasis: "100%",
    paddingRight: 30,
  },
  action: {
    flexBasis: "0%",
    minWidth: 63,
    maxWidth: 63,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginLeft: "auto",
          // marginTop: -32,
          marginBottom: 10,
        };
    },
  },
  providerListComponent: {
    margin: "55px 16px 50px 16px",
    background: "#fff",
    paddingBottom: 16,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          margin: 0, //"265px 16px 50px 16px",
        };
    },
  },
  provideName: {
    marginRight: 8,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginRight: 0,
        };
    },
  },
  enterNPI: {
    marginLeft: 8,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginLeft: 0,
        };
    },
  },
  // providerListComponent: {
  //   marginTop: 197,
  // },

  rightInputHolder: {
    marginRight: 8,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginRight: 0,
          flexBasis: "100%",
        };
      }
    },
  },
  leftInputHolder: {
    marginLeft: 8,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          flexBasis: "100%",
          marginLeft: 0,
        };
      }
    },
  },

  buttonWrapper: {
    justifyContent: "flex-end",
  },
  addBtn: {
    minWidth: 108,
    height: 38,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginRight: 0,
          width: "100%",
        };
      }
    },
  },
  buttonHolderLeft: {
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginRight: 8,
        };
      }
    },
  },
  buttonHolderRight: {
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          marginLeft: 8,
        };
      }
    },
  },
  addDoctorsSection: {
    background: "#fff",
    ...StyleSheet.margin(10, 16, 0, 16),
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          ...StyleSheet.margin(0, 0, 0, 0),
        };
      }
    },
  },
  noRecordsStyle: {
    marginTop: 0,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          marginTop: 240,
        };
    },
  },
  customFontWeight600: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          fontWeight: 600,
        };
    },
  },

  btnWrapper: {
    display: "flex",
    justifyContent: "center",
    margin: "15px 0",
  },
  bodyWrapper: {
    width: 300,
    screen: ({ width }) => {
      if (width <= 991)
        return {
          width: "100vw",
          marginTop: "auto",
          borderRadius: "14px 14px 0 0",
          fontWeight: 600,
        };
    },
  },
  noButton: {
    minWidth: 79,
  },
  customPaddingRight16: {
    paddingRight: 16,
    screen: ({ width }) => {
      if (width <= 992) {
        return {
          paddingRight: 0,
        };
      }
    },
  },
  customMarginRight16: {
    marginRight: 16,
    screen: ({ width }) => {
      if (width <= 992) {
        return {
          marginRight: 0,
        };
      }
    },
  },
  customHelperTextCss: {
    screen: ({ width }) => {
      if (width <= 359) {
        return {
          lineHeight: 12,
          bottom: -26,
        };
      }
    },
  },
  breakWord: {
    wordBreak: "break-word",
  },
  sectionHolder: {
    color: "red",
  },
  customTopMargin: {
    marginTop: 50,
  },
  buttons: {
    height: 40,
  },
  styleClass: {},
});
