import React from "react";
import R14, {
  ScreenLayout,
  Form,
  View,
  Text,
  StyleSheet,
  Touchable,
  SelectMenuField,
  Image,
  TextInputField,
  Button,
  SubmitButton,
  AsyncStorage,
} from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import ModalPopUp from "../components/ModalPopUp";
import FileUploadIcon from "../components/simpleMessage/FileUploadIcon";
import CommonValidators from "../utils/CommonValidators";

const commentsIcon = require("../assets/images/Comments_icon.png");
const deleteIcon = require("../assets/images/delete.png");
const cameraIcon = require("../assets/images/camera_icon.png");
const galleryIcon = require("../assets/images/gallery_icon.png");
const driveIcon = require("../assets/images/drive_icon.png");

export default R14.connect(
  class CreatingMessageScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isAddAttachmentsVisible: false,
        attachments: [],
        isDisable: false,
      };
      this.form = null;
    }

    shouldComponentUpdate(nextProps, nextStates) {
      const { attachments, isAddAttachmentsVisible, isDisable } = this.state;
      if (
        attachments !== nextStates.attachments ||
        isAddAttachmentsVisible !== nextStates.isAddAttachmentsVisible ||
        isDisable !== nextStates.isDisable
      ) {
        return true;
      }
      return false;
    }

    componentDidMount() {
      const { senderPid, billerList } = this.props;
      this.form = this.props.app.ui.form("creatingMessage");
      if (!!senderPid && !!billerList.length) {
        let d = billerList.find((el) => el.label === senderPid);
        this.form.elmts.billerName.setValue({ label: d.value, value: d.label });
        this.form.elmts.recipientPid.setValue({ label: d.label, value: d.value });
      }
    }

    openAttachments = () => {
      this.setState({
        isAddAttachmentsVisible: !this.state.isAddAttachmentsVisible,
      });
    };

    closeAttachmentModal = () => {
      this.setState({
        isAddAttachmentsVisible: false,
      });
    };

    handleSubmit = async (form) => {
      this.setState({ isDisable: true });
      try {
        const { pid } = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
        const { recipientPid, subject, messageBody } = form.values;
        const { attachments } = this.state;
        const data = {
          subject,
          messageBody,
          recipientPid: recipientPid.label,
          recipientName: recipientPid.value,
          attachments: !!attachments.length ? attachments : null,
          payerId: pid,
        };
        let rtpPayload = await this.props.app.dm.simpleMessage.getMessagePayload(data);
        let res = await this.props.app.dm.simpleMessage.postSimpleMessage(rtpPayload);
        if (res && !!res.threadId) {
          this.props.app.dm.commonDomain.showToastr("success", "Message sent to biller successfully");
          let payload = this.props.app.dm.simpleMessage.getMsgPayloadr14({ ...rtpPayload, ...data });
          await this.props.app.dm.simpleMessage.saveMessage(payload);
          this.props.app.nav.to("simpleMessageRoute");
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Message sent to biller unsuccessfully");
        }
      } catch (error) {
        this.props.app.dm.commonDomain.showToastr("error", error);
      }
      return false;
    };

    onFileInputChange = (val) => {
      let x = [val];
      const { attachments } = this.state;

      let existingFiles = attachments.filter((value) => value.filename === val.filename && value.actualsize === val.actualsize);
      if (existingFiles.length > 0) {
        this.props.app.dm.commonDomain.showToastr("error", "File already added.");
        return false;
      }

      let filesSize = attachments.reduce((prevVal, val) => prevVal + val.actualsize, 0);
      filesSize = filesSize + val.actualsize;
      if (filesSize / 1042 / 1024 > 10) {
        this.props.app.dm.commonDomain.showToastr("error", "File size is greater than 10 MB");
        return false;
      }

      if (attachments.length > 4) {
        this.props.app.dm.commonDomain.showToastr("error", "Max 5 attachments are allowed");
        this.setState({ isAddAttachmentsVisible: false });
        return false;
      }
      this.setState({ isAddAttachmentsVisible: false, attachments: [...attachments, ...x] });
    };

    onDeleteFile = (ind) => {
      const { attachments } = this.state;
      this.setState({
        attachments: attachments.filter((value, index, arr) => index !== ind),
      });
    };

    onBillerPid = (val) => {
      const { label, value } = val;
      if (label === "No Results Found") {
        this.form.elmts.recipientPid.setValue(null);
        this.form.elmts.billerName.setValue(null);
      } else {
        this.form.elmts.billerName.setValue({ label: value, value: label });
      }
      this.form.elmts.recipientPid.setError(null);
      this.form.elmts.billerName.setError(null);
    };

    onBillerName = (val) => {
      const { label, value } = val;
      if (label === "No Results Found") {
        this.form.elmts.recipientPid.setValue(null);
        this.form.elmts.billerName.setValue(null);
      } else {
        this.form.elmts.recipientPid.setValue({ label: value, value: label });
      }
      this.form.elmts.billerName.setError(null);
      this.form.elmts.recipientPid.setError(null);
    };

    onCancel = () => {
      this.props.app.nav.to("simpleMessageRoute");
    };

    readText = (val) => {
      if ((!val && !!this.form) || val.length <= 140) {
        this.form.elmts.messageBody.setError(null);
      } else if (val.length > 140) {
        this.form.elmts.messageBody.setError("Character Limit Exceeds 140 Characters");
      }
    };

    readTextForSubject = (val) => {
      if ((!val && !!this.form) || val.length <= 140) {
        this.form.elmts.subject.setError(null);
      } else if (val.length > 140) {
        this.form.elmts.subject.setError("Character Limit Exceeds 140 Characters");
      }
    };

    render() {
      const { billerList, billerNameList, senderPid } = this.props;
      const { attachments } = this.state;
      return (
        <ScreenLayout scrollEnabled style={[styles.creatingMessageScreen]}>
          <View style={[commonStyles.marginTop15, commonStyles.paddingLeftRight16]}>
            <Text style={[commonStyles.heading5, commonFontStyles.workSans, commonStyles.fontWeight500]}>{this.props.app.nav.route.label} </Text>
          </View>
          <View style={[commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.flexGrow1, commonStyles.marginTop15]}>
            <Form
              name="creatingMessage"
              style={[commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.flexGrow1]}
              validateBeforeSubmit={true}
              validators={CommonValidators.getFormValidator()}
              // controlsBottomRight={[]}
              onSubmit={this.handleSubmit}>
              <View>
                {/* <SelectMenuField
                  style={[commonFontStyles.workSans, commonStyles.heading6]}
                  labelTextStyle={[commonStyles.labelText,commonFontStyles.workSans, commonStyles.heading6]}
                  name="billerName"
                  label="Biller Name*"
                  required="Select the Biller Name"
                  items={billerNameList}
                  onValueChange={this.onBillerName}
                  searchable
                  itemLoader={async ({ search }) => {
                    let items = billerNameList;
                    return items
                      .filter((item) => {
                        return item.label.toLowerCase().includes(search.toLowerCase());
                      })
                      .sort((a, b) => b.label - a.label);
                  }}
                  disabled={senderPid ? true : false}
                /> */}
              </View>
              <View>
                {/* <SelectMenuField
                  style={[commonFontStyles.workSans, commonStyles.heading6, commonFontStyles.workSans]}
                  labelTextStyle={[commonStyles.labelText,commonFontStyles.workSans, commonStyles.heading6]}
                  name="recipientPid"
                  label="Biller PID*"
                  required="Select the Biller Pid"
                  items={billerList}
                  onValueChange={this.onBillerPid}
                  searchable
                  itemLoader={async ({ search }) => {
                    let items = billerList;
                    return items
                      .filter((item) => {
                        return item.label.toLowerCase().includes(search.toLowerCase());
                      })
                      .sort((a, b) => b.label - a.label);
                  }}
                  disabled={senderPid ? true : false}
                /> */}
              </View>
              <View>
                <TextInputField
                  name="subject"
                  label="Subject*"
                  required="Enter the Subject"
                  validator="cmtsVali"
                  onChangeText={this.readTextForSubject}
                  style={[commonFontStyles.workSans, commonStyles.heading6]}
                  labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                  labelStyle={[commonStyles.formLabelStyle]}
                />
              </View>
              <View>
                <View style={commonStyles.marginBottom25}>
                  <View style={[commonStyles.dFlex, commonStyles.alignItemsStart]}>
                    {/* <View style={[commonStyles.marginRight15]}>
                      <Image source={commentsIcon} style={[commonStyles.icon35]} />
                    </View> */}
                    <View style={[commonStyles.dFlex, commonStyles.flexGrow1]}>
                      <TextInputField
                        name="messageBody"
                        label="Enter Message*"
                        required="Enter the Message"
                        multiline
                        validator="cmtsVali"
                        onChangeText={this.readText}
                        style={[
                          commonFontStyles.workSans,
                          commonStyles.heading6,
                          styles.commentsField,
                          commonStyles.resizeVertical,
                          commonStyles.margin0,
                        ]}
                        labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                        labelStyle={[commonStyles.formLabelStyle]}
                      />
                    </View>
                  </View>
                </View>
                <View style={commonStyles.marginBottom25}>
                  <Touchable style={[commonStyles.dFlex, commonStyles.alignItemsCenter, commonStyles.marginBottom10]} onPress={this.openAttachments}>
                    <View style={[commonStyles.marginRight15]}>
                      <Image source={commentsIcon} style={[commonStyles.icon35]} />
                    </View>
                    <View>
                      <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.fontWeight500]}>Add Attachments</Text>
                    </View>
                  </Touchable>
                  <View style={[commonStyles.dFlex]}>
                    <View style={commonStyles.flexGrow1}>
                      {attachments && !!attachments.length
                        ? attachments.map((el, i) => (
                            <View style={[styles.attachments]} key={`atta_${i}`}>
                              <View style={[commonStyles.dFlex, commonStyles.borderBottomSpace10]}>
                                <View>
                                  <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight500, commonStyles.dBlock]}>
                                    {el.filename}
                                  </Text>
                                  <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock, commonStyles.lightGray]}>
                                    {el.size}
                                  </Text>
                                </View>
                                <Touchable style={commonStyles.marLAuto} onPress={() => this.onDeleteFile(i)}>
                                  <Image source={deleteIcon} style={commonStyles.icon24} />
                                </Touchable>
                              </View>
                            </View>
                          ))
                        : null}
                    </View>
                  </View>
                </View>
              </View>
              <View style={[commonStyles.marTAuto, commonStyles.dFlex]}>
                <View style={[commonStyles.flexBasis50, commonStyles.marginRight10]}>
                  <Button
                    title={"Cancel"}
                    style={[commonStyles.blueBorderButton, commonStyles.fullWidth]}
                    titleTextStyle={[
                      commonStyles.blueBorderButtonColor,
                      commonStyles.fontWeight600,
                      commonFontStyles.workSans,
                      commonStyles.heading6,
                    ]}
                    onPress={this.onCancel}
                  />
                </View>
                <View style={[commonStyles.flexBasis50, commonStyles.marginLeft10]}>
                  <SubmitButton
                    title={"Submit"}
                    style={[commonStyles.blueButton, commonStyles.fullWidth]}
                    titleTextStyle={[commonStyles.blueButtonColor, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
                    disabled={!billerList.length || this.state.isDisable}
                  />
                </View>
              </View>
            </Form>
          </View>

          <ModalPopUp
            animationType="fade"
            visibility={this.state.isAddAttachmentsVisible}
            isFooter={false}
            bodyWrapper={styles.addAttachmentBodyWrapper}
            modalOuterWrapper={styles.modalBackdrop}
            closeButton={commonStyles.displayNone}
            modalHeading={[commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5, commonStyles.fontWeight500]}
            modalContent={commonStyles.margin0}
            onBackdropPress={() => {
              // this.setState({ isAddAttachmentsVisible: false });
            }}>
            <View style={[styles.sortByHeading, commonStyles.dFlex, commonStyles.flexDirectionColumn, commonStyles.alignItemsCenter]}>
              <Touchable style={styles.closePopup} onPress={this.closeAttachmentModal}></Touchable>
              <View style={commonStyles.marginBottom20}>
                <Text style={[commonStyles.heading5, commonStyles.fontWeight600, commonFontStyles.workSans]}>Choose Attachment Options</Text>
              </View>
            </View>
            <Form name="attachfile" style={[commonStyles.padding0]}>
              <View style={[commonStyles.dFlex, commonStyles.justifyContentSpaceEvenly]}>
                {/* <Touchable style={(commonStyles.textAlignCenter, styles.attachOpt)}>
                  <Image source={cameraIcon} style={[commonStyles.icon48, commonStyles.margin0Auto]} />
                  <View style={commonStyles.marginTop10}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight500, commonStyles.dBlock]}>Phone</Text>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight500, commonStyles.dBlock]}>Storage</Text>
                  </View>
                </Touchable>
                <Touchable style={(commonStyles.textAlignCenter, styles.attachOpt)}>
                  <Image source={galleryIcon} style={[commonStyles.icon48, commonStyles.margin0Auto]} />
                  <View style={commonStyles.marginTop10}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight500, commonStyles.dBlock]}>External</Text>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight500, commonStyles.dBlock]}>Storage</Text>
                  </View>
                </Touchable> */}
                <View style={(commonStyles.textAlignCenter, styles.attachOpt)}>
                  {/* <Image source={driveIcon} style={[commonStyles.icon48]} /> */}
                  <View style={[commonStyles.margin0Auto]}>
                    <FileUploadIcon onValueChange={this.onFileInputChange} fileInputStyle={[commonStyles.margin0Auto]} />
                  </View>
                  <View style={commonStyles.marginTop10}>
                    <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.fontWeight500]}>Drive</Text>
                  </View>
                </View>
              </View>
            </Form>
          </ModalPopUp>
        </ScreenLayout>
      );
    }
  }
);
const styles = StyleSheet.create({
  creatingMessageScreen: {
    background: "#fff",
  },
  modalBackdrop: {
    background: "#00000052",
    overflowX: "auto",
  },
  bodyWrapper: {
    width: 311,
    padding: 15,
    screen: ({ width }) => {
      if (width <= 359)
        return {
          width: 300,
        };
    },
  },
  addAttachmentBodyWrapper: {
    marginTop: "auto",
    borderRadius: "30px 30px 0 0",
    width: "100vw",
    padding: 15,
  },
  commentsField: {
    minHeight: 128,
    resize: "vertical",
  },
  closePopup: {
    width: "20%",
    height: 8,
    borderRadius: 25,
    background: "#e1e1e1",
    marginBottom: 25,
  },
  attachOpt: {
    width: "33.33%",
    textAlign: "center",
  },
  attachments: {
    paddingLeft: 50,
  },
});
