import React from "react";
import { StyleSheet, Colors, View, Text, AnimatedView } from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import ModalPopUp from "../../components/ModalPopUp";
export default class PCHLoaderComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaderVisible: true,
    };
  }

  render() {
    return this.props.isVisiable ? (
      <ModalPopUp
        visibility={this.state.isLoaderVisible}
        isFooter={false}
        bodyWrapper={[styles.isLoaderVisibleWrapper]}
        modalOuterWrapper={styles.modalOuterWrapper}
        heading={""}
        closeButton={commonStyles.displayNone} //{styles.invalidReferenceModalCloseBtn}
        modalHeading={[styles.sessionModelHeaderStyle, commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5]}
        modalContent={commonStyles.marginTop15}>
        <View style={styles.positionRelative}>
          <AnimatedView
            style={styles.loaderCircle}
            animation={{
              from: {
                rotate: "0deg",
                transform: [{ rotate: "80deg" }],
              },
              to: {
                opacity: 1,
                transform: [{ rotate: "360deg" }],
              },
            }}
            timingFunction="ease-in"
            iterationCount="infinite"
            duration={500}>
            <Text style={styles.circleGap}></Text>
          </AnimatedView>
          <View style={[styles.processingText]}>
            <Text style={[commonFontStyles.workSans, commonStyles.heading5, commonStyles.whiteColor, styles.positionRelative]}>Processing</Text>
          </View>
        </View>
      </ModalPopUp>
    ) : null;
  }
}
const styles = StyleSheet.create({
  positionRelative: {
    position: "relative",
  },
  loaderCircle: {
    height: 100,
    width: 100,
    borderRadius: "100%",
    border: "4px solid #fff",
  },
  circleGap: {
    borderRadius: "100%",
    border: "4px solid #00000020",
    display: "block",
    height: 100,
    width: 100,
    borderRadius: "100%",
    borderLeftColor: "#38c2ef",
    margin: -4,
  },
  processingText: {
    position: "absolute",
    left: 0,
    top: 0,
    height: 100,
    width: 100,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  firstDot: {
    position: "absolute",
  },
  secondDot: {
    position: "absolute",
  },
  thirdDot: {
    position: "absolute",
  },
  isLoaderVisibleWrapper: {
    background: "rgba(255,255,255, 0.0)",
    justifyContent: "center",
    alignItems: "center",
    display: "flex",
    border: 0,
  },
});
