import React from "react";
import ModalPopUp from "./../components/ModalPopUp";
import R14, { View, Text, Button, Colors, StyleSheet, AsyncStorage, ScreenLayout, ScrollView } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";
import { USER_TYPE } from "../constant/app.data.constant";
import CopyRight from "../components/CopyRight";
import LeftMenuComponent from "../components/LeftMenuComponent";

export default R14.connect(
  class Logout extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isLogoutVisible: true,
      };
      this.fromRoute = this.props.app.dm.dashboard.state.oldRoute;
    }

    handleLogoutPress = async () => {
      this.props.app.dm.userSession.logout();
      //Handle xbp/pch logout
      const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
      let userType = !!userLoginDetail ? userLoginDetail.userType : null;

      if (userType === USER_TYPE.PCH_PROVIDER || userType === USER_TYPE.PCH_PATIENT) this.props.app.nav.to("pchLogin");
      else this.props.app.nav.to("login");
    };

    closeLogoutScreen = async () => {
      this.props.app.nav.to(this.fromRoute);
    };

    render() {
      const { isLogoutVisible } = this.state;
      const { leftMenuToggle } = this.props.app.dm.userSession.state;
      const { isMobileDevice } = this.props.app.dm.notificationDomain.state;
      return (
        <ScreenLayout scrollEnabled={false} style={[styles.profileViewScreen, commonStyles.padding0]}>
          <ScrollView
            style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu, styles.logoutScreen]}>
            {isMobileDevice ? null : (
              <View style={commonStyles.copyRightSection}>
                <CopyRight />
              </View>
            )}
            <ModalPopUp
              visibility={isLogoutVisible}
              isFooter={false}
              bodyWrapper={styles.bodyWrapper}
              modalOuterWrapper={styles.modalOuterWrapper}
              closeButton={commonStyles.displayNone}>
              <View style={commonStyles.textAlignCenter}>
                <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>Are you sure you want</Text>
                <Text style={[commonStyles.marginBottom15, commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>to logout ?</Text>
              </View>
              <View style={[styles.payNowWrapper]}>
                <Button
                  title={"Yes"}
                  style={[commonStyles.blueBorderButton, commonStyles.marginRight15, styles.buttons]}
                  onPress={this.handleLogoutPress}
                  titleTextStyle={[commonStyles.blueBorderButtonColor, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
                />
                <Button
                  title={"No"}
                  style={[commonStyles.blueButton, styles.buttons]}
                  onPress={this.closeLogoutScreen}
                  titleTextStyle={[styles.btnTexClr, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
                />
              </View>
            </ModalPopUp>
          </ScrollView>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);

const styles = StyleSheet.create({
  profileViewScreen: {
    flex: 0,
    alignItems: "flex-start",
    padding: 15,
    margin: 0,
    height: "100%",
    screen: ({ width }) => {
      if (width <= 640)
        return {
          background: "#fff",
          alignItems: "center",
          margin: 0,
        };
    },
  },
  logoutScreen: {
    display: "flex",
    flexDirection: "column",
    height: "calc(100vh - 60px)",
  },
  bodyWrapper: {
    width: 280,
  },
  modalOuterWrapper: {
    background: "#00000059",
  },
  payNowWrapper: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  btnTexClr: {
    color: "#333",
  },
  buttons: {
    minWidth: "40%",
    height: 40,
  },
  marginBottom5: {
    marginBottom: 5,
  },
});
