import React from "react";
import R14, {
  TextInputField,
  SubmitButton,
  StyleSheet,
  Surface,
  View,
  Text,
  ScreenLayout,
  Form,
  SelectMenuField,
  Touchable,
  Image,
  Button,
  IconButton,
} from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import CommonValidators from "../../utils/CommonValidators";
import { LOGIN_TYPE, USER_TYPE } from "../../constant/app.data.constant";
import RSAencryption from "../../utils/RSAencryption";
import CommonUtility from "../../utils/CommonUtility";
import AddUsers from "../../screens/PCH/AddUsers";

export default R14.connect(
  class PCHSignupComponent extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        enableErrorStyle: false,
        defaultDomain: "#xbp.exela.global",
        socialMediaRegistrationEnabled: this.props.registrationType !== "new" ? true : false,
        currentContextProfileId: "",
        isOpenRegistrationHelpTooltip: false,
      };
      this.form = null;
      this.password = "";
      this.confirmPassword = "";
    }
    componentDidMount() {
      const { pchRegistrationTypePatient, unbounceEmail } = this.props.app.dm.userSession.state;

      this.form = this.props.app.ui.form("registrationForm");
      this.form.elmts.countryCode.setValue({ label: "+1", value: "+1" });
      if (this.state.socialMediaRegistrationEnabled) {
        let password = CommonUtility.randomPassword();
        let form = this.props.app.ui.form("registrationForm");
        form.elmts.newPassword.setValue(password + "@R");
        this.password = password + "@R";
        form.elmts.confirmPassword.setValue(password + "@R");
        this.confirmPassword = password + "@R";
      }
      // Fill data received from google auth
      const { socialLoginProfile, socialLoginType } = this.props.app.dm.userLoginDomain.state;
      if (this.state.socialMediaRegistrationEnabled && socialLoginType === LOGIN_TYPE.GOOGLE) {
        let form = this.props.app.ui.form("registrationForm");
        if (pchRegistrationTypePatient) {
          form.elmts.firstName.setValue(socialLoginProfile.givenName);
          form.elmts.lastName.setValue(socialLoginProfile.familyName);
        } else {
          form.elmts.firstName.setValue(socialLoginProfile.givenName + " " + socialLoginProfile.familyName);
        }
        form.elmts.userEmail.setValue(socialLoginProfile.email);
        this.setState({ currentContextProfileId: socialLoginProfile.googleId });
      } else if (this.state.socialMediaRegistrationEnabled && socialLoginType === LOGIN_TYPE.APPLE) {
        let form = this.props.app.ui.form("registrationForm");
        const { authorization, user = "" } = socialLoginProfile;
        const payload = RSAencryption.AppleTokenDecode(authorization.id_token);
        this.setState({ currentContextProfileId: payload.sub });
        if (user != "") {
          if (pchRegistrationTypePatient) {
            form.elmts.firstName.setValue(user.name.firstName);
            form.elmts.lastName.setValue(user.name.lastName);
          } else {
            form.elmts.firstName.setValue(user.name.firstName + " " + user.name.lastName);
          }

          form.elmts.userEmail.setValue(user.email);
        } else form.elmts.userEmail.setValue(payload.email);
      }
      if (this.state.socialMediaRegistrationEnabled && socialLoginType === LOGIN_TYPE.AMAZON) {
        let form = this.props.app.ui.form("registrationForm");
        form.elmts.firstName.setValue(socialLoginProfile.firstName);
        form.elmts.lastName.setValue(socialLoginProfile.lastName);
        form.elmts.userEmail.setValue(socialLoginProfile.email);
        this.setState({ currentContextProfileId: socialLoginProfile.id });
      }
      if (!!unbounceEmail && !!this.form.elmts.userEmail) this.form.elmts.userEmail.setValue(unbounceEmail);
    }

    handleSubmit = async (form) => {
      this.props.app.ui.progressIndicator.show();

      const { pchRegistrationTypeProvider, pchRegistrationTypePatient } = this.props.app.dm.userSession.state;
      const { socialLoginType } = this.props.app.dm.userLoginDomain.state;
      let payerPhoneNumber = form.elmts.countryCode.value.value + " " + form.elmts.mobileNumber.value.replace(/[^\w\d\s]/g, "").replace(" ", "");
      this.clearAllValidationMessages();
      let validateNameText = this.nameFieldsValidation();
      let validatePassword = false;
      let validateBothPasswords = false;
      let validateAppleMail = false;
      let passwordCriteriaCheck = false;
      if (!!socialLoginType) {
        validateAppleMail = await this.applePrivateEmailValidation();
        validatePassword = true;
        validateBothPasswords = true;
        passwordCriteriaCheck = true;
      } else {
        validateAppleMail = true;
        passwordCriteriaCheck = this.min8Max15(this.password);
        validatePassword = this.passwordValidation();
        validateBothPasswords = this.matchPasswordValidation();
      }

      if (validateNameText && validateAppleMail && validatePassword && validateBothPasswords && passwordCriteriaCheck) {
        let reqBody = {
          firstName: form.elmts.firstName.value,
          lastName: (pchRegistrationTypePatient || this.props.screen === "addUser") && !!form.elmts.lastName.value ? form.elmts.lastName.value : "",
          email: form.elmts.userEmail.value.trim(),
          mobileNumber: payerPhoneNumber,
          password: this.confirmPassword, //form.elmts.confirmPassword.value,
          socialProfileId: this.state.currentContextProfileId,
          userType: pchRegistrationTypeProvider ? USER_TYPE.PCH_PROVIDER : USER_TYPE.PCH_PATIENT,
          loginType: !!socialLoginType ? socialLoginType : LOGIN_TYPE.NORMAL,
          EIN: pchRegistrationTypeProvider && this.props.screen != "addUser" ? form.elmts.EIN.value : "",
          parentUid: this.props.screen === "addUser" ? this.props.parentUID : null,
        };
        try {
          let res = await this.props.app.dm.userRegistration.signup(reqBody);
          if (!!res && !!res.success) {
            this.props.app.dm.commonDomain.showToastr("success", res.message);
            if (this.props.screen === "addUser") {
              this.props.refressList();
            } else {
              this.props.app.ui.progressIndicator.hide({ timeout: 750 });
              if (!!reqBody.socialProfileId && !!res.userLoginDetail && !!res.userLoginDetail.uid) {
                await this.props.app.dm.userSession._processAuthResult(res);
                await this.redirectToReturnUrl();
                return false;
              } else {
                this.props.navPchLogin();
                return false;
              }
            }
          } else {
            this.props.app.dm.commonDomain.showToastr("error", res.message);
            this.props.app.ui.progressIndicator.hide({ timeout: 750 });
            return false;
          }
        } catch (err) {
          this.props.app.dm.commonDomain.showToastr("error", err.message);
          this.props.app.ui.progressIndicator.hide({ timeout: 750 });
          return false;
        }
      } else {
        this.props.app.ui.progressIndicator.hide({ timeout: 750 });
        return false;
      }
    };

    // Clear Validation Message
    clearAllValidationMessages = () => {
      const { pchRegistrationTypeProvider, pchRegistrationTypePatient } = this.props.app.dm.userSession.state;
      let regForm = this.props.app.ui.form("registrationForm");
      regForm.elmts.firstName.setError("");
      if (pchRegistrationTypePatient) regForm.elmts.lastName.setError("");
      regForm.elmts.userEmail.setError("");
      regForm.elmts.mobileNumber.setError("");
      regForm.elmts.newPassword.setError("");
      regForm.elmts.confirmPassword.setError("");
      // } else {
      //    regForm.elmts.password.setError("");
      // }
    };

    resetValues = () => {
      const { pchRegistrationTypeProvider, pchRegistrationTypePatient } = this.props.app.dm.userSession.state;
      let regForm = this.props.app.ui.form("registrationForm");
      regForm.elmts.firstName.setValue("");
      if (pchRegistrationTypePatient) regForm.elmts.lastName.setValue("");
      regForm.elmts.userEmail.setValue("");
      regForm.elmts.mobileNumber.setValue("");
      regForm.elmts.newPassword.setValue("");
      this.password = "";
      regForm.elmts.confirmPassword.setValue("");
      this.confirmPassword = "";
      // } else {
      //    regForm.elmts.password.setError("");
      // }
    };

    // Validate names fields.
    nameFieldsValidation = () => {
      let fName = this.props.app.ui.form("registrationForm").elmts.firstName;
      //  let lName = this.props.app.ui.form("registrationForm").elmts.lastName;
      let isValidFirstName = false;
      // let isValidLastName = false;
      // --- First Name
      if (fName.value !== "") {
        if (/^[a-zA-Z0-9\s]{2,50}$/gi.test(fName.value.trim()) === false) {
          fName.setError("Min 2 - Max 50 characters and 0-9 only allowed");
        } else {
          fName.setError("");
          isValidFirstName = true;
        }
      }
      // --- Last Name
      // if (lName.value !== "") {
      //   if (/^[a-zA-Z0-9\s]{2,50}$/gi.test(lName.value.trim()) === false) {
      //     lName.setError("Min 2 - Max 50 characters and 0-9 only allowed");
      //   } else {
      //     lName.setError("");
      //     isValidLastName = true;
      //   }
      // }
      // if (isValidFirstName && isValidLastName) {
      if (isValidFirstName) {
        return true;
      } else {
        return false;
      }
    };

    trimWhiteSpacesFirstName = (val) => {
      this.form.elmts.firstName.setValue(val.trimStart());
    };

    trimWhiteSpacesLastName = (val) => {
      const { pchRegistrationTypeProvider, pchRegistrationTypePatient } = this.props.app.dm.userSession.state;
      if (pchRegistrationTypePatient) this.form.elmts.lastName.setValue(val.trimStart());
      //  this.form.elmts.lastName.setValue(val.trimStart());
    };

    // Validate password field.
    passwordValidation = () => {
      let formElement = this.props.app.ui.form("registrationForm").elmts.newPassword;
      let passwordTxt = this.password; // formElement.value;
      let isUpper = false;
      let isNumeric = false;
      let isSpecial = false;
      let minMaxLength = false;
      let bContainsSpace = false;
      let bContainsLowerCase = false;

      // Upper Case check
      if (passwordTxt.replace(/[^A-Z]/g, "").length > 0) {
        isUpper = true;
      }
      // Numeric Character check
      if (passwordTxt.replace(/[^0-9]/g, "").length > 0) {
        isNumeric = true;
      }
      // Special Character check
      if (passwordTxt.replace(/[^@$!%*?]/g, "").length > 0) {
        isSpecial = true;
      }
      // Lower case check
      if (passwordTxt.replace(/[^a-z]/g, "").length > 0) {
        bContainsLowerCase = true;
      }
      // Check if any whitespace.
      if (/\s/.test(passwordTxt)) {
        bContainsSpace = true;
      }
      // Length Check
      if (passwordTxt.length >= 8 && passwordTxt.length <= 15) {
        minMaxLength = true;
      }
      if (isUpper && isNumeric && isSpecial && minMaxLength && bContainsLowerCase && !bContainsSpace) {
        formElement.setError("");
        return true;
      } else {
        formElement.setError("Enter Min 8, Max 15 Characters at least 1 Uppercase and 1 lower case, 1 numeric with Special Char @$!%*?");
        return false;
      }
    };

    // Validate both password fields.
    matchPasswordValidation = () => {
      let newPasswordTxt = this.props.app.ui.form("registrationForm").elmts.newPassword;
      let confirmPasswordTxt = this.props.app.ui.form("registrationForm").elmts.confirmPassword;

      let isValid = false;
      if (this.password !== "") {
        if (this.confirmPassword !== "") {
          if (this.password !== this.confirmPassword) {
            confirmPasswordTxt.setError("Entered password not Matched");
            isValid = false;
          } else {
            confirmPasswordTxt.setError("");
            isValid = true;
          }
        } else {
          isValid = false;
        }
      } else {
        isValid = false;
      }
      return isValid;
    };

    // Mobile number validation.
    mobileNumberText(val) {
      let mobNumText = val;
      if (!!val) {
        let cleaned = ("" + mobNumText).replace(/\D/g, "").substring(0, 10);
        this.form.elmts.mobileNumber.setValue(cleaned);
        let match = cleaned.match(/^(1|)?(\d{3})(\d{3})(\d{4})$/);
        if (match) {
          let formattedString = "";
          if (match[1] !== undefined) {
            match[1] = this.form.elmts.countryCode.value.value;
          } else {
            match[1] = "+1 ";
          }
          formattedString = ["(", match[2], ") ", match[3], "-", match[4]].join("");
          this.form.elmts.mobileNumber.setValue(formattedString);
        }
      }
    }

    //NOTE: Not in Use. Validate Username field.
    bUsernameExists = async () => {
      const { pchRegistrationTypePatient } = this.props.app.dm.userSession.state;
      // --- User Name
      let firstName = this.props.app.ui.form("registrationForm").elmts.firstName;
      let lastName = pchRegistrationTypePatient ? this.props.app.ui.form("registrationForm").elmts.lastName : "";

      let generatedPid = firstName.value + "." + lastName.value + this.state.defaultDomain;
      let userDetails = await this.props.app.dm.userLoginDomain.checkUserloginDetail(generatedPid.toLowerCase().trim());
      if (userDetails.length > 0) {
        this.props.app.ui.form("registrationForm").addError("Username exists.");
        return true;
      } else {
        return false;
      }
    };

    // User Mail validation.
    userEmailValidation = async () => {
      // --- User email validation
      let frmElmt = this.props.app.ui.form("registrationForm").elmts.userEmail;
      let userDetails = await this.props.app.dm.userRegistration.bUserExistsByEmail(frmElmt.value);
      if (userDetails.length > 0) {
        frmElmt.setError("Email ID Already Exists");
        return false;
      } else {
        frmElmt.setError("");
        return true;
      }
    };

    //Apple Private validation.
    applePrivateEmailValidation = async () => {
      // --- mg6bydhtm5@privaterelay.appleid.com
      let frmElmt = this.props.app.ui.form("registrationForm").elmts.userEmail;
      let privateEmail = frmElmt.value;
      let res = privateEmail.includes("privaterelay.appleid.com");
      if (res) {
        frmElmt.setError("Please Enter Vaild Email ID.");
        return false;
      } else {
        frmElmt.setError("");
        return true;
      }
    };

    // user PID Password ExistEmail
    userPIDPasswordExistEmail = async () => {
      // --- User email validation
      let frmElmt = this.props.app.ui.form("registrationForm").elmts.userEmail;
      let userDetails = await this.props.app.dm.userRegistration.bUserExistsByEmail(frmElmt.value);
      if (userDetails[0].email === frmElmt.value) return userDetails;
      else return null;
    };

    openRegistrationHelpTooltip = () => {
      this.setState({
        isOpenRegistrationHelpTooltip: !this.state.isOpenRegistrationHelpTooltip,
      });
    };

    redirectToReturnUrl = async () => {
      let returnUrl = this.props.app.route._location.search;
      if (returnUrl.indexOf("?ReturnUrl=") > -1) {
        if (returnUrl.indexOf("medical-bills") > -1) {
          let uid = returnUrl.split("/");
          uid = uid[uid.length - 1];
          this.props.app.nav.to("uploadedBillsRoute", { uid: uid });
          return false;
        } else {
          this.props.app.dm.commonDomain.showToastr("error", "Invalid Bill Reference");
          this.props.app.nav.to("pchProfileViewRoute");
        }
      } else {
        this.props.app.nav.to("pchProfileViewRoute");
      }
    };

    EINValidation(val) {
      if (!!val) {
        let cleaned = val.replace(/\W\D/g, "").substring(0, 15);
        this.props.app.ui.form("registrationForm").elmts.EIN.setValue(cleaned);
      }
    }

    readText = (val) => {
      this.password = val.length === 1 ? val : this.password + val.substr(val.length - 1);
      let text = "";
      for (let i = 0; i < val.length; i++) {
        text = text + "*";
      }
      this.props.app.ui.form("registrationForm").elmts.newPassword.setValue(text);
    };

    readTextConfirmPassword = (val) => {
      this.confirmPassword = val.length === 1 ? val : this.confirmPassword + val.substr(val.length - 1);
      let text = "";
      for (let i = 0; i < val.length; i++) {
        text = text + "*";
      }
      this.props.app.ui.form("registrationForm").elmts.confirmPassword.setValue(text);
    };

    min8Max15 = async (value) => {
      let isValid = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[@$!%*?]).{8,15}$/gi.test(value);
      return isValid
        ? true
        : {
            error: "Enter Min 8 - Max 15 characters with atleast 1 uppercase, 1 lowercase, 1 number and @$!%*? are allowed",
          };
    };

    render() {
      // this.enlistedDomains = [{ label: "rtp.exelatech.com",value: "rtp.exelatech.com" }, { label: "xbp.exela.global", value: "xbp.exela.global" }];
      this.enlistedCountryCode = [
        {
          label: "+1",
          value: "+1",
        },
      ];
      const {
        pchRegistrationTypeProvider,
        pchRegistrationTypePatient,
        facebookMediaLoginPID,
        googleMediaLoginPID,
      } = this.props.app.dm.userSession.state;
      const { formFlexDirection, addUsersFormAlignment, leftInput, rightInput, addUserBtnHolder, adduserBtns, addBtn } = this.props;
      const userLoginDetail = !!localStorage.getItem("profiles") ? JSON.parse(localStorage.getItem("profiles")) : {};
      let userType = !!userLoginDetail ? userLoginDetail.userType : null;

      let isSocialLogin = ["sociallogin"].includes(this.props.registrationType);
      return (
        <Form
          name="registrationForm"
          style={[commonStyles.dFlex, commonStyles.flexDirectionColumn, styles.form, formFlexDirection]}
          onSubmit={this.handleSubmit}
          validateBeforeSubmit={true}
          validators={CommonValidators.getFormValidator()}>
          {/* NOTE: An approach to bunk browser autofill. */}
          <View style={styles.hideField}>
            <TextInputField name="uEmail" label="Email Address" style={styles.hideField} />
          </View>
          <View style={[addUsersFormAlignment, leftInput]}>
            <TextInputField
              name="firstName"
              label={pchRegistrationTypeProvider ? (this.props.screen === "addUser" ? "First Name*" : "Practitioner Name*") : "First Name*"}
              // icon="account"
              required={
                pchRegistrationTypeProvider ? (this.props.screen === "addUser" ? "Enter First Name" : "Enter Practitioner  Name") : "Enter First Name"
              }
              style={[styles.fNameField, commonFontStyles.workSans, commonStyles.heading6]}
              labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
              labelStyle={[commonStyles.formLabelStyle]}
              tooltip="Min 2 - Max 50 characters and  0-9 only allowed"
              validator="min2Max50"
              autoComplete={false}
              onChangeText={this.trimWhiteSpacesFirstName}
              helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
              // disabled={this.state.socialMediaRegistrationEnabled}
            />
          </View>
          {pchRegistrationTypePatient || this.props.screen === "addUser" ? (
            <View style={[addUsersFormAlignment, rightInput, this.props.screen === "addUser" ? null : styles.margintopM15]}>
              <TextInputField
                name="lastName"
                label="Last Name*"
                // required="Enter Provider last name"
                required={pchRegistrationTypeProvider ? "Enter Last Name" : "Enter Last Name"}
                style={[styles.lNameField, commonFontStyles.workSans, commonStyles.heading6]}
                labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                labelStyle={[commonStyles.formLabelStyle]}
                tooltip="Min 2 - Max 50 characters and  0-9 only allowed"
                autoComplete={false}
                validator="min2Max50"
                onChangeText={this.trimWhiteSpacesLastName}
                helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
                // disabled={this.state.socialMediaRegistrationEnabled}
              />
            </View>
          ) : null}
          <View
            style={[
              addUsersFormAlignment,
              userType === USER_TYPE.PCH_PATIENT ? leftInput : this.props.screen === "addUser" ? leftInput : styles.margintopM15,
            ]}>
            <TextInputField
              name="userEmail"
              label="Email ID*"
              required={this.props.screen === "addUser" ? "Enter Email ID" : "Enter Email ID"}
              style={[commonFontStyles.workSans, commonStyles.heading6]}
              labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
              labelStyle={[commonStyles.formLabelStyle]}
              validator="validateEmail"
              disabled={false}
              autoComplete={false}
              helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
              // disabled={this.state.socialMediaRegistrationEnabled}
            />
          </View>
          <View style={this.props.screen === "addUser" || !!pchRegistrationTypePatient ? null : styles.margintopM15}>
            {!!pchRegistrationTypeProvider && this.props.screen != "addUser" ? (
              <TextInputField
                name="EIN"
                label="EIN#*"
                required="Enter EIN"
                style={[commonFontStyles.workSans, commonStyles.heading6]}
                labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                labelStyle={[commonStyles.formLabelStyle]}
                validator="EINValidation"
                // onChangeText={this.EINValidation}
                //valueParser={(val) => parseInt(val)}
                autoComplete={false}
                helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
              />
            ) : null}
          </View>
          {/* NOTE: An approach to bunk browser autofill. */}
          <TextInputField name="mobileNumber" label="Mobile Number" style={styles.hideField} value="checked" />
          {/* TODO: To add drop down listing the International country code and a text box to enter the mobile number.*/}
          <View
            style={[
              addUsersFormAlignment,
              userType === USER_TYPE.PCH_PATIENT ? rightInput : this.props.screen === "addUser" ? rightInput : styles.margintopM15,
            ]}>
            <View style={[commonStyles.dFlex]}>
              <SelectMenuField
                style={[commonFontStyles.workSans, commonStyles.heading6, styles.countryCode]}
                labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                labelStyle={[commonStyles.formLabelStyle]}
                name="countryCode"
                required="Select Country Code"
                items={this.enlistedCountryCode}
                // onValueChange={this.onDomainExtensionChange}
                searchable
                disabled={true}
                itemLoader={async ({ search }) => {
                  let items = this.enlistedCountryCode;
                  return items
                    .filter((item) => {
                      return item.label.toLowerCase().includes(search.toLowerCase());
                    })
                    .sort((a, b) => b.label - a.label);
                }}
                helperTextStyle={[commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
              />
              <TextInputField
                name="mobileNumber"
                label="Mobile Number*"
                required="Enter mobile number"
                style={[commonFontStyles.workSans, commonStyles.heading6]}
                labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading6]}
                labelStyle={[commonStyles.formLabelStyle]}
                validator="max10phoneNoValidator"
                onChangeText={this.mobileNumberText}
                valueParser={(val) => parseInt(val)}
                autoComplete={false}
                helperTextStyle={[styles.customHelperTextStyleMobile, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
              />
            </View>
          </View>
          {/* <TextInputField
                  name="address"
                  label="Address"
                  required="Enter Address"
                  style={[styles.lNameField, commonFontStyles.workSans, commonStyles.heading6]}
                  labelTextStyle={[commonStyles.labelText,commonFontStyles.workSans, commonStyles.heading6]}
                  tooltip="Min 2 - Max 50 characters and  0-9 only allowed"
                  autoComplete={false}
                  validator="min2Max50"
                  onChangeText={this.trimWhiteSpacesLastName}
                  helperTextStyle={styles.customHelperTextStyle}
                  // disabled={this.state.socialMediaRegistrationEnabled}
                /> */}
          {/* SECTION: Social Media Integration */}
          <View
            style={[
              addUsersFormAlignment,
              // userType === USER_TYPE.PCH_PATIENT
              //   ? this.props.screen === "addUser"
              //     ? leftInput
              //     : null
              //   : this.props.screen === "addUser"
              //   ? leftInput
              //   : rightInput,
              this.props.screen === "addUser" || !!isSocialLogin ? leftInput : styles.margintopM15,
            ]}>
            {/* NOTE: An approach to bunk browser autofill. */}
            <TextInputField name="uPassword" label="Password" style={styles.hideField} value="checked" />
            <TextInputField
              name="newPassword"
              label="Password*"
              required="Enter Password"
              // style={[commonFontStyles.workSans, commonStyles.heading5]}
              style={
                this.state.socialMediaRegistrationEnabled ? styles.hideField : [commonFontStyles.workSans, commonStyles.heading5, styles.showField]
              }
              labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading5]}
              labelStyle={[commonStyles.formLabelStyle]}
              secure={false}
              autoComplete={false}
              //validator="min8Max15"
              //validator={(val) => this.min8Max15(this.password)}
              onChangeText={this.readText}
              helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
            />
          </View>
          <View
            style={[
              addUsersFormAlignment,
              // userType === USER_TYPE.PCH_PATIENT
              //   ? this.props.screen === "addUser"
              //     ? rightInput
              //     : null
              //   : this.props.screen === "addUser"
              //   ? rightInput
              //   : leftInput,
              this.props.screen === "addUser" || !!isSocialLogin ? rightInput : styles.margintopM15,
            ]}>
            {/* NOTE: An approach to bunk browser autofill. */}
            <TextInputField name="cPassword" label="Confirm Password" style={styles.hideField} />
            <TextInputField
              name="confirmPassword"
              label="Confirm Password*"
              required="Enter Confirm Password"
              onChangeText={this.readTextConfirmPassword}
              //style={[commonFontStyles.workSans, commonStyles.heading5]}
              style={
                this.state.socialMediaRegistrationEnabled ? styles.hideField : [commonFontStyles.workSans, commonStyles.heading5, styles.showField]
              }
              labelTextStyle={[commonStyles.labelText, commonFontStyles.workSans, commonStyles.heading5]}
              labelStyle={[commonStyles.formLabelStyle]}
              secure={false}
              autoComplete={false}
              //validator="min8Max15"
              helperTextStyle={[styles.customHelperTextStyle, commonStyles.helperTextFontStyle, commonFontStyles.workSans]}
            />
          </View>
          <View style={[addUsersFormAlignment, userType === USER_TYPE.PCH_PATIENT ? leftInput : rightInput]}></View>
          <View
            style={[commonStyles.marTAuto, commonStyles.dFlex, addUserBtnHolder, this.props.screen === "addUser" ? rightInput : styles.margintopM15]}>
            <View style={[commonStyles.flexBasis50, commonStyles.marginRight8, adduserBtns]}>
              <Button
                key="Cancel"
                title="Cancel"
                onPress={this.props.screen === "addUser" ? this.props.handleCancel : this.props.navToWelcomeToPch}
                style={[commonStyles.blueBorderButton, commonStyles.fullWidth]}
                titleTextStyle={[commonStyles.blueBorderButtonColor, commonStyles.fontWeight600, commonFontStyles.workSans, commonStyles.heading6]}
              />
            </View>
            <View style={[commonStyles.flexBasis50, commonStyles.marginLeft8, adduserBtns]}>
              <SubmitButton
                key="register"
                title={this.props.screen === "addUser" ? "Add" : "Register"}
                titleColor="#fff"
                style={[styles.loginButton, commonStyles.blueButton, commonStyles.fullWidth, addBtn]}
                titleTextStyle={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.transformNone, styles.loginButtonTextStyle]}
              />
            </View>
          </View>
        </Form>
      );
    }
  }
);

