import React, { PureComponent } from "react";
import { StyleSheet, Image, View, Text, Touchable } from "../core";
import { commonStyles } from "../assets/css/commonStyle";
import { commonFontStyles } from "../assets/css/commonFontStyles";

export default class RadioButtons extends PureComponent {
  render() {
    const { options, onValueChange, isSelected, radioButtonsClass } = this.props;
    let imgpath =
      isSelected === options.value ? require("../assets/images/selete_icon_active.png") : require("../assets/images/selete_icon_normal.png");
    return (
      <Touchable style={[styles.buttonContainer, radioButtonsClass]} onPress={() => onValueChange(options.value)}>
        <Image resizeMode="contain" style={[styles.imgCls, styles.checkMark]} source={imgpath} />
        <Text style={[commonFontStyles.workSans, commonStyles.heading6]}>{options.label}</Text>
      </Touchable>
    );
  }
}

const styles = StyleSheet.create({
  buttonContainer: {
    display: "flex",
    alignItems: "center",
    flexDirection: "row",
    padding: "6px 8px",
  },
  imgCls: {
    display: "flex",
    marginRight: 10,
  },
  checkMark: {
    height: 20,
    width: 20,
  },
});
