import React from "react";
import R14, {
  ScreenLayout,
  Modal,
  Form,
  View,
  Text,
  StyleSheet,
  Touchable,
  DatePickerField,
  Image,
  TextInputField,
  Button,
  SubmitButton,
  AsyncStorage,
  ScrollView,
} from "../../core";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import FileUploadIcon from "../../components/simpleMessage/FileUploadIcon";
import CommonValidators from "../../utils/CommonValidators";
import AddDoctors from "../../components/PCH/AddDoctors";
import LeftMenuComponent from "../../components/LeftMenuComponent";

const plusSolidIcon = require("../../assets/images/plus_solid.png");

export default R14.connect(
  class ProviderListScreen extends React.Component {
    constructor(props) {
      super(props);
      const { defaultMode } = this.props;
      if (defaultMode === "add") {
        this.props.app.dm.userSession.setState({
          isAddProviderVisible: true,
        });
      }
    }

    toggleProviderFormList = () => {
      this.props.app.dm.userSession.setState({
        isAddProviderVisible: !this.props.app.dm.userSession.state.isAddProviderVisible,
      });
    };

    render() {
      const { providerItems, defaultMode } = this.props;
      const { leftMenuToggle, isAddProviderVisible } = this.props.app.dm.userSession.state;
      return (
        <ScreenLayout scrollEnabled style={[styles.uploadBillsScreen]}>
          <ScrollView style={[commonStyles.dashboardScrollView, leftMenuToggle ? commonStyles.openMenu : commonStyles.closeMenu]}>
            <View style={[commonStyles.marginTop15, commonStyles.paddingLeftRight16, commonStyles.dFlex, commonStyles.justifyContentSpaceBetween]}>
              <View>
                <Text style={[commonStyles.heading5, commonFontStyles.workSans, commonStyles.fontWeight500]}>{this.props.app.nav.route.label} </Text>
              </View>
              {isAddProviderVisible ? null : (
                <Touchable style={[commonStyles.dFlex, commonStyles.alignItemsCenter]} onPress={this.toggleProviderFormList}>
                  <View style={styles.plusIcon}>
                    <Image source={plusSolidIcon} style={[commonStyles.icon24]} />
                  </View>
                  <Text style={[commonFontStyles.workSans, commonStyles.fontWeight400, commonStyles.heading6, styles.custompaddingLeftRight]}>
                    Add Provider
                  </Text>
                </Touchable>
              )}
            </View>
            <View>
              <AddDoctors defaultMode={defaultMode} DisplayType={"Edit"} providerItems={providerItems} />
            </View>
          </ScrollView>
          <LeftMenuComponent />
        </ScreenLayout>
      );
    }
  }
);
const styles = StyleSheet.create({
  uploadBillsScreen: {
    screen: ({ width }) => {
      if (width <= 991)
        return {
          background: "#fff",
        };
    },
  },
  modalBackdrop: {
    background: "#00000052",
    overflowX: "auto",
  },
  bodyWrapper: {
    width: 311,
    padding: 15,
    screen: ({ width }) => {
      if (width <= 359)
        return {
          width: 300,
        };
    },
  },
  addAttachmentBodyWrapper: {
    marginTop: "auto",
    borderRadius: "30px 30px 0 0",
    width: "100vw",
    padding: 15,
  },
  commentsField: {
    minHeight: 128,
    resize: "vertical",
  },
  closePopup: {
    width: "20%",
    height: 8,
    borderRadius: 25,
    background: "#e1e1e1",
    marginBottom: 25,
  },
  attachOpt: {
    textAlign: "center",
    border: "2px dotted #dadada",
    padding: 10,
  },
  attachments: {
    paddingLeft: 50,
  },
  ucountIconDiv: {
    width: 40,
    minWidth: 40,
    height: 40,
    borderRadius: "100%",
    background: "#cff2ff",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    position: "absolute",
    margin: "5px 0px 0px 5px",
  },

  filePassowrdModalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },

  modalWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
    background: "rgba(0, 0, 0, 0.4)",
  },

  filePassowrdModalBodyWrapper: {
    background: "#fff",
    border: "1px solid #a7a7a7",
    padding: "10px",
    width: 350,
    display: "flex",
    flexDirection: "column",
    borderRadius: 4,
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          width: "100vw",
          borderRadius: "15px 15px 0 0",
          marginTop: "auto",
        };
      }
    },
  },
  inputDisable: {
    cursor: "not-allowed",
    pointerEvents: "none",
    opacity: 0.5,
  },
  custompaddingLeftRight: {
    ...StyleSheet.padding(0, 10, 0, 5),
  },
});
