import React from "react";
import R14, { View, Text, Image, StyleSheet, Button, AsyncStorage } from "../../core";
import { APPLE_AUTHKEY } from "../../constant/app.constant";
import { commonStyles } from "../../assets/css/commonStyle";
import { commonFontStyles } from "../../assets/css/commonFontStyles";
import AppleSignin from "react-apple-signin-auth";
import RSAencryption from "../../utils/RSAencryption";
import { SOCIAL_MEDIA_ENTITY, LOGIN_TYPE, USER_TYPE } from "../../constant/app.data.constant";
import PCHLoaderComponent from "../../components/PCH/PCHLoaderComponent";
import ModalPopUp from "../../components/ModalPopUp";

//const APPLE_REDIRECT_URI =  window.location.origin + "/pchlite/login";
const APPLE_REDIRECT_URI = window.location.origin + "/login";

const appleIcon = require("../../assets/images/appleIcon48.png");

export default R14.connect(
  class AppleLogin extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        buttonType: this.props.buttonType,
        buttonClickable: true,
        currentRoute: "",
        isVisible: false,
      };
    }

    componentDidMount() {
      this.setState({
        currentRoute: this.props.app.nav.route.name,
      });
    }

    // SECTION: Route Registration.
    routeToRegistration = (val) => {
      let returnUrl = this.props.app.route._location.search;
      if(this.props.userType === USER_TYPE.XBP)
      {
      	this.props.app.nav.to("registration", { type: `${val.toLowerCase()}${!!returnUrl ? returnUrl : ""}` });
      } else{
        this.props.app.nav.to("pchSignup", { type: `${val.toLowerCase()}${!!returnUrl ? returnUrl : ""}` });
      }
    };

    onSuccess = async (res) => {
      this.setState({ buttonClickable: false });
      this.props.app.dm.userLoginDomain.setState({
        socialLoginProfile: res,
        socialLoginType: LOGIN_TYPE.APPLE,
      });

      const { authorization, user = "" } = res;
      const payload = RSAencryption.AppleTokenDecode(authorization.id_token);
      if(this.props.userType === USER_TYPE.XBP)
      {
          const { userDetailUID } = this.props.app.dm.userLoginDomain.state;
          if (this.state.buttonType === "Profile") {
            const { userDetailUID, payerId } = this.props.app.dm.userLoginDomain.state;
            let reqBody = {
              uid: userDetailUID,
              appleProfileID: payload.sub,
            };
            let apppleEmail = user != "" ? user.email : payload.email;
            // Check same email in user detail
            let emailExist = await this.props.app.dm.userLoginDomain.getLoginDetail({
              email: { eq: apppleEmail.toLowerCase().trim() },
              uid: { eq: userDetailUID },
            });
    
            if (emailExist.length > 0) {
              let res = await this.props.app.dm.userRegistration.updateUserDetails(reqBody, true);
              //.userLoginDomain.socialLoginUpdate(reqBody);
              if (!!res) {
                this.props.app.dm.commonDomain.showToastr("success", "Apple Profile is synced");
                this.setState({ buttonClickable: true });
                this.setState({ buttonType: "ProfileSynced" });
                return false;
              }
            } else {
              this.props.app.dm.commonDomain.showToastr("error", "Email ID not matched");
              this.setState({ buttonClickable: true });
              return false;
            }
          } else {
            const { authorization, user = "" } = res;
            const payload = RSAencryption.AppleTokenDecode(authorization.id_token);
            if (!!payload) {
              try {
                
                if (!!payload.email) {
                  let privateAppleId = payload.email.includes("privaterelay.appleid.com");
                  if (privateAppleId) {
                    this.setState({ isVisible: true });
                    this.setState({ buttonClickable: true });
                    return false;
                  }
                }
                else if (!!!payload.email)
                {
                  this.setState({ isVisible: true });
                  this.setState({ buttonClickable: true });
                  return false;
                }
    
                let apppleEmail = user != "" ? user.email : payload.email;
                let response = await this.props.app.dm.userSession.loginAuth({
                  socialProfileId: payload.sub,
                  username:!!apppleEmail ? apppleEmail.toLowerCase().trim() : apppleEmail,
                  loginType: LOGIN_TYPE.APPLE,
                });
    
                if (!!response) {
                  const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
                  if (!!userLoginDetail) {
                    //NOTE: check obsolute pid
                    if (userLoginDetail.payerId.includes("#") || userLoginDetail.tokenKey === "" || userLoginDetail.tokenKey === undefined) {
                      this.props.app.dm.commonDomain.showToastr("error", "Auth failed due to invalid credentials");
                      this.setState({ buttonClickable: true });
                      return false;
                    }
    
                    if (!!!userLoginDetail.userType) {
                      this.setState({ buttonClickable: true });
                      this.routeToRegistration("existingapple");
                      return false;
                    } else {
                      this.props.app.dm.commonDomain.showToastr("success", "You are successfully logged in");
                      await this.props.redirectToReturnUrl();
                      this.setState({ buttonClickable: true });
                      return false;
                    }
                  }
                  else
                  this.routeToRegistration("SocialLogin");
                }
                else
                  this.routeToRegistration("SocialLogin");
              }
              catch(err)
              {
                this.routeToRegistration("SocialLogin");
              }  
            }else
              this.routeToRegistration("SocialLogin");
          }
      } else {
          if (!!payload) {
            try {

              if (!!payload.email) {
                let privateAppleId = payload.email.includes("privaterelay.appleid.com");
                if (privateAppleId) {
                  this.setState({ isVisible: true });
                  this.setState({ buttonClickable: true });
                  return false;
                }
              }
              else if (!!!payload.email)
              {
                this.setState({ isVisible: true });
                this.setState({ buttonClickable: true });
                return false;
              }

              let apppleEmail = user != "" ? user.email : payload.email;
              let response = await this.props.app.dm.userSession.loginAuth({
                socialProfileId: payload.sub,
                username:!!apppleEmail ? apppleEmail.toLowerCase().trim() : apppleEmail,
                loginType: LOGIN_TYPE.APPLE,
              });

              if (!!response) {
                const userLoginDetail = !!(await AsyncStorage.getItem("profiles")) ? JSON.parse(await AsyncStorage.getItem("profiles")) : {};
                if (!!userLoginDetail) {
                  //NOTE: check obsolute pid
                  if (userLoginDetail.payerId.includes("#") || userLoginDetail.tokenKey === "" || userLoginDetail.tokenKey === undefined) {
                    this.props.app.dm.commonDomain.showToastr("error", "Auth failed due to invalid credentials");
                    this.setState({ buttonClickable: true });
                    return false;
                  }

                  if (!!!userLoginDetail.userType) {
                    this.setState({ buttonClickable: true });
                    this.routeToRegistration("existingapple");
                    return false;
                  } else {
                    this.props.app.dm.commonDomain.showToastr("success", "You are successfully logged in");
                    await this.props.redirectToReturnUrl();
                    this.setState({ buttonClickable: true });
                    return false;
                  }
                } else this.routeToRegistration("SocialLogin");
              } else this.routeToRegistration("SocialLogin");
            } catch (err) {
              this.routeToRegistration("SocialLogin");
            }
          } else this.routeToRegistration("SocialLogin");
      }
    };

    onFailure = (res) => {
      console.log("Login failed:", res);
      this.setState({ buttonClickable: true });
    };

    onClose = async () => {
      this.setState({ isVisible: false });
    };

    render() {
      return (
        <View>
          {
            this.props.userType === USER_TYPE.XBP ? 
            (
              <>
                <AppleSignin
                authOptions={{
                  clientId: APPLE_AUTHKEY,
                  scope: "email name",
                  redirectURI: APPLE_REDIRECT_URI,
                  state: "state",
                  nonce: "nonce",
                  usePopup: true,
                }}
                uiType="dark"
                className="apple-auth-btn"
                noDefaultStyle={false}
                buttonExtraChildren=""
                onSuccess={(res) => this.onSuccess(res)}
                onError={(error) => this.onFailure(error)}
                skipScript={false}
                iconprop={{ style: { marginTop: "10px" } }}
                render={(props) =>
                  this.state.buttonType === "Login" ? (
                    <button
                      {...props}
                      style={
                        this.state.buttonClickable
                          ? { background: "none", padding: 0, border: 0, cursor: "pointer" }
                          : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }
                      }>
                      <Image source={appleIcon} style={commonStyles.icon48} />
                    </button>
                  ) : this.state.buttonType === "Profile" ? (
                    <button
                      {...props}
                      style={
                        this.state.buttonClickable && this.state.currentRoute != "profileEditRoute"
                          ? { background: "none", padding: 0, border: 0, cursor: "pointer" }
                          : this.state.currentRoute === "profileEditRoute"
                          ? { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none" }
                          : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }
                      }>
                      <Image source={appleIcon} style={commonStyles.icon24} />
                    </button>
                  ) : (
                    <button
                      {...props}
                      style={{
                        background: "none",
                        padding: 0,
                        border: "1px solid #dadada",
                        cursor: "pointer",
                        padding: "4px 8px",
                        borderRadius: 50,
                        display: "flex",
                        alignItems: "center",
                        pointerEvents: "none",
                      }}>
                      <Image source={appleIcon} style={[commonStyles.icon24, commonStyles.marginRight4]} />
                      <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.darkGray]}>Synced</Text>
                    </button>
                  )
                }/>
              </>
           ):
           (
              <>
                <AppleSignin
                  authOptions={{
                    clientId: APPLE_AUTHKEY,
                    scope: "email name",
                    redirectURI: APPLE_REDIRECT_URI,
                    state: "state",
                    nonce: "nonce",
                    usePopup: true,
                  }}
                  uiType="dark"
                  className="apple-auth-btn"
                  noDefaultStyle={false}
                  buttonExtraChildren=""
                  onSuccess={(res) => this.onSuccess(res)}
                  onError={(error) => this.onFailure(error)}
                  skipScript={false}
                  iconprop={{ style: { marginTop: "10px" } }}
                  render={(props) =>
                    this.state.buttonType === "Login" ? (
                      // <button {...props}
                      // style={ this.state.buttonClickable ?  { background: "none", padding: 0, border: 0, cursor: "pointer"  } :  { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }}>
                      //   <Image source={appleIcon} style={commonStyles.icon48} />
                      // </button>
                      <button
                        {...props}
                        style={
                          this.state.buttonClickable
                            ? {
                                background: "none",
                                display: "flex",
                                width: "100%",
                                border: "1px solid #dadada",
                                minHeight: 40,
                                alignItems: "center",
                                borderRadius: 5,
                                // justifyContent: "center",
                                cursor: "pointer",
                                paddingLeft: 20,
                              }
                            : {
                                background: "none",
                                display: "flex",
                                width: "100%",
                                border: "1px solid #dadada",
                                minHeight: 40,
                                alignItems: "center",
                                borderRadius: 5,
                                // justifyContent: "center",
                                pointerEvents: "none",
                                opacity: 0.5,
                                paddingLeft: 20,
                              }
                        }>
                        <Image
                          resizeMode="contain"
                          style={[commonStyles.icon24, commonStyles.marginRight10]}
                          source={require("../../assets/images/Apple_logo_black.svg")}
                        />
                        <Text style={[commonFontStyles.workSans, commonStyles.fontWeight500, commonStyles.darkGrayColor, styles.socialLoginText]}>
                          Sign-up/Sign-in with Apple
                        </Text>
                      </button>
                    ) : this.state.buttonType === "Profile" ? (
                      <button
                        {...props}
                        style={
                          this.state.buttonClickable && this.state.currentRoute != "profileEditRoute"
                            ? { background: "none", padding: 0, border: 0, cursor: "pointer" }
                            : this.state.currentRoute === "profileEditRoute"
                            ? { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none" }
                            : { background: "none", padding: 0, border: 0, cursor: "pointer", pointerEvents: "none", opacity: 0.5 }
                        }>
                        <Image source={appleIcon} style={commonStyles.icon24} />
                      </button>
                    ) : (
                      <button
                        {...props}
                        style={{
                          background: "none",
                          padding: 0,
                          border: "1px solid #dadada",
                          cursor: "pointer",
                          padding: "4px 8px",
                          borderRadius: 50,
                          display: "flex",
                          alignItems: "center",
                          pointerEvents: "none",
                        }}>
                        <Image source={appleIcon} style={[commonStyles.icon24, commonStyles.marginRight4]} />
                        <Text style={[commonFontStyles.workSans, commonStyles.smallHeading, commonStyles.darkGray]}>Synced</Text>
                      </button>
                    )
                  }/>
                <PCHLoaderComponent isVisiable={!this.state.buttonClickable} />
                <ModalPopUp

                visibility={this.state.isVisible}
                isFooter={false}
                bodyWrapper={styles.bodyWrapper}
                modalOuterWrapper={styles.modalOuterWrapper}
                heading={" "}
                handleCancel={this.onClose}
                closeButton={styles.closeBtn}
                modalHeading={[]}
                modalContent={commonStyles.marginTop15}>
                <View style={commonStyles.textAlignCenter}>
                  <View style={[styles.sessionModelHeaderStyle]}>
                    <Text style={[commonFontStyles.workSans, commonStyles.textAlignCenter, commonStyles.heading5, commonStyles.fontWeight500]}>
                      Apple Signup!
                    </Text>
                  </View>
                  <Text style={[commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>Please share email id to </Text>
                  <Text style={[commonStyles.marginBottom15, commonFontStyles.workSans, commonStyles.heading6, commonStyles.dBlock]}>
                    signup {this.state.displayAccountNumber}
                  </Text>
                </View>
                <View style={[styles.payNowWrapper]}>
                  <Button
                    title={"Ok"}
                    style={[commonStyles.blueButton, styles.buttons]}
                    onPress={this.onClose}
                    titleTextStyle={[
                      styles.btnTexClr,
                      commonStyles.fontWeight600,
                      commonFontStyles.workSans,
                      commonStyles.heading6,
                      commonStyles.blueButtonColor,
                    ]}
                  />
                </View>
              </ModalPopUp>
              </>
           )}
        </View>
      );
    }
  }
);

const styles = StyleSheet.create({
  bankIcon: {
    marginRight: 10,
  },
  bankLogo: {
    width: 40,
    height: 40,
    display: "block",
    backgroundSize: "contain",
  },
  cardListHolder: {
    top: 50,
    paddingBottom: 60,
  },
  bottomButtons: {
    position: "relative",
    zIndex: 999,
  },
  buttons: {
    padding: 0,
    minWidth: "40%",
  },
  bodyWrapper: {
    width: 280,
  },
  modalOuterWrapper: {
    background: "#00000059",
  },
  payNowWrapper: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  margin15: {
    margin: 15,
  },
  marginLeftTopRight: {
    marginLeft: 15,
    // marginTop:15,
    marginRight: 15,
  },
  sessionModelHeaderStyle: {
    margin: "-45px 0 15px",
  },
  closeBtn: {
    position: "relative",
    zIndex: 999,
  },
  socialLoginText: {
    screen: ({ width }) => {
      if (width <= 991) {
        return {
          fontSize: 12,
        };
      }
    },
  },
});
